import { __ } from "@wordpress/i18n";
import { MediaUpload, MediaUploadCheck } from "@wordpress/block-editor";
import { PanelRow, Button, TextControl } from "@wordpress/components";

//Inner Content Image
const CardHeaderIcon = (props) => {
  const { attributes, setAttributes } = props;
  const { colImgID, colImgURL, colImgAlt } = attributes;

  // Upload image
  const onImageChange = (img) => {
    setAttributes({
      colImgURL: img.url,
      colImgID: img.id,
    });
  };
  // Remove image
  const removeImage = () => {
    setAttributes({
      colImgURL: "",
      colImgID: 0,
      colImgAlt: "",
    });
  };
  return (
    <>
      <PanelRow>{__("Card header icon", "")}</PanelRow>
      <div className="components-base-control image-preview__row">
        <div className="editor-post-featured-image">
          <MediaUploadCheck>
            <MediaUpload
              onSelect={onImageChange}
              allowedTypes={["image"]}
              value={colImgID}
              render={({ open }) => (
                <>
                  {colImgID === 0 ? (
                    <Button
                      className={"editor-post-featured-image__toggle"}
                      onClick={open}
                    >
                      {__("Choose the image", "")}
                    </Button>
                  ) : (
                    <>
                      <Button onClick={open} isPrimary>
                        {__("Replace image", "")}
                      </Button>
                      <Button onClick={removeImage} isSecondary isDestructive>
                        {__("Remove image", "")}
                      </Button>
                    </>
                  )}
                </>
              )}
            ></MediaUpload>
          </MediaUploadCheck>
        </div>
        {colImgURL && (
          <div className="image-preview__img">
            <img src={colImgURL} />
          </div>
        )}
      </div>
      {colImgURL && (
        <>
          <TextControl
            label={__("Image alternate text", "")}
            value={colImgAlt}
            onChange={(colImgAltVal) => {
              setAttributes({
                colImgAlt: colImgAltVal,
              });
            }}
          />
        </>
      )}
    </>
  );
};

export default CardHeaderIcon;
