/**
 * useBlockProps is a React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-block-editor/#useBlockProps
 *
 * RichText is a component that allows developers to render a contenteditable input,
 * providing users with the option to format block content to make it bold, italics,
 * linked, or use other formatting.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/richtext/
 */
import { InspectorControls, useBlockProps } from "@wordpress/block-editor"
const { InnerBlocks } = wp.editor
import { __ } from "@wordpress/i18n"

import {
  TabPanel,
  PanelBody,
  Button,
  Tooltip,
  Modal,
  Panel,
  ToggleControl,
  TextControl,
  __experimentalText as Text,
} from "@wordpress/components"
import { select, withSelect } from "@wordpress/data"
import React, { useState } from "react"
import { useEffect } from "@wordpress/element"
import { Icon, replace, group } from "@wordpress/icons"

import { IGNORE_FROM_BLOCK_TREE, IGNORE_FROM_REPLACING_INNERBLOCKS } from "./lib/constants"

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @param {Object}   param0
 * @param {Object}   param0.attributes
 * @param {string}   param0.attributes.message
 * @param {Function} param0.setAttributes
 * @return {WPElement} Element to render.
 */
const Edit = props => {
  const { attributes, setAttributes, environment, siteUrl } = props
  const { activeTab, enableMobileTab, nonIdenticalBlockStructure, friendlyName } = attributes
  const blockProps = useBlockProps()
  const _ = require("lodash")
  const sha1 = require("js-sha1")
  const [initialTabName] = useState("desktop")

  useEffect(() => {
    if (initialTabName != activeTab) {
      setAttributes({ activeTab: initialTabName })
    }
  }, [])

  const innerBlockProps = {
    template: [["e25m/desktop-content-wrapper"], ["e25m/mobile-content-wrapper"]],
    templateLock: "all",
  }

  // To toggle the active tab
  const onTabSelect = activeTab => {
    setAttributes({ activeTab })
  }

  const [desktopBlockId, mobileBlockId] = select("core/block-editor").getBlockOrder(props.clientId)

  // Clone all the inner content from the source block to the target block
  const cloneFromSourceToTarget = (sourceBlock, targetBlock) => {
    let sourceBlocks = wp.data.select("core/block-editor").getBlockOrder(sourceBlock)
    wp.data
      .dispatch("core/block-editor")
      .duplicateBlocks(sourceBlocks)
      .then(blocks => {
        wp.data.dispatch("core/block-editor").moveBlocksToPosition(blocks, sourceBlock, targetBlock)
      })
  }

  const [desktopTabBlockObjects, setDesktopTabBlockObjects] = useState([])
  const [mobileTabBlockObjects, setMobileTabBlockObjects] = useState([])
  const [isIdentical, setIsIdentical] = useState(false)
  const [isOpen, setIsOpen] = useState(false)

  let desktopTabBlockTreeData = []
  let mobileTabBlockTreeData = []

  const arrayColumn = (array, column) => {
    return array.map(item => item[column])
  }

  // Ignoring the defined blocks from the block trees before the comparison
  const generateBlockTreeArray = (blocks, ignoreFromBlockTree, desktop = false) => {
    const blockTreeArray = []
    const idMap = {}
    blocks.forEach((block, blockIndex) => {
      if (typeof block.clientId !== "undefined") {
        const currentBlockParentIds = wp.data.select("core/block-editor").getBlockParents(block.clientId)
        const currentBlockParents = wp.data.select("core/block-editor").getBlocksByClientId(currentBlockParentIds)

        const currentBlockParentNames =
          currentBlockParents.length && currentBlockParents[0] != null ? arrayColumn(currentBlockParents, "name") : []
        const intersection = _.intersection(ignoreFromBlockTree, currentBlockParentNames)

        let blockClone = _.cloneDeep(block)
        if (desktop) {
          const processBlockRecursively = (blockToProcess, path = blockIndex) => {
            let updatedBlock = blockToProcess
           
            // Have innerBlocks, process them too
            if (Array.isArray(blockToProcess.innerBlocks)) {
              blockToProcess.innerBlocks = blockToProcess.innerBlocks.map((ib, ibx) =>
                processBlockRecursively(ib, `${path}.innerBlocks.${ibx}`)
              )
            }
            // Update ids(re-use if possible)
            if (updatedBlock.clientId) {
              const newId = idMap[updatedBlock.clientId] || sha1.hex(updatedBlock.clientId)
              idMap[updatedBlock.clientId] = newId
              updatedBlock.clientId = newId
            }
            return updatedBlock
          }
          blockClone = processBlockRecursively(blockClone)
        }

        if (
          !ignoreFromBlockTree.includes(blockClone.name) &&
          Array.isArray(intersection) &&
          intersection.length === 0
        ) {
          blockTreeArray[blockIndex] = blockClone
        }
      }
    })
    return blockTreeArray
  }

  const checkIfBlockTreesAreIdentical = (sourceBlock, targetBlock, ignoreFromBlockTree) => {
    desktopTabBlockTreeData = []
    mobileTabBlockTreeData = []

    const desktopTabBlockIds = wp.data.select("core/block-editor").getClientIdsOfDescendants([sourceBlock])
    const desktopTabBlockData = wp.data.select("core/block-editor").getBlocksByClientId(desktopTabBlockIds)

    const mobileTabBlockIds = wp.data.select("core/block-editor").getClientIdsOfDescendants([targetBlock])
    const mobileTabBlockData = wp.data.select("core/block-editor").getBlocksByClientId(mobileTabBlockIds)

    desktopTabBlockTreeData = generateBlockTreeArray(desktopTabBlockData, ignoreFromBlockTree, true)
    setDesktopTabBlockObjects(desktopTabBlockTreeData)

    mobileTabBlockTreeData = generateBlockTreeArray(mobileTabBlockData, ignoreFromBlockTree)
    setMobileTabBlockObjects(mobileTabBlockTreeData)

    let identical = false
    if (desktopTabBlockTreeData?.length && mobileTabBlockTreeData?.length) {
      identical = _.isEqual(arrayColumn(desktopTabBlockTreeData, "name"), arrayColumn(mobileTabBlockTreeData, "name"))
      setIsIdentical(identical)
    }

    return identical
  }

  const replaceAttributesFromSourceToTarget = (ignoreFromReplacing = []) => {
    for (let index = 0; index < desktopTabBlockObjects.length; index++) {
      if (ignoreFromReplacing.includes(desktopTabBlockObjects[index].name)) {
        continue
      }
      if (
        typeof mobileTabBlockObjects[index].clientId !== "undefined" &&
        typeof desktopTabBlockObjects[index].clientId !== "undefined" &&
        typeof desktopTabBlockObjects[index].innerBlocks !== "undefined"
      ) {
        wp.data
          .dispatch("core/block-editor")
          .replaceInnerBlocks(mobileTabBlockObjects[index].clientId, desktopTabBlockObjects[index].innerBlocks)
      }
    }
    setIsOpen(false)
  }

  const configurePopupModal = (sourceBlock, targetBlock, ignoreFromBlockTree = []) => {
    const identical = checkIfBlockTreesAreIdentical(sourceBlock, targetBlock, ignoreFromBlockTree)
    setIsIdentical(identical)
    setIsOpen(true)
  }

  // Generates a custom preview url for the block and calls to an ajax function to load the preview template.
  const blockPreviewModeUrl =
    siteUrl + "/wp-admin/admin-ajax.php?action=preview_mode&post=" + wp.data.select("core/editor").getCurrentPostId()

  return (
    <div {...blockProps} key={blockProps.id}>
      {isOpen && (
        <Modal
          className="mobile-content-modal"
          title={__("Replace content from Desktop", "mobile-content")}
          onRequestClose={e => setIsOpen(false)}
        >
          <p>
            {isIdentical
              ? __(
                  "This will replace all the the content and block settings of the mobile tab from the desktop tab excluding the layout blocks. Are you sure you want to proceed?",
                  "mobile-content"
                )
              : __(
                  "Cannot copy and replace the content since the block trees are not identical in both desktop and mobile tabs",
                  "mobile-content"
                )}
          </p>
          <div className="mobile-content-modal__button-wrapper">
            {isIdentical && (
              <Button
                variant="primary"
                onClick={e => replaceAttributesFromSourceToTarget(IGNORE_FROM_REPLACING_INNERBLOCKS)}
              >
                {__("Copy and replace the content", "mobile-content")}
              </Button>
            )}
            <Button variant="secondary" onClick={e => setIsOpen(false)}>
              {__("Close")}
            </Button>
          </div>
        </Modal>
      )}
      <InspectorControls>
        <Panel>
          <PanelBody title={__("Section Name", "")} initialOpen={true}>
            <TextControl
              value={friendlyName}
              type="text"
              className="block-editor-inserter"
              onChange={val => {
                setAttributes({
                  friendlyName: val || "",
                })
              }}
            />
          </PanelBody>
          {(environment === "development" || (environment === "production" && !nonIdenticalBlockStructure)) && (
            <PanelBody>
              <ToggleControl
                label={__("Enable Mobile content tab", "mobile-content")}
                checked={enableMobileTab}
                onChange={enableMobileTab => {
                  setAttributes({ enableMobileTab })
                  if (!enableMobileTab) {
                    setAttributes({ activeTab: "desktop" })
                  }
                }}
                disabled={nonIdenticalBlockStructure || activeTab === "mobile"}
              />
            </PanelBody>
          )}
          {/* These settings will be displayed only on the Development environment */}
          {environment === "development" && (
            <PanelBody title={__("Advanced Developer Settings", "mobile-content")} initialOpen={false}>
              <ToggleControl
                label={__("Mobile structure different from desktop", "mobile-content")}
                checked={nonIdenticalBlockStructure}
                help={__(
                  "Please enable this option when the block structures of the two blocks are not identical. Ex: A Slider for desktop and an Accordion for mobile",
                  "mobile-content"
                )}
                onChange={nonIdenticalBlockStructure => {
                  setAttributes({ nonIdenticalBlockStructure })
                  if (nonIdenticalBlockStructure) {
                    setAttributes({ enableMobileTab: true })
                  }
                }}
              />
            </PanelBody>
          )}
        </Panel>
      </InspectorControls>
      <div className="mobile-content-tabs">
        <div className="mobile-content-tabs__button-wrapper">
          <div>
            {activeTab === "mobile" && !nonIdenticalBlockStructure && (
              <>
                <Tooltip text={__("Duplicate content from Desktop", "mobile-content")}>
                  <Button variant="secondary" onClick={e => cloneFromSourceToTarget(desktopBlockId, mobileBlockId)}>
                    <Icon icon={group} />
                  </Button>
                </Tooltip>
                <Tooltip text={__("Replace content from desktop", "mobile-content")}>
                  <Button
                    variant="secondary"
                    className="disabled"
                    onClick={e => configurePopupModal(desktopBlockId, mobileBlockId, IGNORE_FROM_BLOCK_TREE)}
                  >
                    <Icon icon={replace} />
                  </Button>
                </Tooltip>
              </>
            )}
            <Tooltip text={__("Mobile preview", "mobile-content")}>
              <Button variant="secondary" href={blockPreviewModeUrl} target="_blank">
                <span className="dashicons dashicons-smartphone"></span>
              </Button>
            </Tooltip>
          </div>
        </div>
        <TabPanel
          activeClass="bs-active-tab"
          orientation="horizontal"
          initialTabName={initialTabName != activeTab ? initialTabName : activeTab}
          onSelect={onTabSelect}
          tabs={[
            {
              name: "desktop",
              title: __("Desktop content", "mobile-content"),
              className: "tab-desktop",
            },
            {
              name: "mobile",
              title: (
                <>
                  {__("Mobile content ", "mobile-content")}
                  <Tooltip
                    position="top right"
                    text={__(
                      "The block tree structures of the two tabs have been identified as non-identical",
                      "mobile-content"
                    )}
                  >
                    <span className="tooltip-icon">?</span>
                  </Tooltip>
                </>
              ),
              className: `tab-mobile ${nonIdenticalBlockStructure && `tab-non-identical`} ${
                !enableMobileTab && `tab-disabled`
              }`,
            },
          ]}
        >
          {tab => (
            <PanelBody>
              <div className={`${tab.className} bs-active`}>
                <InnerBlocks {...innerBlockProps} />
              </div>
            </PanelBody>
          )}
        </TabPanel>
      </div>
    </div>
  )
}

export default withSelect(select => {
  const theme = select("core").getCurrentTheme()
  const siteData = select("core").getSite()
  const { wp_environment_type: environment = "production" } = theme || {}
  const { url: siteUrl } = siteData || {}
  return { environment, siteUrl }
})(Edit)
