import { Fragment } from 'react';

const BackgroundImageAndColorSave = (props) => {
  const {
    deviceSizes,
    deviceSizesForPictureTag,
    backgroundImageAndColorOptions,
    sectionBlockID,
  } = props;

  let backgroundColorstyle = "";
  deviceSizes.map(([text, deviceSize], index) => {
    let backgroundColorSettings =
      backgroundImageAndColorOptions[deviceSize].settings;
    let backgroundColor = backgroundColorSettings.backgroundColor
      ? backgroundColorSettings.backgroundColor
      : "";

    if (backgroundColor) {
      if (deviceSize === "regular") {
        backgroundColorstyle += `.bs-section.bs-section-${sectionBlockID} {background-color: ${backgroundColor};} `;
      } else {
        backgroundColorstyle += `@media only screen and (max-width: ${backgroundImageAndColorOptions[deviceSize].breakpoint}px) {
              .bs-section.bs-section-${sectionBlockID} {
                background-color: ${backgroundColor};}}`;
      }
    }
  });

  if (
    backgroundImageAndColorOptions.regular.settings.sectionImgUrl &&
    backgroundColorstyle
  ) {
    return (
      <Fragment key={sectionBlockID}>
        <style>{backgroundColorstyle}</style>
        <div className="bs-background">
          <picture>
            {deviceSizesForPictureTag.map(([text, deviceSize], index) => {
              if (deviceSize === "regular") {
                return (
                  <img
                    src={
                      backgroundImageAndColorOptions[deviceSize].settings
                        .sectionImgUrl
                    }
                    key={deviceSize}
                  />
                );
              } else {
                if (
                  backgroundImageAndColorOptions[deviceSize].settings
                    .sectionImgUrl
                ) {
                  return (
                    <source
                      srcSet={
                        backgroundImageAndColorOptions[deviceSize].settings
                          .image2xUrl
                          ? `${backgroundImageAndColorOptions[deviceSize].settings.sectionImgUrl}, 
                      ${backgroundImageAndColorOptions[deviceSize].settings.image2xUrl} 2x`
                          : `${backgroundImageAndColorOptions[deviceSize].settings.sectionImgUrl}`
                      }
                      media={`(max-width:${backgroundImageAndColorOptions[deviceSize].breakpoint}px)`}
                      key={deviceSize}
                    ></source>
                  );
                } else {
                  return "";
                }
              }
            })}
          </picture>
        </div>
      </Fragment>
    );
  } else if (backgroundColorstyle) {
    return <style>{backgroundColorstyle}</style>;
  } else if (backgroundImageAndColorOptions.regular.settings.sectionImgUrl) {
    return (
      <Fragment key={sectionBlockID}>
        <div className="bs-background">
          <picture>
            {deviceSizesForPictureTag.map(([text, deviceSize], index) => {
              if (deviceSize === "regular") {
                return (
                  <img
                    src={
                      backgroundImageAndColorOptions[deviceSize].settings
                        .sectionImgUrl
                    }
                    key={deviceSize}
                  />
                );
              } else {
                if (
                  backgroundImageAndColorOptions[deviceSize].settings
                    .sectionImgUrl
                ) {
                  return (
                    <source
                      srcSet={
                        backgroundImageAndColorOptions[deviceSize].settings
                          .image2xUrl
                          ? `${backgroundImageAndColorOptions[deviceSize].settings.sectionImgUrl}, 
                    ${backgroundImageAndColorOptions[deviceSize].settings.image2xUrl} 2x`
                          : `${backgroundImageAndColorOptions[deviceSize].settings.sectionImgUrl}`
                      }
                      media={`(max-width:${backgroundImageAndColorOptions[deviceSize].breakpoint}px)`}
                      key={deviceSize}
                    ></source>
                  );
                } else {
                  return "";
                }
              }
            })}
          </picture>
        </div>
      </Fragment>
    );
  } else {
    return "";
  }
};

export default BackgroundImageAndColorSave;
