const BackgroundImageSave = (props) => {
  const { deviceSizes, backgroundImageOptions, sectionBlockID } = props;
  
  let regularDataSourceKey = '';
  let desktopDataSourceKey = "";
  let mobileDataSourceKey = "";

  { deviceSizes.map(([text, properyName], index) => {
      let backGroundColorSettings = backgroundImageOptions[properyName].settings;
      let sectionImgUrl = backGroundColorSettings.sectionImgUrl
        ? backGroundColorSettings.sectionImgUrl
        : "";
      if (properyName === "regular") {
        regularDataSourceKey = sectionImgUrl;
      } else if (properyName === "desktop") {
        desktopDataSourceKey = sectionImgUrl;
      } else {
        mobileDataSourceKey = sectionImgUrl;
      }
    });
  }

  if (regularDataSourceKey || desktopDataSourceKey || mobileDataSourceKey) {
    return (
      <>
        <style>
          {`.bs-section.bs-section-${sectionBlockID}{ background-image: url("");background-position: center center;
    background-size: cover;} `}
        </style>
        <div
          className="sectionDataSource"
          id={sectionBlockID}
          data-regular-source={regularDataSourceKey}
          data-desktop-source={desktopDataSourceKey}
          data-mobile-source={mobileDataSourceKey}
        ></div>
      </>
    );
  } else {
    return "";
  }
};

export default BackgroundImageSave;

