const BackgroundImageSave = (props) => {
  const { deviceSizes, backgroundImageOptions, columnBlockID } = props;
  return (
    <style>
      {deviceSizes.map(([text, deviceSize], index) => {
        let backgroundImageSettings =
          backgroundImageOptions[deviceSize].settings;
        let columnImgUrl = backgroundImageSettings.columnImgUrl
          ? backgroundImageSettings.columnImgUrl
          : "";
        let image2xUrl = backgroundImageSettings.image2xUrl
          ? backgroundImageSettings.image2xUrl
          : "";
        if (columnImgUrl) {
          let imageSetAttribute = image2xUrl
            ? `url(${columnImgUrl}) 1x, url(${image2xUrl}) 2x`
            : `url(${columnImgUrl}) 1x`;
          if (deviceSize === "regular") {
            return `.bs-column.bs-column-${columnBlockID}{ 
                      background-image: url(${columnImgUrl});
                      background-position: center center;
                      background-size: cover;} `;
          } else {
            return `@media only screen and (max-width: ${backgroundImageOptions[deviceSize].breakpoint}px) {
                      .bs-column.bs-column-${columnBlockID}{ 
                        background-image: url(${columnImgUrl});
                        background-image: 
                        -webkit-image-set(
                          ${imageSetAttribute}
                        );
                      background-image: 
                        image-set(
                          ${imageSetAttribute}
                        );
                        background-position: center center;
                        background-size: cover;} 
                      }`;
          }
        }
      })}
    </style>
  );
};
export default BackgroundImageSave;
