import {
  PanelBody,
  PanelRow,
  ColorPalette,
  Button,
} from "@wordpress/components";
import { MediaUpload, MediaUploadCheck } from "@wordpress/block-editor";
import { __ } from "@wordpress/i18n";

const changeImgAttributeValue = (
  sectionImgId,
  sectionImgUrl,
  sectionImgAlt,
  deviceSize,
  backgroundImageAndColorOptions,
  setAttributes
) => {
  let newValuesObj = backgroundImageAndColorOptions[deviceSize].settings;
  let newValues = {
    sectionImgId,
    sectionImgUrl,
    sectionImgAlt,
  };
  newValuesObj = { ...newValuesObj, ...newValues };

  let optionsNew = backgroundImageAndColorOptions[deviceSize];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    backgroundImageAndColorOptions: {
      ...backgroundImageAndColorOptions,
      [deviceSize]: optionsNew,
    },
  });
};

const change2xImageAttributeValue = (
  image2xId,
  image2xUrl,
  image2xAlt,
  deviceSize,
  backgroundImageAndColorOptions,
  setAttributes
) => {
  let newValuesObj = backgroundImageAndColorOptions[deviceSize].settings;
  let newValues = { image2xId, image2xUrl, image2xAlt };
  newValuesObj = { ...newValuesObj, ...newValues };

  let optionsNew = backgroundImageAndColorOptions[deviceSize];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    backgroundImageAndColorOptions: {
      ...backgroundImageAndColorOptions,
      [deviceSize]: optionsNew,
    },
  });
};

const changeColorAttributeValue = (
  value,
  deviceSize,
  backgroundImageAndColorOptions,
  setAttributes,
  attributeVariable
) => {
  let newValuesObj = backgroundImageAndColorOptions[deviceSize].settings;
  newValuesObj = { ...newValuesObj, [attributeVariable]: value };
  let optionsNew = backgroundImageAndColorOptions[deviceSize];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    backgroundImageAndColorOptions: {
      ...backgroundImageAndColorOptions,
      [deviceSize]: optionsNew,
    },
  });
};

const BackgroundImageAndColorSetting = (props) => {
  const { deviceSizes, backgroundImageAndColorOptions, colors, setAttributes } =
    props;
  return (
    <>
      {deviceSizes.map(([text, deviceSize], index) => {
        const { sectionImgUrl, sectionImgId, image2xId, image2xUrl } =
          backgroundImageAndColorOptions[deviceSize].settings;
        const title =
          deviceSize === "regular"
            ? `${text} (Greater than 1280px)`
            : deviceSize === "desktop"
              ? `${text} (Between 576px and 1280px)`
              : `${text} (Less than 576px)`;
        return (
          <PanelBody title={title} initialOpen={index === 0} key={index}>
            <>
              <ColorPalette
                label={__("Select Background Color", "")}
                colors={colors}
                value={
                  backgroundImageAndColorOptions[deviceSize].settings
                    .backgroundColor
                }
                onChange={(value) =>
                  changeColorAttributeValue(
                    value,
                    deviceSize,
                    backgroundImageAndColorOptions,
                    setAttributes,
                    "backgroundColor"
                  )
                }
              />
              <MediaUploadCheck>
                <MediaUpload
                  allowedType={["image"]}
                  value={sectionImgId}
                  onSelect={(img) => {
                    let sectionImgId = img.id ? img.id : "";
                    let sectionImgUrl = img.url ? img.url : "";
                    let sectionImgAlt = img.alt ? img.alt : "";
                    changeImgAttributeValue(
                      sectionImgId,
                      sectionImgUrl,
                      sectionImgAlt,
                      deviceSize,
                      backgroundImageAndColorOptions,
                      setAttributes
                    );
                  }}
                  render={({ open }) => (
                    <div className="media-upload image-upload" key={sectionImgId}>
                      {sectionImgUrl ? (
                        <Button
                          className="media-button remove-button"
                          onClick={() => {
                            changeImgAttributeValue(
                              "",
                              "",
                              "",
                              deviceSize,
                              backgroundImageAndColorOptions,
                              setAttributes
                            );
                          }}
                        >
                          {__("Remove Image", "berg")}
                        </Button>
                      ) : (
                        <Button
                          className={"media-button upload-button"}
                          onClick={open}
                        >
                          {__("Upload Image", "berg")}
                        </Button>
                      )}
                      <div className="image-thumb-wrapper" onClick={open}>
                        {sectionImgUrl ? (
                          <img
                            className="image-thumb"
                            src={
                              sectionImgUrl !== undefined ? sectionImgUrl : ""
                            }
                          />
                        ) : (
                          ""
                        )}
                      </div>
                    </div>
                  )}
                ></MediaUpload>
              </MediaUploadCheck>
              {/* Settings for 2x size image for responsive images */}
              {deviceSize != "regular" && (
                <MediaUploadCheck>
                  <MediaUpload
                    allowedType={["image"]}
                    value={image2xId}
                    onSelect={(img) => {
                      let image2xId = img.id ? img.id : "";
                      let image2xUrl = img.url ? img.url : "";
                      let image2xAlt = img.alt ? img.alt : "";
                      change2xImageAttributeValue(
                        image2xId,
                        image2xUrl,
                        image2xAlt,
                        deviceSize,
                        backgroundImageAndColorOptions,
                        setAttributes
                      );
                    }}
                    render={({ open }) => (
                      <div className="media-upload image-upload" key={image2xId}>
                        {image2xUrl ? (
                          <Button
                            className="media-button remove-button"
                            onClick={() => {
                              change2xImageAttributeValue(
                                "",
                                "",
                                "",
                                deviceSize,
                                backgroundImageAndColorOptions,
                                setAttributes
                              );
                            }}
                          >
                            {__("Remove 2x Image", "berg")}
                          </Button>
                        ) : (
                          <Button
                            className={"media-button upload-button"}
                            onClick={open}
                          >
                            {__("Upload 2x Image", "berg")}
                          </Button>
                        )}
                        <div className="image-thumb-wrapper" onClick={open}>
                          {image2xUrl ? (
                            <img
                              className="image-thumb"
                              src={image2xUrl !== undefined ? image2xUrl : ""}
                            />
                          ) : (
                            ""
                          )}
                        </div>
                      </div>
                    )}
                  ></MediaUpload>
                </MediaUploadCheck>
              )}
            </>
          </PanelBody>
        );
      })}
    </>
  );
};

export default BackgroundImageAndColorSetting;
