import { __ } from '@wordpress/i18n';
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import { useEffect } from '@wordpress/element';
import { setUniqueIdAttribute } from '~berg/utils';

const Edit = (props) => {
	const blockProps = useBlockProps();

	useEffect(() => {
		setUniqueIdAttribute(props, 'blockID');
	}, [props.attributes]);

	return ( <div {...blockProps} key={blockProps.id}><InnerBlocks templateLock={false} /></div> );
};

export default Edit;
