/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n"
import {
  InspectorControls,
  MediaUpload,
  URLInput,
  MediaUploadCheck,
  __experimentalLinkControl,
  useBlockProps,
} from "@wordpress/block-editor"

import {
  Disabled,
  PanelBody,
  PanelRow,
  TextControl,
  SelectControl,
  Button,
  ToggleControl,
  Dashicon,
  TextareaControl,
  Tooltip,
} from "@wordpress/components"

import React from "react"
import Select from "react-select"
import { setUniqueIdAttribute } from "~berg/utils"
import ServerSideRender from "@wordpress/server-side-render"
import ServerSideRendererError from "~berg/ui-components/server-side-renderer-error"
import { capitalize } from "lodash"

const { useEffect, useState } = wp.element
const { withSelect } = wp.data

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
//import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @param {Object} [props]           Properties passed from the editor.
 * @param {string} [props.className] Class name generated for the block.
 *
 * @return {WPElement} Element to render.
 */

/* TO DO: Mailchimp and Salesforce forms need to be enabled and tested when we get an actual requirement */
import {
  ContactFormSettings,
  HubspotSettings,
  MarketoSettings,
  // MailchimpSettings,
  // SalesforceSettings,
} from "./_partials"

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {}
  const { attributes, setAttributes } = props
  const {
    embeddedFormId,
    formType,
    responseType,
    responseMessage,
    redirectURL,
    linkOpenType,
    downloadFileURL,
    downloadFileID,
    popupVideo,
    customVideoScript,
    popupVideoURL,
    popupVideoUploadURL,
    embeddedFormClass,
    embeddedFormClassNames,
    disableMarketoDefaultFollowUp,
  } = attributes
  // File download
  const onFileChange = img => {
    setAttributes({
      downloadFileURL: img.url,
      downloadFileID: img.id,
    })
  }
  // Remove file
  const removeFile = () => {
    setAttributes({
      downloadFileURL: null,
      downloadFileID: 0,
    })
  }

  //set popup Video
  const setPopupVideo = popupVideoVal => {
    setAttributes({
      popupVideo: popupVideoVal,
    })
  }

  //set popup video URL
  const setPopupVideoURL = popupVideoUrlVal => {
    setAttributes({
      popupVideoURL: popupVideoUrlVal,
    })
  }
  //set popup upload video
  const setPopupVideoUpload = popupVideoUploadVal => {
    setAttributes({
      popupVideoUploadURL: popupVideoUploadVal.url,
    })
  }
  //remove popup upload video
  const removePopupVideoUpload = () => {
    setAttributes({
      popupVideoUploadURL: null,
    })
  }

  // To reload the block ( This option can be used when block rendering fails with a JSON error)
  const reloadBlock = () => {
    setAttributes({
      lastUpdated: Date.now(),
    })
  }

  useEffect(() => {
    setUniqueIdAttribute(props, "embeddedFormId")
  }, [props.attributes])

  const [embeddedFormClasses, setEmbeddedFormClasses] = useState([])
  const [defaultClassValues, setDefaultClassValues] = useState([])

  useEffect(() => {
    const classList = []
    if (coreComponents["bs-embedded-forms"] && coreComponents["bs-embedded-forms"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-embedded-forms"]}`,
        label: "Default",
      })
      props.setAttributes({
        embeddedFormClass: "bs-embedded-forms---default",
      })
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      })
    }

    if (coreComponentsTheme["bs-embedded-forms"] && coreComponentsTheme["bs-embedded-forms"].length > 0) {
      coreComponentsTheme["bs-embedded-forms"].map((className, index) => {
        let classLabel = className.replace("bs-embedded-forms--", "").split("-")
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        })
      })
    }

    const defaultValues = embeddedFormClassNames ? embeddedFormClassNames : [classList[0]]
    setEmbeddedFormClasses(classList)
    setDefaultClassValues(defaultValues)
  }, [JSON.stringify(props.blockClasses)])

  const classNames = embeddedFormClassNames ? embeddedFormClassNames.map(x => x.value).join(" ") : ""

  const blockProps = useBlockProps()

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls initialOpen={true}>
        <PanelBody title={__("Embedded Form Details", "embedded-forms")} initialOpen={true}>
          <PanelRow>
            <SelectControl
              label={__("Form Type", "embedded-forms")}
              value={formType}
              options={[
                { label: "Contact Form 7", value: "cf7" },
                { label: "Hubspot", value: "hubspot" },
                { label: "Marketo", value: "marketo" },
                // { label: "Mailchimp", value: "mailchimp" },
                // { label: "Salesforce", value: "salesforce" },
              ]}
              onChange={formType => {
                setAttributes({ formType: formType })
              }}
            />
          </PanelRow>
          {formType == "cf7" && <ContactFormSettings setAttributes={setAttributes} attributes={attributes} />}
          {formType == "hubspot" && <HubspotSettings setAttributes={setAttributes} attributes={attributes} />}
          {formType == "marketo" && <MarketoSettings setAttributes={setAttributes} attributes={attributes} />}
          {/* {formType == "mailchimp" && (
            <MailchimpSettings
              setAttributes={setAttributes}
              attributes={attributes}
            />
          )}
          {formType == "salesforce" && (
            <SalesforceSettings
              setAttributes={setAttributes}
              attributes={attributes}
            />
          )} */}
        </PanelBody>
        {(formType == "hubspot" || formType == "marketo" || formType == "cf7") && (
          <PanelBody title={__("Response Options", "embedded-forms")} initialOpen={true}>
            {/* No need to use a custom response message for Cf7 since it's customizable from the backend */}
            {formType != "cf7" && (
              <PanelRow>
                <TextControl
                  label={__("Response Message", "embedded-forms")}
                  value={responseMessage}
                  onChange={responseMessage => {
                    setAttributes({ responseMessage: responseMessage })
                  }}
                />
              </PanelRow>
            )}
            <PanelRow>
              <SelectControl
                label={__("Success Action", "embedded-forms")}
                value={responseType}
                options={[
                  { label: "Custom URL", value: "redirect" },
                  { label: "Download", value: "download" },
                  { label: "Video Popup", value: "popup" },
                ]}
                onChange={responseType => {
                  setAttributes({ responseType: responseType })
                }}
              />
            </PanelRow>
            {responseType === "redirect" && (
              <>
                <PanelRow>
                  <URLInput
                    label={__("Internal / External URL", "embedded-forms")}
                    value={redirectURL}
                    onChange={redirectURL => {
                      setAttributes({ redirectURL: redirectURL })
                    }}
                  />
                </PanelRow>
                <PanelRow>
                  <ToggleControl
                    label="Open in a new tab"
                    checked={linkOpenType}
                    onChange={linkOpenType => {
                      setAttributes({ linkOpenType: linkOpenType })
                    }}
                  />
                </PanelRow>
                {formType == "marketo" && (
                  <PanelRow>
                    <ToggleControl
                      label="Disable default follow up"
                      checked={disableMarketoDefaultFollowUp}
                      onChange={disableMarketoDefaultFollowUp => {
                        setAttributes({ disableMarketoDefaultFollowUp })
                      }}
                    />
                  </PanelRow>
                )}
              </>
            )}
            {responseType === "download" && (
              <PanelRow className="download-panel">
                {downloadFileURL ? (
                  <p>{downloadFileURL}</p>
                ) : (
                  <MediaUpload
                    onSelect={onFileChange}
                    allowedTypes={["image", "video", "audio", "application", "text", "multipart"]}
                    value={downloadFileID}
                    render={({ open }) => (
                      <Button className={"button button-large"} onClick={open}>
                        {__("Upload a file", "embedded-forms")}
                      </Button>
                    )}
                  />
                )}
                {downloadFileURL ? (
                  <Button className={"button button-large"} onClick={removeFile}>
                    {__("Remove file", "embedded-forms")}
                  </Button>
                ) : null}
              </PanelRow>
            )}

            {responseType === "popup" && (
              <>
                <PanelRow>
                  <SelectControl
                    label="Video Source"
                    options={[
                      { value: "embedded", label: "Embedded Code" },
                      { value: "url", label: "URL" },
                      { value: "upload", label: "Upload" },
                    ]}
                    value={popupVideo}
                    onChange={setPopupVideo}
                  />
                </PanelRow>
                {popupVideo === "embedded" && (
                  <>
                    <PanelRow>
                      <TextareaControl
                        label={__("Custom Embed Code", "embedded-forms")}
                        help="Copy & Paste the full embed code here"
                        value={customVideoScript}
                        onChange={customVideoScript => {
                          setAttributes({ customVideoScript: customVideoScript })
                        }}
                      />
                    </PanelRow>
                  </>
                )}
                {popupVideo === "upload" && (
                  <PanelRow className="video-popup-video">
                    <MediaUploadCheck>
                      <MediaUpload
                        onSelect={setPopupVideoUpload}
                        allowedTypes={["video"]}
                        render={({ open }) => (
                          <>
                            <Button className={"button button-large"} onClick={open}>
                              {__("Upload Video", "berg")}
                            </Button>
                            {popupVideoUploadURL ? (
                              <div className="videoUploadWrapper">
                                <video width="50" height="50">
                                  <source src={popupVideoUploadURL} type="video/mp4" />
                                </video>
                                <Button className="btn-remove" onClick={removePopupVideoUpload}>
                                  <Dashicon icon="no" size="20" />
                                </Button>
                              </div>
                            ) : null}
                          </>
                        )}
                      />
                    </MediaUploadCheck>
                  </PanelRow>
                )}
                {popupVideo === "url" && (
                  <PanelRow>
                    <TextControl label="Video URL" type="text" value={popupVideoURL} onChange={setPopupVideoURL} />
                  </PanelRow>
                )}
              </>
            )}
          </PanelBody>
        )}
        <PanelBody title={__("Embedded Form Class", "")} initialOpen={false}>
          <Select
            defaultValue={defaultClassValues}
            isMulti
            name="embeddedFormClasses"
            options={embeddedFormClasses}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={val => {
              setAttributes({
                embeddedFormClassNames: val ? val : [],
              })
            }}
          />
        </PanelBody>
      </InspectorControls>
      <div>
        <div className={`bs-embedded-forms ${classNames} bs-embedded-forms-${embeddedFormId}`}>
          {__("Embedded Forms Block  Enter form data from the settings panel", "Embedded Form")}
        </div>
        <div className="button-wrapper">
          <Tooltip text="Reload block">
            <button className="btn btn-default reload" type="button" onClick={reloadBlock}></button>
          </Tooltip>
        </div>
        <Disabled>
          <ServerSideRender
            block="e25m/embedded-forms"
            attributes={attributes}
            httpMethod="POST"
            ErrorResponsePlaceholder={ServerSideRendererError}
          />
        </Disabled>
      </div>
    </div>
  )
}

export default withSelect((select, ownProps) => {
  const theme = select("core").getCurrentTheme()
  const { block_classes: blockClasses = {} } = theme || {}
  return { blockClasses }
})(Edit)
