const GradientSave = (props) => {
  const { deviceSizes, gradientOptions, columnBlockID } = props;
  return (
    <style>
      {deviceSizes.map(([text, propertyName], index) => {
        let gradientSettings = gradientOptions[propertyName].settings;
        let gradientDirection = gradientSettings.gradientDirection
          ? gradientSettings.gradientDirection
          : "";
        let gradientColorOne = gradientSettings.gradientColorOne
          ? gradientSettings.gradientColorOne
          : "";
        let gradientColorTwo = gradientSettings.gradientColorTwo
          ? gradientSettings.gradientColorTwo
          : "";
        let gradientColorOneLocation = gradientSettings.gradientColorOneLocation
          ? `${gradientSettings.gradientColorOneLocation}%`
          : "";
        let gradientColorTwoLocation = gradientSettings.gradientColorTwoLocation
          ? `${gradientSettings.gradientColorTwoLocation}%`
          : "";
        if (gradientColorOne && gradientColorTwo) {
          if (propertyName === "regular") {
            return `.bs-column.bs-column-${columnBlockID} {
                                background-image: linear-gradient(${gradientDirection}deg, ${gradientColorOne} ${gradientColorOneLocation}, ${gradientColorTwo} ${gradientColorTwoLocation});}`;
          } else {
            return `@media only screen and (max-width: ${gradientOptions[propertyName].breakpoint}px) { .bs-column.bs-column-${columnBlockID} {
                                background-image: linear-gradient(${gradientDirection}deg, ${gradientColorOne} ${gradientColorOneLocation}, ${gradientColorTwo} ${gradientColorTwoLocation});} }`;
          }
        }
      })}
    </style>
  );
};

export default GradientSave;
