import { InnerBlocks } from "@wordpress/block-editor";
import Slider from "react-slick";
import { Component, Fragment } from "@wordpress/element";
import { Disabled, Button } from "@wordpress/components";
import RemoveSliderButton from "./remove-slider-button";
import { SlideContext } from "./../utils";
import BlockPreview from "~berg/ui-components/block-preview";

class EditorView extends Component {
	constructor(props) {
		super(props);
		this.setNavSlider = this.setNavSlider.bind(this);
		this.setContentSlider = this.setContentSlider.bind(this);
		this.addNewSlide = this.addNewSlide.bind(this);

		const { navSlides, contentSlides, tabNavsSliderOptions, tabBlockClientId } = props;

		this.state = {
			navSlides,
			contentSlides,
			tabNavsSliderOptions,
			navSlider: null,
			contentSlider: null,
			tabBlockClientId: tabBlockClientId,
		};
	}

	setNavSlider(ref) {
		this.setState({
			navSlider: ref,
		});
	}

	setContentSlider(ref) {
		this.setState({
			contentSlider: ref,
		});
	}

	static getDerivedStateFromProps(props, state) {
		const stateToWatch = (({ tabNavsSliderOptions, navSlides, contentSlides }) => ({
			tabNavsSliderOptions,
			navSlides,
			contentSlides,
		}))(state);

		const propsToWatch = (({ tabNavsSliderOptions, navSlides, contentSlides }) => ({
			tabNavsSliderOptions,
			navSlides,
			contentSlides,
		}))(props);

		return JSON.stringify(propsToWatch) !== JSON.stringify(stateToWatch)
			? Object.assign({}, state, propsToWatch)
			: state;
	}

	setNavSlider(ref) {
		this.setState({
			navSlider: ref,
		});
	}

	setContentSlider(ref) {
		this.setState({
			contentSlider: ref,
		});
	}

	//add new slider
	addNewSlide() {
		let selectedParentBlock = wp.data.select("core/block-editor").getBlock(this.state.tabBlockClientId);
		let selectedParentBlockId = selectedParentBlock.clientId;
		let selectedParentBlockTabCount = selectedParentBlock.innerBlocks.length;
		const newContentObject = wp.blocks.createBlock("e25m/slider-v2-tab");

		// insert block
		wp.data
			.dispatch("core/block-editor")
			.insertBlocks(
				newContentObject,
				selectedParentBlockTabCount,
				selectedParentBlockId,
				this.props.editMode ? true : false
			);

		// Push to array
		const newNavSlides = this.state.navSlides;

		newNavSlides.push(newContentObject);
		this.setState({
			navSlides: newNavSlides,
		});
	}

	render() {
		const sliderSettings = {
			responsive: [
				{
					breakpoint: 9999,
					settings: this.props.editorModeSliderSettings.desktop.settings,
				},
				this.props.editorModeSliderSettings.mobile,
				this.props.editorModeSliderSettings.tablet,
				this.props.editorModeSliderSettings.tabletLandscape,
			].map(settingsGroup => {
				if (settingsGroup.settings && settingsGroup.settings.unslick == true) {
					settingsGroup.settings = "unslick";
				}
				return settingsGroup;
			}),
		};

		const tabSliderClassNames = this.props.tabSliderClassNames.map(x => x.value).join(" ");
		const navSliderClassNames = this.props.tabNavSliderClassNames.map(x => x.value).join(" ");

		const layoutSwitcher = () => (
			<div className="action-bar">
				<Button
					isPrimary
					onClick={() => {
						this.props.setEditMode(!this.props.editMode);
					}}
				>
					{this.props.editMode ? "Switch to preview mode" : "Switch to edit mode"}
				</Button>
			</div>
		);

		const slidesToShow = this.props.tabNavsSliderOptions.desktop.settings.slidesToShow;

		return (
			<div
				id={`bs-slider-${this.props.blockID}`}
				className={`bs-slider ${tabSliderClassNames} ${this.props.editMode ? "edit-mode" : ""} ${this.props.sliderVisibility ? "enable" : "disable"
					}`}
			>
				{this.props.editMode ? (
					<Fragment>
						{layoutSwitcher()}
						<SlideContext.Provider
							value={{
								slides: this.state.navSlides.map(x => x.clientId),
								slidesToShow,
								collapsable: true,
								editMode: this.props.editMode,
							}}
						>
							<div data-slides={slidesToShow}>
								<InnerBlocks allowedBlocks={["e25m/slider-v2-tab"]} />
							</div>
						</SlideContext.Provider>
					</Fragment>
				) : (
					<Fragment>
						{layoutSwitcher()}
						{this.state.navSlides.length ?
							<Slider
								ref={this.setNavSlider}
								{...sliderSettings}
								className={navSliderClassNames}
								asNavFor={this.state.contentSlider}
							>
								{this.state.navSlides.map((slide, index) => {
									if (!slide.attributes.show == true) return;
									return (
										<div className="slick-slide-wrapper" key={index}>
											<RemoveSliderButton
												index={index}
												navSlides={this.state.navSlides}
												removeCurrentSlide={this.props.removeCurrentSlide}
											/>
											<BlockPreview blocks={[slide]} />
										</div>
									);
								})}
							</Slider>
							: null}
						<div className="d-none">
							<Disabled>
								<InnerBlocks />
							</Disabled>
						</div>
					</Fragment>
				)}
			</div>
		);
	}
}

export default EditorView;
