const BackgroundImageAndColorSave = (props) => {
  const {
    deviceSizes,
    deviceSizesForPictureTag,
    backgroundImageAndColorOptions,
    columnBlockID,
  } = props;

  let backgroundColorStyle = "";
  deviceSizes.map(([text, propertyName], index) => {
    let backgroundColorSettings =
      backgroundImageAndColorOptions[propertyName].settings;
    let backgroundColor = backgroundColorSettings.backgroundColor
      ? backgroundColorSettings.backgroundColor
      : "";

    if (backgroundColor) {
      if (propertyName === "regular") {
        backgroundColorStyle += `.bs-column.bs-column-${columnBlockID} {background-color: ${backgroundColor};} `;
      } else {
        backgroundColorStyle += `@media only screen and (max-width: ${backgroundImageAndColorOptions[propertyName].breakpoint}px) {
              .bs-column.bs-column-${columnBlockID} {
                background-color: ${backgroundColor};}}`;
      }
    }
  });

  if (
    backgroundImageAndColorOptions.regular.settings.columnImgUrl &&
    backgroundColorStyle
  ) {
    return (
      <>
        <style>{backgroundColorStyle}</style>
        <div className="bs-background">
          <picture>
            {deviceSizesForPictureTag.map(([text, propertyName], index) => {
              if (propertyName === "regular") {
                return (
                  <img
                    src={
                      backgroundImageAndColorOptions[propertyName].settings
                        .columnImgUrl
                    }
                  />
                );
              } else {
                if (
                  backgroundImageAndColorOptions[propertyName].settings
                    .columnImgUrl
                ) {
                  return (
                    <source
                      srcSet={
                        backgroundImageAndColorOptions[propertyName].settings
                          .image2xUrl
                          ? `${backgroundImageAndColorOptions[propertyName].settings.columnImgUrl}, 
                      ${backgroundImageAndColorOptions[propertyName].settings.image2xUrl} 2x`
                          : `${backgroundImageAndColorOptions[propertyName].settings.columnImgUrl}`
                      }
                      media={`(max-width:${backgroundImageAndColorOptions[propertyName].breakpoint}px)`}
                    ></source>
                  );
                } else {
                  return "";
                }
              }
            })}
          </picture>
        </div>
      </>
    );
  } else if (backgroundColorStyle) {
    return <style>{backgroundColorStyle}</style>;
  } else {
    return (
      <div className="bs-background">
        <picture>
          {deviceSizesForPictureTag.map(([text, propertyName], index) => {
            if (propertyName === "regular") {
              return (
                <img
                  src={
                    backgroundImageAndColorOptions[propertyName].settings
                      .columnImgUrl
                  }
                />
              );
            } else {
              if (
                backgroundImageAndColorOptions[propertyName].settings
                  .columnImgUrl
              ) {
                return (
                  <source
                    srcSet={
                      backgroundImageAndColorOptions[propertyName].settings
                        .image2xUrl
                        ? `${backgroundImageAndColorOptions[propertyName].settings.columnImgUrl}, 
                      ${backgroundImageAndColorOptions[propertyName].settings.image2xUrl} 2x`
                        : `${backgroundImageAndColorOptions[propertyName].settings.columnImgUrl}`
                    }
                    media={`(max-width:${backgroundImageAndColorOptions[propertyName].breakpoint}px)`}
                  ></source>
                );
              }
            }
          })}
        </picture>
      </div>
    );
  }
};

export default BackgroundImageAndColorSave;
