/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import { __experimentalLinkControl } from "@wordpress/block-editor";

import { PanelRow, TextControl, ToggleControl } from "@wordpress/components";

import React from "react";

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
//import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @param {Object} [props]           Properties passed from the editor.
 * @param {string} [props.className] Class name generated for the block.
 *
 * @return {WPElement} Element to render.
 */
const HubspotSettings = (props) => {
  const { attributes, setAttributes } = props;
  const { portalId, hubspotFormId, sfdcCampaignId, regionId, enableCss } =
    attributes;
  return (
    <>
      <PanelRow>
        <TextControl
          label={__("Region", "embedded-forms")}
          value={regionId}
          onChange={(regionId) => {
            setAttributes({ regionId: regionId });
          }}
        />
      </PanelRow>
      <PanelRow>
        <TextControl
          label={__("Portal ID", "embedded-forms")}
          value={portalId}
          onChange={(portalId) => {
            setAttributes({ portalId: portalId });
          }}
        />
      </PanelRow>
      <PanelRow>
        <TextControl
          label={__("Form ID", "embedded-forms")}
          value={hubspotFormId}
          onChange={(hubspotFormId) => {
            setAttributes({ hubspotFormId: hubspotFormId });
          }}
        />
      </PanelRow>
      <PanelRow>
        <TextControl
          label={__("Campaign ID", "embedded-forms")}
          value={sfdcCampaignId}
          onChange={(sfdcCampaignId) => {
            setAttributes({ sfdcCampaignId: sfdcCampaignId });
          }}
        />
      </PanelRow>
      <PanelRow>
        <ToggleControl
          label={__("Enable CSS", "embedded-forms")}
          help={__(
            "This option will remove the iframe and allow you to do custom styling",
            "embedded-forms"
          )}
          checked={enableCss}
          onChange={(enableCss) => {
            setAttributes({ enableCss: enableCss });
          }}
        />
      </PanelRow>
    </>
  );
};

export default HubspotSettings;
