const PictureTagSave = (props) => {
  const { deviceSizesForPictureTag, pictureTagOptions } = props;

  if (pictureTagOptions.regular.settings.columnImgUrl) {
    return (
      <div className="bs-column__image-container">
        <picture>
          {deviceSizesForPictureTag.map(([text, propertyName], index) => {
            if (propertyName === "regular") {
              return (
                <img
                  src={pictureTagOptions[propertyName].settings.columnImgUrl}
                />
              );
            } else {
              if (pictureTagOptions[propertyName].settings.columnImgUrl) {
                return (
                  <source
                    srcSet={
                      pictureTagOptions[propertyName].settings.image2xUrl
                        ? `${pictureTagOptions[propertyName].settings.columnImgUrl}, 
                      ${pictureTagOptions[propertyName].settings.image2xUrl} 2x`
                        : `${pictureTagOptions[propertyName].settings.columnImgUrl}`
                    }
                    media={`(max-width:${pictureTagOptions[propertyName].breakpoint}px)`}
                  ></source>
                );
              } else {
                return "";
              }
            }
          })}
        </picture>
      </div>
    );
  } else {
    return "";
  }
};

export default PictureTagSave;
