const BackgroundColorSave = (props) => {
  const { deviceSizes, backgroundColorOptions, sectionBlockID } = props;
  return (
    <style>
      {deviceSizes.map(([text, deviceSize], index) => {
        let backgroundColorSettings =
          backgroundColorOptions[deviceSize].settings;
        let backgroundColor = backgroundColorSettings.backgroundColor
          ? backgroundColorSettings.backgroundColor
          : "";
        if (backgroundColor) {
          if (deviceSize === "regular") {
            return `.bs-section.bs-section-${sectionBlockID}{ background-color: ${backgroundColor};} `;
          } else {
            return `@media only screen and (max-width: ${backgroundColorOptions[deviceSize].breakpoint}px) {
                  .bs-section.bs-section-${sectionBlockID} {
                    background-color: ${backgroundColor};}}`;
          }
        } else {
          return "";
        }
      })}
    </style>
  );
};

export default BackgroundColorSave;

