/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import React from "react";
import CountUp from "react-countup";
import { RichText } from "@wordpress/block-editor";

/**
 * The save function defines the way in which the different attributes should
 * be combined into the final markup, which is then serialized by the block
 * editor into `post_content`.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#save
 *
 * @return {WPElement} Element to render.
 */
export default function save(props) {
	const { attributes } = props;
	const {
		title,
		titleTag,
		summary,
		decimalPlaces,
		displayThousandSeparator,
		delay,
		start,
		end,
		duration,
		prefixImg,
		postfixImg,
		prefixSelection,
		postfixSelection,
		prefixText,
		postfixText,
		displayOrder,
		counterClassNames
	} = attributes;

	const TitleTag = titleTag;

	const counterOptions = {
		startVal: start,
		endVal: end,
		decimalPlaces: decimalPlaces,
		duration: duration,
		separator: (displayThousandSeparator) ? ',' : ''
	}

	const classNames = counterClassNames
		? counterClassNames.map((x) => x.value).join(" ")
		: "";

	return (
		<>
			{attributes.counterVisibility == true ? (
				<div className={`bs-counter ${classNames}`}>
					<div className="bs-counter__container">
						{ title && (
							<div className="bs-counter__title">
							<TitleTag>{title}</TitleTag>
						</div>
            )}
            <div className="bs-counter__wrapper">
              {prefixSelection === 'image' && prefixImg.url &&
                <div className="bs-counter__prefix">
                  <img src={prefixImg.url} width='25%' />
                </div>
              }
              {prefixSelection === 'text' && prefixText &&
                <div className="bs-counter__prefix">
                  <p>{prefixText}</p>
                </div>
              }
              <CountUp
                start={start == '' ? 0 : start}
                end={end == '' ? 0 : end}
                delay={delay}
                duration={duration}
                decimals={decimalPlaces}
                separator={(displayThousandSeparator) ? ',' : ''}
              >
                {({ countUpRef }) => (
                  <div className="bs-counter__count">
                    <span data-counterup data-options={JSON.stringify(counterOptions)}
                      ref={countUpRef}>{end}</span>
                  </div>
                )}
              </CountUp>
              {postfixSelection === 'image' && postfixImg.url &&
                <div className="bs-counter__postfix">
                  <img src={postfixImg.url} width='25%' />
                </div>
              }
              {postfixSelection === 'text' && postfixText &&
                <div className="bs-counter__postfix">
                  <p>{postfixText}</p>
                </div>
                }
            </div>
						{attributes.summary && (
						<div className="bs-counter__summary">
							<RichText.Content value={attributes.summary} tagName="p" />
						</div>

						)}
						
					</div>
				</div>
			) : (
				null
			)}
		</>
	)
}
