import { __ } from "@wordpress/i18n"
import { InspectorControls, useBlockProps } from "@wordpress/block-editor"

import { Panel, PanelBody, ToggleControl, TextControl } from "@wordpress/components"
import Select from "react-select"
const { useEffect, useState } = wp.element
import LogoImageUploader from "./_partials/logo-image-uploader"
import { capitalize } from "lodash"

const { withSelect } = wp.data

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {}
  const { attributes, setAttributes } = props
  const {
    images,
    colCount,
    mobileItems,
    tplClass,
    slideArrows,
    slideDots,
    autoPlay,
    showCaption,
    sliderEnabled,
    allowLayoutControl,
    variableWidth,
    slidesToScroll,
    mobileSlidesToScroll,
    logoSliderClass,
    logoSliderClassNames,
  } = attributes

  const blockProps = useBlockProps({
    className: "logo-slider",
  })

  /* On gallery Images select function */
  const onSelect = items => {
    setAttributes({
      images: items.map(item => {
        return {
          mediaID: parseInt(item.id, 10),
          mediaURL: item.url,
          alt: item.alt,
          title: item.caption,
        }
      }),
    })
  }

  let mxColumns = images.length // make default max colums and add max columns by uploaded image count

  const [logoSliderClasses, setLogoSliderClasses] = useState([])
  const [defaultClassValues, setDefaultClassValues] = useState([])

  useEffect(() => {
    const classList = []
    if (coreComponents["bs-logo-slider"] && coreComponents["bs-logo-slider"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-logo-slider"]}`,
        label: "Default",
      })
      props.setAttributes({
        logoSliderClass: "bs-logo-slider---default",
      })
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      })
    }
    if (coreComponentsTheme["bs-logo-slider"] && coreComponentsTheme["bs-logo-slider"].length > 0) {
      coreComponentsTheme["bs-logo-slider"].map((className, index) => {
        let classLabel = className.replace("bs-logo-slider--", "").split("-")
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        })
      })
    }
    const defaultValues = logoSliderClassNames ? logoSliderClassNames : [classList[0]]
    setLogoSliderClasses(classList)
    setDefaultClassValues(defaultValues)
  }, [JSON.stringify(props.blockClasses)])

  const classNames = logoSliderClassNames ? logoSliderClassNames.map(x => x.value).join(" ") : ""

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        <Panel>
          <PanelBody title={__("Display Options", "logo-slider")} initialOpen={true}>
            {allowLayoutControl && (
              <>
                <ToggleControl
                  checked={sliderEnabled}
                  onChange={val => setAttributes({ sliderEnabled: val })}
                  label={__("Enable Slider", "logo-slider")}
                />
              </>
            )}

            {sliderEnabled && (
              <>
                <ToggleControl
                  checked={slideArrows}
                  onChange={val => setAttributes({ slideArrows: val })}
                  label={__("Show slider Arrows", "logo-slider")}
                  disabled={!sliderEnabled}
                />
                <ToggleControl
                  checked={slideDots}
                  onChange={val => setAttributes({ slideDots: val })}
                  label={__("Show slider Dots", "logo-slider")}
                  disabled={!sliderEnabled}
                />
                <ToggleControl
                  checked={autoPlay}
                  onChange={val => setAttributes({ autoPlay: val })}
                  label={__("Auto Play Slider", "logo-slider")}
                  disabled={!sliderEnabled}
                />
              </>
            )}

            <ToggleControl
              checked={showCaption}
              onChange={val => setAttributes({ showCaption: val })}
              label={__("Show Logo Caption", "logo-slider")}
            />

            <ToggleControl
              checked={variableWidth}
              onChange={val => setAttributes({ variableWidth: val })}
              label={__("Variable Width", "logo-slider")}
            />

            <TextControl
              label={__("Logo Count", "logo-slider")}
              value={colCount}
              onChange={val =>
                setAttributes({
                  colCount: Number(val),
                  tplClass: val ? "grid-items-" + val : "--",
                })
              }
              type="number"
              min={1}
              max={mxColumns}
              disabled={variableWidth && !sliderEnabled}
            />
            {sliderEnabled && (
              <TextControl
                label={__("Logos to Scroll", "logo-slider")}
                value={slidesToScroll}
                onChange={val =>
                  setAttributes({
                    slidesToScroll: Number(val),
                  })
                }
                type="number"
                min={1}
                max={colCount}
                disabled={variableWidth}
              />
            )}
          </PanelBody>
          <PanelBody title={__("Mobile View Options", "logo-slider")} initialOpen={false}>
            <TextControl
              label={__("Logo Count", "logo-slider")}
              value={mobileItems}
              onChange={val =>
                setAttributes({
                  mobileItems: Number(val),
                })
              }
              type="number"
              min={1}
              max={3}
              disabled={variableWidth && !sliderEnabled}
            />
            {sliderEnabled && (
              <TextControl
                label={__("Logos to Scroll", "logo-slider")}
                value={mobileSlidesToScroll}
                onChange={val =>
                  setAttributes({
                    mobileSlidesToScroll: Number(val),
                  })
                }
                type="number"
                min={1}
                max={3}
                disabled={variableWidth}
              />
            )}
          </PanelBody>
          <PanelBody title={__("Logo Slider Class", "")} initialOpen={false}>
            <Select
              defaultValue={defaultClassValues}
              isMulti
              name="logoSliderClasses"
              options={logoSliderClasses}
              className="basic-multi-select"
              classNamePrefix="select"
              onChange={val => {
                setAttributes({
                  logoSliderClassNames: val ? val : [],
                })
              }}
            />
          </PanelBody>
        </Panel>
      </InspectorControls>
      <div>
        {images.length >= 1 ? (
          <div className={`logo-slider-inner ${classNames}`}>
            {images.map(item => (
              <div className="logo__item" key={"image-" + item.mediaID} style={{ width: `calc(100% / ${colCount})` }}>
                <div className="logo__item__inner">
                  <img
                    src={item.thumbnail || item.mediaURL}
                    alt={item.alt ? item.alt : " "}
                    title={item.caption ? item.caption : " "}
                    className="logo__item--image"
                  />
                  {showCaption && <div>{item.title}</div>}
                </div>
              </div>
            ))}
          </div>
        ) : (
          <p>
            Please add logos to Logo slider <br />
            <small>Better if you can add Same dimension for all Logo Images</small>
          </p>
        )}

        <LogoImageUploader mediaIDs={images.map(item => item.mediaID)} onSelect={onSelect} />
      </div>
    </div>
  )
}

export default withSelect((select) => {
  const theme = select("core").getCurrentTheme()
  const { block_classes: blockClasses = {} } = theme || {}
  return { blockClasses }
})(Edit)
