import { __ } from '@wordpress/i18n';
import { InspectorControls, useBlockProps } from '@wordpress/block-editor';
import { useEffect, useState } from '@wordpress/element';
import {
	PanelBody,
	PanelRow,
	TextControl,
	ToggleControl,
} from '@wordpress/components';
import Select from 'react-select';
import {
	CommonSettings,
	NavSliderSettings,
	ContentSliderSettings,
	EditorView,
} from './_partials';
import {
	getElementClasses,
	setSliderSettingAttribute as updateResponsiveAttributes,
} from './utils';
import { setUniqueIdAttribute } from '~berg/utils';
const { withSelect } = wp.data;

const Edit = (props) => {
	const { coreComponents = {}, coreComponentsTheme = {} } =
		props.blockClasses || {};
	const { attributes, setAttributes, clientId } = props;
	const {
		tabSliderVisibility,
		blockID,
		tabSliderClassNames,
		tabNavSliderClassNames,
		tabContentSliderClassNames,
		tabNavsSliderOptions,
		tabContentsSliderOptions,
		identifier,
		editorModeTabNavsSliderSettings,
		editorModeTabContentsSliderSettings,
		enableprogressBar,
	} = attributes;

	useEffect(() => {
		setUniqueIdAttribute(props, 'blockID');
	}, [props.attributes]);

	const [tabSliderClasses, setTabSliderClasses] = useState([]);
	const [defaultTabClassValues, setDefaultTabClassValues] = useState([]);
	const [navSlides, setNavSlides] = useState([]);
	const [contentSlides, setContentSlides] = useState([]);
	const [editMode, setEditMode] = useState(false);
	const tabBlockClientId = clientId;
	const blockProps = useBlockProps();

	useEffect(() => {
		getElementClasses(
			'bs-tab-slider',
			tabSliderClassNames,
			coreComponents,
			coreComponentsTheme,
			(classes, defaultValues) => {
				setTabSliderClasses(classes);
				setDefaultTabClassValues(defaultValues);
			}
		);
	}, [JSON.stringify(props.blockClasses)]);

	// Update Mobile, Tablet and Table Landscape setting(s) through cloned copies
	const setSliderSettingAttribute = (attribute, layout, property, value) => {
		updateResponsiveAttributes(
			attributes,
			setAttributes,
			attribute,
			layout,
			property,
			value
		);
	};

	const { innerBlocks } = wp.data
		.select('core/block-editor')
		.getBlock(clientId)
		? wp.data.select('core/block-editor').getBlock(clientId)
		: {};
		
	useEffect(() => {
		if (Array.isArray(innerBlocks) && innerBlocks.length) {
			const navSlides = [],
				contentSlides = [];
			innerBlocks.forEach((tabBlock) => {
				if (tabBlock.attributes && tabBlock.attributes.show == false) {
					return;
				}

				tabBlock.innerBlocks.forEach((innerBlockOfTab) => {
					if (innerBlockOfTab.name === 'e25m/tab-slider-v2-tab-nav') {
						navSlides.push(innerBlockOfTab);
					} else {
						contentSlides.push(innerBlockOfTab);
					}
				});
			});
			setNavSlides(navSlides);
			setContentSlides(contentSlides);
		} else {
			const defaultLength = 3;

			for (let i = 0; i < defaultLength; i++) {
				const parentTabBlock = wp.blocks.createBlock(
					'e25m/tab-slider-v2-tab'
				);

				// Insert block
				wp.data
					.dispatch('core/block-editor')
					.insertBlocks(
						parentTabBlock,
						i + 1,
						tabBlockClientId,
						true
					);

				// Push to array
				const newNavObject = wp.blocks.createBlock(
					'e25m/tab-slider-v2-tab-nav'
				);
				const newContentObject = wp.blocks.createBlock(
					'e25m/tab-slider-v2-tab-content'
				);

				// Insert child block
				wp.data
					.dispatch('core/block-editor')
					.insertBlocks(newNavObject, 0, parentTabBlock, true);
				wp.data
					.dispatch('core/block-editor')
					.insertBlocks(newContentObject, 1, parentTabBlock, true);

				navSlides.push(newNavObject);
				contentSlides.push(newContentObject);
			}
		}
	}, [editMode, innerBlocks]);

	const deviceSizes = [
		['Desktop', 'desktop'],
		['IPad', 'tablet'],
		['IPad Landscape', 'tabletLandscape'],
		['Mobile', 'mobile'],
	];

	const FieldTypes = {
		Range: 'range',
		Toggle: 'toggle',
		Number: 'number',
		Select: 'select',
		Text: 'text',
	};

	const removeCurrentSlide = (index) => {
		const currentClientId = navSlides[index].clientId;
		const parentClientId = wp.data
			.select('core/block-editor')
			.getBlockRootClientId(currentClientId);
		wp.data.dispatch('core/block-editor').removeBlocks(parentClientId);

		setNavSlides(navSlides.filter((slide, i) => i !== index));
		setContentSlides(contentSlides.filter((slide, i) => i !== index));
	};

	return (
		<div {...blockProps} key={blockProps.id}>
			<InspectorControls>
				<PanelBody title={__('Visibility', '')} initialOpen={false}>
					<ToggleControl
						label="Enable/Disable"
						checked={tabSliderVisibility}
						onChange={(val) => {
							setAttributes({ tabSliderVisibility: val });
						}}
					/>
				</PanelBody>
				<PanelBody
					title={__('Editor control', 'tab-slider-v2')}
					initialOpen={true}
				>
					<PanelRow>
						<TextControl
							label={__(
								'Name to identify this slider easily',
								'tab-slider-v2'
							)}
							value={identifier}
							onChange={(value) => {
								setAttributes({ identifier: value });
							}}
							max={25}
						/>
					</PanelRow>
					<PanelRow>
						<ToggleControl
							checked={editMode}
							onChange={setEditMode}
							label={__('Edit Mode', 'tab-slider-v2')}
						/>
					</PanelRow>
					<PanelRow>
						<ToggleControl
							checked={enableprogressBar}
							onChange={(val) => {
								setAttributes({ enableprogressBar: val });
							}}
							label={__('Progress Line', 'tab-slider-v2')}
						/>
					</PanelRow>
				</PanelBody>
				<CommonSettings
					{...{
						setSliderSettingAttribute,
						deviceSizes,
						tabNavsSliderOptions,
						tabContentsSliderOptions,
					}}
				/>
				<NavSliderSettings
					{...{
						tabNavSliderClassNames,
						setSliderSettingAttribute,
						setAttributes,
						deviceSizes,
						tabNavsSliderOptions,
						tabContentsSliderOptions,
						navSlides,
						contentSlides,
						FieldTypes,
					}}
				/>
				<ContentSliderSettings
					{...{
						tabContentSliderClassNames,
						setSliderSettingAttribute,
						setAttributes,
						deviceSizes,
						tabNavsSliderOptions,
						tabContentsSliderOptions,
						navSlides,
						contentSlides,
						FieldTypes,
					}}
				/>

				<PanelBody
					title={__('Tab slider classes', 'tab-slider-v2')}
					initialOpen={false}
				>
					<Select
						defaultValue={defaultTabClassValues}
						isMulti
						name="sectionClasses"
						options={tabSliderClasses}
						className="basic-multi-select"
						classNamePrefix="select"
						onChange={(val) => {
							setAttributes({
								tabSliderClassNames: val ? val : [],
							});
						}}
					/>
				</PanelBody>
			</InspectorControls>
			<div>
				<EditorView
					{...{
						blockID,
						identifier,
						navSlides,
						contentSlides,
						tabNavsSliderOptions,
						tabContentsSliderOptions,
						tabSliderClassNames,
						tabNavSliderClassNames,
						tabContentSliderClassNames,
						tabBlockClientId,
						removeCurrentSlide,
						editorModeTabNavsSliderSettings,
						editorModeTabContentsSliderSettings,
						editMode,
						setEditMode,
						tabs: innerBlocks,
						tabSliderVisibility,
					}}
				/>
			</div>
		</div>
	);
};

export default withSelect((select) => {
	const theme = select('core').getCurrentTheme();
	const { block_classes: blockClasses = {} } = theme || {};
	return { blockClasses };
})(Edit);
