import { __ } from '@wordpress/i18n';
import { InnerBlocks } from '@wordpress/block-editor';
import { InspectorControls, useBlockProps } from '@wordpress/block-editor';
import {
  PanelRow,
  PanelBody,
  ToggleControl,
  TextControl,
} from '@wordpress/components';
import { Fragment, useContext, useState, useEffect } from '@wordpress/element';
import { TabContext } from './../utils';
import { setUniqueIdAttribute } from '~berg/utils';

const Edit = (props) => {
  const { attributes, setAttributes, clientId } = props;
  const [collapsed, setCollapsed] = useState(true);
  const tabContexts = useContext(TabContext);
  const rootClientId = wp.data
    .select('core/block-editor')
    .getBlockRootClientId(clientId);
  const { editMode, tabs } = tabContexts[rootClientId] || { editMode: null, tabs: [] };
  const { show, tabName, enableCustomDots, customDotHtml } = attributes;
  const blockProps = useBlockProps();

  useEffect(() => {
    setUniqueIdAttribute(props, 'blockID');
  }, [props.attributes]);

  const isEditMode = editMode && editMode == true;
  const TabId =
    isEditMode && Array.isArray(tabs) ? tabs.indexOf(clientId) + 1 : '';

  useEffect(() => {
    if (isEditMode && tabName === '') {
      setAttributes({ tabName: `Tab ${TabId}` });
    }
  }, []);

  const editorLayout = () =>
    show ? (
      <InnerBlocks
        template={[
          ['e25m/tab-slider-v2-tab-nav', {}],
          ['e25m/tab-slider-v2-tab-content', {}],
        ]}
        templateLock={true}
      />
    ) : (
      <Fragment>
        <div className="blurred-content" />
        <InnerBlocks
          template={[
            ['e25m/tab-slider-v2-tab-nav', {}],
            ['e25m/tab-slider-v2-tab-content', {}],
          ]}
          templateLock={true}
        />
      </Fragment>
    );

  return (
    <div {...blockProps} key={blockProps.id}>
      {
        isEditMode ? (
          <>
            <InspectorControls>
              <PanelBody
                title={__('Tab settings', 'tab-slider-v2')}
                initialOpen={true}
              >
                <PanelRow>
                  <ToggleControl
                    label={__(
                      'Show slide in the slider',
                      'tab-slider-v2'
                    )}
                    checked={show === true}
                    onChange={(isChecked) => {
                      setAttributes({ show: isChecked });
                      setCollapsed(true);
                    }}
                  />
                </PanelRow>
                <PanelRow>
                  <TextControl
                    label="Tab name"
                    value={tabName}
                    onChange={(value) => {
                      setAttributes({ tabName: value });
                    }}
                  />
                </PanelRow>
                <ToggleControl
                  label={__(
                    'Allow custom HTML for slider dot',
                    'tab-slider-v2'
                  )}
                  checked={enableCustomDots === true}
                  onChange={(isChecked) => {
                    setAttributes({ enableCustomDots: isChecked });
                  }}
                />
                {enableCustomDots && (
                  <>
                    <TextControl
                      label={__('Slider dot custom HTML')}
                      value={customDotHtml}
                      onChange={(customDotHtmlVal) => {
                        setAttributes({
                          customDotHtml: customDotHtmlVal,
                        });
                      }}
                    />
                  </>
                )}
              </PanelBody>
            </InspectorControls>
            <div className={`collapsible ${collapsed ? 'collapsed' : ''}`}>
              <div className="collapsible-control">
                <a
                  onClick={(e) => {
                    e.preventDefault();
                    setCollapsed(!collapsed);
                  }}
                >
                  {tabName}
                </a>
              </div>
              <div className="collapsible-body">{editorLayout()}</div>
            </div>
          </>
        ) : (editorLayout())
      }
    </div>
  );
};

export default Edit;
