import { __ } from "@wordpress/i18n";
import { InnerBlocks } from "@wordpress/block-editor";
import { InspectorControls, useBlockProps } from "@wordpress/block-editor";
import { useState, useContext } from "@wordpress/element";
import { PanelBody, ToggleControl, TextControl } from "@wordpress/components";
import { Fragment, useEffect } from "@wordpress/element";
import { SlideContext } from "./../utils";
import { setUniqueIdAttribute } from "~berg/utils";

const Edit = (props) => {
  const { attributes, setAttributes, clientId } = props;
  const { show, blockID, enableCustomDots, customDotHtml } = attributes;
  const { editMode, slidesToShow, slides, collapsable } =
    useContext(SlideContext);
  const [collapsed, setCollapsed] = useState(true);
  const blockProps = useBlockProps();

  useEffect(() => {
    setUniqueIdAttribute(props, "blockID");
  }, [props.attributes]);

  const isEditMode = editMode && editMode == true;
  const SlideId =
    isEditMode && Array.isArray(slides) ? slides.indexOf(clientId) + 1 : "";

  const editorLayout = () =>
    show == true ? (
      <InnerBlocks templateLock={false} />
    ) : (
      <Fragment>
        <div className="blurred-content" />
        <InnerBlocks templateLock={false} />
      </Fragment>
    );

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        <PanelBody title={__("Tab settings", "tab-slider-v2")} initialOpen={true}>
          <ToggleControl
            label={__("Show slide in the slider", "tab-slider-v2")}
            checked={show === true}
            onChange={(isChecked) => {
              setAttributes({ show: isChecked });
            }}
          />
          <ToggleControl
            label={__("Allow custom HTML for slider dot", "tab-slider-v2")}
            checked={enableCustomDots === true}
            onChange={(isChecked) => {
              setAttributes({ enableCustomDots: isChecked });
            }}
          />
          {enableCustomDots && (
            <>
              <TextControl
                label={__("Slider dot custom HTML")}
                value={customDotHtml}
                onChange={(customDotHtmlVal) => {
                  setAttributes({ customDotHtml: customDotHtmlVal });
                }}
              />
            </>
          )}
        </PanelBody>
      </InspectorControls>
      {
        isEditMode && collapsable ? (
          <div className={`collapsable ${collapsed ? "collapsed" : ""}`}>
            <div className="collapsable-control">
              <a
                onClick={(e) => {
                  e.preventDefault();
                  setCollapsed(!collapsed);
                }}
              >
                Slide {SlideId}
              </a>
            </div>
            <div className="collapsable-body">{editorLayout()}</div>
          </div>
        ) : (
          // (
          //   isEditMode ? (
          //   <div style={{ width: `calc(100% / ${slidesToShow})` }}>{editorLayout()}</div>
          //   )
          editorLayout()
        )
      }
    </div>
  );
};

export default Edit;
