import { Component } from '@wordpress/element';

class NavAddNewSlide extends Component {
	constructor(props) {
		super(props);

		const { addNewSlide } = props;
	}

	render() {
		return (
			<div>
				<button
					className="btn btn-default"
					type="button"
					onClick={() => this.props.addNewSlide()}
				>
					Add New Tab
				</button>
			</div>
		);
	}
}

export default NavAddNewSlide;
