import apiFetch from "@wordpress/api-fetch"
import Select from "react-select"
import { withSelect } from "@wordpress/data"

const { __ } = wp.i18n
const { SelectControl, TextControl, ResponsiveWrapper, ToggleControl, DateTimePicker, Button } = wp.components
const { PluginDocumentSettingPanel } = wp.editPost
const { registerPlugin } = wp.plugins
const { Component } = wp.element
URLInput
const { URLInput } = wp.blockEditor
// const { __experimentalLinkControl } = wp.blockEditor;
// const LinkControl = __experimentalLinkControl;
const { MediaUpload, MediaUploadCheck } = wp.editor
let getPostsTimeout = null

class FeaturedImageField extends Component {
  constructor(props) {
    super(props)
    this.state = {
      imageId: this.getMeta("featured_image") || 0,
      imageObj: null,
    }
  }

  componentWillMount() {
    if (this.state.imageId) {
      apiFetch({
        path: "/wp/v2/media/" + this.state.imageId,
      }).then(data => this.setState({ imageObj: data }))
    }
    if (this.state.fileId) {
      apiFetch({
        path: "/wp/v2/media/" + this.state.fileId,
      }).then(data => this.setState({ fileObj: data }))
    }
  }

  getMeta(name) {
    const postMeta = wp.data.select("core/editor").getEditedPostAttribute("meta")
    return postMeta && name in postMeta ? postMeta[name] : null
  }

  onSelect(field, value) {
    // Save value to metadata.
    wp.data.dispatch("core/editor").editPost({
      meta: {
        [field]: value,
      },
    })
  }

  render() {
    if ("page" === this.state.postType) {
      return null
    }

    return (
      <PluginDocumentSettingPanel name="custom-featured-image" title={__("Featured Image", "")} initialOpen={true}>
        <div className="components-base-control">
        <div className="editor-post-featured-image media-upload">
            <MediaUploadCheck>
              <MediaUpload
                onSelect={value => {
                  value["media_details"] = {
                    width: value.width,
                    height: value.height,
                  }
                  value["source_url"] = value.url
                  this.setState({
                    featured_image: value.id,
                    imageId: value.id,
                    imageObj: value,
                  })
                  this.onSelect("featured_image", value.id)
                }}
                value={this.state.imageId}
                allowedTypes={["image"]}
                render={({ open }) => (
                  <>
                    <Button
                      className={
                        this.state.imageId === 0
                          ? "editor-post-featured-image__toggle"
                          : "editor-post-featured-image__preview"
                      }
                      onClick={open}
                    >
                      {this.state.imageId === 0 && __("Choose the Featured Image", "")}
                      {this.state.imageObj && (
                        <ResponsiveWrapper
                          naturalWidth={this.state.imageObj.media_details.width}
                          naturalHeight={this.state.imageObj.media_details.height}
                        >
                          <img src={this.state.imageObj.source_url} />
                        </ResponsiveWrapper>
                      )}
                    </Button>

                    {this.state.imageId !== 0 && (
                      <div className="editor-post-featured-image__actions">
                        <Button onClick={open} isPrimary className="editor-post-featured-image__action">
                          {__("Replace", "")}
                        </Button>
                        <Button
                          onClick={value => {
                            this.setState({
                              featured_image: 0,
                              imageId: 0,
                              imageObj: null,
                            })
                            this.onSelect("featured_image", 0)
                          }}
                          isSecondary
                          isDestructive
                          className="editor-post-featured-image__action"
                        >
                          {__("Remove", "")}
                        </Button>
                      </div>
                    )}
                  </>
                )}
              />
            </MediaUploadCheck>
          </div>
        </div>
      </PluginDocumentSettingPanel>
    )
  }
}

export default FeaturedImageField

registerPlugin("custom-featured-image", {
  render: FeaturedImageField,
  icon: "editor-table",
})
