const PictureTagSave = (props) => {
  const { deviceSizesForPictureTag, pictureTagOptions } = props;

  if (pictureTagOptions.regular.settings.divImgUrl) {
    return (
      <div className="bs-div__image-container">
        <picture>
          {deviceSizesForPictureTag.map(([text, deviceSize], index) => {
            if (deviceSize === "regular") {
              return (
                <img
                  src={pictureTagOptions[deviceSize].settings.divImgUrl}
                  alt={pictureTagOptions[deviceSize].settings.divImgAlt}
                  key={deviceSize}
                />
              );
            } else {
              if (pictureTagOptions[deviceSize].settings.divImgUrl) {
                return (
                  <source
                    srcSet={
                      pictureTagOptions[deviceSize].settings.image2xUrl
                        ? `${pictureTagOptions[deviceSize].settings.divImgUrl}, 
                    ${pictureTagOptions[deviceSize].settings.image2xUrl} 2x`
                        : `${pictureTagOptions[deviceSize].settings.divImgUrl}`
                    }
                    media={`(max-width:${pictureTagOptions[deviceSize].breakpoint}px)`}
                    key={deviceSize}
                  ></source>
                );
              } else {
                return "";
              }
            }
          })}
        </picture>
      </div>
    );
  } else {
    return "";
  }
};

export default PictureTagSave;
