import { __ } from "@wordpress/i18n";
import {
  RichText,
  InnerBlocks,
  InspectorControls,
  useBlockProps,
} from "@wordpress/block-editor";

import { PanelBody, ToggleControl } from "@wordpress/components";
import { useSelect } from "@wordpress/data";

import CardHeaderIcon from "./_partials/card-header-icon";
import TagSelector from "~berg/ui-components/tag-selector";
import { useEffect } from 'react';

const edit = (props) => {
  const { attributes, setAttributes, clientId, context } = props;
  const {
    colHeading,
    colPrefix,
    colImgURL,
    showCard = true,
    titleTag,
  } = attributes;

  //Set title tag
  const setTitleTag = (titleTagVal) => {
    setAttributes({
      titleTag: titleTagVal,
    });
  };

  const rootClientId = wp.data
    .select("core/block-editor")
    .getBlockRootClientId(clientId); // get root client id
  const blockIndex = wp.data
    .select("core/block-editor")
    .getBlockIndex(clientId, rootClientId); // get block blockIndex for increment accordion data
  const collapseId = `ap-${rootClientId}-${blockIndex}`;

  useEffect(() => {
    setAttributes({ colPrefix: collapseId });
  }, []);

  const cardBlocks = useSelect((select) =>
    select("core/block-editor")
      .getBlock(rootClientId)
      .innerBlocks.filter((obj) => {
        return obj.name === "e25m/advance-accordion-card";
      })
  );
  const cardBlockIndex = cardBlocks.findIndex((x) => x.clientId === clientId);
  const blockProps = useBlockProps();

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls initialOpen={true}>
        <PanelBody title={__("Accordion card settings", "")} initialOpen={true}>
          <CardHeaderIcon
            setAttributes={setAttributes}
            attributes={attributes}
          />
          <ToggleControl
            label={__("Card Visibility", "")}
            checked={showCard}
            onChange={(showCard) => {
              setAttributes({ showCard });
            }}
          />
          <TagSelector
            label={__("Title Tag", "berg")}
            value={titleTag}
            onChange={setTitleTag}
          />
        </PanelBody>
      </InspectorControls>
      <div
        className={`accordion__block__card ${!showCard && "bs-fade"}`}
        key={`acc-${blockIndex}`}
      >
        <div
          className="accordion__block__card-header"
          id={`heading${blockIndex}`}
        >
          <h5 className="mb-0">
            <button
              className="accordion__block__btn btn-link"
              data-toggle="collapse"
              data-target={`#${collapseId}`}
              aria-expanded={blockIndex == 0 ? "true" : "false"}
              aria-controls={collapseId}
            >
              {colImgURL && (
                <div className="accordion__block__card-header-icon">
                  <img src={colImgURL} />
                </div>
              )}
              <RichText
                value={colHeading}
                onChange={(val) => setAttributes({ colHeading: val })}
                placeholder={`Accordion Heading ${cardBlockIndex + 1} ...`}
                tagName={titleTag}
              />
            </button>
          </h5>
        </div>
        <div
          id={collapseId}
          className={`accordion__block__desc collapse ${
            blockIndex == 0 ? "show" : ""
          }`}
          aria-labelledby={`heading${blockIndex}`}
          data-parent={`#bs_aa-${rootClientId}`}
        >
          <div className={`card-body ${context["e25m/floatClassName"]}`}>
            <InnerBlocks
              template={[
                ["e25m/advance-accordion-body-left", {}],
                ["e25m/advance-accordion-body-right", {}],
              ]}
              templateLock={true}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default edit;
