const BackgroundImageSave = (props) => {
  const { deviceSizes, backgroundImageOptions, divBlockID } = props;
  return (
    <style>
      {deviceSizes.map(([text, deviceSize], index) => {
        let backGroundImageSettings =
          backgroundImageOptions[deviceSize].settings;
        let divImgUrl = backGroundImageSettings.divImgUrl
          ? backGroundImageSettings.divImgUrl
          : "";
        let image2xUrl = backGroundImageSettings.image2xUrl
          ? backGroundImageSettings.image2xUrl
          : "";
        if (divImgUrl) {
          let imageSetAttribute = image2xUrl
            ? `url(${divImgUrl}) 1x, url(${image2xUrl}) 2x`
            : `url(${divImgUrl}) 1x`;
          if (deviceSize === "regular") {
            return `.bs-div.bs-div-${divBlockID} {background-image: url(${divImgUrl}); background-position: center center;
    background-size: cover;} `;
          } else {
            return `@media only screen and (max-width: ${backgroundImageOptions[deviceSize].breakpoint}px) {.bs-div.bs-div-${divBlockID} {background-image: url(${divImgUrl}); 
            background-image: 
            -webkit-image-set(
              ${imageSetAttribute}
            );
          background-image: 
            image-set(
              ${imageSetAttribute}
            ); 
            background-position: center center;
            background-size: cover;}}`;
          }
        }
      })}
    </style>
  );
};
export default BackgroundImageSave;
