import { __ } from '@wordpress/i18n';
import { PanelBody, ToggleControl, RangeControl } from '@wordpress/components';

const CommonSettings = (props) => {
	const {
		setSliderSettingAttribute,
		deviceSizes,
		tabNavsSliderOptions,
		tabContentsSliderOptions,
	} = props;

	return (
		<PanelBody
			title={__('Common settings', 'tab-slider-v2')}
			initialOpen={false}
		>
			{deviceSizes.map(([text, propertyName], index) => (
				<PanelBody
					title={__(text, 'tab-slider-v2')}
					initialOpen={index === 0}
					key={index}
				>
					<ToggleControl
						label={__('Infinite scroll', 'tab-slider-v2')}
						checked={
							tabNavsSliderOptions[propertyName].settings
								.infinite &&
							tabContentsSliderOptions[propertyName].settings
								.infinite
						}
						onChange={(isChecked) => {
							setSliderSettingAttribute([
								{
									attribute: 'tabNavsSliderOptions',
									layout: propertyName,
									property: 'infinite',
									value: isChecked,
								},
								{
									attribute: 'tabContentsSliderOptions',
									layout: propertyName,
									property: 'infinite',
									value: isChecked,
								},
							]);
						}}
					/>
					<ToggleControl
						label={__('Autoplay', 'tab-slider-v2')}
						checked={
							tabNavsSliderOptions[propertyName].settings
								.autoplay &&
							tabContentsSliderOptions[propertyName].settings
								.autoplay
						}
						onChange={(isChecked) => {
							const attributeUpdates = [
								{
									attribute: 'tabNavsSliderOptions',
									layout: propertyName,
									property: 'autoplay',
									value: isChecked,
								},
								{
									attribute: 'tabContentsSliderOptions',
									layout: propertyName,
									property: 'autoplay',
									value: isChecked,
								},
							];

							if (isChecked === false) {
								attributeUpdates.push(
									{
										attribute: 'tabNavsSliderOptions',
										layout: propertyName,
										property: 'autoplaySpeed',
										value: 3000,
									},
									{
										attribute: 'tabContentsSliderOptions',
										layout: propertyName,
										property: 'autoplaySpeed',
										value: 3000,
									}
								);
							}
							setSliderSettingAttribute(attributeUpdates);
						}}
					/>
					{tabNavsSliderOptions[propertyName].settings.autoplay &&
					tabContentsSliderOptions[propertyName].settings.autoplay ? (
						<RangeControl
							label={__('Autoplay speed', 'tab-slider-v2')}
							value={
								(tabNavsSliderOptions[propertyName].settings
									.autoplaySpeed +
									tabContentsSliderOptions[propertyName]
										.settings.autoplaySpeed) /
								2
							}
							min={1}
							max={10000}
							onChange={(value) => {
								setSliderSettingAttribute([
									{
										attribute: 'tabNavsSliderOptions',
										layout: propertyName,
										property: 'autoplaySpeed',
										value: value,
									},
									{
										attribute: 'tabContentsSliderOptions',
										layout: propertyName,
										property: 'autoplaySpeed',
										value: value,
									},
								]);
							}}
						/>
					) : null}
				</PanelBody>
			))}
		</PanelBody>
	);
};

export default CommonSettings;
