/**
 * Mobile block - Shows content only when user agent is mobile.
 */

const { __ } = wp.i18n
import { useSelect } from "@wordpress/data"
const { InnerBlocks, useBlockProps, } = wp.blockEditor;
export const name = "e25m/mobile-content-wrapper"

export const settings = {
  title: __("Mobile Content Wrapper", "mobile-content"),
  description: __("Shows content only on Mobile", "mobile-content"),
  parent: ["e25m/mobile-content"],
  apiVersion: 2,
  icon: {
    src: "smartphone",
    foreground: "#76C000",
  },
  keywords: [__("Mobile layout")],
  category: "layout",
  example: {},
  usesContext: ["e25m/activeTab", "e25m/enableMobileTab"],
  supports: {
    // Removes support for an HTML mode.
    html: false,
    customClassName: false,
  },
  edit: props => {
    const { context, clientId } = props
    const activeTab = context["e25m/activeTab"]
    const enableMobileTab = context["e25m/enableMobileTab"]
    const blockProps = useBlockProps()
    const innerBlockObj = useSelect( ( select ) => select( 'core/block-editor' ).getBlock( clientId ).innerBlocks );

    // Allowed inner blocks are decided based on the following array of patterns
    const allowedBlockPatterns = ['e25m/section', 'e25m-realm', 'core/block'];
    const allowedBlockTypes =  wp.blocks.getBlockTypes().map(({name})=> name)?.filter(blockName => allowedBlockPatterns?.some(v => blockName.includes(v)));

    // Limits the number of blocks to be used as top-level inner blocks to 1
    const appenderToUse = () => ( innerBlockObj.length < 1 ) ? <InnerBlocks.ButtonBlockAppender/> : false

    return (
      <div {...blockProps}>
        {activeTab == "mobile" && enableMobileTab && (
          <InnerBlocks
            templateLock={ false }
            renderAppender={ () => appenderToUse() }
            allowedBlocks={ allowedBlockTypes }
          />
        )}
      </div>
    )
  },

  save: props => {
    return <InnerBlocks.Content />
  },
}
