/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import { RichText } from "@wordpress/block-editor";
import React from "react";

/**
 * The save function defines the way in which the different attributes should
 * be combined into the final markup, which is then serialized by the block
 * editor into `post_content`.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#save
 *
 * @return {WPElement} Element to render.
 *
 *
 *
 */

const InnerContent = (props) => {
  const { attributes } = props;
  const {
    url,
    linkText,
    linkOpenType,
    orderDiv,
    title,
    subtitle,
    content,
    vpopupHtml,
    vpopupFancyBoxOptions,
    desktopBreakpoint,
    mobileBreakpoint,
    isImgLazyLoad
  } = attributes;
  const innerElements = [];
  const noLinkClass = attributes.linkType === "none" ? "no-link" : "";
  const emptyLinkTextClass =
    attributes.linkType == "innerLink" && !attributes.linkText
      ? "empty-text"
      : "";
  const image_lazy_load = isImgLazyLoad ? 'lazy' : null;

  let decodedVpopHtml = "";
  if (vpopupHtml && vpopupHtml.length > 0) {
    try {
      decodedVpopHtml = Buffer.from(vpopupHtml, "base64");
    } catch (error) {
      // ignore errors
    }
  }

  orderDiv.map((item) => {
    if (item.value == "title" && title) {
      innerElements.push(
        <div className="bs-advanced-blurb__title" key={item.value}>
          <RichText.Content value={title} tagName={attributes.titleTag} />
        </div>
      );
    }

    if (item.value == "subtitle" && subtitle) {
      innerElements.push(
        <div className="bs-advanced-blurb__subtitle" key={item.value}>
          <RichText.Content value={subtitle} tagName={attributes.subtitleTag} />
        </div>
      );
    }

    if (item.value == "description" && content) {
      innerElements.push(
        <div className="bs-advanced-blurb__description" key={item.value}>
          <RichText.Content value={content} tagName={attributes.paragraphTag} />
        </div>
      );
    }

    if (item.value == "bottom-text") {
      innerElements.push(
        <div
          className={`bs-advanced-blurb__read-more ${noLinkClass} ${emptyLinkTextClass}`}
          key={item.value}
        >
          {attributes.linkType === "innerLink" ? (
            attributes.linkAction === "imagePopup" ? (
              <div>
                <a
                  data-fancybox
                  data-arrows="false"
                  data-infobar="false"
                  data-touch="false"
                  data-hash="false"
                  data-src={"#" + attributes.blockID}
                  href="void(0);"
                  target={attributes.linkOpenType ? "_blank" : ""}
                  rel={
                    attributes.linkOpenType
                      ? "noopener noreferrer"
                      : "noopener noreferrer"
                  }
                >
                  {linkText}
                </a>
                <div style="display: none;" id={attributes.blockID}>
                  {attributes.popupImgURL ? (
                    <div className="fancybox-content--image">
                      <img
                        width="300"
                        height="300"
                        src={attributes.popupImgURL}
                        alt={
                          attributes.popupImgALT ? attributes.popupImgALT : ""
                        }
                        loading ={image_lazy_load}
                      />
                    </div>
                  ) : null}
                  {attributes.socialURL ? (
                    <div className="fancybox-content--social">
                      <a
                        href={attributes.socialURL}
                        target="_blank"
                        aria-label=""
                        rel="noopener noreferrer"
                      >
                        <img
                          width="40"
                          height="40"
                          src={
                            attributes.socialIconURL
                              ? attributes.socialIconURL
                              : "#"
                          }
                          loading ={image_lazy_load}
                        />
                      </a>
                    </div>
                  ) : null}
                  <div className="fancybox-content--description">
                    {attributes.popupName ? (
                      <RichText.Content
                        value={attributes.popupName}
                        tagName="h3"
                      />
                    ) : null}
                    {attributes.popupSubName ? (
                      <RichText.Content
                        value={attributes.popupSubName}
                        tagName="h4"
                      />
                    ) : null}
                    {attributes.popupContent ? (
                      <RichText.Content
                        value={attributes.popupContent}
                        tagName="p"
                      />
                    ) : null}
                  </div>
                </div>
              </div>
            ) : attributes.linkAction === "videoPopup" ? (
              <div>
                {attributes.popupVideo === "html" ? (
                  <a
                    href={`data:text/html, ${decodedVpopHtml}`}
                    data-fancybox
                    data-type="iframe"
                    data-main-class={vpopupFancyBoxOptions?.classNames || ""}
                    data-click={
                      vpopupFancyBoxOptions?.dismissOnClickOutside || "close"
                    }
                    data-click-slide={
                      vpopupFancyBoxOptions?.dismissOnClickOutside || "close"
                    }
                  >
                    {linkText}
                  </a>
                ) : (
                  <a
                    data-fancybox
                    data-arrows="false"
                    data-infobar="false"
                    data-small-btn="true"
                    data-touch="false"
                    data-hash="false"
                    href={
                      attributes.popupVideo === "upload"
                        ? attributes.popupVideoUploadURL
                        : attributes.popupVideoURL
                    }
                    target={attributes.linkOpenType ? "_blank" : ""}
                    rel={
                      attributes.linkOpenType
                        ? "noopener noreferrer"
                        : "noopener noreferrer"
                    }
                  >
                    {linkText}
                  </a>
                )}
              </div>
            ) : (
              <div>
                <a
                  href={url}
                  target={attributes.linkOpenType ? "_blank" : ""}
                  rel={
                    attributes.linkOpenType
                      ? "noopener noreferrer"
                      : "noopener noreferrer"
                  }
                >
                  {linkText}
                </a>
              </div>
            )
          ) : (
            <RichText.Content value={attributes.bottomText} tagName="span" />
          )}
        </div>
      );
    }
    if (item.value == "image" && attributes.imgURL) {
      innerElements.push(
        attributes.hoverImgURL ? (
          <div
            className="bs-advanced-blurb__image hover-option-enabled"
            key={item.value}
          >
            <figure>
              {attributes.imgURL && (
                <picture>
                  {attributes.mobileImgURL && (
                    <>
                      <source
                        srcSet={
                          attributes.mobile2xImgURL
                            ? `${attributes.mobileImgURL},
                        ${attributes.mobile2xImgURL} 2x`
                            : `${attributes.mobileImgURL}`
                        }
                        media={`(max-width:${mobileBreakpoint - 1}px)`}
                      />
                    </>
                  )}
                  {attributes.desktopImgURL && (
                    <>
                      <source
                        srcSet={
                          attributes.desktop2xImgURL
                            ? `${attributes.desktopImgURL},
                        ${attributes.desktop2xImgURL} 2x`
                            : `${attributes.desktopImgURL}`
                        }
                        media={`(max-width:${desktopBreakpoint}px)`}
                      />
                    </>
                  )}
                  <img
                    className={`${attributes.imageBehavior} img-fluid normal-image`}
                    loading ={image_lazy_load}
                    src={attributes.imgURL}
                    alt={attributes.imgAlt ? attributes.imgAlt : ""}
                  />
                </picture>
              )}
              {attributes.hoverImgURL ? (
                <img
                  className={`${attributes.imageBehavior} img-fluid hover-image`}
                  loading ={image_lazy_load}
                  src={attributes.hoverImgURL}
                  alt={attributes.imgAlt ? attributes.imgAlt : ""}
                />
              ) : null}
            </figure>
            {attributes.playIconURL ? (
              <img
                className="video-play-icon"
                width="50"
                height="50"
                src={attributes.playIconURL}
                loading ={image_lazy_load}
              />
            ) : null}
          </div>
        ) : (
          <div className="bs-advanced-blurb__image" key={item.value}>
            <figure>
              {attributes.imgURL && (
                <picture>
                  {attributes.mobileImgURL && (
                    <>
                      <source
                        srcSet={
                          attributes.mobile2xImgURL
                            ? `${attributes.mobileImgURL},
                          ${attributes.mobile2xImgURL} 2x`
                            : `${attributes.mobileImgURL}`
                        }
                        media={`(max-width:${mobileBreakpoint - 1}px)`}
                      />
                    </>
                  )}
                  {attributes.desktopImgURL && (
                    <>
                      <source
                        srcSet={
                          attributes.desktop2xImgURL
                            ? `${attributes.desktopImgURL},
                          ${attributes.desktop2xImgURL} 2x`
                            : `${attributes.desktopImgURL}`
                        }
                        media={`(max-width:${desktopBreakpoint}px)`}
                      />
                    </>
                  )}
                  <img
                    className={`${attributes.imageBehavior} img-fluid normal-image`}
                    loading ={image_lazy_load}
                    src={attributes.imgURL}
                    alt={attributes.imgAlt ? attributes.imgAlt : ""}
                  />
                </picture>
              )}
            </figure>
            {attributes.playIconURL ? (
              <img
                className="video-play-icon"
                width="50"
                height="50"
                src={attributes.playIconURL}
                loading ={image_lazy_load}
              />
            ) : null}
          </div>
        )
      );
    }
  });

  return (
    <>
      {innerElements}
    </>
  );
};

const save = (props) => {
  const { attributes, setAttributes } = props;
  const {
    url,
    orderDiv,
    advancedBlurbClassNames,
    vpopupHtml,
    vpopupFancyBoxOptions,
  } = attributes;

  const classNames = advancedBlurbClassNames
    ? advancedBlurbClassNames.map((x) => x.value).join(" ")
    : "";

  let decodedVpopHtml = "";
  if (vpopupHtml && vpopupHtml.length > 0) {
    try {
      decodedVpopHtml = Buffer.from(vpopupHtml, "base64");
    } catch (error) {
      // ignore errors
    }
  }
  //To check if the image is available in 'Display order'
  const checkIfImageEnabled = (obj) => obj.value === "image";

  return (
    <>
      {attributes.linkType === "outerLink" &&
        attributes.blurbVisibility == true ? (
        <div
          className={`bs-advanced-blurb ${classNames}
				${!attributes.imgURL || !orderDiv.some(checkIfImageEnabled)
              ? "bs-advanced-blurb--no-image"
              : ""
            }
				`}
        >
          {attributes.linkAction === "imagePopup" ? (
            <div className="bs-advanced-blurb__container">
              <a
                data-fancybox
                data-arrows="false"
                data-infobar="false"
                data-touch="false"
                data-hash="false"
                data-src={"#" + attributes.blockID}
                href="void(0);"
                target={attributes.linkOpenType ? "_blank" : ""}
                rel={
                  attributes.linkOpenType
                    ? "noopener noreferrer"
                    : "noopener noreferrer"
                }
              >
                <InnerContent
                  setAttributes={setAttributes}
                  attributes={attributes}
                />
              </a>
              <div style="display: none;" id={attributes.blockID}>
                {attributes.popupImgURL ? (
                  <div className="fancybox-content--image">
                    <img
                      width="300"
                      height="300"
                      src={attributes.popupImgURL}
                      alt={attributes.popupImgALT ? attributes.popupImgALT : ""}
                      loading ={image_lazy_load}
                    />
                  </div>
                ) : null}
                {attributes.socialURL ? (
                  <div className="fancybox-content--social">
                    <a
                      href={attributes.socialURL}
                      target="_blank"
                      aria-label=""
                      rel="noopener noreferrer"
                    >
                      <img
                        width="40"
                        height="40"
                        src={
                          attributes.socialIconURL
                            ? attributes.socialIconURL
                            : "#"
                        }
                        loading ={image_lazy_load}
                      />
                    </a>
                  </div>
                ) : null}
                <div className="fancybox-content--description">
                  {attributes.popupName ? (
                    <h3>{attributes.popupName}</h3>
                  ) : null}
                  {attributes.popupSubName ? (
                    <h4>{attributes.popupSubName}</h4>
                  ) : null}
                  {attributes.popupContent ? (
                    <p
                      dangerouslySetInnerHTML={{
                        __html: attributes.popupContent,
                      }}
                    ></p>
                  ) : null}
                </div>
              </div>
            </div>
          ) : attributes.linkAction === "videoPopup" ? (
            attributes.popupVideo === "html" ? (
              <a
                href={`data:text/html, ${decodedVpopHtml}`}
                data-fancybox
                data-type="iframe"
                data-main-class={vpopupFancyBoxOptions?.classNames || ""}
                data-click={
                  vpopupFancyBoxOptions?.dismissOnClickOutside || "close"
                }
                data-click-slide={
                  vpopupFancyBoxOptions?.dismissOnClickOutside || "close"
                }
              >
                <div className="bs-advanced-blurb__container">
                  <InnerContent
                    setAttributes={setAttributes}
                    attributes={attributes}
                  />
                </div>
              </a>
            ) : (
              <a
                data-fancybox
                data-arrows="false"
                data-infobar="false"
                data-small-btn="true"
                data-touch="false"
                data-hash="false"
                href={
                  attributes.popupVideo === "upload"
                    ? attributes.popupVideoUploadURL
                    : attributes.popupVideoURL
                }
                target={attributes.linkOpenType ? "_blank" : ""}
                rel={
                  attributes.linkOpenType
                    ? "noopener noreferrer"
                    : "noopener noreferrer"
                }
              >
                <div className="bs-advanced-blurb__container">
                  <InnerContent
                    setAttributes={setAttributes}
                    attributes={attributes}
                  />
                </div>
              </a>
            )
          ) : (
            <a
              href={url}
              target={attributes.linkOpenType ? "_blank" : ""}
              rel={
                attributes.linkOpenType
                  ? "noopener noreferrer"
                  : "noopener noreferrer"
              }
            >
              <div className="bs-advanced-blurb__container">
                <InnerContent
                  setAttributes={setAttributes}
                  attributes={attributes}
                />
              </div>
            </a>
          )}
        </div>
      ) : attributes.blurbVisibility == true ? (
        <div
          className={`bs-advanced-blurb ${classNames}
				${!attributes.imgURL || !orderDiv.some(checkIfImageEnabled)
              ? "bs-advanced-blurb--no-image"
              : ""
            }
				`}
        >
          <div className="bs-advanced-blurb__container">
            <InnerContent
              setAttributes={setAttributes}
              attributes={attributes}
            />
          </div>
        </div>
      ) : null}
    </>
  );
};

export default save;
