import { __ } from "@wordpress/i18n"
import { withSelect } from "@wordpress/data"
import { compose } from "@wordpress/compose"
import { addQueryArgs } from "@wordpress/url"

import { InnerBlocks, InspectorControls, useBlockProps } from "@wordpress/block-editor"
import {
  PanelBody,
  PanelRow,
  TextControl,
  RadioControl,
  Disabled,
  ToggleControl,
  RangeControl,
} from "@wordpress/components"
import Select from "react-select"
import apiFetch from "@wordpress/api-fetch"
const { useEffect, useState } = wp.element
const { useDispatch, useSelect } = wp.data
const { createBlock } = wp.blocks
import { capitalize } from "lodash"

import TagSelector from "~berg/ui-components/tag-selector"
import SortableSelect from "~berg/ui-components/sortable-multi-select"

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {}
  const { attributes, setAttributes, clientId, taxonomies, termList } = props
  const {
    numberOfPosts,
    selection,
    orderBy,
    order,
    displayOrder,
    popupDisplayOrder,
    relatedPostBlockClassNames,
    visibility,
    postTaxonomies,
    dateFormat,
    enableTaxonomiesLink,
    imageAppearance,
    anchorAppearance,
    titleTag,
    titleCharLimit,
  } = attributes

  const [relatedPostBlockClasses, setRelatedPostBlockClasses] = useState([])
  const [defaultClassValues, setDefaultClassValues] = useState([])
  const [defaultPostTaxonomies, setDefaultPostTaxonomies] = useState([])
  const [isLoaded, setIsLoaded] = useState(false)
  const blockProps = useBlockProps()

  const { replaceInnerBlocks } = useDispatch("core/block-editor")
  const { inner_blocks } = useSelect(select => ({
    inner_blocks: select("core/block-editor").getBlocks(clientId),
  }))
  const currentPostId = wp.data.select("core/editor").getCurrentPostId()
  const currentPostType = wp.data.select("core/editor").getCurrentPostType()
  const defaultPostTaxonomyOptions = []

  // create post type related taxonomies list
  const taxonomyList = _.map(taxonomies, taxonomy => {
    let taxonomyName = taxonomy.labels.singular_name
    let taxonomySlug = taxonomy.slug
    let supportedPostTypes = taxonomy.types
    return {
      postTypes: supportedPostTypes,
      value: taxonomySlug,
      label: taxonomyName,
    }
  })
  // filter taxonomies according to current post type
  const filteredTaxonomyList = _.filter(taxonomyList, taxonomy => {
    return Array.isArray(taxonomy.postTypes) && taxonomy.postTypes.indexOf(currentPostType) !== -1
  }).map(taxonomy => {
    return { value: "taxonomy_" + taxonomy.value, label: taxonomy.label }
  })

  let defaultDisplayOrder = [
    { value: "image", label: "Image" },
    { value: "title", label: "Title" },
    { value: "content", label: "Content" },
    { value: "excerpt", label: "Excerpt" },
    { value: "date", label: "Date" },
    { value: "author", label: "Author Name" },
    { value: "more", label: "Read more" },
    { value: "featured_image", label: "Featured image" },
    { value: "event_date", label: "Event Date [start date - End date]" },
    { value: "post_type", label: "Post Type Name" },
    { value: "post_reading_time", label: "Post Reading Time" },
    ...filteredTaxonomyList,
  ]

  let defaultPopupDisplayOrder = [...defaultDisplayOrder]
  const ALLOWED_BLOCKS = ["e25m/single-post"]

  useEffect(() => {
    if (selection === "auto") {
      apiFetch({
        path: "/api/related-posts/getPosts",
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          numberOfPosts,
          orderBy,
          currentPostId,
          order,
          postTaxonomies,
        }),
      }).then(res => {
        let innerBlocksNew = []
        if (res.length > 0) {
          res.map((relatedPost, index) => {
            if (relatedPost && relatedPost.ID && relatedPost.post_title && relatedPost.post_type) {
              const colBlockAttributes =
                inner_blocks[index] && inner_blocks[index].name === "e25m/column"
                  ? inner_blocks[index].attributes
                  : null
              innerBlocksNew.push(
                createBlock(
                  "e25m/column",
                  colBlockAttributes ? colBlockAttributes : { sizeSm: 4, colClassList: ["bs-column", "col-sm-4"] },
                  [
                    createBlock("e25m/single-post", {
                      selectedPost: {
                        label: "",
                        value: relatedPost.ID,
                      },
                      displayOrder,
                      popupDisplayOrder,
                      selectedPostType: relatedPost.post_type,
                      dateFormat,
                      enableTaxonomiesLink,
                      imageAppearance,
                      anchorAppearance,
                      titleTag,
                      titleCharLimit,
                    }),
                  ]
                )
              )
            }
          })
        } else {
          innerBlocksNew.push(
            createBlock("core/paragraph", {
              content: "<span class='bs-no-posts'>No related posts found.</span>",
            })
          )
        }
        replaceInnerBlocks(clientId, innerBlocksNew, false)
      })
    }
  }, [numberOfPosts, selection, orderBy, order, postTaxonomies, dateFormat])

  useEffect(() => {
    const classList = []
    if (coreComponents["bs-related-posts"] && coreComponents["bs-related-posts"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-related-posts"]}`,
        label: "Default",
      })
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      })
    }

    if (coreComponentsTheme["bs-related-posts"] && coreComponentsTheme["bs-related-posts"].length > 0) {
      coreComponentsTheme["bs-related-posts"].map((className, index) => {
        let classLabel = className.replace("bs-related-posts--", "").split("-")
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        })
      })
    }

    const defaultValues = relatedPostBlockClassNames ? relatedPostBlockClassNames : [classList[0]]
    setRelatedPostBlockClasses(classList)
    setDefaultClassValues(defaultValues)
  }, [JSON.stringify(props.blockClasses)])

  useEffect(() => {
    apiFetch({
      path: addQueryArgs(`/wp/v2/berg_terms`, {
        per_page: -1, // eslint-disable-line camelcase
      }),
    })
      .then(termList => {
        setDefaultPostTaxonomies(termList)
        setIsLoaded(true)
      })
      .catch(() => {
        setIsLoaded(true)
      })
  }, [])

  const classNames = relatedPostBlockClassNames
    ? relatedPostBlockClassNames.map(relatedClass => relatedClass.value).join(" ")
    : ""

  const reRenderBlockWithDisplayItem = (
    displayOrderValue,
    popupDisplayOrderValue,
    dateFormatValue = dateFormat,
    enableTaxonomiesLinkValue = enableTaxonomiesLink,
    imageAppearanceValue = imageAppearance,
    anchorAppearanceValue = anchorAppearance,
    titleTagValue = titleTag,
    titleCharLimitValue = titleCharLimit
  ) => {
    let innerBlocksNew = []
    inner_blocks.map(inner_block => {
      innerBlocksNew.push(
        createBlock("e25m/column", { ...inner_block.attributes }, [
          createBlock("e25m/single-post", {
            ...inner_block.innerBlocks[0].attributes,
            displayOrder: displayOrderValue,
            popupDisplayOrder: popupDisplayOrderValue,
            dateFormat: dateFormatValue,
            enableTaxonomiesLink: enableTaxonomiesLinkValue,
            imageAppearance: imageAppearanceValue,
            anchorAppearance: anchorAppearanceValue,
            titleTag: titleTagValue,
            titleCharLimit: titleCharLimitValue,
          }),
        ])
      )
    })
    replaceInnerBlocks(clientId, innerBlocksNew, false)
  }

  const onChangeDisplayOrder = newVal => {
    setAttributes({ displayOrder: newVal })
    reRenderBlockWithDisplayItem(newVal, popupDisplayOrder)
  }
  const onChangePopupDisplayOrder = newVal => {
    setAttributes({ popupDisplayOrder: newVal })
    reRenderBlockWithDisplayItem(displayOrder, newVal)
  }

  //Image Appearance radio button onselect function
  const onSelectImageAppearance = newVal => {
    setAttributes({ imageAppearance: newVal })
    reRenderBlockWithDisplayItem(displayOrder, popupDisplayOrder, dateFormat, enableTaxonomiesLink, newVal)
  }

  //Anchor Element Appearance radio button onselect function
  const onSelectAnchorAppearance = newVal => {
    setAttributes({ anchorAppearance: newVal })
    reRenderBlockWithDisplayItem(
      displayOrder,
      popupDisplayOrder,
      dateFormat,
      enableTaxonomiesLink,
      imageAppearance,
      newVal
    )
  }

  const onChangeTitleTag = newVal => {
    setAttributes({ titleTag: newVal })
    reRenderBlockWithDisplayItem(
      displayOrder,
      popupDisplayOrder,
      dateFormat,
      enableTaxonomiesLink,
      imageAppearance,
      anchorAppearance,
      newVal
    )
  }

  const onChangeTitleCharLimit = newVal => {
    setAttributes({ titleCharLimit: newVal })
    reRenderBlockWithDisplayItem(
      displayOrder,
      popupDisplayOrder,
      dateFormat,
      enableTaxonomiesLink,
      imageAppearance,
      anchorAppearance,
      titleTag,
      newVal
    )
  }

  if (selection === "manual") {
    let innerBlocksNew = []
    let replaceInnerBlock = false
    inner_blocks.map(inner_block => {
      if (!inner_block.innerBlocks.length) {
        if (inner_block.name === "e25m/single-post") {
          innerBlocksNew.push(
            createBlock("e25m/column", { sizeSm: 4, colClassList: ["bs-column", "col-sm-4"] }, [
              createBlock("e25m/single-post", { ...attributes }),
            ])
          )
        }
        replaceInnerBlock = true
      } else {
        innerBlocksNew.push(inner_block)
      }
    })
    if (replaceInnerBlock) {
      replaceInnerBlocks(clientId, innerBlocksNew, false)
    }
  }
  let activeStatus = visibility ? "enable" : "disable"

  let renderContent = (
    <div className={`bs-related-posts ${classNames} ${activeStatus}`}>
      <div className="bs-related-posts__container">
        <div className={`bs-related-posts__items ${selection}`}>
          <InnerBlocks allowedBlocks={ALLOWED_BLOCKS} />
        </div>
      </div>
    </div>
  )

  if (selection === "auto") {
    renderContent = <Disabled>{renderContent}</Disabled>
  }

  renderContent = <div>{renderContent}</div>

  if (defaultPostTaxonomies !== "null" && typeof defaultPostTaxonomies === "object") {
    Object.keys(defaultPostTaxonomies).forEach(postType => {
      const { taxonomies } = defaultPostTaxonomies[postType]
      if (postType === currentPostType) {
        Object.keys(taxonomies).forEach((taxonomy, taxIndex) => {
          const { label, terms } = taxonomies[taxonomy]
          const termListOptions = []
          Object.keys(terms).forEach(i => {
            termListOptions.push({
              label: terms[i].name,
              value: terms[i].term_id,
              type: terms[i].taxonomy,
            })
          })
          defaultPostTaxonomyOptions.push({
            label,
            options: termListOptions,
          })
        })
      }
    })
  }
  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls initialOpen={true}>
        <PanelBody title={__("Visibility", "")} initialOpen={false}>
          <ToggleControl
            label="Enable/Disable"
            checked={visibility}
            onChange={val => {
              setAttributes({ visibility: val })
            }}
          />
        </PanelBody>
        <PanelBody title={__("Related Posts Settings", "berg-related-posts")} initialOpen={true}>
          <PanelRow>
            <RadioControl
              label={__("Related Posts Selection Mode", "berg-related-posts")}
              options={[
                { label: "Auto", value: "auto" },
                { label: "Manual", value: "manual" },
              ]}
              selected={selection}
              onChange={selection => setAttributes({ selection })}
            />
          </PanelRow>
          {selection == 'auto' && [
            <>
            <PanelRow>
              <p className="components-panel__help-text">{__("Please note that the changes to the individual nested single post block attributes \
              will get replaced by the following values when using the 'Auto' mode.", "berg-related-posts")}
                {<br />}
                {__("For example 'Display Order' value of the following input takes precedence over the input in the \
              single-post block.", "berg-related-posts")}
              </p>
            </PanelRow>
            </>
          ]}
          <label>{__("Display Order", "berg")}</label>
          <SortableSelect value={displayOrder} onChange={onChangeDisplayOrder} options={defaultDisplayOrder} />

          <label>{__("Popup Display Order", "berg")}</label>
          <SortableSelect
            value={popupDisplayOrder}
            onChange={onChangePopupDisplayOrder}
            options={defaultPopupDisplayOrder}
          />

          {selection === "auto" && (
            <>
              {currentPostType !== "page" && (
                <>
                  <label>{__("Post Taxonomies", "berg")}</label>
                  <SortableSelect
                    value={postTaxonomies}
                    onChange={postTaxonomiesVal => setAttributes({ postTaxonomies: postTaxonomiesVal })}
                    options={defaultPostTaxonomyOptions}
                  />
                </>
              )}
              <PanelRow>
                <TagSelector label={__("Title Tag", "berg")} value={titleTag} onChange={onChangeTitleTag} />
              </PanelRow>
              <PanelRow>
                <TextControl
                  label={__("Title character limit", "")}
                  type="number"
                  value={titleCharLimit}
                  onChange={onChangeTitleCharLimit}
                />
              </PanelRow>
              <PanelRow>
                <RangeControl
                  label={__("Number of posts", "berg-related-posts")}
                  value={numberOfPosts}
                  onChange={numberOfPostsVal => setAttributes({ numberOfPosts: numberOfPostsVal })}
                  min={1}
                  max={20}
                />
              </PanelRow>
              <PanelRow>
                <RadioControl
                  label={__("Order by", "berg-related-posts")}
                  options={[
                    { label: "Relevance", value: "relevance" },
                    { label: "By Date", value: "date" },
                    { label: "By Title", value: "title" },
                  ]}
                  selected={orderBy}
                  onChange={orderBy => setAttributes({ orderBy })}
                />
              </PanelRow>
              {orderBy !== "relevance" && (
                <PanelRow>
                  <RadioControl
                    label={__("Order", "berg-related-posts")}
                    options={[
                      { label: "Ascending", value: "ASC" },
                      { label: "Descending", value: "DESC" },
                    ]}
                    selected={order}
                    onChange={order => setAttributes({ order })}
                  />
                </PanelRow>
              )}
              <RadioControl
                label={__("Image Appearance", "berg-related-posts")}
                options={[
                  { label: "Image", value: "image" },
                  { label: "Background", value: "background" },
                ]}
                selected={imageAppearance}
                onChange={onSelectImageAppearance}
              />
              <RadioControl
                label={__("Anchor Element Appearance", "berg-related-posts")}
                options={[
                  { label: "Wrap All", value: "full" },
                  { label: "Inner Link", value: "inner" },
                ]}
                selected={anchorAppearance}
                onChange={onSelectAnchorAppearance}
              />
              {anchorAppearance == "inner" && (
                <ToggleControl
                  label={__("Enable clickable links for taxonomies", "berg-related-posts")}
                  checked={enableTaxonomiesLink}
                  onChange={val => {
                    setAttributes({ enableTaxonomiesLink: val })
                    reRenderBlockWithDisplayItem(displayOrder, popupDisplayOrder, dateFormat, val)
                  }}
                />
              )}
              <TextControl
                label={__("Post Date format", "berg-related-posts")}
                help={__("For more info: https://wordpress.org/support/article/formatting-date-and-time/", "berg")}
                value={dateFormat}
                onChange={val => {
                  setAttributes({ dateFormat: val })
                }}
              />
            </>
          )}
        </PanelBody>
        <PanelBody title={__("Related Posts Block Class", "")} initialOpen={false}>
          <Select
            defaultValue={defaultClassValues}
            isMulti
            name="post-blockClasses"
            options={relatedPostBlockClasses}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={val => {
              setAttributes({
                relatedPostBlockClassNames: val ? val : [],
              })
            }}
          />
        </PanelBody>
      </InspectorControls>
      {renderContent}
    </div>
  )
}

const applyWithSelect = withSelect((select, props) => {
  let selectCore = select("core")
  //Getting all the taxonomies
  let taxonomies = selectCore.getTaxonomies({ per_page: -1, public: true })
  let termList = []
  const theme = select("core").getCurrentTheme()
  const { block_classes: blockClasses = {} } = theme || {}
  return {
    taxonomies,
    termList,
    blockClasses,
  }
})

export default compose(applyWithSelect)(Edit)
