/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import React from "react";

import { __ } from "@wordpress/i18n";
import { InspectorControls, RichText, useBlockProps } from "@wordpress/block-editor";
import {
  TabPanel
} from "@wordpress/components";
import CountUp from "react-countup";
import { capitalize } from "lodash";

const { useEffect, useState } = wp.element;
const { withSelect } = wp.data;

import TabGeneral from "./tabs/tab-general";
import TabDeveloper from "./tabs/tab-developer";

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
//import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @param {Object} [props]           Properties passed from the editor.
 * @param {string} [props.className] Class name generated for the block.
 *
 * @return {WPElement} Element to render.
 */

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {};
  const { attributes, setAttributes, environment } = props;
  const {
    title,
    titleTag,
    summary,
    decimalPlaces,
    displayThousandSeparator,
    delay,
    start,
    end,
    duration,
    prefixImg,
    postfixImg,
    prefixSelection,
    postfixSelection,
    prefixText,
    postfixText,
    counterClassNames,
    counterVisibility = true,
    counterWrap,
    countUnit,
    countUnitPosition,
  } = attributes;

  const TitleTag = titleTag;
  const blockProps = useBlockProps();

  const counterOptions = {
    startVal: start,
    endVal: end,
    decimalPlaces: decimalPlaces,
    duration: duration,
    separator: displayThousandSeparator ? "," : "",
  };

  const onChangeSummary = (newVal) => {
    setAttributes({ summary: newVal });
  };

  const [counterClasses, setCounterClasses] = useState([]);
  const [defaultClassValues, setDefaultClassValues] = useState([]);

  useEffect(() => {
    const classList = [];
    if (coreComponents["bs-counter"] && coreComponents["bs-counter"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-counter"]}`,
        label: "Default",
      });
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      });
    }

    if (coreComponentsTheme["bs-counter"] && coreComponentsTheme["bs-counter"].length > 0) {
      coreComponentsTheme["bs-counter"].map((className, index) => {
        let classLabel = className.replace("bs-counter--", "").split("-");
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        });
      });
    }

    const defaultValues = counterClassNames ? counterClassNames : [classList[0]];
    setCounterClasses(classList);
    setDefaultClassValues(defaultValues);
  }, [JSON.stringify(props.blockClasses)]);

  const classNames = counterClassNames ? counterClassNames.map(x => x.value).join(" ") : "";


  const tabPanels = [
    {
      name: "general-settings",
      title: __("General", ""),
      content: <TabGeneral
        setAttributes={setAttributes}
        attributes={{
          ...attributes,
          counterClasses,
          defaultClassValues
        }}
      />,
    },
    /* These settings will be displayed only on the Development environment */
    ...(
      environment === "development" ?
        [
          {
            name: "developer",
            title: __("Developer", ""),
            content: <TabDeveloper
              setAttributes={setAttributes}
              attributes={attributes}
            />,
          }
        ] :
        []
    )
  ];

  const displayPrefixElement = () => {
    return (
      <>
        {
          prefixSelection === "image" &&
          prefixImg.url && (
            <div className="bs-counter__prefix">
              <img src={prefixImg.url} />
            </div>
          )
        }
        {
          prefixSelection === "text" &&
          prefixText && (
            <div className="bs-counter__prefix">
              <p>{prefixText}</p>
            </div>
          )
        }
        {
          countUnitPosition === "before_count" &&
          countUnit &&
          counterWrap === "" && (
            <div className="bs-counter__unit">
              <span>{countUnit}</span>
            </div>
          )
        }
      </>
    );
  };

  const displayPostfixElement = () => {
    return (
      <>
        {
          countUnitPosition === "after_count" &&
          countUnit &&
          counterWrap === "" && (
            <div className="bs-counter__unit">
              <span>{countUnit}</span>
            </div>
          )
        }
        {
          postfixSelection === "image" &&
          postfixImg.url && (
            <div className="bs-counter__postfix">
              <img src={postfixImg.url} />
            </div>
          )
        }
        {
          postfixSelection === "text" &&
          postfixText && (
            <div className="bs-counter__postfix">
              <p>{postfixText}</p>
            </div>
          )
        }
      </>
    );
  };

  const CounterUp = () => {
    return (
      <>
        {
          countUnitPosition === "before_count" &&
          countUnit &&
          counterWrap != "" && (
            <div className="bs-counter__unit">
              <span>{countUnit}</span>
            </div>
          )
        }
        <div className="bs-counter__count">
          <CountUp
            start={start == "" ? 0 : start}
            end={end == "" ? 0 : end}
            delay={delay}
            duration={duration}
            decimals={decimalPlaces}
            separator={displayThousandSeparator ? "," : ""}
          >
            {({ countUpRef }) => (
              <span data-counterup data-options={JSON.stringify(counterOptions)} ref={countUpRef}>
                {end}
              </span>
            )}
          </CountUp>
        </div>
        {
          countUnitPosition === "after_count" &&
          countUnit &&
          counterWrap != "" && (
            <div className="bs-counter__unit">
              <span>{countUnit}</span>
            </div>
          )
        }
      </>
    );
  };

  const CounterWrap = ({ children }) => {
    let counterWrapElem = "";
    switch (counterWrap) {
      case "prefix":
        counterWrapElem = (
          <div className="bs-counter__count__wrap">
            {displayPrefixElement()}
            {children}
          </div>
        );
        break;

      case "postfix":
        counterWrapElem = (
          <div className="bs-counter__count__wrap">
            {children}
            {displayPostfixElement()}
          </div>
        );
        break;

      default:
        counterWrapElem = children;
        break;
    }

    return counterWrapElem;
  };

  return [
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        {
          tabPanels.length > 1 ?
            <TabPanel
              className="components-panel__tabs"
              activeClass="active-tab"
              tabs={tabPanels}
            >
              {({ content }) => <>{content}</>}
            </TabPanel> :
            <TabGeneral
              setAttributes={setAttributes}
              attributes={{
                ...attributes,
                counterClasses,
                defaultClassValues
              }}
            />
        }
      </InspectorControls>
      <div
        className={`bs-counter ${classNames}
              ${counterVisibility ? "enable" : "disable"}`}
      >
        <div className="bs-counter__container">
          <div className="bs-counter__title">
            <TitleTag>{title}</TitleTag>
          </div>
          <div className="bs-counter__wrapper">
            {
              counterWrap != "prefix" && displayPrefixElement()
            }
            {
              <CounterWrap>
                <CounterUp />
              </CounterWrap>
            }
            {
              counterWrap != "postfix" && displayPostfixElement()
            }
          </div>
          <div className="bs-counter__summary">
            <RichText
              tagName="p"
              placeholder="Summary"
              className="summary"
              value={attributes.summary}
              onChange={onChangeSummary}
            />
          </div>
        </div>
        <div className="clearfix"></div>
      </div>
    </div>
  ];
};

export default withSelect((select) => {
  const theme = select("core").getCurrentTheme();
  const { block_classes: blockClasses = {}, wp_environment_type: environment = "production" } = theme || {};
  return { blockClasses, environment };
})(Edit);
