import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";

const edit = ({ context }) => {
  const ALLOWED_BLOCKS = [
    "core/heading",
    "core/paragraph",
    "e25m/media-elements",
    "e25m/lottie-animator",
  ];
  const blockProps = useBlockProps();
  return (
    <div {...blockProps} key={blockProps.id}>
      <div className={`accordion__block__desc`}>
        <div className={`card-body ${context["e25m/floatClassName"]}`}>
          <div className={`bs-advance-accordion-top`}>
            <div className="accordion-right-side">
              <span className="side-info">Accordion Top Content</span>
              <InnerBlocks
                allowedBlocks={ALLOWED_BLOCKS}
                templateLock={false}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default edit;
