/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
 import { InnerBlocks } from "@wordpress/block-editor";

 /**
	* The save function defines the way in which the different attributes should
	* be combined into the final markup, which is then serialized by the block
	* editor into `post_content`.
	*
	* @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#save
	*
	* @return {WPElement} Element to render.
	*/
 import {
	 BackgroundImageSave,
	 BackgroundImageAndColorSave,
	 GradientSave,
	 BackGroundColorSave,
	 PictureTagSave,
 } from "./_partials";

 const save = (props) => {
	 const { attributes } = props;
	 const {
		 divBlockName,
		 divClass,
		 divClassNames,
		 extraSmallFlexClassNames,
		 smallFlexClassNames,
		 mediumFlexClassNames,
		 largeFlexClassNames,
		 extraLargeFlexClassNames,
		 backgroundColor,
		 gradientColorOne,
		 gradientColorTwo,
		 gradientDirection,
		 gradientColorOneLocation,
		 gradientColorTwoLocation,
		 gradientOptions,
		 backGroundColorOptions,
		 backgroundImageOptions,
		 pictureTagOptions,
		 backgroundImageAndColorOptions,
		 backgroundVideoUrl,
		 backgroundVideoControls,
		 backgroundVideoLoop,
		 backgroundVideoMute,
		 backgroundVideoAutoPlay,
		 divImgUrl,
		 divImgUrlTwo,
		 divImgVideoUrl,
		 divBlockID,
		 backgroundTypeDiv,
	 } = attributes;

	 if (!backgroundImageOptions.regular.settings.divImgUrl && divImgUrl) {
		 backgroundImageOptions.regular.settings = {
			 ...backgroundImageOptions.regular.settings,
			 divImgUrl: divImgUrl,
		 };
	 }

	 if (
		 !gradientOptions.regular.settings.gradientColorOne &&
		 gradientColorOne &&
		 !gradientOptions.regular.settings.gradientColorTwo &&
		 gradientColorTwo
	 ) {
		 gradientOptions.regular.settings = {
			 ...gradientOptions.regular.settings,
			 gradientColorOne: gradientColorOne,
			 gradientColorTwo: gradientColorTwo,
			 gradientDirection: gradientDirection,
			 gradientColorOneLocation: gradientColorOneLocation,
			 gradientColorTwoLocation: gradientColorTwoLocation,
		 };
	 }

	 if (
		 !backgroundImageAndColorOptions.regular.settings.divImgUrl &&
		 divImgUrlTwo &&
		 !backgroundImageAndColorOptions.regular.settings.backgroundColor &&
		 backgroundColorTwo
	 ) {
		 backgroundImageAndColorOptions.regular.settings = {
			 ...backgroundImageAndColorOptions.regular.settings,
			 divImgUrl: divImgUrlTwo,
			 backgroundColor: backgroundColorTwo,
		 };
	 }

	 if (
		 !backGroundColorOptions.regular.settings.backgroundColor &&
		 backgroundColor
	 ) {
		 backGroundColorOptions.regular.settings = {
			 ...backGroundColorOptions.regular.settings,
			 backgroundColor: backgroundColor,
		 };
	 }
	 const deviceSizes = [
		 ["Regular size", "regular"],
		 ["Desktop", "desktop"],
		 ["Mobile", "mobile"],
	 ];

	 const deviceSizesForPictureTag = [
		 ["Mobile", "mobile"],
		 ["Desktop", "desktop"],
		 ["Regular size", "regular"],
	 ];
	 const classNames = divClassNames
		 ? divClassNames.map((x) => x.value).join(" ")
		 : "";

	 const esFlexClassNames = extraSmallFlexClassNames
		 ? extraSmallFlexClassNames.map((x) => x.value).join(" ")
		 : "";

	 const sFlexClassNames = smallFlexClassNames
		 ? smallFlexClassNames.map((x) => x.value).join(" ")
		 : "";

	 const mFlexClassNames = mediumFlexClassNames
		 ? mediumFlexClassNames.map((x) => x.value).join(" ")
		 : "";

	 const lFlexClassNames = largeFlexClassNames
		 ? largeFlexClassNames.map((x) => x.value).join(" ")
		 : "";

	 const elFlexClassNames = extraLargeFlexClassNames
		 ? extraLargeFlexClassNames.map((x) => x.value).join(" ")
		 : "";

	 const divId =
		 divBlockName.toString().length > 0
			 ? divBlockName.toLowerCase().replace(/\W/g, "-")
			 : undefined;

	 return (
		 <div id={divId} className={`bs-div bs-div-${divBlockID} ${classNames}`}>
			 {backgroundTypeDiv === "background_image" ? (
				 <BackgroundImageSave
					 deviceSizes={deviceSizes}
					 backgroundImageOptions={backgroundImageOptions}
					 divBlockID={divBlockID}
				 />
			 ) : (
				 ""
			 )}
			 {backgroundTypeDiv === "picture_tag" ? (
				 <PictureTagSave
					 deviceSizesForPictureTag={deviceSizesForPictureTag}
					 pictureTagOptions={pictureTagOptions}
				 />
			 ) : (
				 ""
			 )}
			 {backgroundTypeDiv === "background_color" ? (
				 <BackGroundColorSave
					 deviceSizes={deviceSizes}
					 backGroundColorOptions={backGroundColorOptions}
					 divBlockID={divBlockID}
				 />
			 ) : (
				 ""
			 )}
			 {backgroundTypeDiv === "background_img_color" ? (
				 <BackgroundImageAndColorSave
					 deviceSizes={deviceSizes}
					 backgroundImageAndColorOptions={backgroundImageAndColorOptions}
					 divBlockID={divBlockID}
					 deviceSizesForPictureTag={deviceSizesForPictureTag}
				 />
			 ) : (
				 ""
			 )}
			 {backgroundTypeDiv === "gradient" ? (
				 <GradientSave
					 deviceSizes={deviceSizes}
					 gradientOptions={gradientOptions}
					 divBlockID={divBlockID}
				 />
			 ) : (
				 ""
			 )}
			 {backgroundTypeDiv === "background_video" && (
				 <div
					 className={`bs-div__video${
						 backgroundVideoControls === true ? " has-controls" : ""
					 }`}
				 >
					 <video
						 loop={backgroundVideoLoop}
						 autoPlay={backgroundVideoAutoPlay}
						 muted={backgroundVideoMute}
						 {...(backgroundVideoControls ? { controls: "controls" } : {})}
					 >
						 <source src={backgroundVideoUrl + `#t=0.01`} type="video/mp4" />
					 </video>
					 {divImgVideoUrl && (
						 <span className={`video-poster d-md-none d-lg-none`}>
							 <img src={divImgVideoUrl} alt={`Video Poster`} />
						 </span>
					 )}
				 </div>
			 )}
			 <div
				 className={`bs-div__inner ${esFlexClassNames} ${sFlexClassNames} ${mFlexClassNames} ${lFlexClassNames} ${elFlexClassNames}`}
			 >
				 <InnerBlocks.Content />
			 </div>
		 </div>
	 );
 };

 export default save;
