import { __ } from '@wordpress/i18n';
import {
	ToggleControl,
	RangeControl,
	TextControl,
	__experimentalNumberControl as NumberControl,
	SelectControl,
	__experimentalText as Text,
} from '@wordpress/components';

const SliderControl = (props) => {
	const {
		source,
		FieldTypes,
		field,
		properyName,
		setSliderSettingAttribute,
		sourceAttrName,
	} = props;

	const value = field.valueFormatter
		? field.valueFormatter(source[properyName].settings[field.field])
		: source[properyName].settings[field.field];
	const onChange = (value) =>
		field.onChange
			? field.onChange(properyName, value)
			: setSliderSettingAttribute(
				sourceAttrName,
				properyName,
				field.field,
				value
			);

	let element = null;
	switch (field.type) {
		case FieldTypes.Range:
			element = (
				<RangeControl
					label={__(field.label, 'tab-slider-v2')}
					value={value}
					max={
						typeof field.max === 'function'
							? field.max(properyName)
							: field.max
					}
					min={
						typeof field.min === 'function'
							? field.min(properyName)
							: typeof field.min === 'number'
								? field.min
								: 0
					}
					onChange={onChange}
				/>
			);
			break;
		case FieldTypes.Toggle:
			element = (
				<ToggleControl
					label={__(field.label, 'tab-slider-v2')}
					checked={value}
					onChange={onChange}
				/>
			);
			break;
		case FieldTypes.Number:
			element = (
				<NumberControl
					label={__(field.label, 'tab-slider-v2')}
					isShiftStepEnabled={typeof field.step === 'number'}
					onChange={(value) =>
						field.onChange
							? field.onChange(properyName, value)
							: setSliderSettingAttribute(
								sourceAttrName,
								properyName,
								field.field,
								value
							)
					}
					max={
						typeof field.max === 'function'
							? field.max(properyName)
							: field.max
					}
					min={
						typeof field.min === 'function'
							? field.min(properyName)
							: typeof field.min === 'number'
								? field.min
								: 0
					}
					shiftStep={field.step}
					value={value}
					className="components-base-control"
				/>
			);
			break;
		case FieldTypes.Select:
			element = (
				<SelectControl
					label={__(field.label, 'tab-slider-v2')}
					value={value}
					options={field.options}
					onChange={onChange}
					multiple={field.multiple}
				/>
			);
			break;
		case FieldTypes.Text:
			element = (
				<TextControl
					label={__(field.label, 'tab-slider-v2')}
					value={value}
					onChange={onChange}
				/>
			);
			break;

		default:
			element = null;
			break;
	}
	return element;
};

export default SliderControl;
