import { PanelBody, ColorPalette, RangeControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";

const changeAttributeValue = (
  value,
  propertyName,
  gradientOptions,
  setAttributes,
  attributeVariable
) => {
  let newValuesObj = gradientOptions[propertyName].settings;
  newValuesObj = { ...newValuesObj, [attributeVariable]: value };
  let gradientOptionsNew = gradientOptions[propertyName];
  gradientOptionsNew = {
    ...gradientOptionsNew,
    settings: newValuesObj,
  };
  setAttributes({
    gradientOptions: {
      ...gradientOptions,
      [propertyName]: gradientOptionsNew,
    },
  });
};

const GradientSettings = (props) => {
  const { deviceSizes, gradientOptions, colors, setAttributes } = props;
  return (
    <>
      {deviceSizes.map(([text, propertyName], index) => {
        const title =
          propertyName === "regular"
            ? `${text} (Greater than 1280px)`
            : propertyName === "desktop"
            ? `${text} (Between 576px and 1280px)`
            : `${text} (Less than 576px)`;
        return (
          <PanelBody title={title} initialOpen={index === 0} key={index}>
            <>
              <ColorPalette
                label={__("Select Background Color #1", "berg")}
                colors={colors}
                value={gradientOptions[propertyName].settings.gradientColorOne}
                onChange={(value) =>
                  changeAttributeValue(
                    value,
                    propertyName,
                    gradientOptions,
                    setAttributes,
                    "gradientColorOne"
                  )
                }
              />
              <ColorPalette
                label={__("Select Background Color #2", "berg")}
                colors={colors}
                value={gradientOptions[propertyName].settings.gradientColorTwo}
                onChange={(value) =>
                  changeAttributeValue(
                    value,
                    propertyName,
                    gradientOptions,
                    setAttributes,
                    "gradientColorTwo"
                  )
                }
              />
              <RangeControl
                label={__("Gradient Direction (Degrees)", "berg")}
                min={1}
                max={360}
                value={gradientOptions[propertyName].settings.gradientDirection}
                initialPosition={
                  gradientOptions[propertyName].settings.gradientDirection
                }
                allowReset={true}
                onChange={(val) =>
                  changeAttributeValue(
                    val,
                    propertyName,
                    gradientOptions,
                    setAttributes,
                    "gradientDirection"
                  )
                }
              />
              <RangeControl
                label={__("Color 1 Location", "berg")}
                min={1}
                max={100}
                value={
                  gradientOptions[propertyName].settings
                    .gradientColorOneLocation
                }
                initialPosition={0}
                allowReset={true}
                initialPosition={
                  gradientOptions[propertyName].settings
                    .gradientColorOneLocation
                }
                onChange={(val) =>
                  changeAttributeValue(
                    val,
                    propertyName,
                    gradientOptions,
                    setAttributes,
                    "gradientColorOneLocation"
                  )
                }
              />
              <RangeControl
                label={__("Color 2 Location", "berg")}
                min={1}
                max={100}
                value={
                  gradientOptions[propertyName].settings
                    .gradientColorTwoLocation
                }
                initialPosition={
                  gradientOptions[propertyName].settings
                    .gradientColorTwoLocation
                }
                allowReset={true}
                onChange={(val) =>
                  changeAttributeValue(
                    val,
                    propertyName,
                    gradientOptions,
                    setAttributes,
                    "gradientColorTwoLocation"
                  )
                }
              />
            </>
          </PanelBody>
        );
      })}
    </>
  );
};

export default GradientSettings;
