import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";

const edit = () => {
  const ALLOWED_BLOCKS = [
    "e25m/media-elements",
    "core/paragraph",
    "e25m/blurb",
    "e25m/pro-button",
    "core/list",
    "core/heading",
    "core/quote",
    "e25m/div",
  ];
  const blockProps = useBlockProps();

  return (
    <div {...blockProps} key={blockProps.id}>
      <div className="accordion-right-side">
        <span className="side-info">Slider Accordion Content</span>
        <InnerBlocks allowedBlocks={ALLOWED_BLOCKS} templateLock={false} />
      </div>
    </div>
  );
};

export default edit;
