/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n"
import { InnerBlocks, InspectorControls, useBlockProps } from "@wordpress/block-editor"
import { Panel, PanelBody, SelectControl, ToggleControl } from "@wordpress/components"
const { useEffect, useState } = wp.element
import Select from "react-select"
import { capitalize } from "lodash"

const { withSelect } = wp.data

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
//import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @param {Object} [props]           Properties passed from the editor.
 * @param {string} [props.className] Class name generated for the block.
 *
 * @return {WPElement} Element to render.
 */

const ALLOWED_BLOCK = ["e25m/column"]

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {}
  const { attributes, setAttributes } = props
  const { justifyContentClass, directionClass, rowClassNames, rowVisibility } = attributes
  const [rowClasses, setrowClasses] = useState([])
  const [defaultClassValues, setDefaultClassValues] = useState([])
  const blockProps = useBlockProps()

  useEffect(() => {
    const classList = []
    if (coreComponents["bs-row"] && coreComponents["bs-row"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-row"]}`,
        label: "Default",
      })
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      })
    }

    if (coreComponentsTheme["bs-row"] && coreComponentsTheme["bs-row"].length > 0) {
      coreComponentsTheme["bs-row"].map((className, index) => {
        let classLabel = className.replace("bs-row--", "").split("-")
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        })
      })
    }
    const defaultValues = rowClassNames ? rowClassNames : [classList[0]]
    setrowClasses(classList)
    setDefaultClassValues(defaultValues)
  }, [JSON.stringify(props.blockClasses)])

  const classNames = rowClassNames ? rowClassNames.map(x => x.value).join(" ") : ""

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        <Panel>
          <PanelBody title={__("Row Flex Classes", "")} initialOpen={false}>
            <SelectControl
              label={__("Justify Content", "")}
              value={props.attributes.justifyContentClass}
              options={[
                { value: "", label: "Select Class" },
                {
                  value: "justify-content-start",
                  label: "Justify Content Start",
                },
                {
                  value: "justify-content-end",
                  label: "Justify Content End",
                },
                {
                  value: "justify-content-center",
                  label: "Justify Content Center",
                },
                {
                  value: "justify-content-between",
                  label: "Justify Content Between",
                },
                {
                  value: "justify-content-around",
                  label: "Justify Content Around",
                },
              ]}
              onChange={val => {
                setAttributes({ justifyContentClass: val })
              }}
            />
            <SelectControl
              label={__("Direction", "")}
              value={props.attributes.directionClass}
              options={[
                { value: "", label: "Select Class" },
                { value: "flex-sm-row-reverse", label: "SM Row Reverse" },
                { value: "flex-md-row-reverse", label: "MD Row Reverse" },
                { value: "flex-lg-row-reverse", label: "LG Row Reverse" },
                { value: "flex-xl-row-reverse", label: "XL Row Reverse" },
              ]}
              onChange={val => {
                setAttributes({ directionClass: val })
              }}
            />
          </PanelBody>
          <PanelBody title={__("Row Class", "")} initialOpen={false}>
            <Select
              defaultValue={defaultClassValues}
              isMulti
              name="rowClasses"
              options={rowClasses}
              className="basic-multi-select"
              classNamePrefix="select"
              onChange={val => {
                setAttributes({
                  rowClassNames: val ? val : [],
                })
              }}
            />
          </PanelBody>
          <PanelBody title={__("Visibility", "")} initialOpen={false}>
            <ToggleControl
              label="Row Enable/Disable"
              checked={rowVisibility}
              onChange={val => {
                setAttributes({ rowVisibility: val })
              }}
            />
          </PanelBody>
        </Panel>
      </InspectorControls>
      <div
        className={`bs-row row ${justifyContentClass} ${directionClass} ${classNames} ${
          rowVisibility === true ? "enable" : "disable"
        }`}
      >
        <InnerBlocks allowedBlocks={ALLOWED_BLOCK} />
      </div>
    </div>
  )
}

export default withSelect((select, ownProps) => {
  const theme = select("core").getCurrentTheme()
  const { block_classes: blockClasses = {} } = theme || {}
  return { blockClasses }
})(Edit)
