import { PanelBody, Button } from "@wordpress/components";
import { MediaUpload, MediaUploadCheck } from "@wordpress/block-editor";
import { __ } from "@wordpress/i18n";

const changeAttributeValue = (
  divImgId,
  divImgUrl,
  divImgAlt,
  deviceSize,
  backgroundImageOptions,
  setAttributes
) => {
  let newValuesObj = backgroundImageOptions[deviceSize].settings;
  let newValues = { divImgId, divImgUrl, divImgAlt };
  newValuesObj = { ...newValuesObj, ...newValues };

  let optionsNew = backgroundImageOptions[deviceSize];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    backgroundImageOptions: {
      ...backgroundImageOptions,
      [deviceSize]: optionsNew,
    },
  });
};

const change2xImageAttributeValue = (
  image2xId,
  image2xUrl,
  image2xAlt,
  deviceSize,
  backgroundImageOptions,
  setAttributes
) => {
  let newValuesObj = backgroundImageOptions[deviceSize].settings;
  let newValues = { image2xId, image2xUrl, image2xAlt };
  newValuesObj = { ...newValuesObj, ...newValues };

  let optionsNew = backgroundImageOptions[deviceSize];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    backgroundImageOptions: {
      ...backgroundImageOptions,
      [deviceSize]: optionsNew,
    },
  });
};

const BackgroundImageSetting = (props) => {
  const { deviceSizes, backgroundImageOptions, setAttributes } = props;
  return (
    <>
      {deviceSizes.map(([text, deviceSize], index) => {
        const { divImgUrl, divImgId, image2xId, image2xUrl } =
          backgroundImageOptions[deviceSize].settings;
        const title =
          deviceSize === "regular"
            ? `${text} (Greater than ${backgroundImageOptions.desktop.breakpoint}px)`
            : deviceSize === "desktop"
            ? `${text} (Between ${backgroundImageOptions.mobile.breakpoint}px and ${backgroundImageOptions.desktop.breakpoint}px)`
            : `${text} (Less than ${backgroundImageOptions.mobile.breakpoint}px)`;
        return (
          <PanelBody title={title} initialOpen={index === 0} key={index}>
            <MediaUploadCheck>
              <MediaUpload
                allowedType={["image"]}
                value={divImgId}
                onSelect={(img) => {
                  let divImgId = img.id ? img.id : "";
                  let divImgUrl = img.url ? img.url : "";
                  let divImgAlt = img.alt ? img.alt : "";
                  changeAttributeValue(
                    divImgId,
                    divImgUrl,
                    divImgAlt,
                    deviceSize,
                    backgroundImageOptions,
                    setAttributes
                  );
                }}
                render={({ open }) => (
                  <div className="media-upload image-upload">
                    {divImgUrl ? (
                      <Button
                        className="media-button remove-button"
                        onClick={() => {
                          changeAttributeValue(
                            "",
                            "",
                            "",
                            deviceSize,
                            backgroundImageOptions,
                            setAttributes
                          );
                        }}
                      >
                        {__("Remove Image", "berg")}
                      </Button>
                    ) : (
                      <Button
                        className={"media-button upload-button"}
                        onClick={open}
                      >
                        {__("Upload Image", "berg")}
                      </Button>
                    )}
                    <div className="image-thumb-wrapper" onClick={open}>
                      {divImgUrl ? (
                        <img
                          className="image-thumb"
                          src={divImgUrl !== undefined ? divImgUrl : ""}
                        />
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                )}
              ></MediaUpload>
            </MediaUploadCheck>
            {/* Settings for 2x size image for responsive images */}
            {deviceSize != "regular" && (
              <MediaUploadCheck>
                <MediaUpload
                  allowedType={["image"]}
                  value={image2xId}
                  onSelect={(img) => {
                    let image2xId = img.id ? img.id : "";
                    let image2xUrl = img.url ? img.url : "";
                    let image2xAlt = img.alt ? img.alt : "";
                    change2xImageAttributeValue(
                      image2xId,
                      image2xUrl,
                      image2xAlt,
                      deviceSize,
                      backgroundImageOptions,
                      setAttributes
                    );
                  }}
                  render={({ open }) => (
                    <div className="media-upload image-upload">
                      {image2xUrl ? (
                        <Button
                          className="media-button remove-button"
                          onClick={() => {
                            change2xImageAttributeValue(
                              "",
                              "",
                              "",
                              deviceSize,
                              backgroundImageOptions,
                              setAttributes
                            );
                          }}
                        >
                          {__("Remove 2x Image", "berg")}
                        </Button>
                      ) : (
                        <Button
                          className={"media-button upload-button"}
                          onClick={open}
                        >
                          {__("Upload 2x Image", "berg")}
                        </Button>
                      )}
                      <div className="image-thumb-wrapper" onClick={open}>
                        {image2xUrl ? (
                          <img
                            className="image-thumb"
                            src={image2xUrl !== undefined ? image2xUrl : ""}
                          />
                        ) : (
                          ""
                        )}
                      </div>
                    </div>
                  )}
                ></MediaUpload>
              </MediaUploadCheck>
            )}
          </PanelBody>
        );
      })}
    </>
  );
};

export default BackgroundImageSetting;
