import { __ } from "@wordpress/i18n";
import { InspectorControls, InnerBlocks, useBlockProps } from "@wordpress/block-editor";
import {
  Button,
  Panel,
  PanelBody,
  SelectControl,
  ToggleControl,
  TextControl,
  RangeControl,
} from "@wordpress/components";
import Select from "react-select";
import { useSelect } from "@wordpress/data";
import { capitalize } from "lodash";
const { useEffect, useState } = wp.element;
const { dispatch, withSelect } = wp.data;
const { createBlock } = wp.blocks;

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {};
  const { attributes, setAttributes, clientId } = props;
  const {
    defaultOpenIndex,
    accordionClassNames,
    sliderEnabled,
    slideArrows,
    slideDots,
    slidesToShow,
    slidesToScroll,
    focusOnSelect,
    autoplay,
    autoplaySpeed,
    visibility,
    breakpoint,
    variableWidth,
    infinite,
    enableProgressBar,
    fadeIn,
  } = attributes;

  const ALLOWED_BLOCKS = ["e25m/slider-accordion-card"];
  const ACC_CARD_TYPE = "e25m/slider-accordion-card";

  const innerBlocks = useSelect(select => select("core/block-editor").getBlock(clientId).innerBlocks);

  // Setting the Initial Accordion Tab dropdown values
  const initialAccordionTabOptions = [];
  innerBlocks.forEach(element => {
    if (element.name === "e25m/slider-accordion-card" && element.attributes.showCard) {
      initialAccordionTabOptions.push({
        label: element.attributes.colHeading
          ? element.attributes.colHeading.replace(/(<([^>]+)>)/gi, "")
          : "Card " + (innerBlocks.findIndex(x => x.clientId === element.clientId) + 1),
        value: initialAccordionTabOptions.length,
      });
    }
  });

  const [accordionClasses, setAccordionClasses] = useState([]);
  const [defaultClassValues, setDefaultClassValues] = useState([]);
  const [accodingVisibility, setAccodingVisibility] = useState("enable");
  const blockProps = useBlockProps();

  useEffect(() => {
    const classList = [];
    if (coreComponents["bs-slider-accordion"] && coreComponents["bs-slider-accordion"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-slider-accordion"]}`,
        label: "Default",
      });
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      });
    }

    if (coreComponentsTheme["bs-slider-accordion"] && coreComponentsTheme["bs-slider-accordion"].length > 0) {
      coreComponentsTheme["bs-slider-accordion"].map((className, index) => {
        let classLabel = className.replace("bs-slider-accordion--", "").split("-");
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        });
      });
      const defaultValues = accordionClassNames ? accordionClassNames : [classList[0]];
      setAccordionClasses(classList);
      setDefaultClassValues(defaultValues);
    }
    if (!props.attributes.visibility) {
      setAccodingVisibility("disable");
    }
  }, [JSON.stringify(props.blockClasses)]);

  const classNames = accordionClassNames ? accordionClassNames.map(x => x.value).join(" ") : "";

  const insertCard = (clientId, index) => {
    dispatch("core/editor").insertBlocks(createBlock(ACC_CARD_TYPE), index, clientId);
  };

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        <Panel>
          <PanelBody title={__("Visibility", "")} initialOpen={false}>
            <ToggleControl
              label="Enable/Disable"
              checked={visibility}
              onChange={val => {
                setAccodingVisibility(val ? "enable" : "disable");
                setAttributes({ visibility: val });
              }}
            />
          </PanelBody>

          <PanelBody title={__("Slider Accordion Options", "")} initialOpen={true}>
            <TextControl
              label={__("Break Point Value", "slider-accordion")}
              value={breakpoint}
              onChange={val =>
                setAttributes({
                  breakpoint: Number(val),
                })
              }
              type="number"
            />
            <SelectControl
              label={__("Initial accordion tab", "slider-accordion")}
              value={defaultOpenIndex}
              options={initialAccordionTabOptions}
              onChange={key => setAttributes({ defaultOpenIndex: Number(key) })}
            />
            <ToggleControl
              checked={sliderEnabled}
              onChange={val => setAttributes({ sliderEnabled: val })}
              label={__("Enable Slider", "slider-accordion")}
            />

            {sliderEnabled && (
              <>
                <ToggleControl
                  checked={enableProgressBar}
                  onChange={val => {
                    setAttributes({ enableProgressBar: val });
                  }}
                  label={__("Progress Line", "tab-slider-v2")}
                />
                <ToggleControl
                  checked={slideArrows}
                  onChange={val => setAttributes({ slideArrows: val })}
                  label={__("Show slider Arrows", "slider-accordion")}
                />

                <ToggleControl
                  checked={slideDots}
                  onChange={val => setAttributes({ slideDots: val })}
                  label={__("Show slider Dots", "slider-accordion")}
                />
                <ToggleControl
                  checked={variableWidth}
                  onChange={val => setAttributes({ variableWidth: val })}
                  label={__("Variable width", "slider-accordion")}
                />
                <ToggleControl
                  checked={infinite}
                  onChange={val => setAttributes({ infinite: val })}
                  label={__("Infinite scroll ", "slider-accordion")}
                />
                <ToggleControl
                  label={__("Focus on Select", "slider-accordion")}
                  checked={focusOnSelect}
                  onChange={val => setAttributes({ focusOnSelect: val })}
                />
                <ToggleControl
                  label="FadeIn"
                  checked={fadeIn}
                  onChange={fadeIn => {
                    setAttributes({ fadeIn });
                  }}
                />
                <ToggleControl
                  label={__("Autoplay", "slider-accordion")}
                  checked={autoplay}
                  onChange={val => setAttributes({ autoplay: val })}
                />

                <RangeControl
                  label={__("Autoplay speed", "slider-accordion")}
                  value={autoplaySpeed}
                  min={1}
                  max={10000}
                  onChange={val => {
                    setAttributes({
                      autoplaySpeed: Number(val),
                    });
                  }}
                  disabled={!autoplay}
                />

                <TextControl
                  label={__("Slides To Show", "slider-accordion")}
                  value={slidesToShow}
                  onChange={val =>
                    setAttributes({
                      slidesToShow: Number(val),
                    })
                  }
                  type="number"
                  min={1}
                />
                <TextControl
                  label={__("Slides To Scroll", "slider-accordion")}
                  value={slidesToScroll}
                  onChange={val =>
                    setAttributes({
                      slidesToScroll: Number(val),
                    })
                  }
                  type="number"
                  min={1}
                />
              </>
            )}
          </PanelBody>
          <PanelBody title={__("Slider Accordion Class", "")} initialOpen={false}>
            <Select
              defaultValue={defaultClassValues}
              isMulti
              name="accordionClasses"
              options={accordionClasses}
              className="basic-multi-select"
              classNamePrefix="select"
              onChange={val => {
                setAttributes({
                  accordionClassNames: val ? val : [],
                });
              }}
            />
          </PanelBody>
        </Panel>
      </InspectorControls>
      <div className={`bs-slider-accordion ${classNames} ${accodingVisibility}`}>
        <div id={`bs_aa-${clientId}`} className="accordion__block">
          <InnerBlocks allowedBlocks={ALLOWED_BLOCKS} renderAppender={() => null} />
          <div className="block-list-appender">
            <Button
              className="components-button is-primary bs-slider-accordion__add-card"
              label="Add Accordion Block"
              icon="plus"
              onClick={() => insertCard(props.clientId, innerBlocks.length)}
            >
              Add Card{" "}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withSelect((select) => {
  const theme = select("core").getCurrentTheme();
  const { block_classes: blockClasses = {} } = theme || {};
  return { blockClasses };
})(Edit);
