import { __ } from '@wordpress/i18n';
import { PanelBody, __experimentalText as Text } from '@wordpress/components';
import Select from 'react-select';
import { useEffect, useState } from '@wordpress/element';
import { getElementClasses } from '../utils';
import SliderControl from './slider-control';
const { withSelect } = wp.data

const NavSliderSettings = (props) => {
	const { coreComponents = {}, coreComponentsTheme = {} } =
		props.blockClasses || {};
	const {
		tabNavSliderClassNames,
		setSliderSettingAttribute,
		setAttributes,
		deviceSizes,
		tabNavsSliderOptions,
		tabContentsSliderOptions,
		navSlides,
		FieldTypes,
	} = props;

	const [navSliderClasses, setNavSliderClasses] = useState([]);
	const [defaultNavClassValues, setDefaultNavClassValues] = useState([]);

	useEffect(() => {
			getElementClasses(
				'bs-slider',
				tabNavSliderClassNames,
				coreComponents,
				coreComponentsTheme,
				(classes, defaultValues) => {
					setNavSliderClasses(classes);
					setDefaultNavClassValues(defaultValues);
				}
			);
	}, [JSON.stringify(props.blockClasses)]);
	const fields = [
		{
			label: 'Slides to show',
			field: 'slidesToShow',
			type: FieldTypes.Range,
			min: 1,
			max: navSlides.length,
			onChange(propertyName, value) {
				const attributeUpdates = [
					{
						attribute: 'tabNavsSliderOptions',
						layout: propertyName,
						property: 'slidesToShow',
						value: value,
					},
				];
				if (
					tabContentsSliderOptions[propertyName].settings[
						'slidesToShow'
					] > value
				) {
					attributeUpdates.push({
						attribute: 'tabContentsSliderOptions',
						layout: propertyName,
						property: 'slidesToShow',
						value: value,
					});
				}
				if (
					tabContentsSliderOptions[propertyName].settings[
						'slidesToScroll'
					] > value
				) {
					attributeUpdates.push({
						attribute: 'tabContentsSliderOptions',
						layout: propertyName,
						property: 'slidesToScroll',
						value: value,
					});
				}
				setSliderSettingAttribute(attributeUpdates);
			},
		},
		{
			label: 'Slides to scroll',
			field: 'slidesToScroll',
			type: FieldTypes.Range,
			min: 1,
			max: navSlides.length,
		},
		{
			label: 'Show dots',
			field: 'dots',
			type: FieldTypes.Toggle,
		},
		{
			label: 'Show arrows',
			field: 'arrows',
			type: FieldTypes.Toggle,
		},
		{
			label: "'Previous' arrow custom HTML",
			field: 'prevArrow',
			type: FieldTypes.Text,
			shouldRenderCallback(properyName) {
				return (
					tabNavsSliderOptions[properyName].settings.arrows === true
				);
			},
		},
		{
			label: "'Next' arrow custom HTML",
			field: 'nextArrow',
			type: FieldTypes.Text,
			shouldRenderCallback(properyName) {
				return (
					tabNavsSliderOptions[properyName].settings.arrows === true
				);
			},
		},
		{
			label: 'Scroll speed',
			field: 'speed',
			type: FieldTypes.Range,
			min: 1,
			max: 10000,
		},
		{
			label: 'Draggable',
			field: 'draggable',
			type: FieldTypes.Toggle,
			onChange(propertyName, isChecked) {
				const attributeUpdates = [
					{
						attribute: 'tabNavsSliderOptions',
						layout: propertyName,
						property: 'draggable',
						value: isChecked,
					},
					{
						attribute: 'tabNavsSliderOptions',
						layout: propertyName,
						property: 'swipe',
						value: isChecked,
					},
				];
				setSliderSettingAttribute(attributeUpdates);
			},
		},
		{
			label: 'Swipe',
			field: 'swipe',
			type: FieldTypes.Toggle,
		},
		{
			label: 'Fade',
			field: 'fade',
			type: FieldTypes.Toggle,
		},
		{
			label: 'Variable width',
			field: 'variableWidth',
			type: FieldTypes.Toggle,
		},
		{
			label: 'Adaptive height',
			field: 'adaptiveHeight',
			type: FieldTypes.Toggle,
		},
		{
			label: 'Focus on select',
			field: 'focusOnSelect',
			type: FieldTypes.Toggle,
		},
		{
			label: 'Center mode',
			field: 'centerMode',
			type: FieldTypes.Toggle,
			onChange(propertyName, isChecked) {
				const attributeUpdates = [
					{
						attribute: 'tabNavsSliderOptions',
						layout: propertyName,
						property: 'centerMode',
						value: isChecked,
					},
				];

				if (isChecked === false) {
					attributeUpdates.push({
						attribute: 'tabNavsSliderOptions',
						layout: propertyName,
						property: 'centerPadding',
						value: '50px',
					});
				}
				setSliderSettingAttribute(attributeUpdates);
			},
		},
		{
			label: 'Center padding(px)',
			field: 'centerPadding',
			type: FieldTypes.Number,
			shouldRenderCallback(propertyName) {
				return (
					tabNavsSliderOptions[propertyName].settings.centerMode ===
					true
				);
			},
			onChange(propertyName, value) {
				const castedValue = parseInt(value);
				setSliderSettingAttribute(
					'tabNavsSliderOptions',
					propertyName,
					'centerPadding',
					!isNaN(castedValue) ? `${castedValue}px` : undefined
				);
			},
			valueFormatter(value) {
				const castedValue =
					typeof value === 'string'
						? parseInt(value.replace('px', ''))
						: NaN;
				return !isNaN(castedValue) ? castedValue : '';
			},
		},
		{
			label: 'Lazy load',
			field: 'lazyLoad',
			options: [
				{
					label: __('On demand', 'tab-slider-v2'),
					value: 'ondemand',
				},
				{
					label: __('Progressive', 'tab-slider-v2'),
					value: 'progressive',
				},
			],
			type: FieldTypes.Select,
		},
		{
			label: 'Unslick (Destroy slider)',
			field: 'unslick',
			type: FieldTypes.Toggle,
		},
		{
			label: 'Initial slide',
			field: 'initialSlide',
			type: FieldTypes.Number,
			max: navSlides.length,
			min: 1,
			step: 1,
			onChange(propertyName, value) {
				const castedValue = parseInt(value);
				setSliderSettingAttribute(
					'tabNavsSliderOptions',
					propertyName,
					'initialSlide',
					!isNaN(castedValue) ? castedValue - 1 : undefined
				);
			},
			valueFormatter(value) {
				const castedValue =
					typeof value === 'string'
						? parseInt(value)
						: typeof value === 'number'
						? value
						: NaN;
				return !isNaN(castedValue) ? castedValue + 1 : '';
			},
		},
	];

	return (
		<PanelBody
			title={__('Navigation slider', 'tab-slider-v2')}
			initialOpen={false}
		>
			{deviceSizes.map(([text, propertyName], index) => (
				<PanelBody
					title={__(text, 'tab-slider-v2')}
					initialOpen={index === 0}
					key={index}
				>
					{fields.map((field, fieldIndex) => {
						if (
							typeof field.shouldRenderCallback === 'function' &&
							field.shouldRenderCallback(propertyName) === false
						) {
							return null;
						}
						return (
							<SliderControl
								{...{
									FieldTypes,
									field,
									propertyName,
									setSliderSettingAttribute,
								}}
								source={tabNavsSliderOptions}
								sourceAttrName="tabNavsSliderOptions"
								key={fieldIndex}
							/>
						);
					})}
					<Text>{__('Slider classes', 'tab-slider-v2')}</Text>
					<Select
						defaultValue={defaultNavClassValues}
						isMulti
						name="tabNavSliderClassNames"
						options={navSliderClasses}
						className="basic-multi-select"
						classNamePrefix="select"
						onChange={(val) => {
							setAttributes({
								tabNavSliderClassNames: val ? val : [],
							});
						}}
					/>
				</PanelBody>
			))}
		</PanelBody>
	);
};

export default withSelect((select, ownProps) => {
	const theme = select('core').getCurrentTheme();
	const { block_classes: blockClasses = {} } = theme || {};
	return { blockClasses };
})(NavSliderSettings);
