/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";

//Inner Content Image
const InnerContentImage = (props) => {
  const { attributes } = props;
  const {
    imgURL,
    imgAlt,
    mobileImgURL,
    mobile2xImgURL,
    desktopImgURL,
    desktop2xImgURL,
    desktopBreakpoint,
    mobileBreakpoint,
    isImgLazyLoad
  } = attributes;

  const image_lazy_load = isImgLazyLoad ? 'lazy' : null;
  return (
    <>
      {imgURL ? (
        <div className="bs-card__image" key="image">
          <figure>
            <picture>
              {mobileImgURL && (
                <>
                  <source
                    srcSet={
                      mobile2xImgURL
                        ? `${mobileImgURL},
                            ${mobile2xImgURL} 2x`
                        : `${mobileImgURL}`
                    }
                    media={`(max-width:${mobileBreakpoint - 1}px)`}
                  />
                </>
              )}
              {desktopImgURL && (
                <>
                  <source
                    srcSet={
                      desktop2xImgURL
                        ? `${desktopImgURL},
                            ${desktop2xImgURL} 2x`
                        : `${desktopImgURL}`
                    }
                    media={`(max-width:${desktopBreakpoint}px)`}
                  />
                </>
              )}
              <img
                className={`${attributes.imageBehavior} img-fluid`}
                loading ={image_lazy_load}
                src={imgURL}
                alt={imgAlt ? imgAlt : ""}
              />
            </picture>
          </figure>
        </div>
      ) : null}
    </>
  );
};

export default InnerContentImage;
