/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import { RichText } from "@wordpress/block-editor";

const InnerContent = (props) => {
  const { attributes } = props;
  const { title, subtitle, content, titleTag, subtitleTag, paragraphTag } =
    attributes;

  return (
    <>
      {title ? (
        <div className="bs-card__title" key="title">
          <RichText.Content value={title} tagName={titleTag} />
        </div>
      ) : null}
      {subtitle ? (
        <div className="bs-card__subtitle" key="subtitle">
          <RichText.Content value={subtitle} tagName={subtitleTag} />
        </div>
      ) : null}
      {content ? (
        <div className="bs-card__description" key="description">
          <RichText.Content value={content} tagName={paragraphTag} />
        </div>
      ) : null}
    </>
  );
};

export default InnerContent;
