import { __ } from "@wordpress/i18n";
import { MediaUpload, MediaUploadCheck } from "@wordpress/block-editor";
import {
  Button,
  PanelBody,
  PanelRow,
  RangeControl,
  SelectControl,
  ToggleControl,
  __experimentalConfirmDialog as ConfirmDialog,
  __experimentalNumberControl as NumberControl,
} from "@wordpress/components";
const { useState } = wp.element;
import { Fragment } from 'react';

const LottieAnimationSettings = ({
  settings,
  updateAnimationSettings,
  deviceTypeKey,
  deviceText,
  deviceSizes,
  animationSettings,
  excludeInit,
  enableFileChange,
  enableSegments,
  environment,
}) => {
  const {
    direction,
    delay,
    loop,
    mouseOutAction,
    numberOfLoops,
    path,
    playSpeed,
    scrollRelativeTo,
    trigger,
    triggerMethod,
    fileName,
    animateViewPort,
    oneTimePlay,
    segmentFrames = [],
  } = settings;

  const [isOpen, setIsOpen] = useState(false);
  const [deviceKey, setDeviceKey] = useState("desktop");

  const getDeviceTypeKey = deviceKey => {
    return deviceKey === "ipadLandscape"
      ? "Ipad Landscape"
      : `${deviceKey[0].toUpperCase()}${deviceKey.slice(1).toLowerCase()}`;
  };

  let segmentFramesFields;
  if (Array.isArray(segmentFrames) && segmentFrames.length) {
    segmentFramesFields = segmentFrames.map((segment, index) => {
      let minValue = parseInt(segmentFrames[index].start) + 1;
      return (
        <div className="components-base-control components-repeater-control__row">
          <div className="components-base-control components-repeater-control__column">
            <NumberControl
              placeholder={`Start`}
              min={0}
              value={segmentFrames[index].start}
              onChange={value => {
                const segmentFrames = [...settings.segmentFrames];
                segmentFrames[index].start = parseInt(value);
                updateAnimationSettings(deviceTypeKey, {
                  segmentFrames,
                });
              }}
            />
          </div>
          <div className="components-base-control components-repeater-control__column">
            <NumberControl
              placeholder={`End`}
              min={minValue}
              value={segmentFrames[index].end}
              onChange={value => {
                const segmentFrames = [...settings.segmentFrames];
                segmentFrames[index].end = parseInt(value);
                updateAnimationSettings(deviceTypeKey, {
                  segmentFrames,
                });
              }}
            />
          </div>
          <div className="components-base-control components-repeater-control__column">
            <span
              className="components-repeater-control__icon dashicons dashicons-dismiss"
              onClick={() => {
                const segmentFrames = [...settings.segmentFrames];
                segmentFrames.splice(index, 1);
                updateAnimationSettings(deviceTypeKey, {
                  segmentFrames,
                });
              }}
            ></span>
          </div>
        </div>
      );
    });
  }

  return (
    <div>
      <PanelBody title={__(deviceText, "lottie-animator")} initialOpen={false}>
        {environment === "development" ||
          (environment === "production" && !excludeInit) ||
          (environment === "production" && excludeInit && enableFileChange) ? (
          <div className="components-base-control media-preview__row">
            <label className={"components-base-control__label"}>Lottie JSON File</label>
            <div className="editor-post-featured-image">
              <MediaUploadCheck>
                <MediaUpload
                  onSelect={media =>
                    updateAnimationSettings(deviceTypeKey, {
                      path: media.url,
                      fileName: media.filename,
                    })
                  }
                  allowedTypes={["application/json", "text/plain"]}
                  value={path}
                  render={({ open }) => (
                    <>
                      {path ? (
                        <>
                          <p>{fileName}</p>
                          <Button onClick={open} isPrimary>
                            {__("Replace JSON", "")}
                          </Button>
                          <Button
                            onClick={() =>
                              updateAnimationSettings(deviceTypeKey, {
                                path: "",
                                fileName: "",
                              })
                            }
                            isSecondary
                            isDestructive
                          >
                            {__("Remove JSON", "")}
                          </Button>
                        </>
                      ) : (
                        <Button className={"editor-post-featured-image__toggle"} onClick={open}>
                          Choose the Lottie File
                        </Button>
                      )}
                    </>
                  )}
                />
              </MediaUploadCheck>
            </div>
          </div>
        ) : null}
        {excludeInit ? (
          enableSegments && (
            <div className="components-base-control components-repeater-control">
              <PanelRow>{__("Lottie Segments (By Frames)", "")}</PanelRow>
              {segmentFramesFields}
              <Button
                isDefault
                onClick={value => {
                  const segmentFrames = settings.hasOwnProperty("segmentFrames") ? [...settings.segmentFrames] : [];
                  segmentFrames.push({
                    start: "",
                    end: "",
                  });
                  updateAnimationSettings(deviceTypeKey, {
                    segmentFrames,
                  });
                }}
              >
                <span className="dashicons dashicons-plus-alt"></span>&nbsp;
                {__("Add Segment(s)")}
              </Button>
            </div>
          )
        ) : (
          <>
            <ToggleControl
              label="Animate Only Once"
              help={__(
                "After the page loaded, animation will play only once on page scroll. This is not applicable when the Trigger option is enabled.",
                "lottie-animator"
              )}
              checked={oneTimePlay}
              onChange={oneTimePlay => {
                updateAnimationSettings(deviceTypeKey, {
                  oneTimePlay,
                });
              }}
            />
            <ToggleControl
              label="Trigger"
              checked={trigger}
              onChange={trigger => {
                trigger
                  ? updateAnimationSettings(deviceTypeKey, {
                    trigger,
                    loop: false,
                    animateViewPort: 0,
                  })
                  : updateAnimationSettings(deviceTypeKey, {
                    trigger,
                    animateViewPort: 0.5,
                  });
              }}
            />
            {trigger && (
              <SelectControl
                label={__("Trigger Method", "")}
                value={triggerMethod}
                options={[
                  { value: "pageHover", label: "Play On Hover" },
                  { value: "pageClick", label: "Play On Click" },
                  { value: "pageScroll", label: "Play On Scroll" },
                ]}
                onChange={triggerMethod => {
                  triggerMethod === "pageClick" || triggerMethod === "pageScroll"
                    ? updateAnimationSettings(deviceTypeKey, {
                      triggerMethod,
                      mouseOutAction: "none",
                    })
                    : updateAnimationSettings(deviceTypeKey, {
                      triggerMethod,
                    });
                }}
              />
            )}

            {trigger && triggerMethod === "pageHover" && (
              <SelectControl
                label={__("On Mouse Leave Action", "")}
                value={mouseOutAction}
                options={[
                  { value: "none", label: "None" },
                  { value: "stop", label: "Stop" },
                  { value: "pause", label: "Pause" },
                  { value: "reverse", label: "Reverse" },
                ]}
                onChange={mouseOutAction => {
                  mouseOutAction === "reverse"
                    ? updateAnimationSettings(deviceTypeKey, {
                      mouseOutAction,
                      direction: 1,
                    })
                    : updateAnimationSettings(deviceTypeKey, {
                      mouseOutAction,
                    });
                }}
              />
            )}
            {trigger && triggerMethod === "pageScroll" && (
              <SelectControl
                label={__("Relative To", "")}
                value={scrollRelativeTo}
                options={[
                  { value: "entirePage", label: "Entire Page" },
                  { value: "withinSection", label: "Within Section" },
                ]}
                onChange={scrollRelativeTo => {
                  updateAnimationSettings(deviceTypeKey, {
                    scrollRelativeTo,
                  });
                }}
              />
            )}

            {!trigger && (
              <ToggleControl
                label="Loop"
                checked={loop}
                onChange={loop => {
                  updateAnimationSettings(deviceTypeKey, {
                    loop,
                  });
                }}
              />
            )}
            {!trigger && loop && (
              <RangeControl
                label="Number Of Times"
                min="0"
                max="10"
                value={numberOfLoops}
                onChange={numberOfLoops => {
                  updateAnimationSettings(deviceTypeKey, {
                    numberOfLoops,
                  });
                }}
              />
            )}
            {!trigger && (
              <RangeControl
                label="Delay (ms)"
                min="0"
                max="9999"
                value={delay}
                onChange={delay => {
                  updateAnimationSettings(deviceTypeKey, {
                    delay,
                  });
                }}
              />
            )}
            {mouseOutAction !== "reverse" && (
              <SelectControl
                label={__("Direction", "")}
                value={direction}
                options={[
                  { value: 1, label: "Forwards" },
                  { value: -1, label: "Backwards" },
                ]}
                checked={direction}
                onChange={direction => {
                  updateAnimationSettings(deviceTypeKey, {
                    direction,
                  });
                }}
              />
            )}
            {(!trigger || (trigger && triggerMethod !== "pageScroll")) && (
              <RangeControl
                label="Playback Speed"
                min="0.1"
                max="2.5"
                step="0.1"
                value={playSpeed}
                onChange={playSpeed => {
                  updateAnimationSettings(deviceTypeKey, {
                    playSpeed,
                  });
                }}
              />
            )}
            {!trigger && (
              <RangeControl
                label="Animate in Viewport(%)"
                min="1"
                max="100"
                step="1"
                value={animateViewPort}
                onChange={animateViewPort => {
                  updateAnimationSettings(deviceTypeKey, {
                    animateViewPort,
                  });
                }}
              />
            )}
            <PanelBody title={__("Copy Settings ", "")} initialOpen={false}>
              {deviceSizes.map(([text, key], index) => {
                if (key !== deviceTypeKey) {
                  let {
                    settings: { copy },
                  } = animationSettings[deviceTypeKey];

                  return (
                    <Fragment key={key}>
                      <Button
                        onClick={() => {
                          setIsOpen(true);
                          setDeviceKey(key);
                        }}
                        className="copy-settings"
                        isSecondary
                      >
                        {`To ${text}`}
                      </Button>
                    </Fragment>
                  );
                }
              })}

              <ConfirmDialog
                isOpen={isOpen}
                onConfirm={() => {
                  setIsOpen(false);
                  updateAnimationSettings(deviceKey, {
                    ...settings,
                  });
                }}
                onCancel={() => {
                  setIsOpen(false);
                }}
              >
                {__(
                  `Are you sure you want to copy ${deviceText} settings to ${getDeviceTypeKey(deviceKey)}? `,
                  "lottie-animator"
                )}
                <strong>{__(`This action cannot be undone!`, "lottie-animator")}</strong>
              </ConfirmDialog>
            </PanelBody>
          </>
        )}
      </PanelBody>
    </div>
  );
};

export default LottieAnimationSettings;
