/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
//import './editor.scss';
import { __ } from "@wordpress/i18n";
import { withSelect } from "@wordpress/data";
import { compose, withState } from "@wordpress/compose";
import { InspectorControls, useBlockProps } from "@wordpress/block-editor";
import {
  Disabled,
  PanelBody,
  RadioControl,
  SelectControl,
  TextControl,
  ToggleControl,
  Tooltip,
} from "@wordpress/components"
import ServerSideRender from "@wordpress/server-side-render"
import Select from "react-select"
import SelectAsync from "react-select/async"
import { unstable_batchedUpdates } from "react-dom"
import { addQueryArgs } from "@wordpress/url"
import apiFetch from "@wordpress/api-fetch"

import TagSelector from "~berg/ui-components/tag-selector"
import ServerSideRendererError from "~berg/ui-components/server-side-renderer-error"
import { capitalize } from "lodash"
import SortableSelect from "~berg/ui-components/sortable-multi-select"

const { useEffect, useState } = wp.element;

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @param {Object} [props]           Properties passed from the editor.
 * @param {string} [props.className] Class name generated for the block.
 *
 * @return {WPElement} Element to render.
 */

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {}
  const { attributes, setAttributes, postTypes, taxonomies } = props
  const {
    selectedPostType,
    selectedPost,
    selectedPostId,
    imageAppearance,
    anchorAppearance,
    dateFormat,
    displayOrder,
    titleTag,
    singlePostClass,
    singlePostClassNames,
    popupDisplayOrder,
    fancyboxStyleClassName,
    postVisibility,
    titleCharLimit = 0,
    enableTaxonomiesLink,
    postId,
  } = attributes;

  const [postSelectIsLoading, setPostSelectIsLoading] = useState(false);
  const [posts, setPosts] = useState([]);
  const blockProps = useBlockProps();

  const unwantedPostTypes = ["Media", "Blocks"];

  // filter post types and make post list array to use on post type dropdown
  const filteredPostTypes = _.filter(postTypes, type => {
    return !unwantedPostTypes.includes(type.name);
  }).map(type => {
    return { value: type.slug, label: type.name };
  });

  // decodes unicode decimals(ex: &#8216 -> ')
  const decodeUnicodes = encodedText => {
    let textArea = document.createElement("textarea");
    textArea.innerHTML = encodedText;
    return textArea.innerText;
  };

  // To decode already saved values
  if (typeof selectedPost === "object" && typeof selectedPost.label === "string") {
    selectedPost.label = decodeUnicodes(selectedPost.label);
  }

  // create post type related taxonomies list
  const taxonomyList = _.map(taxonomies, taxonomy => {
    let taxonomyName = taxonomy.labels.singular_name;
    let taxonomySlug = taxonomy.slug;
    let supportedPostTypes = taxonomy.types;
    return {
      postTypes: supportedPostTypes,
      value: taxonomySlug,
      label: taxonomyName,
    };
  });

  // filter taxonomies according to the selected post type
  const filteredTaxonomyList = _.filter(taxonomyList, taxonomy => {
    return Array.isArray(taxonomy.postTypes) && taxonomy.postTypes.indexOf(selectedPostType) !== -1;
  }).map(taxonomy => {
    return { value: "taxonomy_" + taxonomy.value, label: taxonomy.label };
  });

  let defaultDisplayOrder = [
    { value: "image", label: "Image" },
    { value: "title", label: "Title" },
    { value: "content", label: "Content" },
    { value: "excerpt", label: "Excerpt" },
    { value: "date", label: "Date" },
    { value: "author", label: "Author Name" },
    { value: "more", label: "Read more" },
    { value: "featured_image", label: "Featured image" },
    { value: "event_date", label: "Event Date [start date - End date]" },
    { value: "post_type", label: "Post Type Name" },
    { value: "post_reading_time", label: "Post Reading Time" },
    ...filteredTaxonomyList,
  ];

  let defaultPopupDisplayOrder = [
    { value: "image", label: "Image" },
    { value: "title", label: "Title" },
    { value: "content", label: "Content" },
    { value: "excerpt", label: "Excerpt" },
    { value: "date", label: "Date" },
    { value: "author", label: "Author Name" },
    { value: "featured_image", label: "Featured image" },
    { value: "event_date", label: "Event Date [start date - End date]" },
    { value: "post_type", label: "Post Type Name" },
    { value: "post_reading_time", label: "Post Reading Time" },
    ...filteredTaxonomyList,
  ];

  //Post type dropdown onchange function
  const onChangePostType = newVal => {
    setAttributes({
      selectedPostType: newVal,
      selectedPostId: 0,
      selectedPost: undefined,
    });
    // displayOrderCallback();
    getPosts("", null, newVal);
  };

  useEffect(() => {
    getPosts("", null, selectedPostType);
    // displayOrderCallback();
  }, []);

  // check selectedPost is exists.
  // future need to remove selectedPost
  const selectedPId =
    selectedPostId != 0
      ? selectedPostId
      : selectedPost && typeof selectedPost === "object" && selectedPost.value && selectedPost.value != 0
        ? selectedPost.value
        : null;
  const selectedPDropdownOption = posts.find(post => post.value == selectedPId);

  //Post dropdown onchange function
  const onChangePosts = newVal => {
    setAttributes({
      selectedPost: undefined,
      selectedPostId: newVal.value
    });
  };

  //Image Appearance radio button onselect function
  const onSelectImageAppearance = newVal => {
    setAttributes({ imageAppearance: newVal });
  };

  //Anchor Element Appearance radio button onselect function
  const onSelectAnchorAppearance = newVal => {
    setAttributes({ anchorAppearance: newVal });
  };

  //Date format on change function
  const onChangeDateFormat = newVal => {
    setAttributes({ dateFormat: newVal });
  };

  const onChangeTitleTag = newVal => {
    setAttributes({ titleTag: newVal });
  };

  const onChangeDisplayOrder = newVal => {
    setAttributes({ displayOrder: newVal });
  };
  const onChangePopupDisplayOrder = newVal => {
    setAttributes({ popupDisplayOrder: newVal });
  };
  const onSortEnd = ({ oldIndex, newIndex }) => {
    const newValue = arrayMove(displayOrder, oldIndex, newIndex);
    setAttributes({ displayOrder: newValue });
  };

  const onPopupSortEnd = ({ oldIndex, newIndex }) => {
    const newValue = arrayMove(popupDisplayOrder, oldIndex, newIndex);
    setAttributes({ popupDisplayOrder: newValue });
  };

  // To reload the block ( This option can be used when block rendering fails with a JSON error)
  const reloadBlock = () => {
    setAttributes({
      lastUpdated: Date.now(),
    });
  };

  //Setting section classes in a state
  const [singlePostClasses, setsinglePostClasses] = useState([]);
  const [defaultClassValues, setDefaultClassValues] = useState([]);

  const [displayOrderDropdownOptions, setDisplayOrderDropdownOptions] = useState([]);

  useEffect(() => {
    const classList = []
    if (coreComponents["bs-single-post"] && coreComponents["bs-single-post"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-single-post"]}`,
        label: "Default",
      });
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      });
    }

    if (coreComponentsTheme["bs-single-post"] && coreComponentsTheme["bs-single-post"].length > 0) {
      coreComponentsTheme["bs-single-post"].map((className, index) => {
        let classLabel = className.replace("bs-single-post--", "").split("-")
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        })
      })
    }
    const defaultValues = singlePostClassNames ? singlePostClassNames : [classList[0]]
    setsinglePostClasses(classList)
    setDefaultClassValues(defaultValues)
  }, [JSON.stringify(props.blockClasses)])

  useEffect(() => {
    const displayOrderOptions = [];
    apiFetch({
      path: addQueryArgs(`/wp/v2/berg_terms`, {
        per_page: -1, // eslint-disable-line camelcase
      }),
    })
      .then(results => {
        Object.keys(results).forEach(postType => {
          const { display_orders } = results[postType];
          if (postType === selectedPostType) {
            Object.keys(display_orders).forEach((key, index) => {
              displayOrderOptions.push({
                label: display_orders[key],
                value: key,
              });
            });
          }
        });
        if (displayOrderOptions.length > 0) {
          setTimeout(() => {
            setDisplayOrderDropdownOptions(displayOrderOptions);
          }, 1000);
        }
      })
      .catch(() => {
        setTimeout(() => {
          setDisplayOrderDropdownOptions([]);
        }, 1000);
      });
  }, [selectedPostType]);

  // Load posts data
  let getPostsTimeout = null;
  const getPosts = (searchQuery = "", callback = null, postType) => {
    if (getPostsTimeout != null) clearTimeout(getPostsTimeout);
    getPostsTimeout = setTimeout(() => {
      if (typeof postType === "undefined" || typeof postType !== "string") {
        postType = selectedPostType;
      }
      setPostSelectIsLoading(true);
      const query = {
        per_page: 100,
        order: "asc",
        orderby: "title",
        status: "publish",
        search: searchQuery,
        searchBy: "post_title",
      };
      const getData = () => wp.data.select("core").getEntityRecords("postType", postType, query);
      const setPostsData = postList => {
        const postsOptions = postList.map(post => ({
          value: post.id,
          label: decodeUnicodes(post.title.rendered),
        }));
        unstable_batchedUpdates(() => {
          setPostSelectIsLoading(false);
          setPosts(postsOptions);
        });
        if (typeof callback === "function") {
          callback(postsOptions);
        }
      };
      // Request posts data
      let postList = getData();

      // If cached data available return or monitor until request resolved
      if (postList && Array.isArray(postList)) {
        setPostsData(postList);
      } else {
        const unsubscribe = wp.data.subscribe(() => {
          const resolved =
            !wp.data.select("core").isResolving("getEntityRecords", ["postType", postType, query]) &&
            wp.data.select("core").hasFinishedResolution("getEntityRecords", ["postType", postType, query]);
          if (resolved) {
            postList = getData();
            if (postList && Array.isArray(postList)) {
              unsubscribe();
              setPostsData(postList);
            }
          }
        });
      }
    }, 700);
  };

  if (postId) {
    apiFetch({
      path: "/api/bs-single-post/getPostData",
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ postId }),
    }).then(res => {
      if (res) {
        const { selectedPostType } = res;
        setAttributes({
          selectedPostId: postId,
          selectedPostType: selectedPostType,
          postId: 0,
        });
      }
    });
  }

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        <PanelBody title={__("Visibility", "")} initialOpen={false}>
          <ToggleControl
            label="Enable/Disable"
            checked={postVisibility}
            onChange={val => {
              setAttributes({ postVisibility: val });
            }}
          />
        </PanelBody>
        <PanelBody title={__("Post Settings", "berg")} initialOpen={true}>
          <SelectControl
            label={__("Post Type", "berg")}
            help={__("Select a post type", "berg")}
            options={filteredPostTypes}
            value={selectedPostType}
            onChange={onChangePostType}
          />

          <label>{__("Posts", "berg")}</label>
          <SelectAsync
            name="post-list"
            label={__("Post", "berg")}
            help={__("Select a post", "berg")}
            placeholder={__("Select a post", "berg")}
            value={selectedPDropdownOption || null}
            defaultOptions={posts.length > 0 ? posts : true}
            loadOptions={getPosts}
            isLoading={postSelectIsLoading}
            onChange={onChangePosts}
          />

          <TagSelector label={__("Title Tag", "berg")} value={titleTag} onChange={onChangeTitleTag} />

          <TextControl
            label={__("Title character limit", "")}
            type="number"
            value={titleCharLimit}
            onChange={titleCharLimit => setAttributes({ titleCharLimit })}
          />

          <RadioControl
            label={__("Image Appearance", "berg")}
            options={[
              { label: "Image", value: "image" },
              { label: "Background", value: "background" },
            ]}
            selected={imageAppearance}
            onChange={onSelectImageAppearance}
          />

          <RadioControl
            label={__("Anchor Element Appearance", "berg")}
            options={[
              { label: "Wrap All", value: "full" },
              { label: "Inner Link", value: "inner" },
            ]}
            selected={anchorAppearance}
            onChange={onSelectAnchorAppearance}
          />
          {anchorAppearance === "inner" && (
            <ToggleControl
              label={__("Enable clickable links for taxonomies")}
              checked={enableTaxonomiesLink}
              onChange={val => {
                setAttributes({ enableTaxonomiesLink: val });
              }}
            />
          )}

          <TextControl
            label={__("Post Date format", "berg")}
            help={__("For more info: https://wordpress.org/support/article/formatting-date-and-time/", "berg")}
            value={dateFormat}
            onChange={onChangeDateFormat}
          />
          <TextControl
            label="Fancybox class name"
            value={fancyboxStyleClassName}
            placeholder="Fancybox class name"
            onChange={val =>
              setAttributes({
                fancyboxStyleClassName: val,
              })
            }
          />

          <label>{__("Display Order", "berg")}</label>
          <SortableSelect
            value={displayOrder}
            onChange={onChangeDisplayOrder}
            options={displayOrderDropdownOptions ?? defaultDisplayOrder}
          />

          <label>{__("Popup Display Order", "berg")}</label>
          <SortableSelect
            value={popupDisplayOrder}
            onChange={onChangePopupDisplayOrder}
            options={displayOrderDropdownOptions ?? defaultPopupDisplayOrder}
          />
        </PanelBody>
        <PanelBody title={__("Single Post Block Class", "")} initialOpen={false}>
          <Select
            defaultValue={defaultClassValues}
            isMulti
            name="singlePostClasses"
            options={singlePostClasses}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={val => {
              setAttributes({
                singlePostClassNames: val,
              });
            }}
          />
        </PanelBody>
      </InspectorControls>
      <div>
        <div className="button-wrapper">
          <Tooltip text="Reload block">
            <button className="btn btn-default reload" type="button" onClick={reloadBlock}></button>
          </Tooltip>
        </div>
        <Disabled>
          <ServerSideRender
            block="e25m/single-post"
            attributes={attributes}
            urlQueryArgs={{ isEditor: true }}
            httpMethod="POST"
            ErrorResponsePlaceholder={ServerSideRendererError}
          />
        </Disabled>
      </div>
    </div>
  );
};

const applyWithSelect = withSelect((select, props) => {
  let selectCore = select("core");
  //Get all post types
  let postTypes = selectCore.getPostTypes({ per_page: -1 })
  let taxonomies = selectCore.getTaxonomies({ per_page: -1, public: true })
  const theme = selectCore.getCurrentTheme()
  const { block_classes: blockClasses = {} } = theme || {}

  return {
    postTypes,
    taxonomies,
    blockClasses,
  }
})

export default compose(applyWithSelect)(Edit);
