import { __ } from "@wordpress/i18n";
import {
  InspectorControls,
  InnerBlocks,
  useBlockProps,
} from "@wordpress/block-editor";
import {
  Button,
  Panel,
  PanelBody,
  SelectControl,
  RadioControl,
  ToggleControl,
} from "@wordpress/components";
import Select from "react-select";
const { useEffect, useState } = wp.element;
const { createBlock } = wp.blocks;
import { useSelect, dispatch, withSelect } from "@wordpress/data";
import { capitalize } from "lodash";

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {};
  const { attributes, setAttributes, clientId } = props;
  const { acSideChange, floatClassName, alwaysExpanded, defaultOpenIndex, colBreakpoint, accordionClassNames } =
    attributes;

  const ALLOWED_BLOCKS = ["e25m/advance-accordion-card", "e25m/advance-accordion-top"];
  const ACC_CARD_TYPE = "e25m/advance-accordion-card";
  const ACC_TOP_TYPE = "e25m/advance-accordion-top";

  const innerBlocks = useSelect(select => select("core/block-editor").getBlock(clientId).innerBlocks);

  const cardBlocks = innerBlocks.filter(obj => {
    return obj.name === "e25m/advance-accordion-card";
  });

  // Setting the Initial Accordion Tab dropdown values
  const initialAccordionTabOptions = [];
  let arrayIndex = 0;
  innerBlocks.forEach(element => {
    if (typeof element.attributes.showCard == "undefined" || element.attributes.showCard) {
      if (element.name === "e25m/advance-accordion-card" && element.attributes.showCard) {
        initialAccordionTabOptions.push({
          label: element.attributes.colHeading
            ? element.attributes.colHeading.replace(/(<([^>]+)>)/gi, "")
            : "Card " + (cardBlocks.findIndex(x => x.clientId === element.clientId) + 1),
          value: arrayIndex,
        });
      }
      arrayIndex++;
    }
  });

  const colBreakpointOptions = [
    {
      label: "Desktop",
      value: 9999,
    },
    {
      label: "IPad Landscape",
      value: 1200,
    },
    {
      label: "IPad",
      value: 992,
    },
    {
      label: "Mobile",
      value: 576,
    },
  ];

  const [accordionClasses, setAccordionClasses] = useState([]);
  const [defaultClassValues, setDefaultClassValues] = useState([]);

  useEffect(() => {
    const classList = [];
    if (coreComponents["bs-advance-accordion"] && coreComponents["bs-advance-accordion"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-advance-accordion"]}`,
        label: "Default",
      });
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      });
    }

    if (coreComponentsTheme["bs-advance-accordion"] && coreComponentsTheme["bs-advance-accordion"].length > 0) {
      coreComponentsTheme["bs-advance-accordion"].map((className, index) => {
        let classLabel = className.replace("bs-advance-accordion--", "").split("-");
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        });
      });
      const defaultValues = accordionClassNames ? accordionClassNames : [classList[0]];
      setAccordionClasses(classList);
      setDefaultClassValues(defaultValues);
    }
  }, [JSON.stringify(props.blockClasses)]);

  const classNames = accordionClassNames ? accordionClassNames.map(x => x.value).join(" ") : "";

  const insertComponent = (blockName, clientId, index) => {
    dispatch("core/editor").insertBlocks(createBlock(blockName), index, clientId);
  };

  const blockProps = useBlockProps();

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        <Panel>
          <PanelBody title={__("Advanced Accordion Options", "")} initialOpen={true}>
            <RadioControl
              label={__("Floating Panel Side", "accordion")}
              selected={acSideChange}
              options={[
                { label: __("Left Side", "accordion"), value: "left-float" },
                { label: __("Right Side", "accordion"), value: "right-float" },
              ]}
              onChange={option => {
                setAttributes({ acSideChange: option });

                let floatClassName = "right-panel-floating";

                if (option == "left-float") {
                  floatClassName = "left-panel-floating";
                }
                setAttributes({ floatClassName });
              }}
            />
            <ToggleControl
              label="Keep the tabs always expanded"
              checked={alwaysExpanded}
              onChange={val => {
                setAttributes({ alwaysExpanded: val });
              }}
            />
            <SelectControl
              label={__("Initial Accordion Card", "accordion")}
              value={defaultOpenIndex}
              options={initialAccordionTabOptions}
              onChange={key => setAttributes({ defaultOpenIndex: Number(key) })}
            />
            <SelectControl
              label={__("Single column breakpoint", "accordion")}
              value={colBreakpoint}
              options={colBreakpointOptions}
              onChange={breakpoint => setAttributes({ colBreakpoint: Number(breakpoint) })}
            />
          </PanelBody>
          <PanelBody title={__("Accordion Class", "")} initialOpen={false}>
            <Select
              defaultValue={defaultClassValues}
              isMulti
              name="accordionClasses"
              options={accordionClasses}
              className="basic-multi-select"
              classNamePrefix="select"
              onChange={val => {
                setAttributes({
                  accordionClassNames: val ? val : [],
                });
              }}
            />
          </PanelBody>
        </Panel>
      </InspectorControls>
      <div className={`accordion-section ${floatClassName} ${classNames}`}>
        <div id={`bs_aa-${clientId}`} className="accordion__block">
          <div className="block-list-appender">
            <Button
              className="components-button is-primary aac-add-card"
              label="Add Accordion Block Top"
              icon="plus"
              onClick={() => insertComponent(ACC_TOP_TYPE, props.clientId, 0)}
            >
              Add Top Content{" "}
            </Button>
          </div>
          <InnerBlocks allowedBlocks={ALLOWED_BLOCKS} renderAppender={() => null} />
          <div className="block-list-appender">
            <Button
              className="components-button is-primary aac-add-card"
              label="Add Accordion Block"
              icon="plus"
              onClick={() => insertComponent(ACC_CARD_TYPE, props.clientId, innerBlocks.length)}
            >
              Add Card{" "}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withSelect((select, ownProps) => {
  const theme = select("core").getCurrentTheme();
  const { block_classes: blockClasses = {} } = theme || {};
  return { blockClasses };
})(Edit);
