const BackgroundColorSave = (props) => {
  const { deviceSizes, backgroundColorOptions, columnBlockID } = props;
  return (
    <style>
      {deviceSizes.map(([text, propertyName], index) => {
        let backgroundColorSettings =
          backgroundColorOptions[propertyName].settings;
        let backgroundColor = backgroundColorSettings.backgroundColor
          ? backgroundColorSettings.backgroundColor
          : "";
        if (backgroundColor) {
          if (propertyName === "regular") {
            return `.bs-column.bs-column-${columnBlockID}{ background-color: ${backgroundColor};} `;
          } else {
            return `@media only screen and (max-width: ${backgroundColorOptions[propertyName].breakpoint}px) {
                  .bs-column.bs-column-${columnBlockID} {
                    background-color: ${backgroundColor};}}`;
          }
        }
      })}
    </style>
  );
};

export default BackgroundColorSave;
