/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
 import { __ } from "@wordpress/i18n";

 /**
	* The save function defines the way in which the different attributes should
	* be combined into the final markup, which is then serialized by the block
	* editor into `post_content`.
	*
	* @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#save
	*
	* @return {WPElement} Element to render.
	*
	*
	*
	*/

 import InnerContent from "./_partials/save-inner-content";
 import InnerContentImage from "./_partials/save-inner-content-image";

 const save = (props) => {
	 const { attributes, setAttributes } = props;
	 const { url, cardClassNames, layoutType, imgURL } = attributes;
	 const classNames = cardClassNames
		 ? cardClassNames.map((x) => x.value).join(" ")
		 : "";

	 return (
		 <>
			 <div className={`bs-card ${classNames}`}>
				 <div
					 className={`bs-card__container
				 ${imgURL && layoutType === "side-by-side" ? "bs-card__container--flex" : ""}
				 `}
				 >
					 {imgURL && layoutType === "side-by-side" ? (
						 <>
							 <div className="bs-card__container-left">
								 <InnerContentImage
									 setAttributes={setAttributes}
									 attributes={attributes}
								 />
							 </div>
							 <div className="bs-card__container-right">
								 <InnerContent
									 setAttributes={setAttributes}
									 attributes={attributes}
								 />
							 </div>
						 </>
					 ) : (
						 <>
							 <InnerContentImage
								 setAttributes={setAttributes}
								 attributes={attributes}
							 />
							 <InnerContent
								 setAttributes={setAttributes}
								 attributes={attributes}
							 />
						 </>
					 )}
				 </div>
			 </div>
		 </>
	 );
 };

 export default save;
