/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import { RichText } from "@wordpress/block-editor";

//Inner Content
const InnerContent = (props) => {
  const { attributes, setAttributes } = props;
  const { title, subtitle, content, titleTag, subtitleTag, paragraphTag } =
    attributes;

  //Title
  const setTitle = (titleVal) => {
    setAttributes({ title: titleVal });
  };

  //Subtitle
  const setSubtitle = (subtitleVal) => {
    setAttributes({ subtitle: subtitleVal });
  };

  //Paragraph
  const setParagraph = (paragraphVal) => {
    setAttributes({ content: paragraphVal });
  };

  return (
    <>
      <div className="bs-card__title" key="title">
        <RichText
          tagName={titleTag}
          placeholder="Title"
          value={title}
          onChange={setTitle}
        />
      </div>
      <div className="bs-card__subtitle" key="subtitle">
        <RichText
          tagName={subtitleTag}
          placeholder="Subtitle"
          value={subtitle}
          onChange={setSubtitle}
        />
      </div>
      <div className="bs-card__description" key="description">
        <RichText
          tagName={paragraphTag}
          placeholder="Description"
          value={content}
          onChange={setParagraph}
        />
      </div>
    </>
  );
};

export default InnerContent;
