/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import { MediaUpload } from "@wordpress/block-editor";
import { Button } from "@wordpress/components";

//Inner Content Image
const InnerContentImage = (props) => {
  const { attributes, setAttributes } = props;
  const {
    imgURL,
    imgAlt,
    mobileImgURL,
    mobile2xImgURL,
    desktopImgURL,
    desktop2xImgURL,
    desktopBreakpoint,
    mobileBreakpoint,
  } = attributes;

  return (
    <>
      <div className="bs-card__image" key="image">
        {imgURL && (
          <figure>
            <picture>
              {mobileImgURL && (
                <>
                  <source
                    srcSet={
                      mobile2xImgURL
                        ? `${mobileImgURL},
                            ${mobile2xImgURL} 2x`
                        : `${mobileImgURL}`
                    }
                    media={`(max-width:${mobileBreakpoint - 1}px)`}
                  />
                </>
              )}
              {desktopImgURL && (
                <>
                  <source
                    srcSet={
                      desktop2xImgURL
                        ? `${desktopImgURL},
                            ${desktop2xImgURL} 2x`
                        : `${desktopImgURL}`
                    }
                    media={`(max-width:${desktopBreakpoint}px)`}
                  />
                </>
              )}
              <img
                className={`${attributes.imageBehavior} img-fluid normal-image`}
                src={imgURL}
                alt={imgAlt ? imgAlt : ""}
              />
            </picture>
          </figure>
        )}
      </div>
    </>
  );
};

export default InnerContentImage;
