const BackgroundImageSave = (props) => {
  const { deviceSizes, backgroundImageOptions, sectionBlockID } = props;
  return (
    <style>
      {deviceSizes.map(([text, deviceSize], index) => {
        let backgroundImageSettings =
          backgroundImageOptions[deviceSize].settings;
        let sectionImgUrl = backgroundImageSettings.sectionImgUrl
          ? backgroundImageSettings.sectionImgUrl
          : "";
        let image2xUrl = backgroundImageSettings.image2xUrl
          ? backgroundImageSettings.image2xUrl
          : "";
        if (sectionImgUrl) {
          let imageSetAttribute = image2xUrl
            ? `url(${sectionImgUrl}) 1x, url(${image2xUrl}) 2x`
            : `url(${sectionImgUrl}) 1x`;
          if (deviceSize === "regular") {
            return `.bs-section.bs-section-${sectionBlockID}{ background-image: url(${sectionImgUrl});background-position: center center;background-size: cover;} `;
          } else {
            return `@media only screen and (max-width: ${backgroundImageOptions[deviceSize].breakpoint}px) {.bs-section.bs-section-${sectionBlockID}{ 
            background-image: url(${sectionImgUrl});
            background-image: 
              -webkit-image-set(
                ${imageSetAttribute}
              );
            background-image: 
              image-set(
                ${imageSetAttribute}
              );
            background-position: center center;
            background-size: cover;} }`;
          }
        }
      })}
    </style>
  );
};

export default BackgroundImageSave;
