const BackGroundColorSave = (props) => {
  const { deviceSizes, backGroundColorOptions, divBlockID } = props;
  return (
    <style>
      {deviceSizes.map(([text, deviceSize], index) => {
        let backGroundColorSettings =
          backGroundColorOptions[deviceSize].settings;
        let backgroundColor = backGroundColorSettings.backgroundColor
          ? backGroundColorSettings.backgroundColor
          : "";
        if (backgroundColor) {
          if (deviceSize === "regular") {
            return `.bs-div.bs-div-${divBlockID}{ background-color: ${backgroundColor};} `;
          } else {
            let breakpointValue =
              deviceSize === "mobile"
                ? backGroundColorOptions[deviceSize].breakpoint - 1
                : backGroundColorOptions[deviceSize].breakpoint;
            return `@media only screen and (max-width: ${breakpointValue}px) {
              .bs-div.bs-div-${divBlockID} {
                background-color: ${backgroundColor};}}`;
          }
        } else {
          return "";
        }
      })}
    </style>
  );
};

export default BackGroundColorSave;
