import { PanelBody, ColorPalette, RangeControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";

const changeAttributeValue = (
  value,
  deviceSize,
  gradientOptions,
  setAttributes,
  attributeVariable
) => {
  let newValuesObj = gradientOptions[deviceSize].settings;
  newValuesObj = { ...newValuesObj, [attributeVariable]: value };
  let gradientOptionsNew = gradientOptions[deviceSize];
  gradientOptionsNew = {
    ...gradientOptionsNew,
    settings: newValuesObj,
  };
  setAttributes({
    gradientOptions: {
      ...gradientOptions,
      [deviceSize]: gradientOptionsNew,
    },
  });
};

const GradientSettings = (props) => {
  const { deviceSizes, gradientOptions, colors, setAttributes } = props;
  return (
    <>
      {deviceSizes.map(([text, deviceSize], index) => {
        const title =
          deviceSize === "regular"
            ? `${text} (Greater than ${gradientOptions.desktop.breakpoint}px)`
            : deviceSize === "desktop"
            ? `${text} (Between ${gradientOptions.mobile.breakpoint}px and ${gradientOptions.desktop.breakpoint}px)`
            : `${text} (Less than ${gradientOptions.mobile.breakpoint}px)`;
        return (
          <PanelBody title={title} initialOpen={index === 0} key={index}>
            <>
              <ColorPalette
                label={__("Select Background Color #1", "berg")}
                colors={colors}
                value={gradientOptions[deviceSize].settings.gradientColorOne}
                onChange={(value) =>
                  changeAttributeValue(
                    value,
                    deviceSize,
                    gradientOptions,
                    setAttributes,
                    "gradientColorOne"
                  )
                }
              />

              <ColorPalette
                label={__("Select Background Color #2", "berg")}
                colors={colors}
                value={gradientOptions[deviceSize].settings.gradientColorTwo}
                onChange={(value) =>
                  changeAttributeValue(
                    value,
                    deviceSize,
                    gradientOptions,
                    setAttributes,
                    "gradientColorTwo"
                  )
                }
              />
              <RangeControl
                label={__("Gradient Direction (Degrees)", "berg")}
                min={1}
                max={360}
                value={gradientOptions[deviceSize].settings.gradientDirection}
                initialPosition={
                  gradientOptions[deviceSize].settings.gradientDirection
                }
                allowReset={true}
                onChange={(val) =>
                  changeAttributeValue(
                    val,
                    deviceSize,
                    gradientOptions,
                    setAttributes,
                    "gradientDirection"
                  )
                }
              />
              <RangeControl
                label={__("Color 1 Location", "berg")}
                min={1}
                max={100}
                value={
                  gradientOptions[deviceSize].settings.gradientColorOneLocation
                }
                initialPosition={0}
                allowReset={true}
                initialPosition={
                  gradientOptions[deviceSize].settings.gradientColorOneLocation
                }
                onChange={(val) =>
                  changeAttributeValue(
                    val,
                    deviceSize,
                    gradientOptions,
                    setAttributes,
                    "gradientColorOneLocation"
                  )
                }
              />
              <RangeControl
                label={__("Color 2 Location", "berg")}
                min={1}
                max={100}
                value={
                  gradientOptions[deviceSize].settings.gradientColorTwoLocation
                }
                initialPosition={
                  gradientOptions[deviceSize].settings.gradientColorTwoLocation
                }
                allowReset={true}
                onChange={(val) =>
                  changeAttributeValue(
                    val,
                    deviceSize,
                    gradientOptions,
                    setAttributes,
                    "gradientColorTwoLocation"
                  )
                }
              />
            </>
          </PanelBody>
        );
      })}
    </>
  );
};

export default GradientSettings;
