import {
  PanelBody,
  ColorPalette,
  RangeControl,
  __experimentalNumberControl as NumberControl,
} from "@wordpress/components";
import { __ } from "@wordpress/i18n";

const changeAttributeValue = (
  value,
  deviceSize,
  gradientOptions,
  setAttributes,
  attributeVariable
) => {
  let newValuesObj = gradientOptions[deviceSize].settings;
  newValuesObj = { ...newValuesObj, [attributeVariable]: value };
  let gradientOptionsNew = gradientOptions[deviceSize];
  gradientOptionsNew = {
    ...gradientOptionsNew,
    settings: newValuesObj,
  };
  setAttributes({
    gradientOptions: {
      ...gradientOptions,
      [deviceSize]: gradientOptionsNew,
    },
  });
};

const GradientSettings = (props) => {
  const { deviceSizes, gradientOptions, colors, setAttributes } = props;
  return (
    <>
      {deviceSizes.map(([text, deviceSize], index) => {
        const title =
          deviceSize === "regular"
            ? `${text} (Greater than 1280px)`
            : deviceSize === "desktop"
            ? `${text} (Between 576px and 1280px)`
            : `${text} (Less than 576px)`;
        return (
          <PanelBody title={title} initialOpen={index === 0} key={index}>
            <>
              <ColorPalette
                label={__("Select Background Color #1", "berg")}
                colors={colors}
                value={gradientOptions[deviceSize].settings.gradientColorOne}
                onChange={(value) =>
                  changeAttributeValue(
                    value,
                    deviceSize,
                    gradientOptions,
                    setAttributes,
                    "gradientColorOne"
                  )
                }
              />
              <ColorPalette
                label={__("Select Background Color #2", "berg")}
                colors={colors}
                value={gradientOptions[deviceSize].settings.gradientColorTwo}
                onChange={(value) =>
                  changeAttributeValue(
                    value,
                    deviceSize,
                    gradientOptions,
                    setAttributes,
                    "gradientColorTwo"
                  )
                }
              />
              <div className="input-field-wrapper">
                <NumberControl
                  label={__("Gradient Direction (Degrees)", "berg")}
                  step="any"
                  min={0}
                  max={360}
                  onChange={(val) =>
                    changeAttributeValue(
                      val,
                      deviceSize,
                      gradientOptions,
                      setAttributes,
                      "gradientDirection"
                    )
                  }
                  value={gradientOptions[deviceSize].settings.gradientDirection}
                />
              </div>
              <div className="input-field-wrapper">
                <NumberControl
                  label={__("Color 1 Location (%)", "berg")}
                  step="any"
                  min={0}
                  max={100}
                  onChange={(val) =>
                    changeAttributeValue(
                      val,
                      deviceSize,
                      gradientOptions,
                      setAttributes,
                      "gradientColorOneLocation"
                    )
                  }
                  value={
                    gradientOptions[deviceSize].settings
                      .gradientColorOneLocation
                  }
                />
              </div>
              <div className="input-field-wrapper">
                <NumberControl
                  label={__("Color 2 Location (%)", "berg")}
                  step="any"
                  min={0}
                  max={100}
                  onChange={(val) =>
                    changeAttributeValue(
                      val,
                      deviceSize,
                      gradientOptions,
                      setAttributes,
                      "gradientColorTwoLocation"
                    )
                  }
                  value={
                    gradientOptions[deviceSize].settings
                      .gradientColorTwoLocation
                  }
                />
              </div>
            </>
          </PanelBody>
        );
      })}
    </>
  );
};

export default GradientSettings;
