const PictureTagSave = (props) => {
  const { deviceSizesForPictureTag, pictureTagOptions } = props;

  if (pictureTagOptions.regular.settings.sectionImgUrl) {
    return (
      <div className="bs-section__image-container">
        <picture>
          {deviceSizesForPictureTag.map(([text, deviceSize], index) => {
            if (deviceSize === "regular") {
              return (
                <img
                  src={pictureTagOptions[deviceSize].settings.sectionImgUrl}
                />
              );
            } else {
              if (pictureTagOptions[deviceSize].settings.sectionImgUrl) {
                return (
                  <source
                    srcSet={
                      pictureTagOptions[deviceSize].settings.image2xUrl
                        ? `${pictureTagOptions[deviceSize].settings.sectionImgUrl},
                    ${pictureTagOptions[deviceSize].settings.image2xUrl} 2x`
                        : `${pictureTagOptions[deviceSize].settings.sectionImgUrl}`
                    }
                    media={`(max-width:${pictureTagOptions[deviceSize].breakpoint}px)`}
                  ></source>
                );
              }
            }
          })}
        </picture>
      </div>
    );
  } else {
    return "";
  }
};

export default PictureTagSave;
