import { PanelBody, ColorPalette, RangeControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";

const changeAttributeValue = (
  value,
  deviceSize,
  backGroundColorOptions,
  setAttributes,
  attributeVariable
) => {
  let newValuesObj = backGroundColorOptions[deviceSize].settings;
  newValuesObj = { ...newValuesObj, [attributeVariable]: value };
  let optionsNew = backGroundColorOptions[deviceSize];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    backGroundColorOptions: {
      ...backGroundColorOptions,
      [deviceSize]: optionsNew,
    },
  });
};

const BackgroundColorSetting = (props) => {
  const { deviceSizes, backGroundColorOptions, colors, setAttributes } = props;
  return (
    <>
      {deviceSizes.map(([text, deviceSize], index) => {
        const title =
          deviceSize === "regular"
            ? `${text} (Greater than ${backGroundColorOptions.desktop.breakpoint}px)`
            : deviceSize === "desktop"
            ? `${text} (Between ${backGroundColorOptions.mobile.breakpoint}px and ${backGroundColorOptions.desktop.breakpoint}px)`
            : `${text} (Less than ${backGroundColorOptions.mobile.breakpoint}px)`;
        return (
          <PanelBody title={title} initialOpen={index === 0} key={index}>
            <ColorPalette
              label={__("Select Background Color", "")}
              colors={colors}
              value={
                backGroundColorOptions[deviceSize].settings.backgroundColor
              }
              onChange={(value) =>
                changeAttributeValue(
                  value,
                  deviceSize,
                  backGroundColorOptions,
                  setAttributes,
                  "backgroundColor"
                )
              }
            />
          </PanelBody>
        );
      })}
    </>
  );
};

export default BackgroundColorSetting;
