import React from "react";

import { __ } from "@wordpress/i18n";
import {
  MediaUpload,
} from "@wordpress/block-editor";
import {
  Button,
  PanelBody,
  PanelRow,
  RangeControl,
  SelectControl,
  TextControl,
  __experimentalNumberControl as NumberControl,
  ToggleControl,
  __experimentalDivider as Divider,
  __experimentalVStack as VStack,
} from "@wordpress/components";
import TagSelector from "~berg/ui-components/tag-selector";
import Visibility from "~berg/ui-components/visibility";
import Select from "react-select";

const TabGeneral = (props) => {
  const { attributes, setAttributes } = props;
  const {
    title,
    titleTag,
    decimalPlaces,
    displayThousandSeparator,
    start,
    end,
    duration,
    prefixImg,
    postfixImg,
    prefixSelection,
    postfixSelection,
    prefixText,
    postfixText,
    counterVisibility = true,
    countUnit,
    countUnitPosition,
    counterClasses,
    defaultClassValues
  } = attributes;

  /* Counter Class Panel */
  const onChangeCounterClasses = (newVal) => {
    setAttributes({ counterClassNames: newVal ? newVal : [] });
  };

  /* Counter Content Panel */
  const onChangeTitle = (newVal) => {
    setAttributes({ title: newVal });
  };
  const onChangeTitleTag = (newVal) => {
    setAttributes({ titleTag: newVal });
  };
  const onChangePrefix = (newVal) => {
    if (newVal == "none") {
      removePrefixImage();
      setAttributes({ prefixText: "" });
    }
    setAttributes({ prefixSelection: newVal });
  };
  const removePrefixImage = () => {
    setAttributes({
      prefixImg: {
        id: "",
        url: "",
        alt: "",
      },
    });
  };
  const onPrefixImgChange = (newImgObj) => {
    setAttributes({
      prefixImg: {
        id: newImgObj.id,
        url: newImgObj.url,
        alt: newImgObj.alt,
      },
    });
  };
  const onChangePrefixText = (newVal) => {
    setAttributes({ prefixText: newVal });
  };
  const onChangePostfix = (newVal) => {
    if (newVal == "none") {
      removePostfixImage();
      setAttributes({ postfixText: "" });
    }
    setAttributes({ postfixSelection: newVal });
  };
  const removePostfixImage = () => {
    setAttributes({
      postfixImg: {
        id: "",
        url: "",
        alt: "",
      },
    });
  };
  const onPostfixImgChange = (newImgObj) => {
    setAttributes({
      postfixImg: {
        id: newImgObj.id,
        url: newImgObj.url,
        alt: newImgObj.alt,
      },
    });
  };
  const onChangePostfixText = (newVal) => {
    setAttributes({ postfixText: newVal });
  };

  /* Counter Settings Panel */
  const onChangeStart = (newVal) => {
    setAttributes({ start: newVal != "" ? parseFloat(newVal) : newVal });
  };
  const onChangeEnd = (newVal) => {
    setAttributes({ end: newVal != "" ? parseFloat(newVal) : newVal });
  };
  const onChangeThousandSeparator = (newVal) => {
    setAttributes({ displayThousandSeparator: newVal });
  };
  const onChangeDecimalPlaces = (newVal) => {
    setAttributes({ decimalPlaces: newVal });
  };
  const onChangeDuration = (newVal) => {
    setAttributes({ duration: newVal });
  };
  const onChangeCountUnit = (newVal) => {
    setAttributes({ countUnit: newVal });
  };
  const onChangeCountUnitPosition = (newVal) => {
    setAttributes({ countUnitPosition: newVal });
  };

  return (
    <div>
      <Visibility { ...{attributeName: 'counterVisibility', attributes, setAttributes} }></Visibility>
      <PanelBody title={__("Counter Class", "")} initialOpen={false}>
        <Select
          defaultValue={defaultClassValues}
          isMulti
          name="counterClasses"
          options={counterClasses}
          className="basic-multi-select"
          classNamePrefix="select"
          onChange={onChangeCounterClasses}
        />
      </PanelBody>
      <PanelBody title={__("Counter Content", "berg")} initialOpen={true}>
        <PanelRow>
          <TextControl
            label={__("Title", "berg")}
            value={title}
            onChange={onChangeTitle}
          />
        </PanelRow>
        <PanelRow>
          <TagSelector
            label={__("Title Tag", "berg")}
            value={titleTag}
            onChange={onChangeTitleTag}
          />
        </PanelRow>
        <VStack spacing={1}>
          <Divider />
        </VStack>
        <PanelRow>
          <SelectControl
            label={__("Prefix", "berg")}
            value={prefixSelection}
            options={[
              { label: "None", value: "none" },
              { label: "Image", value: "image" },
              { label: "Text", value: "text" },
            ]}
            onChange={onChangePrefix}
          />
        </PanelRow>
        <VStack spacing={4}>
          {prefixSelection === "image" && (
            <div className="counter-prefix-preview">
              {prefixImg.url ? (
                <div>
                  <div>
                    <img src={prefixImg.url} />
                  </div>
                  <Button isSecondary onClick={removePrefixImage}>
                    Remove Image
                  </Button>
                </div>
              ) : (
                <MediaUpload
                  onSelect={onPrefixImgChange}
                  allowedTypes={["image"]}
                  render={({ open }) => (
                    <Button isPrimary onClick={open}>
                      Upload Image
                    </Button>
                  )}
                />
              )}
            </div>
          )}
          {prefixSelection === "text" && (
            <div>
              <TextControl
                label="Prefix Text"
                placeholder="Prefix Text"
                value={prefixText}
                onChange={onChangePrefixText}
              />
            </div>
          )}
        </VStack>
        <VStack spacing={1}>
          <Divider />
        </VStack>
        <PanelRow>
          <SelectControl
            label={__("Postfix", "berg")}
            value={postfixSelection}
            options={[
              { label: "None", value: "none" },
              { label: "Image", value: "image" },
              { label: "Text", value: "text" },
            ]}
            onChange={onChangePostfix}
          />
        </PanelRow>
        <VStack spacing={4}>
          {postfixSelection === "image" && (
            <div className="counter-prefix-preview">
              {postfixImg.url ? (
                <div>
                  <div>
                    <img src={postfixImg.url}/>
                  </div>
                  <Button isSecondary onClick={removePostfixImage}>
                    Remove Image
                  </Button>
                </div>
              ) : (
                <MediaUpload
                  onSelect={onPostfixImgChange}
                  allowedTypes={["image"]}
                  render={({ open }) => (
                    <Button isPrimary onClick={open}>
                      Upload Image
                    </Button>
                  )}
                />
              )}
            </div>
          )}
          {postfixSelection === "text" && (
            <div>
              <TextControl
                label="Postfix Text"
                placeholder="Postfix Text"
                value={postfixText}
                onChange={onChangePostfixText}
              />
            </div>
          )}
        </VStack>
      </PanelBody>
      <PanelBody title={__("Counter Settings", "berg")} initialOpen={false}>
        <PanelRow>
          <NumberControl
            label={__("Start", "berg")}
            min={0}
            step="any"
            onChange={onChangeStart}
            value={start}
          />
        </PanelRow>
        <PanelRow>
          <NumberControl
            label={__("End", "berg")}
            min={0}
            step="any"
            onChange={onChangeEnd}
            value={end}
          />
        </PanelRow>
        <PanelRow>
          <ToggleControl
            label={__("Display Thousands separator", "berg")}
            onChange={onChangeThousandSeparator}
            checked={displayThousandSeparator}
          />
        </PanelRow>
        <PanelRow>
          <RangeControl
            label={__("Decimal places", "berg")}
            help={__("Amount of decimals to display", "berg")}
            value={decimalPlaces}
            onChange={onChangeDecimalPlaces}
            min={0}
            max={4}
          />
        </PanelRow>
        <PanelRow>
          <RangeControl
            label={__("Duration", "berg")}
            help={__("Animation duration in seconds.", "berg")}
            value={duration}
            onChange={onChangeDuration}
            min={2}
            max={15}
          />
        </PanelRow>
        <PanelRow>
          <TextControl
            label="Counter Unit"
            placeholder="Counter Unit"
            value={countUnit}
            onChange={onChangeCountUnit}
          />
        </PanelRow>
        <PanelRow>
          <SelectControl
            label={__("Unit Position", "berg")}
            value={countUnitPosition}
            options={[
              { label: "Before count", value: "before_count" },
              { label: "After count", value: "after_count" },
            ]}
            onChange={onChangeCountUnitPosition}
          />
        </PanelRow>
      </PanelBody>
    </div>
  );
};

export default TabGeneral;