/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
 import { __ } from "@wordpress/i18n";
 import React from "react";

 /**
	* The save function defines the way in which the different attributes should
	* be combined into the final markup, which is then serialized by the block
	* editor into `post_content`.
	*
	* @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#save
	*
	* @return {WPElement} Element to render.
	*
	*
	*
	*/

 const save = (props) => {
	 const { attributes } = props;

	 const {
		 buttonTitle,
		 popupID,
		 popupContent,
		 popupContentTitle,
		 url,
		 customUrl,
		 linkOpenType,
		 titleColor,
		 wrapperBgColor,
		 optionSelect,
		 vpopOptionSelect,
		 imgURL,
		 downloadFileURL,
		 vImgURL,
		 buttonClassNames,
		 selectedPost,
		 prefixImg,
		 postfixImg,
		 prefixSelection,
		 postfixSelection,
		 prefixText,
		 postfixText,
		 vpopupHtml,
		 vpopupFancyBoxOptions
	 } = attributes;

	 const classNames = buttonClassNames
		 ? buttonClassNames.map((x) => x.value).join(" ")
		 : "";
	 let styleOverrides = '';
	 if (wrapperBgColor && wrapperBgColor.length > 0) {
		 styleOverrides = `background-color: ${wrapperBgColor};`;
	 }
	 if (titleColor && titleColor.length > 0) {
		 styleOverrides += `${styleOverrides.length > 0 ?
			 ' ' :
			 ''}color: ${titleColor};`;
	 }
	 return (
		 <>
			 {attributes.proButtonVisibility == true ? (
				 <span
					 className={`bs-pro-button ${classNames} bs-pro-button-${popupID}`}
				 >
					 {styleOverrides.length > 0 ?
						 <style>{`.bs-pro-button-${popupID} .bs-pro-button__container {${styleOverrides}}`}</style> :
						 null
					 }
					 {prefixSelection === "image" && prefixImg.url && (
						 <div className="bs-pro-button__prefix">
							 <img src={prefixImg.url} width="25%" />
						 </div>
					 )}
					 {prefixSelection === "text" && prefixText && (
						 <div className="bs-pro-button__prefix">
							 <p>{prefixText}</p>
						 </div>
					 )}

					 {"custom" === optionSelect && (
						 <a
							 href={customUrl}
							 target={linkOpenType ? "_blank" : ""}
							 rel="noopener noreferrer"
							 className={`bs-pro-button__container`}
							 dangerouslySetInnerHTML={{ __html: buttonTitle }}
						 ></a>
					 )}

					 {"download" === optionSelect && (
						 <a
							 href={downloadFileURL}
							 download={!linkOpenType}
							 target={linkOpenType ? "_blank" : ""}
							 className={`bs-pro-button__container`}
							 rel="noopener noreferrer"
							 dangerouslySetInnerHTML={{ __html: buttonTitle }}
						 ></a>
					 )}

					 {"vpopup" === optionSelect && (
						 <>
							 {"vlink" === vpopOptionSelect && (
								 <a
									 href={url}
									 data-fancybox=""
									 className={`bs-pro-button__container`}
									 dangerouslySetInnerHTML={{ __html: buttonTitle }}
								 ></a>
							 )}

							 {"vupload" === vpopOptionSelect && (
								 <a
									 href={vImgURL}
									 data-fancybox=""
									 className={`bs-pro-button__container`}
									 dangerouslySetInnerHTML={{ __html: buttonTitle }}
								 ></a>
							 )}

							 {"html" === vpopOptionSelect && (
								 <a
									 href=""
									 role="button"
									 data-fancybox=""
									 data-encoded-content={vpopupHtml}
									 data-base-class={vpopupFancyBoxOptions?.classNames || ''}
									 data-click-outside={vpopupFancyBoxOptions?.dismissOnClickOutside || 'close'}
									 data-click-slide={vpopupFancyBoxOptions?.dismissOnClickOutside || 'close'}
									 className={`bs-pro-button__container`}
									 dangerouslySetInnerHTML={{ __html: buttonTitle }}
								 ></a>
							 )}
						 </>
					 )}

					 {"imgpopup" === optionSelect && (
						 <a
							 data-fancybox
							 href="void(0);"
							 data-src={`#${popupID}`}
							 className={`bs-pro-button__container`}
							 dangerouslySetInnerHTML={{ __html: buttonTitle }}
						 ></a>
					 )}

					 {"post" === optionSelect && (
						 <a
							 href={selectedPost.value}
							 target={linkOpenType ? "_blank" : ""}
							 rel={linkOpenType ? "noopener noreferrer" : "noopener noreferrer"}
							 className={`bs-pro-button__container`}
							 dangerouslySetInnerHTML={{ __html: buttonTitle }}
						 ></a>
					 )}

					 {postfixSelection === "image" && postfixImg.url && (
						 <div className="bs-pro-button__postfix">
							 <img src={postfixImg.url} width="25%" />
						 </div>
					 )}
					 {postfixSelection === "text" && postfixText && (
						 <div className="bs-pro-button__postfix">
							 <p>{postfixText}</p>
						 </div>
					 )}
				 </span>
			 ) : null}

			 {"imgpopup" === optionSelect ? (
				 <div
					 className="bs-post__target bs-post__target--popup"
					 id={popupID}
					 style="display: none;"
				 >
					 {popupContentTitle ? (
						 <h2 dangerouslySetInnerHTML={{ __html: popupContentTitle }}></h2>
					 ) : null}
					 {popupContent ? (
						 <p dangerouslySetInnerHTML={{ __html: popupContent }}></p>
					 ) : null}
					 {imgURL ? <img src={imgURL} /> : null}
				 </div>
			 ) : null}
		 </>
	 );
 };

 export default save;
