/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n"
import {
  MediaUpload,
  RichText,
  InspectorControls,
  URLInput,
  MediaUploadCheck,
  useBlockProps,
} from "@wordpress/block-editor"
import { Button, PanelBody, SelectControl, PanelRow, TextControl, ToggleControl, Dashicon } from "@wordpress/components"

import Select from "react-select"
import { setUniqueIdAttribute } from "~berg/utils"
import apiFetch from "@wordpress/api-fetch"
import { capitalize } from "lodash"

const { useEffect, useState } = wp.element
const { withSelect } = wp.data

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
//import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @param {Object} [props]           Properties passed from the editor.
 * @param {string} [props.className] Class name generated for the block.
 *
 * @return {WPElement} Element to render.
 */
import ImageVariations from "./_partials/image-variations"
import TagSelector from "~berg/ui-components/tag-selector"
import SortableSelect from "~berg/ui-components/sortable-multi-select"

//Inner Content
const InnerContent = props => {
  const { attributes, setAttributes } = props
  const { orderDiv, desktopBreakpoint, mobileBreakpoint } = attributes
  const innerElements = []

  //Title
  const setTitle = titleVal => {
    setAttributes({ title: titleVal })
  }

  // Sub-title
  const setSubtitle = subtitleVal => {
    setAttributes({ subtitle: subtitleVal })
  }

  //Paragraph
  const setParagraph = paragraphVal => {
    setAttributes({ content: paragraphVal })
  }

  //link text
  const setLinkText = linkTextVal => {
    setAttributes({ linkText: linkTextVal })
  }

  //bottom text
  const setBottomText = bottomTextVal => {
    setAttributes({ bottomText: bottomTextVal })
  }

  // Getting global breakpoint values from Theme settings
  const globalDesktopBreakpoint = typeof bergThemeData !== "undefined" ? bergThemeData.desktopBreakpoint : 1280
  const globalMobileBreakpoint = typeof bergThemeData !== "undefined" ? bergThemeData.mobileBreakpoint : 576

  useEffect(() => {
    // Updating breakpoint values when loading the block to avoid block recovery errors.
    if (desktopBreakpoint != globalDesktopBreakpoint) {
      setAttributes({ desktopBreakpoint: globalDesktopBreakpoint })
    }
    if (mobileBreakpoint != globalMobileBreakpoint) {
      setAttributes({ mobileBreakpoint: globalMobileBreakpoint })
    }
  })

  orderDiv
    ? orderDiv.map(item => {
        if (item.value == "title") {
          innerElements.push(
            <div className="bs-advanced-blurb__title" key={item.value}>
              <RichText
                tagName={attributes.titleTag}
                placeholder="Title"
                value={attributes.title}
                onChange={setTitle}
              />
            </div>
          )
        }

        if (item.value == "subtitle") {
          innerElements.push(
            <div className="bs-advanced-blurb__subtitle" key={item.value}>
              <RichText
                tagName={attributes.subtitleTag}
                placeholder="Sub-title"
                value={attributes.subtitle}
                onChange={setSubtitle}
              />
            </div>
          )
        }

        if (item.value == "description") {
          innerElements.push(
            <div className="bs-advanced-blurb__description" key={item.value}>
              <RichText
                tagName={attributes.paragraphTag}
                placeholder="Description"
                value={attributes.content}
                onChange={setParagraph}
              />
            </div>
          )
        }

        if (item.value == "bottom-text") {
          innerElements.push(
            <div className="bs-advanced-blurb__read-more" key={item.value}>
              {attributes.linkType === "innerLink" ? (
                <RichText tagName="a" placeholder="link text" value={attributes.linkText} onChange={setLinkText} />
              ) : (
                <RichText
                  tagName="span"
                  // placeholder="bottom text"
                  placeholder="Bottom Text"
                  className="bs-advanced-blurb__read-more-text"
                  value={attributes.bottomText}
                  onChange={setBottomText}
                />
              )}
            </div>
          )
        }
        if (item.value == "image") {
          innerElements.push(
            <div className="bs-advanced-blurb__image" key={item.value}>
              {attributes.imgURL && (
                <div>
                  <figure>
                    <picture>
                      {attributes.mobileImgURL && (
                        <>
                          <source
                            srcSet={
                              attributes.mobile2xImgURL
                                ? `${attributes.mobileImgURL},
                            ${attributes.mobile2xImgURL} 2x`
                                : `${attributes.mobileImgURL}`
                            }
                            media={`(max-width:${mobileBreakpoint - 1}px)`}
                          />
                        </>
                      )}
                      {attributes.desktopImgURL && (
                        <>
                          <source
                            srcSet={
                              attributes.desktop2xImgURL
                                ? `${attributes.desktopImgURL},
                            ${attributes.desktop2xImgURL} 2x`
                                : `${attributes.desktopImgURL}`
                            }
                            media={`(max-width:${desktopBreakpoint}px)`}
                          />
                        </>
                      )}
                      <img className={attributes.imageBehavior} src={attributes.imgURL} alt={attributes.imgAlt} />
                    </picture>
                  </figure>
                  {attributes.playIconURL ? (
                    <img className="video-play-icon" width="50" height="50" src={attributes.playIconURL} />
                  ) : null}
                </div>
              )}
            </div>
          )
        }
      })
    : ""

  return <>{innerElements}</>
}

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {}
  const { attributes, setAttributes } = props
  const {
    advancedBlurbClassNames,
    orderDiv,
    blurbVisibility,
    title,
    content,
    bottomText,
    postId,
    imgURL,
    vpopupHtml,
    vpopupFancyBoxOptions,
    isImgLazyLoad,
  } = attributes

  const { blockID } = attributes

  const advanceBlurbPrefix = "a-blurb-"
  const blockProps = useBlockProps()

  useEffect(() => {
    setUniqueIdAttribute(props, "blockID", advanceBlurbPrefix)
  }, [props.attributes])

  //link type
  const setLinkType = linkTypeVal => {
    setAttributes({
      linkType: linkTypeVal,
    })
    /*Clearing the link text if it contains HTML elements
		(When changing the link type from 'Wrap All' to any other)*/
    if (/<[a-z/][\s\S]*>/i.test(attributes.linkText)) {
      setAttributes({
        linkText: null,
      })
    }
  }

  //link action
  const setLinkAction = linkActionVal => {
    setAttributes({
      linkAction: linkActionVal,
    })
  }

  //link open new tab or not
  const setLinkOpenType = setLinkOpenTypeVal => {
    setAttributes({
      linkOpenType: setLinkOpenTypeVal,
    })
  }

  //set title tag
  const setTitleTag = titleTagVal => {
    setAttributes({
      titleTag: titleTagVal,
    })
  }

  //set subtitle tag
  const setSubtitleTag = subtitleTagVal => {
    setAttributes({
      subtitleTag: subtitleTagVal,
    })
  }

  //set paragraph
  const setParagraphTag = paragraphTagVal => {
    setAttributes({
      paragraphTag: paragraphTagVal,
    })
  }

  // set image behavior
  const setImageBehavior = setImageBehaviorVal => {
    props.setAttributes({
      imageBehavior: setImageBehaviorVal,
    })
  }

  //set component class name
  const setClassName = setClassNameVal => {
    setAttributes({
      addClassName: setClassNameVal,
    })
  }

  //set hover image
  const selectHoverImg = img => {
    setAttributes({
      hoverImgID: img.id,
      hoverImgURL: img.url,
    })
  }

  //remove hover image
  const removeHoverImg = () => {
    setAttributes({
      hoverImgID: null,
      hoverImgURL: null,
    })
  }

  //set popup image
  const selectPopupImg = img => {
    setAttributes({
      popupImgID: img.id,
      popupImgURL: img.url,
    })
  }

  //remove image
  const removePopupImg = () => {
    setAttributes({
      popupImgID: null,
      popupImgURL: null,
      popupImgALT: null,
    })
  }

  //set popup name
  const setPopupName = popupNameVal => {
    setAttributes({
      popupName: popupNameVal,
    })
  }

  //set popup sub name
  const setPopupSubName = popupSubNameVal => {
    setAttributes({
      popupSubName: popupSubNameVal,
    })
  }

  //set popup content
  const setPopupContent = popupContentVal => {
    setAttributes({
      popupContent: popupContentVal,
    })
  }

  //set popup Video
  const setPopupVideo = popupVideoVal => {
    setAttributes({
      popupVideo: popupVideoVal,
    })
  }

  //set popup video URL
  const setPopupVideoURL = popupVideoUrlVal => {
    setAttributes({
      popupVideoURL: popupVideoUrlVal,
    })
  }

  //set popup upload video
  const setPopupVideoUpload = popupVideoUploadVal => {
    setAttributes({
      popupVideoUploadID: popupVideoUploadVal.id,
      popupVideoUploadURL: popupVideoUploadVal.url,
    })
  }

  //remove popup upload video
  const removePopupVideoUpload = popupVideoUploadVal => {
    setAttributes({
      popupVideoUploadID: null,
      popupVideoUploadURL: null,
    })
  }

  //set play icon
  const selectPlayIcon = img => {
    setAttributes({
      playIconID: img.id,
      playIconURL: img.url,
    })
  }

  //remove play icon
  const removePlayIcon = () => {
    setAttributes({
      playIconID: null,
      playIconURL: null,
    })
  }

  //set social image
  const setSocialIcon = img => {
    setAttributes({
      socialIconID: img.id,
      socialIconURL: img.url,
      socialIconALT: img.alt,
    })
  }

  //remove social image
  const removeSocialIcon = () => {
    setAttributes({
      socialIconID: null,
      socialIconURL: null,
      socialIconALT: null,
    })
  }

  //set social URL
  const setSocialURL = socialUrlVal => {
    setAttributes({
      socialURL: socialUrlVal,
    })
  }

  const [advancedBlurbClasses, setAdvancedBlurbClasses] = useState([])
  const [defaultClassValues, setDefaultClassValues] = useState([])
  const [htmlValid, setHtmlValid] = useState(true)

  useEffect(() => {
    const classList = []
    if (coreComponents["bs-blurb"] && coreComponents["bs-blurb"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-advanced-blurb"]}`,
        label: "Default",
      })
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      })
    }

    if (coreComponentsTheme["bs-advanced-blurb"] && coreComponentsTheme["bs-advanced-blurb"].length > 0) {
      coreComponentsTheme["bs-advanced-blurb"].map((className, index) => {
        let classLabel = className.replace("bs-advanced-blurb--", "").split("-")
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        })
      })
    }
    const defaultValues = advancedBlurbClassNames ? advancedBlurbClassNames : [classList[0]]
    setAdvancedBlurbClasses(classList)
    setDefaultClassValues(defaultValues)
  }, [JSON.stringify(props.blockClasses)])

  if (postId && title === "" && content === "" && bottomText === "" && imgURL === "") {
    apiFetch({
      path: "/api/bs-advance-blurb/getPostData",
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ postId }),
    }).then(res => {
      if (res) {
        const { title, content, bottomText, imgURL } = res
        setAttributes({ title, content, bottomText, imgURL })
      }
    })
  }

  const classNames = advancedBlurbClassNames ? advancedBlurbClassNames.map(x => x.value).join(" ") : ""
  const onChange = selectedOptions => {
    setAttributes({ orderDiv: selectedOptions })
  }

  let decodedVpopHtml = ""
  if (vpopupHtml && vpopupHtml.length > 0) {
    try {
      decodedVpopHtml = Buffer.from(vpopupHtml, "base64")
    } catch (error) {
      // ignore errors
    }
  }
  //To check if the image is available in 'Display order'
  const checkIfImageEnabled = obj => obj.value === "image"

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls initialOpen={true}>
        <PanelBody title={__("Visibility", "")} initialOpen={false}>
          <ToggleControl
            label="Enable/Disable"
            checked={blurbVisibility}
            onChange={val => {
              setAttributes({ blurbVisibility: val })
            }}
          />
        </PanelBody>
        <PanelBody title={__("Advanced Blurb Class", "")} initialOpen={false}>
          <Select
            defaultValue={defaultClassValues}
            isMulti
            name="advancedBlurbClasses"
            options={advancedBlurbClasses}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={val => {
              setAttributes({
                advancedBlurbClassNames: val ? val : [],
              })
            }}
          />
        </PanelBody>
        <PanelBody title={__("Blurb Image and Content", "")} initialOpen={true}>
          <PanelRow>
            <SelectControl
              label="Image Behavior"
              value={attributes.imageBehavior}
              options={[
                { label: "None", value: "object-fit-none" },
                { label: "object fit contain ", value: "object-fit-contain" },
                { label: "object fit cover ", value: "object-fit-cover" },
                { label: "object fit fill ", value: "object-fit-fill" },
              ]}
              onChange={setImageBehavior}
            />
          </PanelRow>
          <ImageVariations setAttributes={setAttributes} attributes={attributes} />
          <TextControl
            label={__("Image alternate text", "")}
            value={attributes.imgAlt}
            onChange={imgAltVal => {
              setAttributes({
                imgAlt: imgAltVal,
              })
            }}
          />
          {imgURL && (
            <ToggleControl
              label={__("Image lazy loading")}
              checked={isImgLazyLoad}
              onChange={isImgLazyLoad => setAttributes({ isImgLazyLoad })}
            />
          )}
          <label>Hover image</label>
          <PanelRow className="hover-image">
            <MediaUploadCheck>
              <MediaUpload
                allowedType={["image"]}
                value={attributes.hoverImgID}
                onSelect={selectHoverImg}
                render={({ open }) => (
                  <>
                    <Button className={"button button-large"} onClick={open}>
                      {__("Select Hover Image", "berg")}
                    </Button>
                    {attributes.hoverImgURL ? (
                      <div className="imageUploadWrapper">
                        <img src={attributes.hoverImgURL} />
                        <Button className="btn-remove" onClick={removeHoverImg}>
                          <Dashicon icon="no" size="20" />
                        </Button>
                      </div>
                    ) : null}
                  </>
                )}
              ></MediaUpload>
            </MediaUploadCheck>
          </PanelRow>
          <PanelRow>
            <TagSelector label={__("Title Tag", "berg")} value={attributes.titleTag} onChange={setTitleTag} />
          </PanelRow>
          <PanelRow>
            <TagSelector label={__("Subtitle Tag", "berg")} value={attributes.subtitleTag} onChange={setSubtitleTag} />
          </PanelRow>
          <PanelRow>
            <TagSelector label={__("Paragraph Tag", "berg")} value={attributes.paragraphTag} onChange={setParagraphTag} />
          </PanelRow>
        </PanelBody>
        <PanelBody title={__("Display Order", "")} initialOpen={false}>
          <SortableSelect
            value={orderDiv}
            onChange={onChange}
            options={[
              { label: "Title", value: "title" },
              { label: "Sub-title", value: "subtitle" },
              { label: "Description", value: "description" },
              { label: "Bottom Text or Link", value: "bottom-text" },
              { label: "Image", value: "image" },
            ]}
          />
        </PanelBody>
        <PanelBody title={__("Blurb Options", "")} initialOpen={true}>
          <PanelRow>
            <SelectControl
              label="Link Type"
              value={attributes.linkType}
              options={[
                { label: "Inner Link", value: "innerLink" },
                { label: "Wrap All", value: "outerLink" },
                { label: "None", value: "none" },
              ]}
              onChange={setLinkType}
            />
          </PanelRow>

          {attributes.linkType === "outerLink" || attributes.linkType === "innerLink" ? (
            <PanelRow>
              <SelectControl
                label="Link Action"
                value={attributes.linkAction}
                options={[
                  { label: "Select Action", value: "" },
                  { label: "Image + Content Popup", value: "imagePopup" },
                  { label: "Video Popup", value: "videoPopup" },
                  { label: "External or Internal", value: "externalInternal" },
                ]}
                onChange={setLinkAction}
              />
            </PanelRow>
          ) : null}

          {attributes.linkType !== "none" && attributes.linkAction === "externalInternal" ? (
            <PanelBody>
              <PanelRow>
                <URLInput
                  label="Enter Url"
                  value={attributes.url}
                  onChange={(url, post) =>
                    setAttributes({
                      url,
                      text: (post && post.title) || "Click here",
                    })
                  }
                />
              </PanelRow>
              <PanelRow>
                <ToggleControl label="Open in new tab" checked={attributes.linkOpenType} onChange={setLinkOpenType} />
              </PanelRow>
            </PanelBody>
          ) : null}
          {attributes.linkType !== "none" && attributes.linkAction === "imagePopup" ? (
            <PanelBody className="image-popup">
              <PanelRow className="image-popup-image">
                <MediaUploadCheck>
                  <MediaUpload
                    allowedType={["image"]}
                    value={attributes.popupImgID}
                    onSelect={selectPopupImg}
                    render={({ open }) => (
                      <>
                        <Button className={"button button-large"} onClick={open}>
                          {__("Upload Image", "berg")}
                        </Button>
                        {attributes.popupImgURL ? (
                          <div className="imageUploadWrapper">
                            <img src={attributes.popupImgURL} />
                            <Button className="btn-remove" onClick={removePopupImg}>
                              <Dashicon icon="no" size="20" />
                            </Button>
                          </div>
                        ) : null}
                      </>
                    )}
                  ></MediaUpload>
                </MediaUploadCheck>
              </PanelRow>
              {attributes.popupImgURL && (
                <PanelRow>
                  <TextControl
                    label={__("Popup image alternate text", "")}
                    value={attributes.popupImgALT}
                    onChange={imgAltVal => {
                      setAttributes({
                        popupImgALT: imgAltVal,
                      })
                    }}
                  />
                </PanelRow>
              )}
              <PanelRow>
                <TextControl label="Social Media URL" type="text" value={attributes.socialURL} onChange={setSocialURL} />
              </PanelRow>
              <PanelRow className="image-popup-social">
                <MediaUploadCheck>
                  <MediaUpload
                    allowedType={["image"]}
                    value={attributes.socialIconID}
                    onSelect={setSocialIcon}
                    render={({ open }) => (
                      <>
                        <Button className={"button button-large"} onClick={open}>
                          {__("Upload social Icon", "berg")}
                        </Button>
                        {attributes.socialIconURL ? (
                          <div className="imageUploadWrapper">
                            <img src={attributes.socialIconURL} />
                            <Button className="btn-remove" onClick={removeSocialIcon}>
                              <Dashicon icon="no" size="20" />
                            </Button>
                          </div>
                        ) : null}
                      </>
                    )}
                  ></MediaUpload>
                </MediaUploadCheck>
              </PanelRow>
              <PanelRow>
                <TextControl label="Title" value={attributes.popupName} onChange={setPopupName} />
              </PanelRow>
              <PanelRow>
                <TextControl label="Sub Title" value={attributes.popupSubName} onChange={setPopupSubName} />
              </PanelRow>
              <PanelRow>
                <div className="popup-options">
                  Popup Description
                  <RichText
                    className="bs-advance-blurb-popup-text"
                    tagName="p"
                    value={attributes.popupContent}
                    onChange={setPopupContent}
                  />
                </div>
              </PanelRow>
            </PanelBody>
          ) : null}
          {attributes.linkType !== "none" && attributes.linkAction === "videoPopup" ? (
            <PanelBody className="video-popup">
              <PanelRow>
                <SelectControl
                  label="Video Source"
                  options={[
                    { label: "Select Option", value: "select" },
                    { value: "url", label: "URL" },
                    { value: "upload", label: "Upload" },
                    { value: "html", label: "Embed Code or HTML" },
                  ]}
                  value={attributes.popupVideo}
                  onChange={setPopupVideo}
                />
              </PanelRow>
              {attributes.popupVideo === "upload" && (
                <PanelRow className="video-popup-video">
                  <MediaUploadCheck>
                    <MediaUpload
                      onSelect={setPopupVideoUpload}
                      allowedTypes={["video"]}
                      value={attributes.popupVideoUpload}
                      render={({ open }) => (
                        <>
                          <Button className={"button button-large"} onClick={open}>
                            {__("Upload Video", "berg")}
                          </Button>
                          {attributes.popupVideoUploadURL ? (
                            <div className="videoUploadWrapper">
                              <video width="50" height="50">
                                <source src={attributes.popupVideoUploadURL} type="video/mp4" />
                              </video>
                              <Button className="btn-remove" onClick={removePopupVideoUpload}>
                                <Dashicon icon="no" size="20" />
                              </Button>
                            </div>
                          ) : null}
                        </>
                      )}
                    />
                  </MediaUploadCheck>
                </PanelRow>
              )}
              {attributes.popupVideo === "url" && (
                <PanelRow>
                  <TextControl
                    label="Video URL"
                    type="text"
                    value={attributes.popupVideoURL}
                    onChange={setPopupVideoURL}
                  />
                </PanelRow>
              )}
              {attributes.popupVideo === "html" && (
                <React.Fragment>
                  <div className="bs-pro-button__videopopuphtml">
                    <p>Embed code or HTML</p>
                    <textarea
                      placeholder="Paste the Embed code or HTML here"
                      onChange={event => {
                        const htmlInput = event.currentTarget.value
                        setHtmlValid(true)
                        if (/<([A-Za-z][A-Za-z0-9]*)\b[^>]*>(.*?)<\/\1>/.test(htmlInput)) {
                          const htmlCodeEncoded = Buffer.from(htmlInput, "utf-8").toString("base64")
                          setAttributes({ vpopupHtml: htmlCodeEncoded })
                        } else if (htmlInput.length > 0) {
                          setHtmlValid(false)
                        }
                      }}
                      rows="10"
                      defaultValue={decodedVpopHtml}
                    ></textarea>
                    {!htmlValid ? <span className="input-validation-error">Not a valid HTML content</span> : ""}
                  </div>
                  <div>
                    <TextControl
                      label="Popup class(es)"
                      placeholder="Class name(s)"
                      value={vpopupFancyBoxOptions?.classNames || ""}
                      onChange={vpopupClasses => {
                        setAttributes({
                          vpopupFancyBoxOptions: {
                            ...vpopupFancyBoxOptions,
                            classNames: vpopupClasses,
                          },
                        })
                      }}
                    />
                  </div>
                  <div>
                    <SelectControl
                      label="Popup outside click action"
                      value={vpopupFancyBoxOptions?.dismissOnClickOutside || "close"}
                      options={[
                        { label: "Dismiss", value: "close" },
                        { label: "Do nothing", value: false },
                      ]}
                      onChange={selectedVal => {
                        setAttributes({
                          vpopupFancyBoxOptions: {
                            ...vpopupFancyBoxOptions,
                            dismissOnClickOutside: selectedVal,
                          },
                        })
                      }}
                    />
                  </div>
                </React.Fragment>
              )}
              <PanelRow className="video-popup-icon">
                <MediaUploadCheck>
                  <MediaUpload
                    allowedType={["image"]}
                    value={attributes.playIconID}
                    onSelect={selectPlayIcon}
                    render={({ open }) => (
                      <>
                        <Button className={"button button-large"} onClick={open}>
                          {__("Upload Play Icon", "berg")}
                        </Button>
                        {attributes.playIconURL ? (
                          <div className="imageUploadWrapper">
                            <img src={attributes.playIconURL} />
                            <Button className="btn-remove" onClick={removePlayIcon}>
                              <Dashicon icon="no" size="20" />
                            </Button>
                          </div>
                        ) : null}
                      </>
                    )}
                  ></MediaUpload>
                </MediaUploadCheck>
              </PanelRow>
            </PanelBody>
          ) : null}
        </PanelBody>
      </InspectorControls>
      <div>
        {attributes.linkType === "outerLink" ? (
          <div
            className={`bs-advanced-blurb ${classNames} ${blurbVisibility === true ? "enable" : "disable"} ${
              !attributes.imgURL || !orderDiv.some(checkIfImageEnabled) ? "bs-advanced-blurb--no-image" : ""
            }`}
          >
            <div className="bs-advanced-blurb__container">
              <a
                rel={attributes.linkType !== "none" ? "noopener noreferrer" : ""}
                target={attributes.linkOpenType ? "_blank" : ""}
              >
                <InnerContent setAttributes={setAttributes} attributes={attributes} />
              </a>
            </div>
          </div>
        ) : (
          <div
            className={`bs-advanced-blurb ${classNames} ${blurbVisibility === true ? "enable" : "disable"} ${
              !attributes.imgURL || !orderDiv.some(checkIfImageEnabled) ? "bs-advanced-blurb--no-image" : ""
            }`}
          >
            <div className="bs-advanced-blurb__container">
              <InnerContent setAttributes={setAttributes} attributes={attributes} />
            </div>
          </div>
        )}
      </div>
    </div>
  )
}

export default withSelect((select, ownProps) => {
  const theme = select("core").getCurrentTheme()
  const { block_classes: blockClasses = {} } = theme || {}
  return { blockClasses }
})(Edit)