import { PanelBody, Button } from "@wordpress/components";
import { MediaUpload, MediaUploadCheck } from "@wordpress/block-editor";
import { __ } from "@wordpress/i18n";

const changeAttributeValue = (
  sectionImgId,
  sectionImgUrl,
  sectionImgAlt,
  deviceSize,
  pictureTagOptions,
  setAttributes
) => {
  let newValuesObj = pictureTagOptions[deviceSize].settings;
  let newValues = { sectionImgId, sectionImgUrl, sectionImgAlt };
  newValuesObj = { ...newValuesObj, ...newValues };

  let optionsNew = pictureTagOptions[deviceSize];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    pictureTagOptions: {
      ...pictureTagOptions,
      [deviceSize]: optionsNew,
    },
  });
};

const change2xImageAttributeValue = (
  image2xId,
  image2xUrl,
  image2xAlt,
  deviceSize,
  pictureTagOptions,
  setAttributes
) => {
  let newValuesObj = pictureTagOptions[deviceSize].settings;
  let newValues = { image2xId, image2xUrl, image2xAlt };
  newValuesObj = { ...newValuesObj, ...newValues };

  let optionsNew = pictureTagOptions[deviceSize];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    pictureTagOptions: {
      ...pictureTagOptions,
      [deviceSize]: optionsNew,
    },
  });
};

const PictureTagSetting = (props) => {
  const { deviceSizes, pictureTagOptions, setAttributes } = props;
  return (
    <>
      {deviceSizes.map(([text, deviceSize], index) => {
        const { sectionImgUrl, sectionImgId, image2xId, image2xUrl } =
          pictureTagOptions[deviceSize].settings;
        const title =
          deviceSize === "regular"
            ? `${text} (Greater than 1280px)`
            : deviceSize === "desktop"
            ? `${text} (Between 576px and 1280px)`
            : `${text} (Less than 576px)`;
        return (
          <PanelBody title={title} initialOpen={index === 0} key={index}>
            <MediaUploadCheck>
              <MediaUpload
                allowedType={["image"]}
                value={sectionImgId}
                onSelect={(img) => {
                  let sectionImgId = img.id ? img.id : "";
                  let sectionImgUrl = img.url ? img.url : "";
                  let sectionImgAlt = img.alt ? img.alt : "";
                  changeAttributeValue(
                    sectionImgId,
                    sectionImgUrl,
                    sectionImgAlt,
                    deviceSize,
                    pictureTagOptions,
                    setAttributes
                  );
                }}
                render={({ open }) => (
                  <div className="media-upload image-upload" key={sectionImgId}>
                    {sectionImgUrl ? (
                      <Button
                        className="media-button remove-button"
                        onClick={() => {
                          changeAttributeValue(
                            "",
                            "",
                            "",
                            deviceSize,
                            pictureTagOptions,
                            setAttributes
                          );
                        }}
                      >
                        {__("Remove Image", "berg")}
                      </Button>
                    ) : (
                      <Button
                        className={"media-button upload-button"}
                        onClick={open}
                      >
                        {__("Upload Image", "berg")}
                      </Button>
                    )}
                    <div className="image-thumb-wrapper" onClick={open}>
                      {sectionImgUrl ? (
                        <img
                          className="image-thumb"
                          src={sectionImgUrl !== undefined ? sectionImgUrl : ""}
                        />
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                )}
              ></MediaUpload>
            </MediaUploadCheck>
            {/* Settings for 2x size image for responsive images */}
            {deviceSize != "regular" && (
              <MediaUploadCheck>
                <MediaUpload
                  allowedType={["image"]}
                  value={image2xId}
                  onSelect={(img) => {
                    let image2xId = img.id ? img.id : "";
                    let image2xUrl = img.url ? img.url : "";
                    let image2xAlt = img.alt ? img.alt : "";
                    change2xImageAttributeValue(
                      image2xId,
                      image2xUrl,
                      image2xAlt,
                      deviceSize,
                      pictureTagOptions,
                      setAttributes
                    );
                  }}
                  render={({ open }) => (
                    <div className="media-upload image-upload" key={image2xId}>
                      {image2xUrl ? (
                        <Button
                          className="media-button remove-button"
                          onClick={() => {
                            change2xImageAttributeValue(
                              "",
                              "",
                              "",
                              deviceSize,
                              pictureTagOptions,
                              setAttributes
                            );
                          }}
                        >
                          {__("Remove 2x Image", "berg")}
                        </Button>
                      ) : (
                        <Button
                          className={"media-button upload-button"}
                          onClick={open}
                        >
                          {__("Upload 2x Image", "berg")}
                        </Button>
                      )}
                      <div className="image-thumb-wrapper" onClick={open}>
                        {image2xUrl ? (
                          <img
                            className="image-thumb"
                            src={image2xUrl !== undefined ? image2xUrl : ""}
                          />
                        ) : (
                          ""
                        )}
                      </div>
                    </div>
                  )}
                ></MediaUpload>
              </MediaUploadCheck>
            )}
          </PanelBody>
        );
      })}
    </>
  );
};

export default PictureTagSetting;
