/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import {
  MediaUpload,
  RichText,
  InspectorControls,
  URLInput,
  MediaUploadCheck,
  useBlockProps,
} from "@wordpress/block-editor";
import classnames from "classnames";
import { Button, PanelBody, SelectControl, PanelRow, TextControl, ToggleControl, Dashicon } from "@wordpress/components";
import Select from "react-select";
import apiFetch from "@wordpress/api-fetch";
import { capitalize } from "lodash";

const { useEffect, useState } = wp.element;
const { withSelect } = wp.data;

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
//import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @param {Object} [props]           Properties passed from the editor.
 * @param {string} [props.className] Class name generated for the block.
 *
 * @return {WPElement} Element to render.
 */
import ImageVariations from "./_partials/image-variations";
import TagSelector from "~berg/ui-components/tag-selector";
import SortableSelect from "~berg/ui-components/sortable-multi-select";

//Inner Content
const InnerContent = props => {
  const { attributes, setAttributes } = props;
  const { orderDiv, desktopBreakpoint, mobileBreakpoint } = attributes;
  const innerElements = [];

  //Title
  const setTitle = titleVal => {
    setAttributes({ title: titleVal });
  };

  //Sub-title
  const setSubtitle = subtitleVal => {
    setAttributes({ subtitle: subtitleVal });
  };

  //Paragraph

  const setParagraph = paragraphVal => {
    setAttributes({ content: paragraphVal });
  };

  //link text
  const setLinkText = linkTextVal => {
    setAttributes({ linkText: linkTextVal });
  };

  //bottom text
  const setBottomText = bottomTextVal => {
    setAttributes({ bottomText: bottomTextVal });
  };

  // Getting global breakpoint values from Theme settings
  const globalDesktopBreakpoint = typeof bergThemeData !== "undefined" ? bergThemeData.desktopBreakpoint : 1280;
  const globalMobileBreakpoint = typeof bergThemeData !== "undefined" ? bergThemeData.mobileBreakpoint : 576;

  useEffect(() => {
    // Updating breakpoint values when loading the block to avoid block recovery errors.
    if (desktopBreakpoint != globalDesktopBreakpoint) {
      setAttributes({ desktopBreakpoint: parseInt(globalDesktopBreakpoint) });
    }
    if (mobileBreakpoint != globalMobileBreakpoint) {
      setAttributes({ mobileBreakpoint: parseInt(globalMobileBreakpoint) });
    }
  });

  orderDiv
    ? orderDiv.map(item => {
      if (item.value == "title") {
        innerElements.push(
          <div className="bs-blurb__title" key={item.value}>
            <RichText
              tagName={attributes.titleTage}
              placeholder="Title"
              value={attributes.title}
              onChange={setTitle}
            />
          </div>
        );
      }

      if (item.value == "subtitle") {
        innerElements.push(
          <div className="bs-blurb__subtitle" key={item.value}>
            <RichText
              tagName={attributes.subtitleTag}
              placeholder="Sub-title"
              value={attributes.subtitle}
              onChange={setSubtitle}
            />
          </div>
        );
      }

      if (item.value == "description") {
        innerElements.push(
          <div className="bs-blurb__description" key={item.value}>
            <RichText
              tagName={attributes.paragraphTag}
              placeholder="Description"
              value={attributes.content}
              onChange={setParagraph}
            />
          </div>
        );
      }

      if (item.value == "bottom-text") {
        innerElements.push(
          <div className="bs-blurb__read-more" key={item.value}>
            {attributes.linkType === "innerLink" ? (
              <RichText
                tagName="a"
                placeholder="Link Text"
                className="bs-blurb__trigger"
                value={attributes.linkText}
                onChange={setLinkText}
              />
            ) : (
              <RichText
                tagName="span"
                placeholder="Bottom Text"
                className="bs-blurb__read-more-text"
                value={attributes.bottomText}
                onChange={setBottomText}
              />
            )}
          </div>
        );
      }
      if (item.value == "image") {
        innerElements.push(
          <div className="bs-blurb__image" key={item.value}>
            {attributes.imgURL && (
              <figure>
                <picture>
                  {attributes.mobileImgURL && (
                    <>
                      <source
                        srcSet={
                          attributes.mobile2xImgURL
                            ? `${attributes.mobileImgURL},
                          ${attributes.mobile2xImgURL} 2x`
                            : `${attributes.mobileImgURL}`
                        }
                        media={`(max-width:${mobileBreakpoint - 1}px)`}
                      />
                    </>
                  )}
                  {attributes.desktopImgURL && (
                    <>
                      <source
                        srcSet={
                          attributes.desktop2xImgURL
                            ? `${attributes.desktopImgURL},
                          ${attributes.desktop2xImgURL} 2x`
                            : `${attributes.desktopImgURL}`
                        }
                        media={`(max-width:${desktopBreakpoint}px)`}
                      />
                    </>
                  )}
                  <img
                    className={`${attributes.imageBehavior} img-fluid normal-image`}
                    src={attributes.imgURL}
                    alt={attributes.imgAlt ? attributes.imgAlt : ""}
                  />
                </picture>
              </figure>
            )}
          </div>
        );
      }
    })
    : "";

  return <>{innerElements}</>;
};

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {};
  const { attributes, setAttributes, linkOpenType } = props;
  const {
    url,
    blurbClass,
    blurbClassNames,
    orderDiv,
    blurbVisibility,
    imgURL,
    postId,
    title,
    content,
    bottomText,
    isImgLazyLoad,
  } = attributes;
  //link type
  const setLinkType = linkTypeVal => {
    setAttributes({
      linkType: linkTypeVal,
    });
    /*Clearing the link text if it contains HTML elements
    (When changing the link type from 'Wrap All' to any other)*/
    if (/<[a-z/][\s\S]*>/i.test(attributes.linkText)) {
      setAttributes({
        linkText: null,
      });
    }
  };

  //link open new tab or not
  const setLinkOpenType = setLinkOpenTypeVal => {
    setAttributes({
      linkOpenType: setLinkOpenTypeVal,
    });
  };

  //set title tag
  const setTitleTag = titleTagVal => {
    setAttributes({
      titleTage: titleTagVal,
    });
  };

  //set subtitle tag
  const setSubtitleTag = subtitleTagVal => {
    setAttributes({
      subtitleTag: subtitleTagVal,
    });
  };

  //set pharagraph
  const setParagraphTag = paragrapTagVal => {
    setAttributes({
      paragraphTag: paragrapTagVal,
    });
  };

  // set image behavior
  const setImageBehavior = setImageBehaviorVal => {
    props.setAttributes({
      imageBehavior: setImageBehaviorVal,
    });
  };

  //set hover image
  const selectHoverImg = img => {
    setAttributes({
      hoverImgID: img.id,
      hoverImgURL: img.url,
    });
  };

  //remove hover image
  const removeHoverImg = () => {
    setAttributes({
      hoverImgID: null,
      hoverImgURL: null,
    });
  };

  //set component class name

  // const setClassName = (setClassNameVal) => {
  // 	setAttributes({
  // 		addClassName: setClassNameVal,
  // 	});
  // };

  const [blurbClasses, setBlurbClasses] = useState([]);
  const [defaultClassValues, setDefaultClassValues] = useState([]);
  const blockProps = useBlockProps();

  let isImageInDispalyOrder = false;
  orderDiv.filter(order => {
    if (order.label === "Image") {
      isImageInDispalyOrder = true;
    }
  }, false);

  const noImageClassName = !imgURL || !isImageInDispalyOrder ? "bs-blurb--no-image" : "";

  useEffect(() => {
    const classList = [];
    if (coreComponents["bs-blurb"] && coreComponents["bs-blurb"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-blurb"]}`,
        label: "Default",
      });

      props.setAttributes({
        blurbClass: "bs-blurb---default",
      });
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      });
    }

    if (coreComponentsTheme["bs-blurb"] && coreComponentsTheme["bs-blurb"].length > 0) {
      coreComponentsTheme["bs-blurb"].map((className, index) => {
        let classLabel = className.replace("bs-blurb--", "").split("-");
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        });
      });
    }
    const defaultValues = blurbClassNames ? blurbClassNames : [classList[0]];
    setBlurbClasses(classList);
    setDefaultClassValues(defaultValues);
  }, [JSON.stringify(props.blockClasses)]);

  if (postId && title === "" && content === "" && bottomText === "" && imgURL === "") {
    apiFetch({
      path: "/api/bs-blurb/getPostData",
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ postId }),
    }).then(res => {
      if (res) {
        const { title, content, bottomText, imgURL } = res;
        setAttributes({ title, content, bottomText, imgURL });
      }
    });
  }
  const classNames = blurbClassNames ? blurbClassNames.map(x => x.value).join(" ") : "";

  const onChange = selectedOptions => {
    setAttributes({ orderDiv: selectedOptions });
  };
  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls initialOpen={true}>
        <PanelBody title={__("Visibility", "")} initialOpen={false}>
          <ToggleControl
            label="Enable/Disable"
            checked={blurbVisibility}
            onChange={val => {
              setAttributes({ blurbVisibility: val });
            }}
          />
        </PanelBody>
        <PanelBody title={__("Blurb options", "")} initialOpen={true}>
          {/* <PanelRow>
            <TextControl
              label="Add blurb class"
              value={attributes.addClassName}
              onChange={setClassName}
            />
          </PanelRow> */}

          <PanelRow>
            <SelectControl
              label="Link type"
              value={attributes.linkType}
              options={[
                { label: "Inner Link", value: "innerLink" },
                { label: "Wrap All", value: "outerLink" },
                { label: "None", value: "none" },
              ]}
              onChange={setLinkType}
            />
          </PanelRow>

          {attributes.linkType === "outerLink" || attributes.linkType === "innerLink" ? (
            <PanelRow>
              <URLInput
                label="Enter Url"
                value={attributes.url}
                onChange={(url, post) =>
                  setAttributes({
                    url,
                    text: (post && post.title) || "Click here",
                  })
                }
              />
            </PanelRow>
          ) : null}

          {attributes.linkType === "outerLink" || attributes.linkType === "innerLink" ? (
            <PanelRow>
              <ToggleControl label="Open in new tab" checked={attributes.linkOpenType} onChange={setLinkOpenType} />
            </PanelRow>
          ) : null}
        </PanelBody>
        <PanelBody title={__("Blurb content and image options ", "")} initialOpen={true}>
          <PanelRow>
            <SelectControl
              label="Image behavior"
              value={attributes.imageBehavior}
              options={[
                { label: "None", value: "none" },
                { label: "Contain ", value: "contain" },
                { label: "Cover", value: "cover" },
                { label: "Fill", value: "fill" },
              ]}
              onChange={setImageBehavior}
            />
          </PanelRow>
          <ImageVariations setAttributes={setAttributes} attributes={attributes} />
          <TextControl
            label={__("Image alternate text", "")}
            value={attributes.imgAlt}
            onChange={imgAltVal => {
              setAttributes({
                imgAlt: imgAltVal,
              });
            }}
          />
          {imgURL && (
            <ToggleControl
              label={__("Image lazy loading")}
              checked={isImgLazyLoad}
              onChange={isImgLazyLoad => setAttributes({ isImgLazyLoad })}
            />
          )}
          <label>Hover image</label>
          <PanelRow className="hover-image">
            <MediaUploadCheck>
              <MediaUpload
                allowedType={["image"]}
                value={attributes.hoverImgID}
                onSelect={selectHoverImg}
                render={({ open }) => (
                  <>
                    <Button className={"button button-large"} onClick={open}>
                      {__("Select Hover Image", "berg")}
                    </Button>
                    {attributes.hoverImgURL ? (
                      <div className="imageUploadWrapper">
                        <img src={attributes.hoverImgURL} />
                        <Button className="btn-remove" onClick={removeHoverImg}>
                          <Dashicon icon="no" size="20" />
                        </Button>
                      </div>
                    ) : null}
                  </>
                )}
              ></MediaUpload>
            </MediaUploadCheck>
          </PanelRow>

          <PanelRow>
            <TagSelector label={__("Title Tag", "berg")} value={attributes.titleTage} onChange={setTitleTag} />
          </PanelRow>
          <PanelRow>
            <TagSelector label={__("Subtitle Tag", "berg")} value={attributes.subtitleTag} onChange={setSubtitleTag} />
          </PanelRow>
          <PanelRow>
            <TagSelector label={__("Paragraph Tag", "berg")} value={attributes.paragraphTag} onChange={setParagraphTag} />
          </PanelRow>
          <label>{__("Display Order", "berg")}</label>
          <SortableSelect
            value={orderDiv}
            onChange={onChange}
            options={[
              { label: "Title", value: "title" },
              { label: "Sub-title", value: "subtitle" },
              { label: "Description", value: "description" },
              { label: "Bottom Text or Link", value: "bottom-text" },
              { label: "Image", value: "image" },
            ]}
          ></SortableSelect>
        </PanelBody>
        <PanelBody title={__("Blurb Class", "")} initialOpen={false}>
          {/* <SelectControl
            label={__("Select Blurb Class", "")}
            value={props.attributes.blurbClass}
            options={blurbClasses}
            onChange={(val) => {
              props.setAttributes({ blurbClass: val });
            }}
          /> */}
          <Select
            defaultValue={defaultClassValues}
            isMulti
            name="blurbClasses"
            options={blurbClasses}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={val => {
              setAttributes({
                blurbClassNames: val ? val : [],
              });
            }}
          />
        </PanelBody>
      </InspectorControls>
      <div>
        {attributes.linkType === "outerLink" ? (
          <div className={`bs-blurb ${classNames} ${blurbVisibility === true ? "enable" : "disable"} ${noImageClassName}`}>
            <div className="bs-blurb__container">
              <a
                className="bs-blurb__trigger"
                rel={attributes.linkType !== "none" ? "noopener noreferrer" : ""}
                target={attributes.linkOpenType ? "_blank" : ""}
              >
                <InnerContent setAttributes={setAttributes} attributes={attributes} />
              </a>
            </div>
          </div>
        ) : (
          <div
            className={`bs-blurb ${classNames} ${blurbVisibility === true ? "enable" : "disable"} ${noImageClassName}`}
          >
            <div className="bs-blurb__container">
              <InnerContent setAttributes={setAttributes} attributes={attributes} />
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default withSelect((select, ownProps) => {
  const theme = select("core").getCurrentTheme();
  const { block_classes: blockClasses = {} } = theme || {};
  return { blockClasses };
})(Edit);
