/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n"
import { InnerBlocks, InspectorControls, MediaUpload, MediaUploadCheck, useBlockProps } from "@wordpress/block-editor"
import {
  Panel,
  PanelBody,
  RadioControl,
  SelectControl,
  ToggleControl,
  Button,
  Dashicon,
  TextControl,
  Tooltip,
} from "@wordpress/components"

const { useEffect, useState } = wp.element
import Select from "react-select"
import { setUniqueIdAttribute } from "~berg/utils"
import { capitalize } from "lodash"
import { select, dispatch, withSelect } from "@wordpress/data"

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
//import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @param {Object} [props]           Properties passed from the editor.
 * @param {string} [props.className] Class name generated for the block.
 *
 * @return {WPElement} Element to render.
 */
// import ImageVariations from "./_partials/image-variations";
import {
  GradientSettings,
  BackgroundColorSetting,
  PictureTagSetting,
  BackgroundImageSetting,
  BackgroundImageAndColorSetting,
} from "./_partials"

const ALLOWED_BLOCK = ["e25m/row", "e25m/slider"]
const deviceSizes = [
  ["Regular size", "regular"],
  ["Desktop", "desktop"],
  ["Mobile", "mobile"],
]

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {}
  const { attributes, setAttributes } = props
  const {
    containerType,
    sectionVisibility,
    backgroundType,
    sectionBlockID,
    backgroundVideoUrl,
    backgroundVideoName,
    backgroundVideoControls,
    backgroundVideoLoop,
    backgroundVideoMute,
    backgroundVideoAutoPlay,
    secImgIdVideo,
    secImgVideoUrl,
    sectionClassNames,
    sectionName,
    gradientOptions,
    backgroundColorOptions,
    backgroundImageOptions,
    pictureTagOptions,
    backgroundImageAndColorOptions,
    isCollapsed,
    realmDefaultClass,
    realmClassNames,
  } = attributes

  useEffect(() => {
    setUniqueIdAttribute(props, "sectionBlockID")
  }, [props.attributes])

  const [sectionClasses, setSectionClasses] = useState([])
  const [defaultClassValues, setDefaultClassValues] = useState([])

  const blockProps = useBlockProps()

  useEffect(() => {
    const classList = []
    if (coreComponents["bs-section"] && coreComponents["bs-section"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-section"]}`,
        label: "Default",
      })
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      })
    }

    if (coreComponentsTheme["bs-section"] && coreComponentsTheme["bs-section"].length > 0) {
      coreComponentsTheme["bs-section"].map((className, index) => {
        let classLabel = className.replace("bs-section--", "").split("-")
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        })
      })
    }

    const defaultValues = sectionClassNames ? sectionClassNames : [classList[0]]
    setSectionClasses(classList)
    setDefaultClassValues(defaultValues)
  }, [JSON.stringify(props.blockClasses)])

  const select_video = val => {
    setAttributes({
      backgroundVideoUrl: val.url ? val.url : "",
      backgroundVideoName: val.filename ? val.filename : "",
    })
  }

  const remove_video = val => {
    setAttributes({
      backgroundVideoUrl: "",
      backgroundVideoName: "",
    })
  }

  const select_section_img_video = img => {
    setAttributes({
      secImgIdVideo: img.id,
      secImgVideoUrl: img.url,
    })
  }

  const remove_section_img_video = () => {
    setAttributes({
      secImgIdVideo: null,
      secImgVideoUrl: null,
    })
  }

  const toggle_section = () => {
    setAttributes({
      isCollapsed: !isCollapsed,
    })
  }

  const colors = wp.data.select("core/block-editor").getSettings().colors

  const classNames = sectionClassNames ? sectionClassNames.map(x => x.value).join(" ") : ""

  const realmClasses = realmClassNames ? realmClassNames.map(x => x.value).join(" ") : ""

  const sectionId = sectionName.toString().length > 0 ? sectionName.toLowerCase().replace(/\W/g, "-") : undefined

  const duplicate_section_block = () => {
    const blockIds = select("core/block-editor").getSelectedBlockClientIds()
    dispatch("core/block-editor").duplicateBlocks(blockIds)
  }

  return (
    <div { ...blockProps } key={blockProps.id}>
      <InspectorControls>
        <Panel>
          <PanelBody title={__("Visibility", "")} initialOpen={false}>
            <ToggleControl
              label="Section Enable/Disable"
              checked={sectionVisibility}
              onChange={(val) => {
                setAttributes({ sectionVisibility: val });
              }}
            />
          </PanelBody>
          <PanelBody title={__("Container Type", "")} initialOpen={false}>
            <RadioControl
              label={__("Select Container Type", "")}
              selected={containerType}
              options={[
                { label: "Container", value: "container" },
                { label: "Fluid Container", value: "container-fluid" },
              ]}
              onChange={(val) => {
                setAttributes({ containerType: val });
              }}
            />
          </PanelBody>
          <PanelBody title={__("Section Class", "")} initialOpen={false}>
            <Select
              defaultValue={defaultClassValues}
              isMulti
              name="sectionClasses"
              options={sectionClasses}
              className="basic-multi-select"
              classNamePrefix="select"
              onChange={(val) => {
                setAttributes({
                  sectionClassNames: val ? val : [],
                });
              }}
            />
          </PanelBody>
          <PanelBody title={__("Section Name", "")} initialOpen={false}>
            <TextControl
              value={sectionName}
              type="text"
              className="block-editor-inserter"
              onChange={(val) => {
                setAttributes({
                  sectionName: val ? val : "",
                });
              }}
            />
          </PanelBody>
          <PanelBody title={__("Background Type", "")} initialOpen={false}>
            <SelectControl
              label={__("Select Background Type", "")}
              value={backgroundType}
              options={[
                { value: "", label: "Select Background" },
                { value: "background_image", label: "Background Image" },
                { value: "background_color", label: "Background Color" },
                {
                  value: "background_img_color",
                  label: "Background Image & Color",
                },
                { value: "background_video", label: "Background Video" },
                { value: "gradient", label: "Gradient" },
                { value: "picture_tag", label: "Image as a Background" },
              ]}
              onChange={(val) => {
                setAttributes({ backgroundType: val });
              }}
            />

            {backgroundType === "picture_tag" && (
              <PictureTagSetting
                deviceSizes={deviceSizes}
                pictureTagOptions={pictureTagOptions}
                setAttributes={setAttributes}
              />
            )}

            {backgroundType === "background_image" && (
              <BackgroundImageSetting
                deviceSizes={deviceSizes}
                backgroundImageOptions={backgroundImageOptions}
                setAttributes={setAttributes}
              />
            )}

            {backgroundType === "background_color" && (
              <BackgroundColorSetting
                deviceSizes={deviceSizes}
                backgroundColorOptions={backgroundColorOptions}
                colors={colors}
                setAttributes={setAttributes}
              />
            )}

            {backgroundType === "background_img_color" && (
              <BackgroundImageAndColorSetting
                deviceSizes={deviceSizes}
                backgroundImageAndColorOptions={backgroundImageAndColorOptions}
                colors={colors}
                setAttributes={setAttributes}
              />
            )}

            {backgroundType === "background_video" && (
              <>
                <MediaUploadCheck>
                  <MediaUpload
                    allowedType={["video"]}
                    value={backgroundVideoUrl}
                    onSelect={select_video}
                    render={({ open }) => (
                      <div className="media-upload video-upload">
                        {backgroundVideoUrl ? (
                          <Button
                            className="media-button remove-button"
                            onClick={remove_video}
                          >
                            {__("Remove Video", "berg")}
                          </Button>
                        ) : (
                          <Button
                            className="media-button upload-button"
                            onClick={open}
                          >
                            {__("Upload Video", "berg")}
                          </Button>
                        )}

                        <Dashicon icon="video-alt3" size="20" />
                        <div className="video-file-name">
                          <p>{backgroundVideoName}</p>
                        </div>
                      </div>
                    )}
                  ></MediaUpload>
                </MediaUploadCheck>
                <MediaUploadCheck>
                  <MediaUpload
                    allowedType={["image"]}
                    value={secImgIdVideo}
                    onSelect={select_section_img_video}
                    render={({ open }) => (
                      <div className="media-upload image-upload">
                        {secImgVideoUrl ? (
                          <Button
                            className="media-button remove-button"
                            onClick={remove_section_img_video}
                          >
                            {__("Remove Image", "berg")}
                          </Button>
                        ) : (
                          <Button
                            className={"media-button upload-button"}
                            onClick={open}
                          >
                            {__("Upload Image", "berg")}
                          </Button>
                        )}
                        <div className="image-thumb-wrapper" onClick={open}>
                          {secImgVideoUrl ? (
                            <img
                              className="image-thumb"
                              src={
                                secImgVideoUrl !== undefined ? secImgVideoUrl : ""
                              }
                            />
                          ) : (
                            ""
                          )}
                        </div>
                      </div>
                    )}
                  ></MediaUpload>
                </MediaUploadCheck>
                <ToggleControl
                  label="Show Controls"
                  checked={backgroundVideoControls}
                  onChange={(new_val) => {
                    setAttributes({ backgroundVideoControls: new_val });
                  }}
                />
                <ToggleControl
                  label="Auto Play"
                  checked={backgroundVideoAutoPlay}
                  onChange={(new_val) => {
                    setAttributes({ backgroundVideoAutoPlay: new_val });
                  }}
                />
                <ToggleControl
                  label="Loop"
                  checked={backgroundVideoLoop}
                  onChange={(new_val) => {
                    setAttributes({ backgroundVideoLoop: new_val });
                  }}
                />
                <ToggleControl
                  label="Mute"
                  checked={backgroundVideoMute}
                  onChange={(new_val) => {
                    setAttributes({ backgroundVideoMute: new_val });
                  }}
                />
              </>
            )}
            {backgroundType === "gradient" && (
              <GradientSettings
                deviceSizes={deviceSizes}
                gradientOptions={gradientOptions}
                colors={colors}
                setAttributes={setAttributes}
              />
            )}
          </PanelBody>
        </Panel>
      </InspectorControls>
      <section
        id={sectionId}
        className={`bs-section bs-section-${sectionBlockID}
      ${classNames}
      ${realmDefaultClass}
      ${realmClasses}
      ${sectionVisibility === true ? "enable" : "disable"}
      ${isCollapsed === true ? "collapsed" : ""}
      `}
      >
        <span className="bs-section__button-container">
          <Tooltip text={`${isCollapsed === true ? "Expand" : "Collapse"}`}>
            <button className="bs-section__collapse-button">
              <span
                className={`dashicons dashicons-${isCollapsed === true ? "arrow-down-alt2" : "arrow-up-alt2"
                  }`}
                onClick={toggle_section}
              ></span>
            </button>
          </Tooltip>
          <Tooltip text="Duplicate block">
            <button className="bs-section__duplicate-button">
              <span
                className="dashicons dashicons-edit-page"
                onClick={duplicate_section_block}
              ></span>
            </button>
          </Tooltip>
        </span>
        <div className={`${containerType}`}>
          <InnerBlocks allowedBlocks={ALLOWED_BLOCK} />
        </div>
      </section>
    </div>
  );
};

export default withSelect((select) => {
  const theme = select("core").getCurrentTheme()
  const { block_classes: blockClasses = {} } = theme || {}
  return { blockClasses }
})(Edit)
