import { PanelBody, ColorPalette } from "@wordpress/components";
import { __ } from "@wordpress/i18n";

const changeAttributeValue = (
  value,
  propertyName,
  backgroundColorOptions,
  setAttributes,
  attributeVariable
) => {
  let newValuesObj = backgroundColorOptions[propertyName].settings;
  newValuesObj = { ...newValuesObj, [attributeVariable]: value };
  let optionsNew = backgroundColorOptions[propertyName];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    backgroundColorOptions: {
      ...backgroundColorOptions,
      [propertyName]: optionsNew,
    },
  });
};

const BackgroundColorSetting = (props) => {
  const { deviceSizes, backgroundColorOptions, colors, setAttributes } = props;
  return (
    <>
      {deviceSizes.map(([text, propertyName], index) => {
        const title =
          propertyName === "regular"
            ? `${text} (Greater than 1280px)`
            : propertyName === "desktop"
            ? `${text} (Between 576px and 1280px)`
            : `${text} (Less than 576px)`;
        return (
          <PanelBody title={title} initialOpen={index === 0} key={index}>
            <ColorPalette
              label={__("Select Background Color", "")}
              colors={colors}
              value={
                backgroundColorOptions[propertyName].settings.backgroundColor
              }
              onChange={(value) =>
                changeAttributeValue(
                  value,
                  propertyName,
                  backgroundColorOptions,
                  setAttributes,
                  "backgroundColor"
                )
              }
            />
          </PanelBody>
        );
      })}
    </>
  );
};

export default BackgroundColorSetting;
