import { __ } from "@wordpress/i18n";
import { InspectorControls, useBlockProps } from "@wordpress/block-editor";
import { useEffect, useState } from "@wordpress/element";
import {
  PanelBody,
  PanelRow,
  TextControl,
  ToggleControl,
} from "@wordpress/components";
import Select from "react-select";
import { NavSliderSettings, EditorView } from "./_partials";
import {
  getElementClasses,
  setSliderSettingAttribute as updateResponsiveAttributes,
} from "./utils";
import { setUniqueIdAttribute } from "~berg/utils";
const { withSelect } = wp.data;

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {};
  const { attributes, setAttributes, clientId } = props;
  const {
    blockID,
    tabSliderClassNames,
    tabNavSliderClassNames,
    tabContentSliderClassNames,
    tabNavsSliderOptions,
    identifier,
    editorModeSliderSettings,
    sliderVisibility,
  } = attributes;

  useEffect(() => {
    setUniqueIdAttribute(props, "blockID");
  }, [props.attributes]);

  const [tabSliderClasses, setTabSliderClasses] = useState([]);
  const [defaultTabClassValues, setDefaultTabClassValues] = useState([]);
  const [navSlides, setNavSlides] = useState([]);
  const [contentSlides, setContentSlides] = useState([]);
  const [editMode, setEditMode] = useState(false);
  const tabBlockClientId = clientId;
  const blockProps = useBlockProps();

  useEffect(() => {
    getElementClasses(
      "bs-slider",
      tabSliderClassNames,
      coreComponents,
      coreComponentsTheme,
      (classes, defaultValues) => {
        setTabSliderClasses(classes);
        setDefaultTabClassValues(defaultValues);
      }
    );
  }, [JSON.stringify(props.blockClasses)]);

  // Update Mobile, Tablet and Table Landscape setting(s) through cloned copies
  const setSliderSettingAttribute = (attribute, layout, property, value) => {
    updateResponsiveAttributes(attributes, setAttributes, attribute, layout, property, value);
  };

  const { innerBlocks } = wp.data.select("core/block-editor").getBlock(clientId)
    ? wp.data.select("core/block-editor").getBlock(clientId)
    : {};
  useEffect(() => {
    if (Array.isArray(innerBlocks) && innerBlocks.length) {
      const navSlides = [];
      innerBlocks.forEach(tabBlock => {
        navSlides.push(tabBlock);
      });
      setNavSlides(navSlides);
    } else {
      const defaultLength = 3;

      for (let i = 0; i < defaultLength; i++) {
        const parentTabBlock = wp.blocks.createBlock("e25m/slider-v2-tab");
        // Insert block
        wp.data
          .dispatch("core/block-editor")
          .insertBlocks(parentTabBlock, i + 1, tabBlockClientId, editMode ? true : false);
        navSlides.push(parentTabBlock);
      }
    }
  }, [editMode, innerBlocks]);

  const deviceSizes = [
    ["Desktop", "desktop"],
    ["IPad", "tablet"],
    ["IPad Landscape", "tabletLandscape"],
    ["Mobile", "mobile"],
  ];

  const FieldTypes = {
    Range: "range",
    Toggle: "toggle",
    Number: "number",
    Select: "select",
    Text: "text",
  };

  //remove currunt slider
  const removeCurrentSlide = index => {
    const currentClientId = navSlides[index].clientId;
    wp.data.dispatch("core/block-editor").removeBlocks(currentClientId);
    setNavSlides(navSlides.filter((slide, i) => i !== index));
  };

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        <PanelBody title={__("Visibility", "")} initialOpen={false}>
          <ToggleControl
            label="Enable/Disable"
            checked={sliderVisibility}
            onChange={val => {
              setAttributes({ sliderVisibility: val });
            }}
          />
        </PanelBody>
        <PanelBody title={__("Editor control", "tab-slider-v2")} initialOpen={true}>
          <PanelRow>
            <TextControl
              label={__("Name to identify this slider easily", "tab-slider-v2")}
              value={identifier}
              onChange={value => {
                setAttributes({ identifier: value });
              }}
              max={25}
            />
          </PanelRow>
          <PanelRow>
            <ToggleControl checked={editMode} onChange={setEditMode} label={__("Edit Mode", "tab-slider-v2")} />
          </PanelRow>
        </PanelBody>
        <NavSliderSettings
          {...{
            tabNavSliderClassNames,
            setSliderSettingAttribute,
            setAttributes,
            deviceSizes,
            tabNavsSliderOptions,
            navSlides,
            contentSlides,
            FieldTypes,
          }}
        />
        <PanelBody title={__("Slider classes", "tab-slider-v2")} initialOpen={false}>
          <Select
            defaultValue={defaultTabClassValues}
            isMulti
            name="sectionClasses"
            options={tabSliderClasses}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={val => {
              setAttributes({
                tabSliderClassNames: val ? val : [],
              });
            }}
          />
        </PanelBody>
      </InspectorControls>
      <div>
        <EditorView
          {...{
            blockID,
            identifier,
            navSlides,
            contentSlides,
            editMode,
            setEditMode,
            tabNavsSliderOptions,
            tabSliderClassNames,
            tabNavSliderClassNames,
            tabContentSliderClassNames,
            tabBlockClientId,
            removeCurrentSlide,
            editorModeSliderSettings,
            sliderVisibility,
          }}
        />
      </div>
    </div>
  );
};

export default withSelect((select) => {
  const theme = select("core").getCurrentTheme();
  const { block_classes: blockClasses = {} } = theme || {};
  return { blockClasses };
})(Edit);
