import { __ } from "@wordpress/i18n";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";

export default function save(props) {
  const blockProps = useBlockProps.save();
  const { attributes } = props;
  const { relatedPostBlockClassNames, visibility } = attributes;
  const classNames = relatedPostBlockClassNames
    ? relatedPostBlockClassNames
        .map((relatedClass) => relatedClass.value)
        .join(" ")
    : "";
  return (
    visibility && (
      <div 
      { ...blockProps }
      className={`bs-related-posts ${classNames}`}
    >
        <div className="bs-related-posts__container">
          <div className="bs-related-posts__items">
            <InnerBlocks.Content />
          </div>
        </div>
      </div>
    )
  );
}
