/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import { InspectorControls, useBlockProps } from "@wordpress/block-editor";
import { PanelBody, SelectControl, TextControl, PanelRow, ToggleControl } from "@wordpress/components";
import Select from "react-select";
import { capitalize } from "lodash";
const { useEffect, useState } = wp.element;
const { withSelect } = wp.data;

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
//import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @param {Object} [props]           Properties passed from the editor.
 * @param {string} [props.className] Class name generated for the block.
 *
 * @return {WPElement} Element to render.
 */

import InnerContent from "./_partials/edit-inner-content";
import InnerContentImage from "./_partials/edit-inner-content-image";
import ImageVariations from "./_partials/image-variations";
import TagSelector from "~berg/ui-components/tag-selector";

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {};
  const { attributes, setAttributes } = props;
  const {
    url,
    cardClass,
    cardClassNames,
    layoutType,
    imageBehavior,
    titleTag,
    subtitleTag,
    paragraphTag,
    imgURL,
    imgAlt,
    desktopBreakpoint,
    mobileBreakpoint,
    isImgLazyLoad,
  } = attributes;

  // set card layout
  const setLayoutType = setLayoutTypeVal => {
    props.setAttributes({
      layoutType: setLayoutTypeVal,
    });
  };

  //set title tag
  const setTitleTag = titleTagVal => {
    setAttributes({
      titleTag: titleTagVal,
    });
  };

  //set subtitle tag
  const setSubtitleTag = subtitleTagVal => {
    setAttributes({
      subtitleTag: subtitleTagVal,
    });
  };

  //set pharagraph
  const setParagraphTag = paragrapTagVal => {
    setAttributes({
      paragraphTag: paragrapTagVal,
    });
  };

  // set image behavior
  const setImageBehavior = setImageBehaviorVal => {
    props.setAttributes({
      imageBehavior: setImageBehaviorVal,
    });
  };

  const [cardClasses, setCardClasses] = useState([]);
  const [defaultClassValues, setDefaultClassValues] = useState([]);
  const blockProps = useBlockProps();

  useEffect(() => {
    const classList = [];
    if (coreComponents["bs-card"] && coreComponents["bs-card"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-card"]}`,
        label: "Default",
      });

      props.setAttributes({
        cardClass: "bs-card---default",
      });
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      });
    }

    if (coreComponentsTheme["bs-card"] && coreComponentsTheme["bs-card"].length > 0) {
      coreComponentsTheme["bs-card"].map((className, index) => {
        let classLabel = className.replace("bs-card--", "").split("-");
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        });
      });
    }

    const defaultValues = cardClassNames ? cardClassNames : [classList[0]];
    setCardClasses(classList);
    setDefaultClassValues(defaultValues);
  }, [JSON.stringify(props.blockClasses)]);

  const classNames = cardClassNames ? cardClassNames.map(x => x.value).join(" ") : "";

  // Getting global breakpoint values from Theme settings
  const globalDesktopBreakpoint = typeof bergThemeData !== "undefined" ? bergThemeData.desktopBreakpoint : 1280;
  const globalMobileBreakpoint = typeof bergThemeData !== "undefined" ? bergThemeData.mobileBreakpoint : 576;

  useEffect(() => {
    // Updating breakpoint values when loading the block to avoid block recovery errors.
    if (desktopBreakpoint != globalDesktopBreakpoint) {
      setAttributes({ desktopBreakpoint: parseInt(globalDesktopBreakpoint) });
    }
    if (mobileBreakpoint != globalMobileBreakpoint) {
      setAttributes({ mobileBreakpoint: parseInt(globalMobileBreakpoint) });
    }
  });

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls initialOpen={true}>
        {imgURL ? (
          <PanelBody title={__("Card Layout", "")} initialOpen={true}>
            <PanelRow>
              <SelectControl
                label="Card Layout Type"
                value={layoutType}
                options={[
                  { label: "Regular", value: "regular" },
                  { label: "Side by Side ", value: "side-by-side" },
                ]}
                onChange={setLayoutType}
              />
            </PanelRow>
          </PanelBody>
        ) : null}
        <PanelBody title={__("Card content and image options ", "")} initialOpen={true}>
          <PanelRow>
            <SelectControl
              label="Image behavior"
              value={imageBehavior}
              options={[
                { label: "None", value: "none" },
                { label: "Contain", value: "contain" },
                { label: "Fill", value: "cover" },
                { label: "Original Size", value: "scale-down" },
              ]}
              onChange={setImageBehavior}
            />
          </PanelRow>
          <ImageVariations setAttributes={setAttributes} attributes={attributes} />
          <PanelRow>
            <TextControl
              label={__("Image alternate text", "")}
              value={imgAlt}
              onChange={imgAltVal => {
                setAttributes({
                  imgAlt: imgAltVal,
                });
              }}
            />
          </PanelRow>
          <PanelRow>
            {imgURL && (
              <ToggleControl
                label={__("Image lazy loading")}
                checked={isImgLazyLoad}
                onChange={isImgLazyLoad => setAttributes({ isImgLazyLoad })}
              />
            )}
          </PanelRow>
          <PanelRow>
            <TagSelector label={__("Title Tag", "berg")} value={titleTag} onChange={setTitleTag} />
          </PanelRow>

          <PanelRow>
            <TagSelector label={__("Subtitle Tag", "berg")} value={subtitleTag} onChange={setSubtitleTag} />
          </PanelRow>

          <PanelRow>
            <TagSelector label={__("Paragraph Tag", "berg")} value={paragraphTag} onChange={setParagraphTag} />
          </PanelRow>
        </PanelBody>
        <PanelBody title={__("Card Class", "")} initialOpen={false}>
          <Select
            defaultValue={defaultClassValues}
            isMulti
            name="cardClasses"
            options={cardClasses}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={val => {
              setAttributes({
                cardClassNames: val ? val : [],
              });
            }}
          />
        </PanelBody>
      </InspectorControls>
      <div className={`bs-card ${classNames} ${!imgURL ? "bs-card--no-image" : ""}`}>
        <div
          className={`bs-card__container ${imgURL && layoutType === "side-by-side" ? "bs-card__container--flex" : ""}` }
        >
          {imgURL && layoutType === "side-by-side" ? (
            <>
              <div className="bs-card__container-left">
                <InnerContentImage setAttributes={setAttributes} attributes={attributes} />
              </div>
              <div className="bs-card__container-right">
                <InnerContent setAttributes={setAttributes} attributes={attributes} />
              </div>
            </>
          ) : (
            <>
              <InnerContentImage setAttributes={setAttributes} attributes={attributes} />
              <InnerContent setAttributes={setAttributes} attributes={attributes} />
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default withSelect((select, ownProps) => {
  const theme = select("core").getCurrentTheme();
  const { block_classes: blockClasses = {} } = theme || {};
  return { blockClasses };
})(Edit);
