import { PanelBody, Button } from "@wordpress/components";
import { MediaUpload, MediaUploadCheck } from "@wordpress/block-editor";
import { __ } from "@wordpress/i18n";

const changeAttributeValue = (
  columnImgId,
  columnImgUrl,
  columnImgAlt,
  propertyName,
  backgroundImageOptions,
  setAttributes
) => {
  let newValuesObj = backgroundImageOptions[propertyName].settings;
  let newValues = { columnImgId, columnImgUrl, columnImgAlt };
  newValuesObj = { ...newValuesObj, ...newValues };

  let optionsNew = backgroundImageOptions[propertyName];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    backgroundImageOptions: {
      ...backgroundImageOptions,
      [propertyName]: optionsNew,
    },
    //Restoring deprecated attribute values
    columnImgId: "",
    columnImgUrl: "",
    columnImgAlt: "",
  });
};

const change2xImageAttributeValue = (
  image2xId,
  image2xUrl,
  image2xAlt,
  propertyName,
  backgroundImageOptions,
  setAttributes
) => {
  let newValuesObj = backgroundImageOptions[propertyName].settings;
  let newValues = { image2xId, image2xUrl, image2xAlt };
  newValuesObj = { ...newValuesObj, ...newValues };

  let optionsNew = backgroundImageOptions[propertyName];
  optionsNew = {
    ...optionsNew,
    settings: newValuesObj,
  };

  setAttributes({
    backgroundImageOptions: {
      ...backgroundImageOptions,
      [propertyName]: optionsNew,
    },
  });
};

const BackgroundImageSetting = (props) => {
  const { deviceSizes, backgroundImageOptions, setAttributes } = props;
  return (
    <>
      {deviceSizes.map(([text, propertyName], index) => {
        const { columnImgUrl, columnImgId, image2xId, image2xUrl } =
          backgroundImageOptions[propertyName].settings;
        const title =
          propertyName === "regular"
            ? `${text} (Greater than 1280px)`
            : propertyName === "desktop"
            ? `${text} (Between 576px and 1280px)`
            : `${text} (Less than 576px)`;
        return (
          <PanelBody title={title} initialOpen={index === 0} key={index}>
            <MediaUploadCheck>
              <MediaUpload
                allowedType={["image"]}
                value={columnImgId}
                onSelect={(img) => {
                  let columnImgId = img.id ? img.id : "";
                  let columnImgUrl = img.url ? img.url : "";
                  let columnImgAlt = img.alt ? img.alt : "";
                  changeAttributeValue(
                    columnImgId,
                    columnImgUrl,
                    columnImgAlt,
                    propertyName,
                    backgroundImageOptions,
                    setAttributes
                  );
                }}
                render={({ open }) => (
                  <div className="media-upload image-upload">
                    {columnImgUrl ? (
                      <Button
                        className="media-button remove-button"
                        onClick={() => {
                          changeAttributeValue(
                            "",
                            "",
                            "",
                            propertyName,
                            backgroundImageOptions,
                            setAttributes
                          );
                        }}
                      >
                        {__("Remove Image", "berg")}
                      </Button>
                    ) : (
                      <Button
                        className={"media-button upload-button"}
                        onClick={open}
                      >
                        {__("Upload Image", "berg")}
                      </Button>
                    )}
                    <div className="image-thumb-wrapper" onClick={open}>
                      {columnImgUrl ? (
                        <img
                          className="image-thumb"
                          src={columnImgUrl !== undefined ? columnImgUrl : ""}
                        />
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                )}
              ></MediaUpload>
            </MediaUploadCheck>
            {/* Settings for 2x size image for responsive images */}
            {propertyName != "regular" && (
              <MediaUploadCheck>
                <MediaUpload
                  allowedType={["image"]}
                  value={image2xId}
                  onSelect={(img) => {
                    let image2xId = img.id ? img.id : "";
                    let image2xUrl = img.url ? img.url : "";
                    let image2xAlt = img.alt ? img.alt : "";
                    change2xImageAttributeValue(
                      image2xId,
                      image2xUrl,
                      image2xAlt,
                      propertyName,
                      backgroundImageOptions,
                      setAttributes
                    );
                  }}
                  render={({ open }) => (
                    <div className="media-upload image-upload">
                      {image2xUrl ? (
                        <Button
                          className="media-button remove-button"
                          onClick={() => {
                            change2xImageAttributeValue(
                              "",
                              "",
                              "",
                              propertyName,
                              backgroundImageOptions,
                              setAttributes
                            );
                          }}
                        >
                          {__("Remove 2x Image", "berg")}
                        </Button>
                      ) : (
                        <Button
                          className={"media-button upload-button"}
                          onClick={open}
                        >
                          {__("Upload 2x Image", "berg")}
                        </Button>
                      )}
                      <div className="image-thumb-wrapper" onClick={open}>
                        {image2xUrl ? (
                          <img
                            className="image-thumb"
                            src={image2xUrl !== undefined ? image2xUrl : ""}
                          />
                        ) : (
                          ""
                        )}
                      </div>
                    </div>
                  )}
                ></MediaUpload>
              </MediaUploadCheck>
            )}
          </PanelBody>
        );
      })}
    </>
  );
};

export default BackgroundImageSetting;
