import { Component } from "@wordpress/element";

class RemoveSliderButton extends Component {
  constructor(props) {
    super(props);

    const { index, removeCurrentSlide, navSlides } = props;
  }

  render() {
    return (
      <div className="bs-slider-btn-wrapper">
        <button
          className="btn btn-default"
          type="button"
          onClick={() => this.props.removeCurrentSlide(this.props.index)}
        ></button>
      </div>
    );
  }
}

export default RemoveSliderButton;
