const GradientSave = (props) => {
  const { deviceSizes, gradientOptions, sectionBlockID } = props;
  return (
    <style>
      {deviceSizes.map(([text, deviceSize], index) => {
        let gradientSettings = gradientOptions[deviceSize].settings;
        let gradientDirection =
          typeof gradientSettings.gradientDirection !== "undefined"
            ? gradientSettings.gradientDirection
            : "";
        let gradientColorOne = gradientSettings.gradientColorOne
          ? gradientSettings.gradientColorOne
          : "";
        let gradientColorTwo = gradientSettings.gradientColorTwo
          ? gradientSettings.gradientColorTwo
          : "";
        let gradientColorOneLocation =
          typeof gradientSettings.gradientColorOneLocation !== "undefined"
            ? `${gradientSettings.gradientColorOneLocation}%`
            : "";
        let gradientColorTwoLocation =
          typeof gradientSettings.gradientColorTwoLocation !== "undefined"
            ? `${gradientSettings.gradientColorTwoLocation}%`
            : "";
        if (gradientColorOne && gradientColorTwo) {
          if (deviceSize === "regular") {
            return `.bs-section.bs-section-${sectionBlockID} {
                                background-image: linear-gradient(${gradientDirection}deg, ${gradientColorOne} ${gradientColorOneLocation}, ${gradientColorTwo} ${gradientColorTwoLocation});}`;
          } else {
            return `@media only screen and (max-width: ${gradientOptions[deviceSize].breakpoint}px) { .bs-section.bs-section-${sectionBlockID} {
                                background-image: linear-gradient(${gradientDirection}deg, ${gradientColorOne} ${gradientColorOneLocation}, ${gradientColorTwo} ${gradientColorTwoLocation});} }`;
          }
        }
      })}
    </style>
  );
};

export default GradientSave;
