/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import {
  RichText,
  InspectorControls,
  ColorPalette,
  URLInput,
  MediaUpload,
  __experimentalLinkControl,
  useBlockProps,
} from "@wordpress/block-editor";
import { Button, PanelBody, SelectControl, PanelRow, ToggleControl, TextControl } from "@wordpress/components";
import Select from "react-select";
import SelectAsync from "react-select/async";
import { withSelect } from "@wordpress/data";
import { compose } from "@wordpress/compose";
import { unstable_batchedUpdates } from "react-dom";
import { setUniqueIdAttribute } from "~berg/utils";
import { capitalize } from "lodash";
const { useEffect, useState } = wp.element;

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {};
  const { attributes, setAttributes, imageBehavior, postTypes } = props;

  const {
    buttonTitle,
    popupID,
    popupContent,
    popupContentTitle,
    url,
    customUrl,
    linkOpenType,
    titleColor,
    wrapperBgColor,
    optionSelect,
    vpopOptionSelect,
    imgURL,
    imgID,
    imgAlt,
    downloadFileType,
    downloadFileName,
    downloadFileURL,
    downloadFileID,
    downloadFileAlt,
    vImgType,
    vImgName,
    vImgURL,
    vImgID,
    vImgAlt,
    buttonClassNames,
    selectedPostType,
    selectedPost,
    prefixImg,
    postfixImg,
    prefixSelection,
    postfixSelection,
    prefixText,
    postfixText,
    proButtonVisibility,
    vpopupHtml,
    vpopupFancyBoxOptions,
  } = attributes;

  const proButtonPrefix = "p-btn-";

  const [buttonClasses, setButtonClasses] = useState([]);
  const [defaultClassValues, setDefaultClassValues] = useState([]);
  const [postSelectIsLoading, setPostSelectIsLoading] = useState(false);
  const [posts, setPosts] = useState([]);
  const [htmlValid, setHtmlValid] = useState(true);
  const blockProps = useBlockProps();

  useEffect(() => {
    setUniqueIdAttribute(props, "popupID", proButtonPrefix);
  }, [props.attributes]);

  const unwantedPostTypes = ["Media", "Blocks", "Reusable Blocks"];
  // filter post types and make post list array to use on post type dropdown
  const filteredPostTypes = _.filter(postTypes, type => {
    return !unwantedPostTypes.includes(type.name);
  }).map(type => {
    return { value: type.slug, label: type.name };
  });

  //Post type dropdown onchange function
  const onChangePostType = newVal => {
    setAttributes({ selectedPostType: newVal });
    setAttributes({ selectedPost: "" });
    getPosts("", null, newVal);
  };
  //Post dropdown onchange function
  const onChangePosts = newVal => {
    setAttributes({ selectedPost: newVal });
  };

  // decodes unicode decimals(ex: &#8216 -> ')
  const decodeUnicodes = encodedText => {
    let textArea = document.createElement("textarea");
    textArea.innerHTML = encodedText;
    return textArea.innerText;
  };

  // To decode already saved values
  if (typeof selectedPost === "object" && typeof selectedPost.label === "string") {
    selectedPost.label = decodeUnicodes(selectedPost.label);
  }

  useEffect(() => {
    const classList = [];
    if (coreComponents["bs-pro-button"] && coreComponents["bs-pro-button"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-pro-button"]}`,
        label: "Default",
      });
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      });
    }
    if (coreComponentsTheme["bs-pro-button"] && coreComponentsTheme["bs-pro-button"].length > 0) {
      coreComponentsTheme["bs-pro-button"].map((className, index) => {
        let classLabel = className.replace("bs-pro-button--", "").split("-");
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        });
      });
    }
    const defaultValues = buttonClassNames ? buttonClassNames : [classList[0]];
    setButtonClasses(classList);
    setDefaultClassValues(defaultValues);
  }, [JSON.stringify(props.blockClasses)]);

  const classNames = buttonClassNames ? buttonClassNames.map(x => x.value).join(" ") : "";

  //link open new tab or not
  const setLinkOpenType = setLinkOpenTypeVal => {
    setAttributes({
      linkOpenType: setLinkOpenTypeVal,
    });
  };
  //set button title color
  const setTitleColor = titleColorVal => {
    setAttributes({
      titleColor:
        typeof titleColorVal === "undefined"
          ? // to prevent getting replace by the default value in save.js
          ""
          : `${titleColorVal} !important`,
    });
  };
  //set button bg color
  const setWrapperBgColor = wrapperBgColorVal => {
    setAttributes({
      wrapperBgColor:
        typeof wrapperBgColorVal === "undefined"
          ? // to prevent getting replace by the default value in save.js
          ""
          : `${wrapperBgColorVal} !important`,
    });
  };
  //set select options
  const setOptionSelect = optionSelectVal => {
    setAttributes({
      optionSelect: optionSelectVal,
    });
  };
  //set video popup select options
  const setvpopOptionSelect = vpopOptionSelectVal => {
    setAttributes({
      vpopOptionSelect: vpopOptionSelectVal,
    });
  };
  //set inner link
  const onChangeInnerLink = innerLinkVal => {
    setAttributes({
      innerLink: innerLinkVal,
    });
  };
  // File upload content popup
  const onImageChange = img => {
    setAttributes({
      imgURL: img.url,
      imgID: img.id,
      imgAlt: img.imgAlt,
    });
  };
  // Remove file
  const removeImage = () => {
    setAttributes({
      imgURL: null,
      imgID: null,
      imgAlt: null,
    });
  };
  // File download
  const onFileChange = file => {
    setAttributes({
      downloadFileType: file.type,
      downloadFileName: file.filename,
      downloadFileURL: file.url,
      downloadFileID: file.id,
      downloadFileAlt: file.imgAlt,
    });
  };
  // Remove file
  const removeFile = () => {
    setAttributes({
      downloadFileType: null,
      downloadFileName: null,
      downloadFileURL: null,
      downloadFileID: null,
      downloadFileAlt: null,
    });
  };
  // File upload video popup
  const onVImageChange = img => {
    setAttributes({
      vImgType: img.type,
      vImgName: img.filename,
      vImgURL: img.url,
      vImgID: img.id,
      vImgAlt: img.imgAlt,
    });
  };
  // Remove file
  const removeVImage = () => {
    setAttributes({
      vImgType: null,
      vImgName: null,
      vImgURL: null,
      vImgID: null,
      vImgAlt: null,
    });
  };
  const onChangePrefix = newVal => {
    if (newVal == "none") {
      removePrefixImage();
      setAttributes({ prefixText: "" });
    }
    setAttributes({ prefixSelection: newVal });
  };
  const onChangePostfix = newVal => {
    if (newVal == "none") {
      removePostfixImage();
      setAttributes({ postfixText: "" });
    }
    setAttributes({ postfixSelection: newVal });
  };
  const onChangePrefixText = newVal => {
    setAttributes({ prefixText: newVal });
  };
  const onChangePostfixText = newVal => {
    setAttributes({ postfixText: newVal });
  };
  const onPrefixImgChange = newImgObj => {
    setAttributes({
      prefixImg: {
        id: newImgObj.id,
        url: newImgObj.url,
        alt: newImgObj.alt,
      },
    });
  };
  const onPostfixImgChange = newImgObj => {
    setAttributes({
      postfixImg: {
        id: newImgObj.id,
        url: newImgObj.url,
        alt: newImgObj.alt,
      },
    });
  };
  const removePrefixImage = () => {
    setAttributes({
      prefixImg: {
        id: "",
        url: "",
        alt: "",
      },
    });
  };
  const removePostfixImage = () => {
    setAttributes({
      postfixImg: {
        id: "",
        url: "",
        alt: "",
      },
    });
  };

  // Load posts data
  let getPostsTimeout = null;
  const getPosts = (searchQuery = "", callback = null, postType) => {
    if (getPostsTimeout != null) clearTimeout(getPostsTimeout);
    getPostsTimeout = setTimeout(() => {
      if (typeof postType === "undefined" || typeof postType !== "string") {
        postType = selectedPostType;
      }
      setPostSelectIsLoading(true);
      const query = {
        per_page: 100,
        order: "asc",
        orderby: "title",
        status: "publish",
        search: searchQuery,
        searchBy: "post_title",
      };
      const getData = () => wp.data.select("core").getEntityRecords("postType", postType, query);
      const setPostsData = postList => {
        const postsOptions = postList.map(post => ({
          value: post.link,
          label: decodeUnicodes(post.title.rendered),
        }));
        unstable_batchedUpdates(() => {
          setPostSelectIsLoading(false);
          setPosts(postsOptions);
        });
        if (typeof callback === "function") {
          callback(postsOptions);
        }
      };
      // Request posts data
      let postList = getData();
      // If cached data available return or monitor until request resolved
      if (postList && Array.isArray(postList)) {
        setPostsData(postList);
      } else {
        const unsubscribe = wp.data.subscribe(() => {
          const resolved =
            !wp.data.select("core").isResolving("getEntityRecords", ["postType", postType, query]) &&
            wp.data.select("core").hasFinishedResolution("getEntityRecords", ["postType", postType, query]);
          if (resolved) {
            postList = getData();
            if (postList && Array.isArray(postList)) {
              unsubscribe();
              setPostsData(postList);
            }
          }
        });
      }
    }, 700);
  };

  let decodedVpopHtml = "";
  if (vpopupHtml && vpopupHtml.length > 0) {
    try {
      decodedVpopHtml = Buffer.from(vpopupHtml, "base64");
    } catch (error) {
      // ignore errors
    }
  }

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls initialOpen={true}>
        <PanelBody title={__("Visibility", "")} initialOpen={false}>
          <ToggleControl
            label="Enable/Disable"
            checked={proButtonVisibility}
            onChange={val => {
              setAttributes({ proButtonVisibility: val });
            }}
          />
        </PanelBody>
        <PanelBody title={__("Pro-Button Color Options", "")} initialOpen={false}>
          <PanelRow>
            <p>Pro-Button Text Color</p>
            <ColorPalette value={titleColor} onChange={setTitleColor} />
          </PanelRow>
          <PanelRow>
            <p>Pro-Button BG Color</p>
            <ColorPalette value={wrapperBgColor} onChange={setWrapperBgColor} />
          </PanelRow>
        </PanelBody>

        <PanelBody title={__("Pro-Button Select Options", "")} initialOpen={true}>
          <PanelRow>
            <SelectControl
              label={__("Onclick Action", "berg")}
              value={optionSelect}
              options={[
                { label: "Custom Link", value: "custom" },
                { label: "Post Link", value: "post" },
                { label: "Content Popup", value: "imgpopup" },
                { label: "Video/Image Popup", value: "vpopup" },
                { label: "Download", value: "download" },
              ]}
              onChange={setOptionSelect}
            />
          </PanelRow>

          <PanelRow>
            {"download" === optionSelect && (
              <div className="bs-pro-button__download">
                {downloadFileURL && ["video", "application", "text"].includes(downloadFileType) ? (
                  <div>
                    <p>{downloadFileName}</p>
                  </div>
                ) : downloadFileURL && ["image", "audio"].includes(downloadFileType) ? (
                  <embed className={imageBehavior} src={downloadFileURL} alt={downloadFileAlt} />
                ) : (
                  ""
                )}
                <MediaUpload
                  onSelect={onFileChange}
                  allowedTypes={["image", "video", "audio", "application", "text"]}
                  value={downloadFileID}
                  render={({ open }) => (
                    <Button className="image" onClick={open}>
                      Upload File
                    </Button>
                  )}
                />
                {downloadFileURL ? (
                  <Button className="image-remove" onClick={removeFile}>
                    Remove File
                  </Button>
                ) : null}
              </div>
            )}

            {"vpopup" === optionSelect && (
              <div className="bs-pro-button__videopopupoptions">
                <SelectControl
                  label={__("Select Video Popup Type", "berg")}
                  value={vpopOptionSelect}
                  options={[
                    { label: "Select Option", value: "select" },
                    { label: "Video Link", value: "vlink" },
                    { label: "Video/Image Upload", value: "vupload" },
                    { label: "Embed Code or HTML", value: "html" },
                  ]}
                  onChange={setvpopOptionSelect}
                />

                {"vlink" === vpopOptionSelect && (
                  <div className="bs-pro-button__videopopuplink">
                    <p>Enter Url of popup video</p>
                    <RichText
                      value={url}
                      placeholder="Video Url"
                      onChange={(url, post) =>
                        setAttributes({
                          url: url.trim(),
                          text: (post && post.title) || "Click here",
                        })
                      }
                    />
                  </div>
                )}

                {"vupload" === vpopOptionSelect && (
                  <div className="bs-pro-button__videopopupupload">
                    {vImgURL && vImgType === "video" ? (
                      <div>
                        <p>{vImgName}</p>
                      </div>
                    ) : (
                      ""
                    )}
                    {vImgURL && vImgType !== "video" ? (
                      <embed className={imageBehavior} src={vImgURL} alt={vImgAlt} />
                    ) : (
                      <MediaUpload
                        onSelect={onVImageChange}
                        allowedTypes={["image", "video"]}
                        value={vImgID}
                        render={({ open }) => (
                          <Button className="image" onClick={open}>
                            Upload File
                          </Button>
                        )}
                      />
                    )}
                    {vImgURL ? (
                      <Button className="image-remove" onClick={removeVImage}>
                        Remove File
                      </Button>
                    ) : null}
                  </div>
                )}

                {"html" === vpopOptionSelect && (
                  <React.Fragment>
                    <div className="bs-pro-button__videopopuphtml">
                      <p>Embed code or HTML</p>
                      <textarea
                        placeholder="Paste the Embed code or HTML here"
                        onChange={event => {
                          const htmlInput = event.currentTarget.value;
                          setHtmlValid(true);
                          if (/<([A-Za-z][A-Za-z0-9]*)\b[^>]*>(.*?)<\/\1>/.test(htmlInput)) {
                            const htmlCodeEncoded = Buffer.from(htmlInput, "utf-8").toString("base64");
                            setAttributes({ vpopupHtml: htmlCodeEncoded });
                          } else if (htmlInput.length > 0) {
                            setHtmlValid(false);
                          }
                        }}
                        rows="10"
                        defaultValue={decodedVpopHtml}
                      ></textarea>
                      {!htmlValid ? <span className="input-validation-error">Not a valid HTML content</span> : ""}
                    </div>
                    <div>
                      <TextControl
                        label="Popup class(es)"
                        placeholder="Class name(s)"
                        value={vpopupFancyBoxOptions?.classNames || ""}
                        onChange={vpopupClasses => {
                          setAttributes({
                            vpopupFancyBoxOptions: {
                              ...vpopupFancyBoxOptions,
                              classNames: vpopupClasses,
                            },
                          });
                        }}
                      />
                    </div>
                    <div>
                      <SelectControl
                        label="Popup outside click action"
                        value={vpopupFancyBoxOptions?.dismissOnClickOutside || "close"}
                        options={[
                          { label: "Dismiss", value: "close" },
                          { label: "Do nothing", value: false },
                        ]}
                        onChange={selectedVal => {
                          setAttributes({
                            vpopupFancyBoxOptions: {
                              ...vpopupFancyBoxOptions,
                              dismissOnClickOutside: selectedVal,
                            },
                          });
                        }}
                      />
                    </div>
                  </React.Fragment>
                )}
              </div>
            )}

            {"imgpopup" === optionSelect && (
              <div className="bs-pro-button__popup">
                <>
                  <div className="popup-title popup-options">
                    Enter Popup Title Here
                    <RichText
                      className={`bs-pro-button-popup-text-title`}
                      tagName="h2"
                      placeholder="Title Here"
                      value={popupContentTitle}
                      onChange={titleVal => {
                        setAttributes({ popupContentTitle: titleVal });
                      }}
                    />
                  </div>

                  <div className="popup-options">
                    Enter Popup Content Here
                    <RichText
                      className={`bs-pro-button-popup-text`}
                      tagName="p"
                      placeholder="Content Text Here"
                      value={popupContent}
                      onChange={titleVal => {
                        setAttributes({ popupContent: titleVal });
                      }}
                    />
                  </div>

                  {imgURL ? (
                    <img className={imageBehavior} src={imgURL} alt={imgAlt} />
                  ) : (
                    <MediaUpload
                      onSelect={onImageChange}
                      allowedTypes={["image"]}
                      value={imgID}
                      render={({ open }) => (
                        <Button className="image" onClick={open}>
                          Upload File
                        </Button>
                      )}
                    />
                  )}
                  {imgURL ? (
                    <Button className="image-remove" onClick={removeImage}>
                      Remove File
                    </Button>
                  ) : null}
                </>
              </div>
            )}

            {"custom" === optionSelect && (
              <div className="bs-pro-button__custom">
                <URLInput
                  label="Enter Url or search for Post/Page"
                  value={customUrl}
                  onChange={(customUrl, post) =>
                    setAttributes({
                      customUrl: customUrl.trim(),
                      text: (post && post.title) || "Click here",
                    })
                  }
                />
              </div>
            )}

            {"post" === optionSelect && (
              <div className="bs-pro-button__custom">
                <SelectControl
                  label={__("Post Type", "berg")}
                  help={__("Select a post type", "berg")}
                  value={selectedPostType}
                  options={filteredPostTypes}
                  onChange={onChangePostType}
                />

                <label>{__("Post", "berg")}</label>
                <SelectAsync
                  name="post-list"
                  label={__("Post", "berg")}
                  help={__("Select a post", "berg")}
                  value={selectedPost}
                  defaultOptions={posts.length > 0 ? posts : true}
                  loadOptions={getPosts}
                  isLoading={postSelectIsLoading}
                  onChange={onChangePosts}
                  onMenuOpen={() => getPosts()}
                />
              </div>
            )}
          </PanelRow>

          <PanelRow>
            {("custom" === optionSelect || "download" === optionSelect) && (
              <ToggleControl label="Open new tab" checked={linkOpenType} onChange={setLinkOpenType} />
            )}
          </PanelRow>

          <PanelRow>
            <SelectControl
              label={__("Prefix", "berg")}
              value={prefixSelection}
              options={[
                { label: "None", value: "none" },
                { label: "Image", value: "image" },
                { label: "Text", value: "text" },
              ]}
              onChange={onChangePrefix}
            />
          </PanelRow>
          <PanelRow>
            {prefixSelection === "image" && (
              <div className="pro-button-prefix-preview">
                {prefixImg.url ? (
                  <div>
                    <div>
                      <img src={prefixImg.url} width="25%" />
                    </div>
                    <Button isSecondary onClick={removePrefixImage}>
                      Remove Image
                    </Button>
                  </div>
                ) : (
                  <MediaUpload
                    onSelect={onPrefixImgChange}
                    allowedTypes={["image"]}
                    render={({ open }) => (
                      <Button isPrimary onClick={open}>
                        Upload Image
                      </Button>
                    )}
                  />
                )}
              </div>
            )}
            {prefixSelection === "text" && (
              <div>
                <TextControl
                  label="Prefix Text"
                  placeholder="Prefix Text"
                  value={prefixText}
                  onChange={onChangePrefixText}
                />
              </div>
            )}
          </PanelRow>
          <PanelRow>
            <SelectControl
              label={__("Postfix", "berg")}
              value={postfixSelection}
              options={[
                { label: "None", value: "none" },
                { label: "Image", value: "image" },
                { label: "Text", value: "text" },
              ]}
              onChange={onChangePostfix}
            />
          </PanelRow>
          <PanelRow>
            {postfixSelection === "image" && (
              <div className="pro-button-prefix-preview">
                {postfixImg.url ? (
                  <div>
                    <div>
                      <img src={postfixImg.url} width="25%" />
                    </div>
                    <Button isSecondary onClick={removePostfixImage}>
                      Remove Image
                    </Button>
                  </div>
                ) : (
                  <MediaUpload
                    onSelect={onPostfixImgChange}
                    allowedTypes={["image"]}
                    render={({ open }) => (
                      <Button isPrimary onClick={open}>
                        Upload Image
                      </Button>
                    )}
                  />
                )}
              </div>
            )}
            {postfixSelection === "text" && (
              <div>
                <TextControl
                  label="Postfix Text"
                  placeholder="Postfix Text"
                  value={postfixText}
                  onChange={onChangePostfixText}
                />
              </div>
            )}
          </PanelRow>
        </PanelBody>

        <PanelBody title={__("Block Class", "")} initialOpen={false}>
          <Select
            defaultValue={defaultClassValues}
            isMulti
            name="buttonClasses"
            options={buttonClasses}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={val => {
              setAttributes({
                buttonClassNames: val ? val : [],
              });
            }}
          />
        </PanelBody>
      </InspectorControls>
      <button
        className={`bs-pro-button ${classNames} bs-pro-button-${popupID}
        ${proButtonVisibility ? "enable" : "disable"}`}
        >
        <style>{`.bs-pro-button-${popupID} .bs-pro-button__container {background-color: ${wrapperBgColor}; color: ${titleColor};} `}</style>
        {prefixSelection === "image" && prefixImg.url && (
          <div className="bs-pro-button__prefix">
            <img src={prefixImg.url} width="25%" />
          </div>
        )}
        {prefixSelection === "text" && prefixText && (
          <div className="bs-pro-button__prefix">
            <p>{prefixText}</p>
          </div>
        )}

        {["custom", "popup", "vpopup", "imgpopup", "post"].includes(optionSelect) && (
          <RichText
            className={`bs-pro-button__container`}
            tagName="a"
            placeholder={buttonTitle}
            value={buttonTitle}
            onChange={titleVal => {
              setAttributes({ buttonTitle: titleVal });
            }}
          />
        )}

        {"download" === optionSelect && (
          <RichText
            className={`bs-pro-button__container`}
            tagName="a"
            placeholder={buttonTitle}
            download={!linkOpenType}
            value={buttonTitle}
            onChange={titleVal => {
              setAttributes({ buttonTitle: titleVal });
            }}
          />
        )}

        {postfixSelection === "image" && postfixImg.url && (
          <div className="bs-pro-button__postfix">
            <img src={postfixImg.url} width="25%" />
          </div>
        )}
        {postfixSelection === "text" && postfixText && (
          <div className="bs-pro-button__postfix">
            <p>{postfixText}</p>
          </div>
        )}
      </button>
    </div>
  );
};

const applyWithSelect = withSelect((select, props) => {
  let selectCore = select("core");
  let postTypes = props.attributes.optionSelect === "post" ? selectCore.getPostTypes() : [];
  const theme = select("core").getCurrentTheme();
  const { block_classes: blockClasses = {} } = theme || {};
  return {
    postTypes,
    blockClasses,
  };
});

export default compose(applyWithSelect)(Edit);
