import React from "react";

import { __ } from "@wordpress/i18n";
import { PanelBody, PanelRow, SelectControl } from "@wordpress/components";

const TabAdvanced = (props) => {
  const { attributes, setAttributes } = props;
  const { counterWrap } = attributes;
  const environment = process.env.NODE_ENV;

  const onChangeCounterAppend = newVal => {
    setAttributes({ counterWrap: newVal });
  };

  return (
    <div>
      {environment === "development" && (
        <PanelBody title={__("Configuration Settings", "counter")} initialOpen={true}>
          <PanelRow>
            <SelectControl
              label={__("Wrap Count and Prefix/Postfix", "berg")}
              value={counterWrap}
              options={[
                { label: "Default", value: "" },
                { label: "Wrap Prefix and Count", value: "prefix" },
                { label: "Wrap Count and Postfix", value: "postfix" },
              ]}
              onChange={onChangeCounterAppend}
            />
          </PanelRow>
        </PanelBody>
      )}
    </div>
  );
};

export default TabAdvanced;
