import apiFetch from "@wordpress/api-fetch"
import Select from "react-select"
import { withSelect } from "@wordpress/data"
import { getSettings } from "@wordpress/date"

const { __ } = wp.i18n
const { SelectControl, TextControl, ResponsiveWrapper, ToggleControl, DateTimePicker, Button, PanelRow } = wp.components
const { PluginDocumentSettingPanel } = wp.editPost
const { registerPlugin } = wp.plugins
const { Component } = wp.element
URLInput
const { URLInput } = wp.blockEditor
// const { __experimentalLinkControl } = wp.blockEditor;
// const LinkControl = __experimentalLinkControl;
const { MediaUpload, MediaUploadCheck } = wp.editor
let getPostsTimeout = null

class PostOptions extends Component {
  constructor(props) {
    super(props)
    this.state = {
      postType: wp.data.select("core/editor").getCurrentPostType(),
      show_custom_date: this.getMeta("show_custom_date") || false,
      custom_date:
        this.getMeta("custom_date") && this.getMeta("show_custom_date")
          ? this.getLocalDateTime(this.getMeta("custom_date"))
          : new Date(),
      featured: this.getMeta("featured") || false,
      featured_page_list: this.getMeta("featured_page_list") || null,
      image_alt_text: this.getMeta("image_alt_text") || "",
      learn_more_label: this.getMeta("learn_more_label") || "Read More",
      learn_more_type: this.getMeta("learn_more_type") || "de_link",
      learn_more_link: this.getMeta("learn_more_link") || null,
      show_popup: this.getMeta("show_popup") || false,
      learn_more_link_file: this.getMeta("learn_more_link_file") || null,
      fileId: this.getMeta("learn_more_link_file") || 0,
      fileObj: null,
      event_date: this.getMeta("event_date") || false,
      event_start_date:
        this.getMeta("event_date") && this.getMeta("event_start_date")
          ? this.getLocalDateTime(this.getMeta("event_start_date"))
          : new Date(),
      event_end_date:
        this.getMeta("event_date") && this.getMeta("event_end_date")
          ? this.getLocalDateTime(this.getMeta("event_end_date"))
          : new Date(),
      isPageListLoading: true,
      disable_iframe: this.getMeta("disable_iframe") || false,
      enable_lazy_loading: this.getMeta("enable_lazy_loading") || false,
    }
  }

  componentWillMount() {
    if (this.state.imageId) {
      apiFetch({
        path: "/wp/v2/media/" + this.state.imageId,
      }).then(data => this.setState({ imageObj: data }))
    }
    if (this.state.fileId) {
      apiFetch({
        path: "/wp/v2/media/" + this.state.fileId,
      }).then(data => this.setState({ fileObj: data }))
    }
    this.getPagesList()
  }

  getMeta(name) {
    const postMeta = wp.data.select("core/editor").getEditedPostAttribute("meta")
    return postMeta && name in postMeta ? postMeta[name] : null
  }

  getLocalDateTime(utcDate) {
    const offset = getSettings().timezone.offset ?? 0
    return moment(utcDate).add(offset, "h").format("YYYY-MM-DD HH:mm:ss")
  }

  getUtcDateTime(localDate) {
    const offset = getSettings().timezone.offset ?? 0
    return moment(localDate).subtract(offset, "h").format("YYYY-MM-DD HH:mm:ss")
  }

  onSelect(field, value) {
    // Save value to metadata.
    wp.data.dispatch("core/editor").editPost({
      meta: {
        [field]: value,
      },
    })
  }

  getPagesList() {
    if (getPostsTimeout != null) clearTimeout(getPostsTimeout)
    getPostsTimeout = setTimeout(() => {
      const query = {
        per_page: -1,
        order: "asc",
        status: "publish",
      }
      const getData = () => wp.data.select("core").getEntityRecords("postType", "page", query)

      const featured_page_list = []
      const setPagesData = postList => {
        const pagesListOptions = postList.map(postItem => {
          const pagesListOption = {
            label: postItem.title.raw ? postItem.title.raw : "(no title)",
            value: postItem.id,
          }

          if (this.state.featured_page_list.includes(parseInt(postItem.id))) {
            featured_page_list.push(pagesListOption)
          }
          return pagesListOption
        })
        this.setState({
          pagesListOptions,
          featured_page_list,
          isPageListLoading: false,
        })
      }

      // Request posts data
      let postList = getData()

      if (postList && Array.isArray(postList)) {
        setPagesData(postList)
      } else {
        const unsubscribe = wp.data.subscribe(() => {
          const resolved =
            !wp.data.select("core").isResolving("getEntityRecords", ["postType", "page", query]) &&
            wp.data.select("core").hasFinishedResolution("getEntityRecords", ["postType", "page", query])
          if (resolved) {
            postList = getData()
            if (postList && Array.isArray(postList)) {
              unsubscribe()
              setPagesData(postList)
            }
          }
        })
      }
    }, 700)
  }

  render() {
    if ("page" === this.state.postType) {
      return null
    }

    return (
      <PluginDocumentSettingPanel name="common-post-options" title={__("Post Options", "")} initialOpen={true}>
        <div className="components-base-control">
          <label className={"components-base-control__label"}>Show custom date</label>
          <ToggleControl
            label={__("Show custom date")}
            checked={this.state.show_custom_date}
            onChange={value => {
              this.setState({ show_custom_date: value })
              this.onSelect("show_custom_date", value)
              this.onSelect("custom_date", this.state.custom_date)
            }}
          />
        </div>
        {this.state.show_custom_date && (
          <div className="components-base-control">
            <label className={"components-base-control__label"}>Custom Date</label>
            <DateTimePicker
              label={__("Custom date", "")}
              type="text"
              currentDate={this.state.custom_date}
              onChange={value => {
                const utcValue = this.getUtcDateTime(value)
                this.setState({ custom_date: value })
                this.onSelect("custom_date", utcValue)
              }}
            />
          </div>
        )}
        <div className="components-base-control">
          <label className={"components-base-control__label"}>Featured Post</label>
          <ToggleControl
            label={__("Featured Post")}
            checked={this.state.featured}
            onChange={value => {
              this.setState({ featured: value })
              this.onSelect("featured", value)
            }}
          />
        </div>
        {this.state.featured && (
          <>
            <div className="components-base-control">
              <label className={"components-base-control__label"}>Featured In (Pages)</label>
              <Select
                help={__("Leave this field empty to feature this post on any page")}
                isMulti
                hideSelectedOptions={false}
                closeMenuOnSelect={false}
                placeholder={__("Featured In (Pages)", "")}
                options={this.state.pagesListOptions}
                value={this.state.featured_page_list}
                isLoading={this.state.isPageListLoading}
                onChange={selectedPages => {
                  let selectedPageList = []
                  if (selectedPages) {
                    selectedPageList = selectedPages.map(selectedPage => {
                      return parseInt(selectedPage.value)
                    })
                  }
                  this.setState({ featured_page_list: selectedPages })
                  this.onSelect("featured_page_list", selectedPageList)
                }}
              />
            </div>
          </>
        )}
        <PanelRow>
          <ToggleControl
            label={__("Enable lazy loading")}
            checked={this.state.enable_lazy_loading}
            onChange={value => {
              this.setState({ enable_lazy_loading: value })
              this.onSelect("enable_lazy_loading", value)
            }}
            help={__("This option is applicable for both default and featured images")}
          />
        </PanelRow>
        <TextControl
          label={__("Image alternative text", "")}
          type="text"
          value={this.state.image_alt_text}
          onChange={value => {
            this.setState({ image_alt_text: value })
            this.onSelect("image_alt_text", value)
          }}
        />
        <TextControl
          label={__("Learn more label", "")}
          type="text"
          value={this.state.learn_more_label}
          onChange={value => {
            this.setState({ learn_more_label: value })
            this.onSelect("learn_more_label", value)
          }}
        />
        <SelectControl
          label={__("Learn more type", "")}
          options={[
            { value: "de_link", label: "Detail page Link" },
            { value: "in_link", label: "Internal Link" },
            { value: "ex_link", label: "External Link" },
            { value: "fi_link", label: "File Link" },
            {
              value: "po_link",
              label: "Popup Link [content will appear in a popup]",
            },
            { value: "none", label: "None" },
          ]}
          value={this.state.learn_more_type}
          onChange={value => {
            this.setState({ learn_more_type: value })
            this.onSelect("learn_more_type", value)
          }}
        />
        {["in_link", "ex_link"].includes(this.state.learn_more_type) && (
          <div className="components-base-control link-control-sidebar">
            <label className={"components-base-control__label"}>Internal/External Link</label>
            {/* TODO: LinkControl should be enabled back once the existing issue is fixed form the Gutenberg side */}
            {/* <LinkControl
              value={this.state.learn_more_link}
              onChange={(value) => {
                this.setState({ learn_more_link: value });
                this.onSelect("learn_more_link", value);
              }}
            /> */}
            <URLInput
              value={this.state.learn_more_link.url}
              onChange={value => {
                this.setState(
                  prevState => ({
                    learn_more_link: {
                      ...prevState.learn_more_link,
                      id: value,
                      title: value,
                      type: "URL",
                      url: value,
                    },
                  }),
                  function () {
                    this.onSelect("learn_more_link", this.state.learn_more_link)
                  }
                )
              }}
            />
            {/* NOTE: 'Open in new tab' field comes automatically with LinkControl module.
                Had to create this switch manually since we are using URLInput instead fo LinkControl*/}
            <ToggleControl
              label={__("Open in new tab")}
              checked={
                Array.isArray(this.state.learn_more_link.opensInNewTab)
                  ? this.state.learn_more_link.opensInNewTab.length > 0
                  : this.state.learn_more_link.opensInNewTab
              }
              onChange={value => {
                this.setState(
                  prevState => ({
                    learn_more_link: {
                      ...prevState.learn_more_link,
                      opensInNewTab: value,
                    },
                  }),
                  function () {
                    this.onSelect("learn_more_link", this.state.learn_more_link)
                  }
                )
              }}
            />
            <ToggleControl
              label={__("Show in Popup")}
              checked={this.state.show_popup}
              onChange={value => {
                this.setState({ show_popup: value })
                if (this.state.disable_iframe) {
                  this.setState({ disable_iframe: false })
                  this.onSelect("disable_iframe", false)
                }
                this.onSelect("show_popup", value)
              }}
            />
            {this.state.show_popup && (
              <ToggleControl
                label={__("Disable Iframe from the popup")}
                checked={this.state.disable_iframe}
                onChange={value => {
                  this.setState({ disable_iframe: value })
                  this.onSelect("disable_iframe", value)
                }}
              />
            )}
          </div>
        )}
        {this.state.learn_more_type === "fi_link" && (
          <div className="components-base-control link-control-sidebar">
            <label className={"components-base-control__label"}>Download File</label>
            <div className="editor-post-featured-image">
              <MediaUploadCheck>
                <MediaUpload
                  onSelect={value => {
                    value["title"] = {
                      rendered: value.title,
                    }
                    value["source_url"] = value.url
                    this.setState({
                      learn_more_link_file: value.id,
                      fileId: value.id,
                      fileObj: value,
                    })
                    this.onSelect("learn_more_link_file", value.id)
                  }}
                  value={this.state.fileId}
                  render={({ open }) => (
                    <>
                      {this.state.fileId === 0 && (
                        <Button
                          className={
                            this.state.fileId === 0
                              ? "editor-post-featured-image__toggle"
                              : "editor-post-featured-image__preview"
                          }
                          onClick={open}
                        >
                          {this.state.fileId === 0 && __("Choose the File", "")}
                        </Button>
                      )}
                      {this.state.fileId !== 0 && (
                        <>
                          {this.state.fileObj && (
                            <div className="file-wrap">
                              <div className="file-info">
                                <p>
                                  <strong>File name: </strong>
                                  <strong data-name="title">{this.state.fileObj.title.rendered}</strong>
                                </p>
                                {/*<p>
                            <strong>File: </strong>
                            <a data-name="filename" href={this.state.fileObj.source_url}
                              target="_blank">{this.state.fileObj.filename}</a>
                          </p>
                          <p>
                            <strong>File size: </strong>
                            <span
                              data-name="filesize">{this.state.fileObj.filesizeHumanReadable}</span>
                          </p>*/}
                              </div>
                            </div>
                          )}
                          <Button onClick={open} isPrimary>
                            {__("Replace the File", "")}
                          </Button>
                          <Button
                            onClick={value => {
                              this.setState({
                                learn_more_link_file: 0,
                                fileId: 0,
                                fileObj: null,
                              })
                              this.onSelect("learn_more_link_file", 0)
                            }}
                            isSecondary
                            isDestructive
                          >
                            {__("Remove the File", "")}
                          </Button>
                        </>
                      )}
                    </>
                  )}
                />
              </MediaUploadCheck>
            </div>
          </div>
        )}
        <div className="components-base-control">
          <label className={"components-base-control__label"}>Event date</label>
          <ToggleControl
            label={__("Event date")}
            checked={this.state.event_date}
            onChange={value => {
              this.setState({ event_date: value })
              this.onSelect("event_date", value)
              this.onSelect("event_start_date", this.state.event_start_date)
              this.onSelect("event_end_date", this.state.event_end_date)
            }}
          />
        </div>
        {this.state.event_date && (
          <>
            <div className="components-base-control">
              <label className={"components-base-control__label"}>Event Start Date</label>
              <DateTimePicker
                label={__("Event Start Date", "")}
                type="text"
                currentDate={this.state.event_start_date}
                onChange={value => {
                  // Selected value will be converted into UTC 0 based on the selected timezone before saving;
                  const utcValue = this.getUtcDateTime(value)
                  value = this.setState({
                    event_start_date: value,
                  })
                  this.onSelect("event_start_date", utcValue)
                  //Resetting the end date when selecting a higher start date
                  if (new Date(value) > new Date(this.state.event_end_date)) {
                    this.setState({
                      event_end_date: value,
                    })
                    this.onSelect("event_end_date", utcValue)
                  }
                }}
              />
            </div>
            <div className="components-base-control">
              <label className={"components-base-control__label"}>Event End Date</label>
              <DateTimePicker
                label={__("Event End Date", "")}
                type="text"
                currentDate={this.state.event_end_date}
                isInvalidDate={value => {
                  /* Disabling invalid days individually from the Calendar
                   */
                  return (
                    moment(value).format("YYYY-MM-DD HH:mm:ss") <
                    moment(this.state.event_start_date).format("YYYY-MM-DD HH:mm:ss")
                  )
                }}
                onChange={value => {
                  // Selected value will be converted into UTC 0 based on the selected timezone before saving;
                  const utcValue = this.getUtcDateTime(value)
                  if (new Date(value) >= new Date(this.state.event_start_date)) {
                    this.setState({
                      event_end_date: value,
                    })
                    this.onSelect("event_end_date", utcValue)
                  }
                }}
              />
            </div>
          </>
        )}
      </PluginDocumentSettingPanel>
    )
  }
}

export default PostOptions

registerPlugin("common-post-options", {
  render: PostOptions,
  icon: "editor-table",
})
