import { createContext } from '@wordpress/element';
import { capitalize } from 'lodash';

export const getElementClasses = (
	elementNamespace,
	defaultClasses,
	coreComponents,
	coreComponentsTheme,
	callback
) => {
	const sliderClasses = [];
	sliderClasses.push(
		Array.isArray(coreComponents['bs-tab-slider']) &&
			coreComponents['bs-tab-slider'].length
			? {
					value: `${coreComponents[elementNamespace]}`,
					label: 'Default',
			  }
			: {
					value: '',
					label: 'Select Class',
			  }
	);

	if (
		Array.isArray(coreComponentsTheme[elementNamespace]) &&
		coreComponentsTheme[elementNamespace].length
	) {
		coreComponentsTheme[elementNamespace].map((className, index) => {
			let classLabel = className
				.replace(`${elementNamespace}--`, '')
				.split('-');
			sliderClasses.push({
				value: className,
				label: classLabel.map(capitalize).join(' '),
			});
		});
	}

	const defaultValues = defaultClasses ? defaultClasses : [sliderClasses[0]];
	callback(sliderClasses, defaultValues);
};

export const setSliderSettingAttribute = (
	attributes,
	setAttributes,
	attribute,
	layout,
	property,
	value
) => {
	let attributeUpdates;
	if (Array.isArray(attribute)) {
		attributeUpdates = attributes;
		attribute.forEach(({ attribute, layout, property, value }) => {
			attributeUpdates = {
				...attributeUpdates,
				[attribute]: {
					...attributeUpdates[attribute],
					[layout]: {
						...attributeUpdates[attribute][layout],
						settings: {
							...attributeUpdates[attribute][layout].settings,
							[property]: value,
						},
					},
				},
			};
		});
	} else {
		attributeUpdates = {
			[attribute]: {
				...attributes[attribute],
				[layout]: {
					...attributes[attribute][layout],
					settings: {
						...attributes[attribute][layout].settings,
						[property]: value,
					},
				},
			},
		};
	}
	setAttributes(attributeUpdates);
};

export const TabContext = createContext({});
