  useEffect(() => {
    setUniqueIdAttribute(props, "divBlockID");
  }, [props.attributes]);

  const [divClasses, setDivClasses] = useState([]);
  const [defaultClassValues, setDefaultClassValues] = useState([]);
  const blockProps = useBlockProps();

  useEffect(() => {
    const classList = [];
    if (coreComponents["bs-div"] && coreComponents["bs-div"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-div"]}`,
        label: "Default",
      });
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      });
    }

    if (coreComponentsTheme["bs-div"] && coreComponentsTheme["bs-div"].length > 0) {
      coreComponentsTheme["bs-div"].map((className, index) => {
        let classLabel = className.replace("bs-div--", "").split("-");
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        });
      });
    }
    const defaultValues = divClassNames ? divClassNames : [classList[0]];
    setDivClasses(classList);
    setDefaultClassValues(defaultValues);
  }, [JSON.stringify(props.blockClasses)]);

  const select_video = val => {
    setAttributes({
      backgroundVideoUrl: val.url,
      backgroundVideoName: val.filename,
    });
  };

  const remove_video = () => {
    setAttributes({
      backgroundVideoUrl: null,
      backgroundVideoName: null,
    });
  };

  const select_img_video = img => {
    setAttributes({
      divImgIdVideo: img.id,
      divImgVideoUrl: img.url,
    });
  };

  const remove_img_video = () => {
    setAttributes({
      divImgIdVideo: null,
      divImgVideoUrl: null,
    });
  };

