  const elFlexClassNames = extraLargeFlexClassNames ? extraLargeFlexClassNames.map(x => x.value).join(" ") : "";

  const divId = divBlockName.toString().length > 0 ? divBlockName.toLowerCase().replace(/\W/g, "-") : undefined;

