  const globalDesktopBreakpoint = typeof bergThemeData !== "undefined" ? bergThemeData.desktopBreakpoint : 1280
  const globalMobileBreakpoint = typeof bergThemeData !== "undefined" ? bergThemeData.mobileBreakpoint : 576

  useEffect(() => {
    // Updating breakpoint values when loading the block to avoid block recovery errors.
    const attributeNames = [
      "gradientOptions",
      "backGroundColorOptions",
      "backgroundImageOptions",
      "pictureTagOptions",
      "backgroundImageAndColorOptions",
    ]
    updateBreakpointValue(attributes, attributeNames, "desktop", globalDesktopBreakpoint)
    updateBreakpointValue(attributes, attributeNames, "mobile", globalMobileBreakpoint)
  })

