import { __ } from "@wordpress/i18n";
import React from "react";
import Lottie from "lottie-react";

import { InspectorControls, useBlockProps } from "@wordpress/block-editor"
import { Panel, PanelBody, ToggleControl } from "@wordpress/components"
import Select from "react-select"
import LottieAnimationSettings from "./_partials/lottie-settings"
import { capitalize } from "lodash"

const { useEffect, useState } = wp.element
const { withSelect } = wp.data

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {}
  const { attributes, setAttributes, clientId, environment } = props
  const { classNames, visibility, uniqueId, excludeInit, animationSettings, enableFileChange, enableSegments } =
    attributes;
  const {
    desktop: {
      settings: { path: desktopPath },
    },
  } = animationSettings;

  const deviceSizes = [
    ["Desktop", "desktop"],
    // ["IPad", "ipad"],
    // ["IPad Landscape", "ipadLandscape"],   TODO: These 2 viewports need to be enabled only if required
    ["Mobile", "mobile"],
  ];

  const blockProps = useBlockProps()
  const [defaultClassValues, setDefaultClassValues] = useState([])
  const [lottieClasses, setLottieClasses] = useState([])
  const [animationData, setAnimationData] = useState()

  const blockVisibility = visibility === true ? "enable" : "disable";
  
  useEffect(() => {
    setAttributes({ uniqueId: uniqueId ? uniqueId : `lottie-${clientId}` });
  }, []);

  //set lottie classes
  useEffect(() => {
    const classList = []
    if (coreComponents["bs-lottie-animator"] && coreComponents["bs-lottie-animator"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-lottie-animator"]}`,
        label: "Default",
      });
    }

    if (coreComponentsTheme["bs-lottie-animator"] && coreComponentsTheme["bs-lottie-animator"].length > 0) {
      coreComponentsTheme["bs-lottie-animator"].map((className, index) => {
        let classLabel = className.replace("bs-lottie-animator--", "").split("-")
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        })
      })
    }
    const defaultValues = classNames ? classNames : [classList[0]]
    setLottieClasses(classList)
    setDefaultClassValues(defaultValues)
  }, [JSON.stringify(props.blockClasses)])

  const getAnimationData = async path => {
    const response = await fetch(path);
    const animationData = await response.json();
    setAnimationData(animationData);
  };

  useEffect(() => {
    desktopPath ? getAnimationData(desktopPath) : setAnimationData("");
  }, [desktopPath]);

  const updateAnimationSettings = (deviceTypeKey, updatedValues) => {
    const attributeUpdates = {
      ...animationSettings,
      [deviceTypeKey]: {
        ...animationSettings[deviceTypeKey],
        settings: {
          ...animationSettings[deviceTypeKey].settings,
          ...updatedValues,
        },
      },
    };
    setAttributes({ animationSettings: attributeUpdates });
  };

  return [
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        <Panel>
          {environment === "development" ||
            (environment === "production" && !excludeInit) ||
            (environment === "production" && excludeInit && (enableFileChange || enableSegments)) ? (
            <PanelBody title={__("Animation Settings", "")} initialOpen={false}>
              {deviceSizes.map(([deviceText, deviceTypeKey], index) => {
                const { settings } = animationSettings[deviceTypeKey];
                return (
                  <LottieAnimationSettings
                    key={deviceTypeKey}
                    settings={settings}
                    updateAnimationSettings={updateAnimationSettings}
                    deviceTypeKey={deviceTypeKey}
                    deviceText={deviceText}
                    deviceSizes={deviceSizes}
                    animationSettings={animationSettings}
                    excludeInit={excludeInit}
                    enableFileChange={enableFileChange}
                    enableSegments={enableSegments}
                    environment={environment}
                  />
                );
              })}
            </PanelBody>
          ) : null}
          <PanelBody title={__("Visibility", "")} initialOpen={false}>
            <ToggleControl
              label="Enable/Disable"
              checked={visibility}
              onChange={visibility => {
                setAttributes({ visibility });
              }}
            />
          </PanelBody>
          <PanelBody title={__("Lottie Classes", "")} initialOpen={false}>
            <Select
              defaultValue={defaultClassValues}
              isMulti
              name="lottieClasses"
              options={lottieClasses}
              className="basic-multi-select"
              classNamePrefix="select"
              onChange={val => {
                setAttributes({
                  classNames: val ? val : [],
                });
              }}
            />
          </PanelBody>
          {/* These settings will be displayed only on the Development environment */}
          {environment == "development" && (
            <PanelBody title={__("Advanced Developer Settings", "lottie-animator")} initialOpen={false}>
              <ToggleControl
                label={__("Exclude from default initialization", "lottie-animator")}
                checked={excludeInit}
                help={__(
                  "This option will allow the developer to do custom implementations completely from the child theme.",
                  "lottie-animator"
                )}
                onChange={excludeInit => {
                  setAttributes({ excludeInit });
                  if (!excludeInit) {
                    setAttributes({ enableFileChange: false, enableSegments: false });
                  }
                }}
              />
              {excludeInit && [
                <ToggleControl
                  label={__("Allow modifying the Lottie file(s)", "lottie-animator")}
                  checked={enableFileChange}
                  help={__(
                    "This option will allow the end user to change/remove the selected Lottie file(s) on Production",
                    "lottie-animator"
                  )}
                  onChange={enableFileChange => {
                    setAttributes({ enableFileChange });
                  }}
                />,
                <ToggleControl
                  label={__("Enable defining segments", "lottie-animator")}
                  checked={enableSegments}
                  help={__(
                    "This option will allow the developer/user to define segments for the selected Lottie file(s)",
                    "lottie-animator"
                  )}
                  onChange={enableSegments => {
                    setAttributes({ enableSegments });
                  }}
                />,
              ]}
            </PanelBody>
          )}
        </Panel>
      </InspectorControls>
      <div>
        <span className="block-title">{__("Lottie Animator - Lightweight, scalable animations", "lottie-animator")}</span>
        {animationData && <Lottie className={blockVisibility} animationData={animationData} />}
      </div>
    </div>
  ];
};

export default withSelect((select) => {
  const theme = select("core").getCurrentTheme()
  const { block_classes: blockClasses = {}, wp_environment_type: environment = "production" } = theme || {}
  return { blockClasses, environment }
})(Edit)
