import { Component } from '@wordpress/element';

class NavTitleButtons extends Component {
	constructor(props) {
		super(props);

		const {
			index,
			navSlides,
			contentSlides,
			removeCurrentSlide,
			changeOrder,
		} = props;
	}

	render() {
		return (
			<div className="button-wrapper">
				<button
					className="btn btn-default prev"
					type="button"
					onClick={() =>
						this.props.changeOrder('back', this.props.index)
					}
					disabled={this.props.index == 0}
				></button>
				<button
					className="btn btn-default next"
					type="button"
					onClick={() =>
						this.props.changeOrder('next', this.props.index)
					}
					disabled={
						this.props.index == this.props.navSlides.length - 1
					}
				></button>
				<button
					className="btn btn-default remove"
					type="button"
					onClick={() =>
						this.props.removeCurrentSlide(this.props.index)
					}
				></button>
			</div>
		);
	}
}

export default NavTitleButtons;
