class TaxonomyControl extends Component {
	constructor() {
		super(...arguments)
		this.state = {
			isLoading: cachedTerms.length === 0,
			termList: cachedTerms,
		}
	}

	componentWillMount() {
		if (!this.state.termList.length) {
			this.isStillMounted = true
			this.fetchRequest = apiFetch({
				path: addQueryArgs(`/wp/v2/berg_terms`, {
					per_page: -1, // eslint-disable-line camelcase
				}),
			})
				.then(termList => {
					if (this.isStillMounted) {
						cachedTerms = termList
						this.setState({
							termList,
							isLoading: false,
						})
					}
				})
				.catch(() => {
					if (this.isStillMounted) {
						this.setState({
							termList: [],
							isLoading: false,
						})
					}
				})
		}
	}

	componentWillUnmount() {
		this.isStillMounted = false
	}

	render() {
		const postTypeOptions = []
		const filterByTypeOptions = []
		const filtersOptions = [{ label: "Search Box", value: "search" }]
		const displayOrderOptions = []

		Object.keys(this.state.termList).forEach(postType => {
			const { label, taxonomies, display_orders } = this.state.termList[postType]

			postTypeOptions.push({
				label,
				value: postType,
			})

			if (postType === this.props.postType || (this.props.postType === "" && postType === postTypeOptions[0].value)) {
				Object.keys(taxonomies).forEach((taxonomy, taxIndex) => {
					const { label, terms } = taxonomies[taxonomy]

					filtersOptions.push({
						label,
						value: taxonomy,
					})

					const filterByTermOptions = []
					Object.keys(terms).forEach(i => {
						filterByTermOptions.push({
							label: terms[i].name,
							value: terms[i].term_id,
							type: terms[i].taxonomy,
						})
					})

					filterByTypeOptions.push({
						label,
						options: filterByTermOptions,
					})
				})

				Object.keys(display_orders).forEach((key, index) => {
					displayOrderOptions.push({
						label: display_orders[key],
						value: key,
					})
				})
			}
		})

		if (this.state.isLoading) {
			return (
				<div>
					<Spinner />
				</div>
			)
		}

		const onChangeMultiselectWithFilterByValue = (filters, showFilters) => {
			let filtersTypeCounts = []
			this.props.onChangeMultiselect(false)
			if (showFilters && filters) {
				for (const element of filters) {
					if (filtersTypeCounts[element.type] && filtersTypeCounts[element.type].length) {
						filtersTypeCounts[element.type].push(element.value)
					} else {
						filtersTypeCounts[element.type] = [element.value]
					}
					if (filtersTypeCounts[element.type].length > 1) {
						this.props.onChangeMultiselect(true)
						break
					}
				}
			}
		}

		return (
			<div>
				{
					<SelectControl
						label={__("Post Type", "")}
						options={postTypeOptions}
						value={this.props.postType}
						onChange={value => {
							const taxonomyTypesAvailable = Object.keys(this.state.termList[value].taxonomies)
							this.props.onChangePostType(value)
							this.props.onChangeFilterByType(taxonomyTypesAvailable.length ? taxonomyTypesAvailable[0] : "")
							this.props.onChangeFilters("")
						}}
					/>
				}
				{filterByTypeOptions.length > 0 && (
					<>
						<div className="components-base-control">
							<label className={"components-base-control__label"}>Filter by Type</label>
							<Select
								isMulti
								hideSelectedOptions={false}
								closeMenuOnSelect={false}
								placeholder={__("Filter by Type", "")}
								options={filterByTypeOptions}
								value={this.props.filterByType}
								onChange={value => {
									this.props.onChangeFilterByType(value)
									onChangeMultiselectWithFilterByValue(value, this.props.showFilters)
								}}
							/>
						</div>
					</>
				)}
				<ToggleControl
					label={__("Show Filters")}
					checked={this.props.showFilters}
					onChange={value => {
						this.props.onChangeShowFilters(value)
						onChangeMultiselectWithFilterByValue(this.props.filterByType, value)
					}}
				/>
				{this.props.showFilters > 0 && (
					<>
						<ToggleControl
							label={__("Allow Multi Select")}
							checked={this.props.multiselect}
							onChange={value => this.props.onChangeMultiselect(value)}
						/>
						<TextControl
							label={__("Filter label", "")}
							type="text"
							value={this.props.filterLabel}
							onChange={value => this.props.onChangeFilterLabel(value)}
						/>

						<div className="components-base-control">
							<label className={"components-base-control__label"}>Filters</label>
							<Select
								isMulti
								hideSelectedOptions={false}
								closeMenuOnSelect={false}
								label={__("Filters", "")}
								options={filtersOptions}
								value={this.props.filters}
								onChange={value => {
									this.props.onChangeFilters(value)
								}}
							/>
						</div>
					</>
				)}
				{displayOrderOptions.length > 0 && (
					<>
						<div className="components-base-control">
							<label className={"components-base-control__label"}>Display Order</label>
							<SortableSelect
								value={this.props.displayOrders}
								onChange={value => {
									this.props.onChangeDisplayOrders(value ?? [])
								}}
								options={displayOrderOptions}
							/>
						</div>
						<div className="components-base-control">
							<label className={"components-base-control__label"}>Popup Display Order</label>
							<SortableSelect
								value={this.props.popupDisplayOrders}
								onChange={value => {
									this.props.onChangePopUpDisplayOrders(value ?? [])
								}}
								options={displayOrderOptions}
							/>
						</div>
						<div className="components-base-control">
							<label className={"components-base-control__label"}>Featured Display Order</label>
							<SortableSelect
								value={this.props.featuredDisplayOrders}
								onChange={value => {
									this.props.onChangeFeaturedDisplayOrders(value ?? [])
								}}
								options={displayOrderOptions}
							/>
						</div>
					</>
				)}
			</div>
		)
	}
}

TaxonomyControl.defaultProps = {
	postType: "post",
	onChangePostType: () => { },

	filterByType: "",
	onChangeFilterByType: () => { },
	showFilters: false,
	onChangeShowFilters: () => { },
	filterLabel: "",
	onChangeFilterLabel: () => { },
	filters: "",
	onChangeFilters: () => { },
	multiselect: false,
	onChangeMultiselect: () => { },
	displayOrders: [
		{
			label: "Image",
			value: "image",
		},
		{
			label: "Title",
			value: "title",
		},
		{
			label: "Date",
			value: "date",
		},
		{
			label: "Excerpt",
			value: "excerpt",
		},
		{
			label: "Read More",
			value: "more",
		},
	],
	onChangeDisplayOrders: () => { },
	popupDisplayOrders: [
		{
			label: "Image",
			value: "image",
		},
		{
			label: "Title",
			value: "title",
		},
		{
			label: "Date",
			value: "date",
		},
		{
			label: "Excerpt",
			value: "excerpt",
		},
	],
	onChangePopUpDisplayOrders: () => { },
	featuredDisplayOrders: [
		{
			label: "Image",
			value: "image",
		},
		{
			label: "Title",
			value: "title",
		},
		{
			label: "Date",
			value: "date",
		},
		{
			label: "Excerpt",
			value: "excerpt",
		},
		{
			label: "Read More",
			value: "more",
		},
	],
	onChangeFeaturedDisplayOrders: () => { },
}

export default TaxonomyControl
