const BackgroundImageAndColorSave = (props) => {
  const {
    deviceSizes,
    deviceSizesForPictureTag,
    backgroundImageAndColorOptions,
    divBlockID,
  } = props;

  let backgroundColorstyle = "";
  deviceSizes.map(([text, deviceSize], index) => {
    let backGroundColorSettings =
      backgroundImageAndColorOptions[deviceSize].settings;
    let backgroundColor = backGroundColorSettings.backgroundColor
      ? backGroundColorSettings.backgroundColor
      : "";

    if (backgroundColor) {
      if (deviceSize === "regular") {
        backgroundColorstyle += `.bs-div.bs-div-${divBlockID} {background-color: ${backgroundColor};} `;
      } else {
        let breakpointValue =
          deviceSize === "mobile"
            ? backgroundImageAndColorOptions[deviceSize].breakpoint - 1
            : backgroundImageAndColorOptions[deviceSize].breakpoint;
        backgroundColorstyle += `@media only screen and (max-width: ${breakpointValue}px) {
             .bs-div.bs-div-${divBlockID} {
                background-color: ${backgroundColor};}}`;
      }
    }
  });

  if (
    backgroundImageAndColorOptions.regular.settings.divImgUrl &&
    backgroundColorstyle
  ) {
    return (
      <>
        <style>{backgroundColorstyle}</style>
        <div className="bs-background">
          <picture>
            {deviceSizesForPictureTag.map(([text, deviceSize], index) => {
              if (deviceSize === "regular") {
                return (
                  <img
                    src={
                      backgroundImageAndColorOptions[deviceSize].settings
                        .divImgUrl
                    }
                    key={deviceSize}
                  />
                );
              } else {
                if (
                  backgroundImageAndColorOptions[deviceSize].settings.divImgUrl
                ) {
                  let breakpointValue =
                    deviceSize === "mobile"
                      ? backgroundImageAndColorOptions[deviceSize].breakpoint -
                        1
                      : backgroundImageAndColorOptions[deviceSize].breakpoint;
                  return (
                    <source
                      srcSet={
                        backgroundImageAndColorOptions[deviceSize].settings
                          .image2xUrl
                          ? `${backgroundImageAndColorOptions[deviceSize].settings.divImgUrl},
                      ${backgroundImageAndColorOptions[deviceSize].settings.image2xUrl} 2x`
                          : `${backgroundImageAndColorOptions[deviceSize].settings.divImgUrl}`
                      }
                      media={`(max-width:${breakpointValue}px)`}
                      key={deviceSize}
                    ></source>
                  );
                } else {
                  return "";
                }
              }
            })}
          </picture>
        </div>
      </>
    );
  } else if (backgroundColorstyle) {
    return <style>{backgroundColorstyle}</style>;
  } else if (backgroundImageAndColorOptions.regular.settings.divImgUrl) {
    return (
      <div className="bs-background">
        <picture>
          {deviceSizesForPictureTag.map(([text, deviceSize], index) => {
            if (deviceSize === "regular") {
              return (
                <img
                  src={
                    backgroundImageAndColorOptions[deviceSize].settings
                      .divImgUrl
                  }
                  key={deviceSize}
                />
              );
            } else {
              if (
                backgroundImageAndColorOptions[deviceSize].settings.divImgUrl
              ) {
                return (
                  <source
                    srcSet={
                      backgroundImageAndColorOptions[deviceSize].settings
                        .image2xUrl
                        ? `${backgroundImageAndColorOptions[deviceSize].settings.divImgUrl},
                  ${backgroundImageAndColorOptions[deviceSize].settings.image2xUrl} 2x`
                        : `${backgroundImageAndColorOptions[deviceSize].settings.divImgUrl}`
                    }
                    media={`(max-width:${backgroundImageAndColorOptions[deviceSize].breakpoint}px)`}
                    key={deviceSize}
                  ></source>
                );
              } else {
                return "";
              }
            }
          })}
        </picture>
      </div>
    );
  } else {
    return "";
  }
};

export default BackgroundImageAndColorSave;
