/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";

/**
 * The save function defines the way in which the different attributes should
 * be combined into the final markup, which is then serialized by the block
 * editor into `post_content`.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#save
 *
 * @return {WPElement} Element to render.
 */
import {
  BackgroundImageSave,
  BackgroundImageAndColorSave,
  GradientSave,
  BackgroundColorSave,
  PictureTagSave,
} from "./_partials";

const save = (props) => {
  const blockProps = useBlockProps.save();
  const { className, attributes, setAttributes } = props;
  const {
    colClassList,
    columnClass,
    extraSmallFlexClassNames,
    smallFlexClassNames,
    mediumFlexClassNames,
    largeFlexClassNames,
    extraLargeFlexClassNames,
    backgroundType,
    columnImgId,
    columnImgUrl,
    columnImgAlt,
    backgroundVideoUrl,
    backgroundVideoName,
    backgroundVideoControls,
    backgroundVideoLoop,
    backgroundVideoMute,
    backgroundVideoAutoPlay,
    columnBlockID,
    columnClassNames,
    gradientOptions,
    backgroundColorOptions,
    backgroundImageOptions,
    pictureTagOptions,
    backgroundImageAndColorOptions,
  } = attributes;

  if (!backgroundImageOptions.regular.settings.columnImgUrl && columnImgUrl) {
    backgroundImageOptions.regular.settings = {
      ...backgroundImageOptions.regular.settings,
      columnImgUrl: columnImgUrl,
    };
  }

  const deviceSizes = [
    ["Regular", "regular"],
    ["Desktop", "desktop"],
    ["Mobile", "mobile"],
  ];

  const deviceSizesForPictureTag = [
    ["Mobile", "mobile"],
    ["Desktop", "desktop"],
    ["Regular", "regular"],
  ];
  const classNames = columnClassNames
    ? columnClassNames.map((x) => x.value).join(" ")
    : "";

  const esFlexClassNames = extraSmallFlexClassNames
    ? extraSmallFlexClassNames.map((x) => x.value).join(" ")
    : "";

  const sFlexClassNames = smallFlexClassNames
    ? smallFlexClassNames.map((x) => x.value).join(" ")
    : "";

  const mFlexClassNames = mediumFlexClassNames
    ? mediumFlexClassNames.map((x) => x.value).join(" ")
    : "";

  const lFlexClassNames = largeFlexClassNames
    ? largeFlexClassNames.map((x) => x.value).join(" ")
    : "";

  const elFlexClassNames = extraLargeFlexClassNames
    ? extraLargeFlexClassNames.map((x) => x.value).join(" ")
    : "";

  return (
    <div
      { ...blockProps }
      className={` ${colClassList.join(
        " "
      )} ${columnClass}  bs-column-${columnBlockID} ${classNames} ${esFlexClassNames} ${sFlexClassNames} ${mFlexClassNames} ${lFlexClassNames} ${elFlexClassNames}`}
    >
      {backgroundType === "background_image" && (
        <BackgroundImageSave
          deviceSizes={deviceSizes}
          backgroundImageOptions={backgroundImageOptions}
          columnBlockID={columnBlockID}
        />
      )}
      {backgroundType === "picture_tag" && (
        <PictureTagSave
          deviceSizesForPictureTag={deviceSizesForPictureTag}
          pictureTagOptions={pictureTagOptions}
        />
      )}
      {backgroundType === "background_color" && (
        <BackgroundColorSave
          deviceSizes={deviceSizes}
          backgroundColorOptions={backgroundColorOptions}
          columnBlockID={columnBlockID}
        />
      )}
      {backgroundType === "background_img_color" && (
        <BackgroundImageAndColorSave
          deviceSizes={deviceSizes}
          backgroundImageAndColorOptions={backgroundImageAndColorOptions}
          columnBlockID={columnBlockID}
          deviceSizesForPictureTag={deviceSizesForPictureTag}
        />
      )}
      {backgroundType === "gradient" && (
        <GradientSave
          deviceSizes={deviceSizes}
          gradientOptions={gradientOptions}
          columnBlockID={columnBlockID}
        />
      )}
      {backgroundType === "background_video" && (
        <div className="bs-column__video-container">
          <video
            loop={backgroundVideoLoop}
            autoPlay={backgroundVideoAutoPlay}
            muted={backgroundVideoMute}
            {...(backgroundVideoControls ? { controls: "controls" } : {})}
            {...(backgroundVideoAutoPlay ? { playsInline: true } : {})}
          >
            <source src={backgroundVideoUrl} type="video/mp4" />
          </video>
        </div>
      )}
      <InnerBlocks.Content />
    </div>
  );
};

export default save;
