import { __ } from "@wordpress/i18n";
import ServerSideRender from "@wordpress/server-side-render";
import TaxonomyControl from "./taxonomy-control";
import {
	TextareaControl,
	PanelBody,
	PanelRow,
	TextControl,
	SelectControl,
	RadioControl,
	RangeControl,
	ToggleControl,
	Disabled,
	Button,
	ResponsiveWrapper,
	Tooltip,
} from "@wordpress/components";
import { useBlockProps } from "@wordpress/block-editor";
import Select from "react-select";
import React from "react";

const { withSelect } = wp.data;
const { InspectorControls, MediaUpload, MediaUploadCheck } = wp.editor;
const { useEffect, useState } = wp.element;
import { capitalize } from "lodash";

import TagSelector from "~berg/ui-components/tag-selector";
import ServerSideRendererError from "~berg/ui-components/server-side-renderer-error";
import ServerSideRendererLoading from "~berg/ui-components/server-side-renderer-loading"
import SortableSelect from "~berg/ui-components/sortable-multi-select";
import Visibility from "~berg/ui-components/visibility";

const Edit = props => {
  const { coreComponents = {}, coreComponentsTheme = {} } = props.blockClasses || {}
  const { attributes, setAttributes, image } = props
  const {
    postBlockClass,
    dateFormat = "d F Y",
    orderBy = "date",
    order = "desc",
    showPlaceholderImage = false,
    placeholderImage = 0,
    titleCharLimit = "",

    postText = "",
    postFrom = "custom_posts",
    postType = "post",
    selectedPosts = "",
    filterByType = "",
    showFilters = false,
    multiselect = false,
    filterLabel = "",
    filters = "",
    displayOrders,
    imageAppearance = "image",
    backgroundImageField,
    popupDisplayOrders,
    popupImageAppearance = "image",
    popupBackgroundImageField,
    featuredDisplayOrders,
    featuredImageAppearance = "image",
    featuredBackgroundImageField,
    anchorElementAppearance = "inner",
    noEntriesFoundText = "Sorry no articles found!",
    layoutDisplayOrders,

    blockType = "all",
    postLayout = "content-grid",

    showFeatured = false,
    featuredText,
    showDefaultFeatured = true,
    showFeaturedFirst = false,
    featuredNum = 1,
    featuredPostSlider,
    displayFeaturedPostOnPostGrid,
    featuredSliderSettings,

    postsInXs = 1,
    postsInSm = 1,
    postsInMd = 3,
    postsInLg = 3,
    postsInXl = 3,

    loadType = "pagination",
    postsNumberFirstLoad = 0,
    postsPerPage = 6,
    prevLabel = " Previous",
    nextLabel = "Next ",
    loadMoreLabel = "Load more",

    showAllOption = false,
    paged = 1,
    postBlockClassNames,
    postVisibility,
    fancyboxStyleClassName,
    custom_load_more_button,
    enablTaxonomiesLink = false,
    titleTag,
		postTextTag,
		lastUpdated,
  } = props.attributes;

  const [postBlockClasses, setPostBlockClasses] = useState([]);
  const [defaultClassValues, setDefaultClassValues] = useState([]);
  const blockProps = useBlockProps();

  // To reload the block ( This option can be used when block rendering fails with a JSON error)
  const reloadBlock = () => {
    setAttributes({
      lastUpdated: Date.now(),
    });
  };

  useEffect(() => {
    const classList = []
    if (coreComponents["bs-post-block"] && coreComponents["bs-post-block"].length > 0) {
      classList.push({
        value: `${coreComponents["bs-post-block"]}`,
        label: "Default",
      });
    } else {
      classList.push({
        value: "",
        label: "Select Class",
      });
    }

    if (coreComponentsTheme["bs-post-block"] && coreComponentsTheme["bs-post-block"].length > 0) {
      coreComponentsTheme["bs-post-block"].map((className, index) => {
        let classLabel = className.replace("bs-post-block--", "").split("-")
        classList.push({
          value: className,
          label: classLabel.map(capitalize).join(" "),
        })
      })
    }
    const defaultValues = postBlockClassNames ? postBlockClassNames : [classList[0]]
    setPostBlockClasses(classList)
    setDefaultClassValues(defaultValues)
  }, [JSON.stringify(props.blockClasses)])

  const onChangeLayoutDisplayOrder = newVal => {
    setAttributes({ layoutDisplayOrders: newVal });
  };
  const onLayoutDisplaySortEnd = ({ oldIndex, newIndex }) => {
    const newValue = arrayMove(layoutDisplayOrders, oldIndex, newIndex);
    setAttributes({ layoutDisplayOrders: newValue });
  };

  const handleCustomLoadMoreButtonChange = mediaObj => {
    setAttributes({
      custom_load_more_button: {
        ...custom_load_more_button,
        image:
          typeof mediaObj === "undefined"
            ? {
              id: 0,
              url: null,
            }
            : {
              id: mediaObj.id,
              url: mediaObj.url,
            },
      },
    });
  };
  const onChangeEnablTaxonomiesLink = enablTaxonomiesLink => {
    setAttributes({ enablTaxonomiesLink });
  };

  const onChangePostTextTag = value => {
    setAttributes({ postTextTag: value });
  };

  const onChangeTitleTag = value => {
    setAttributes({ titleTag: value });
  };

  return (
    <div {...blockProps} key={blockProps.id}>
      <InspectorControls>
        <Visibility { ...{attributeName: 'postVisibility', attributes, setAttributes} }></Visibility>
        <PanelBody title={__("General Settings", "")}>
          <TextControl
            label={__("Date format", "")}
            help={__("For more info: https://codex.wordpress.org/Formatting_Date_and_Time", "")}
            type="text"
            value={dateFormat}
            onChange={dateFormat => setAttributes({ dateFormat })}
          />
          <SelectControl
            label={__("Post Display Order", "")}
            options={[
              { value: "date", label: "Published Date" },
              { value: "id", label: "Post ID" },
              { value: "menu_order", label: "Custom Order (re-order plugin)" },
              {
                value: "meta_event_start_date",
                label: "Event Start Date (Upcoming Event Use Ascending Order",
              },
              {
                value: "meta_event_end_date",
                label: "Event End Date (Past Event Use Descending Order)",
              },
              {
                value: "meta_value_num",
                label: "Most View",
              },
              { value: "custom_date", label: "Custom Date" },
            ]}
            value={orderBy}
            onChange={orderBy => setAttributes({ orderBy })}
          />
          <SelectControl
            label={__("Order", "")}
            options={[
              { value: "desc", label: "DESC" },
              { value: "asc", label: "ASC" },
            ]}
            value={order}
            onChange={order => setAttributes({ order })}
          />
          <ToggleControl
            label={__("Show placeholder image")}
            checked={showPlaceholderImage}
            onChange={showPlaceholderImage => setAttributes({ showPlaceholderImage })}
          />
          {showPlaceholderImage && (
            <div className="components-base-control">
              <label className={"components-base-control__label"}>Placeholder Image</label>
              <div className="editor-post-featured-image media-upload">
                <MediaUploadCheck>
                  <MediaUpload
                    onSelect={value => {
                      setAttributes({ placeholderImage: value.id })
                    }}
                    value={placeholderImage}
                    allowedTypes={["image"]}
                    render={({ open }) => (
                      <>
                        <Button
                          className={
                            placeholderImage === 0
                              ? "editor-post-featured-image__toggle"
                              : "editor-post-featured-image__preview"
                          }
                          onClick={open}
                        >
                          {placeholderImage === 0 && __("Choose the Placeholder Image", "")}
                          {image && (
                            <ResponsiveWrapper
                              naturalWidth={image.media_details.width}
                              naturalHeight={image.media_details.height}
                            >
                              <img src={image.source_url} />
                            </ResponsiveWrapper>
                          )}
                        </Button>

                        {placeholderImage !== 0 && (
                          <div className="editor-post-featured-image__actions">
                            <Button onClick={open} isPrimary className="editor-post-featured-image__action">
                              {__("Replace image", "")}
                            </Button>
                            <Button
                              onClick={value => {
                                setAttributes({ placeholderImage: 0 })
                              }}
                              isSecondary
                              isDestructive
                              className="editor-post-featured-image__action"
                            >
                              {__("Remove image", "")}
                            </Button>
                            </div>
                        )}
                      </>
                    )}
                  />
                </MediaUploadCheck>
              </div>
            </div>
          )}
          <TagSelector label={__("Title tag", "berg")} value={titleTag} onChange={onChangeTitleTag} />
          <TextControl
            label={__("Title character limit", "")}
            type="number"
            value={titleCharLimit}
            onChange={titleCharLimit => setAttributes({ titleCharLimit })}
          />
        </PanelBody>
        <PanelBody title={__("Posts Settings", "")} initialOpen={false}>
          <TextareaControl
            label="Post text"
            help="This will appear top of the layout [before featured]"
            value={postText}
            onChange={postText => setAttributes({ postText })}
          />
          <TagSelector label={__("Post text tag", "berg")} value={postTextTag} onChange={onChangePostTextTag} />
          <TaxonomyControl
            postType={postType}
            onChangePostType={postType => setAttributes({ postType })}
            filterByType={filterByType}
            onChangeFilterByType={filterByType => setAttributes({ filterByType })}
            showFilters={showFilters}
            onChangeShowFilters={showFilters => setAttributes({ showFilters })}
            multiselect={multiselect}
            onChangeMultiselect={multiselect => setAttributes({ multiselect })}
            filterLabel={filterLabel}
            onChangeFilterLabel={filterLabel => setAttributes({ filterLabel })}
            filters={filters}
            onChangeFilters={filters => setAttributes({ filters })}
            displayOrders={displayOrders}
            onChangeDisplayOrders={displayOrders => setAttributes({ displayOrders })}
            popupDisplayOrders={popupDisplayOrders}
            onChangePopUpDisplayOrders={popupDisplayOrders => setAttributes({ popupDisplayOrders })}
            featuredDisplayOrders={featuredDisplayOrders}
            onChangeFeaturedDisplayOrders={featuredDisplayOrders => setAttributes({ featuredDisplayOrders })}
          />

          <RadioControl
            label="Image Appearance"
            options={[
              { label: "Image", value: "image" },
              { label: "Background", value: "background" },
            ]}
            selected={imageAppearance}
            onChange={imageAppearance => setAttributes({ imageAppearance })}
          />
          {imageAppearance === "background" && (
            <SelectControl
              label={__("Select image field", "")}
              options={[
                { value: "image", label: "Default Image" },
                { value: "meta_featured_image", label: "Featured Image" },
              ]}
              value={backgroundImageField}
              onChange={backgroundImageField => setAttributes({ backgroundImageField })}
            />
          )}

          <RadioControl
            label="Popup Image Appearance"
            options={[
              { label: "Image", value: "image" },
              { label: "Background", value: "background" },
            ]}
            selected={popupImageAppearance}
            onChange={popupImageAppearance => setAttributes({ popupImageAppearance })}
          />

          {popupImageAppearance === "background" && (
            <SelectControl
              label={__("Select image field", "")}
              options={[
                { value: "image", label: "Default Image" },
                { value: "meta_featured_image", label: "Featured Image" },
              ]}
              value={popupBackgroundImageField}
              onChange={popupBackgroundImageField => setAttributes({ popupBackgroundImageField })}
            />
          )}

          <RadioControl
            label="Featured Image Appearance"
            options={[
              { label: "Image", value: "image" },
              { label: "Background", value: "background" },
            ]}
            selected={featuredImageAppearance}
            onChange={featuredImageAppearance => setAttributes({ featuredImageAppearance })}
          />

          {featuredImageAppearance === "background" && (
            <SelectControl
              label={__("Select image field", "")}
              options={[
                { value: "image", label: "Default Image" },
                { value: "meta_featured_image", label: "Featured Image" },
              ]}
              value={featuredBackgroundImageField}
              onChange={featuredBackgroundImageField => setAttributes({ featuredBackgroundImageField })}
            />
          )}

          <RadioControl
            label="Anchor Element Appearance"
            options={[
              { label: "Wrap All", value: "full" },
              { label: "Inner Link", value: "inner" },
            ]}
            selected={anchorElementAppearance}
            onChange={anchorElementAppearance => setAttributes({ anchorElementAppearance })}
          />
          {anchorElementAppearance === "inner" && (
            <ToggleControl
              label={__("Enable clickable links for taxonomies")}
              checked={enablTaxonomiesLink}
              onChange={value => {
                onChangeEnablTaxonomiesLink(value)
              }}
            />
          )}

          <TextControl
            label="Fancybox class name"
            value={fancyboxStyleClassName}
            placeholder="Fancybox class name"
            onChange={val =>
              setAttributes({
                fancyboxStyleClassName: val,
              })
            }
          />

          <TextareaControl
            label="No entries found text"
            value={noEntriesFoundText}
            onChange={noEntriesFoundText => setAttributes({ noEntriesFoundText })}
          />
        </PanelBody>
        <PanelBody title={__("Layout Settings", "")} initialOpen={false}>
          <SelectControl
            label={__("Block Type", "")}
            options={[
              { value: "date", label: "All" },
              { value: "upcoming", label: "Upcoming" },
              { value: "past", label: "Past" },
            ]}
            value={blockType}
            onChange={blockType => setAttributes({ blockType })}
          />
          <SelectControl
            label={__("Layout", "")}
            options={[
              { value: "content-grid", label: "Post Blocks" },
              { value: "content-list", label: "Post List" },
              { value: "content-filter", label: "Post Tab Filter" },
            ]}
            value={postLayout}
            onChange={postLayout => setAttributes({ postLayout })}
          />
          {["content-grid", "content-filter"].includes(postLayout) && (
            <>
              <RangeControl
                label={__("No of Posts in Extra Small Devices", "")}
                min={1}
                max={4}
                allowReset={true}
                value={postsInXs}
                onChange={postsInXs => setAttributes({ postsInXs })}
              />
              <RangeControl
                label={__("No of Posts in Small Devices", "")}
                min={1}
                max={4}
                allowReset={true}
                value={postsInSm}
                onChange={postsInSm => setAttributes({ postsInSm })}
              />
              <RangeControl
                label={__("No of Posts in Medium Devices", "")}
                min={1}
                max={4}
                allowReset={true}
                value={postsInMd}
                onChange={postsInMd => setAttributes({ postsInMd })}
              />
              <RangeControl
                label={__("No of Posts in Large Devices", "")}
                min={1}
                max={4}
                allowReset={true}
                value={postsInLg}
                onChange={postsInLg => setAttributes({ postsInLg })}
              />
              <RangeControl
                label={__("No of Posts in Extra Large Devices", "")}
                min={1}
                max={6}
                allowReset={true}
                value={postsInXl}
                onChange={postsInXl => setAttributes({ postsInXl })}
              />
            </>
          )}
          {(postLayout === "content-grid" || postLayout === "content-list") && (
            <>
              <ToggleControl
                label={__("Show Featured Post")}
                checked={showFeatured}
                onChange={showFeatured => setAttributes({ showFeatured })}
              />
              {showFeatured && (
                <>
                  <ToggleControl
                    label={__("Show Default Featured Post")}
                    checked={showDefaultFeatured}
                    onChange={showDefaultFeatured => setAttributes({ showDefaultFeatured })}
                  />
                  <ToggleControl
                    label={__("Show Featured Post only on 1st page")}
                    checked={showFeaturedFirst}
                    onChange={showFeaturedFirst => setAttributes({ showFeaturedFirst })}
                  />
                  <TextareaControl
                    label="Featured text"
                    value={featuredText}
                    onChange={featuredText => setAttributes({ featuredText })}
                  />
                  <RangeControl
                    label="Number of posts to be featured"
                    value={featuredNum}
                    allowReset={true}
                    min={1}
                    max={10}
                    onChange={featuredNum => setAttributes({ featuredNum })}
                  />
                  <ToggleControl
                    label={__("Display Featured Posts on post grid")}
                    checked={displayFeaturedPostOnPostGrid}
                    onChange={displayFeaturedPostOnPostGrid => setAttributes({ displayFeaturedPostOnPostGrid })}
                  />
                  <ToggleControl
                    label={__("Display Featured Posts as a slider")}
                    checked={featuredPostSlider}
                    onChange={featuredPostSlider => setAttributes({ featuredPostSlider })}
                  />
                  {featuredPostSlider && (
                    <>
                      <PanelRow>
                        <ToggleControl
                          label="Slider Arrows"
                          checked={featuredSliderSettings.arrows}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.arrows = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                        />
                      </PanelRow>
                      <PanelRow>
                        <ToggleControl
                          label="Slider Dots"
                          checked={featuredSliderSettings.dots}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.dots = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                        />
                      </PanelRow>
                      <PanelRow>
                        <ToggleControl
                          label="Slider Infinite"
                          checked={featuredSliderSettings.infinite}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.infinite = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                        />
                      </PanelRow>
                      <PanelRow>
                        <RangeControl
                          label="Slider To Show"
                          value={featuredSliderSettings.slidesToShow}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.slidesToShow = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                          min={1}
                          max={12}
                        />
                      </PanelRow>
                      <PanelRow>
                        <RangeControl
                          label="Slides To Scroll"
                          value={featuredSliderSettings.slidesToScroll}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.slidesToScroll = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                          min={1}
                          max={20}
                        />
                      </PanelRow>
                      <PanelRow>
                        <ToggleControl
                          label="Fade"
                          checked={featuredSliderSettings.fade}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.fade = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                        />
                      </PanelRow>
                      <PanelRow>
                        <ToggleControl
                          label="Variable Width"
                          checked={featuredSliderSettings.variableWidth}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.variableWidth = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                        />
                      </PanelRow>
                      <PanelRow>
                        <ToggleControl
                          label="Adaptive Height"
                          checked={featuredSliderSettings.adaptiveHeight}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.adaptiveHeight = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                        />
                      </PanelRow>
                      <PanelRow>
                        <ToggleControl
                          label="Autoplay"
                          checked={featuredSliderSettings.autoplay}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.autoplay = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                        />
                      </PanelRow>
                      {featuredSliderSettings.autoplay && (
                        <PanelRow>
                          <RangeControl
                            label="Autoplay Speed"
                            value={featuredSliderSettings.autoplaySpeed}
                            onChange={newVal => {
                              let settingsCopy = { ...featuredSliderSettings }
                              settingsCopy.autoplaySpeed = newVal
                              setAttributes({
                                featuredSliderSettings: settingsCopy,
                              })
                            }}
                            help="Autoplay Speed in milliseconds"
                            min={500}
                            max={9000}
                          />
                        </PanelRow>
                      )}
                      <PanelRow>
                        <ToggleControl
                          label="Center Mode"
                          checked={featuredSliderSettings.centerMode}
                          onChange={newVal => {
                            let settingsCopy = { ...featuredSliderSettings }
                            settingsCopy.centerMode = newVal
                            setAttributes({
                              featuredSliderSettings: settingsCopy,
                            })
                          }}
                          help="Enables centered view with partial prev/next slides. Use with odd numbered slidesToShow counts."
                        />
                      </PanelRow>
                      {attributes.centerMode && (
                        <PanelRow>
                          <TextControl
                            label="Center Padding"
                            value={featuredSliderSettings.centerPadding}
                            onChange={newVal => {
                              let settingsCopy = { ...featuredSliderSettings }
                              settingsCopy.centerPadding = newVal
                              setAttributes({
                                featuredSliderSettings: settingsCopy,
                              })
                            }}
                          />
                        </PanelRow>
                      )}
                    </>
                  )}
                </>
              )}
              <SelectControl
                label={__("Load Type", "")}
                options={[
                  { value: "pagination", label: "Pagination" },
                  { value: "load", label: "Load More" },
                  { value: "infinite_scroll", label: "Infinite Scroll" },
                ]}
                value={loadType}
                onChange={loadType => setAttributes({ loadType })}
              />
              {loadType === "pagination" && (
                <>
                  <TextControl
                    label={__("Previous button label", "")}
                    type="text"
                    value={prevLabel}
                    onChange={prevLabel => setAttributes({ prevLabel })}
                  />
                  <TextControl
                    label={__("Next button label", "")}
                    type="text"
                    value={nextLabel}
                    onChange={nextLabel => setAttributes({ nextLabel })}
                  />
                </>
              )}
              {(loadType === "load" || loadType === "infinite_scroll") && (
                <>
                  {loadType === "load" && (
                    <TextControl
                      label={__("Load more button label", "")}
                      type="text"
                      value={loadMoreLabel}
                      onChange={loadMoreLabel => setAttributes({ loadMoreLabel })}
                    />
                  )}
                  <PanelRow>{loadType === "load" ? "Load more button image" : "Infinite scroll button image"}</PanelRow>
                  <div className="components-base-control media-preview__row">
                    <div className="editor-post-featured-image">
                      <MediaUploadCheck>
                        <MediaUpload
                          onSelect={handleCustomLoadMoreButtonChange}
                          allowedTypes={["image"]}
                          value={custom_load_more_button.image.id}
                          render={({ open }) => (
                            <>
                              {custom_load_more_button.image.url ? (
                                <>
                                  <Button onClick={open} isPrimary>
                                    {__("Replace image", "")}
                                  </Button>
                                  <Button onClick={() => handleCustomLoadMoreButtonChange()} isSecondary isDestructive>
                                    {__("Remove image", "")}
                                  </Button>
                                </>
                              ) : (
                                <Button className={"editor-post-featured-image__toggle"} onClick={open}>
                                  Choose an image
                                </Button>
                              )}
                            </>
                          )}
                        />
                      </MediaUploadCheck>
                    </div>
                    {custom_load_more_button.image.url ? (
                      <div className="media-preview__img">
                        <img src={custom_load_more_button.image.url} />
                      </div>
                    ) : null}
                  </div>
                </>
              )}
              <RangeControl
                label={__("No of Posts to load on 1st page", "")}
                min={0}
                max={100}
                allowReset={true}
                value={postsNumberFirstLoad}
                onChange={postsNumberFirstLoad => setAttributes({ postsNumberFirstLoad })}
              />
              <RangeControl
                label={__("No of Posts Per Page", "")}
                min={1}
                max={100}
                allowReset={true}
                value={postsPerPage}
                onChange={postsPerPage => setAttributes({ postsPerPage })}
              />
            </>
          )}
          {postLayout === "content-filter" && (
            <>
              <ToggleControl
                label={__("Show All button")}
                checked={showAllOption}
                onChange={showAllOption => setAttributes({ showAllOption })}
              />
            </>
          )}
          <div className="components-base-control">
            <label className={"components-base-control__label"}>Layout Display Order</label>
            <SortableSelect
              value={layoutDisplayOrders}
              onChange={onChangeLayoutDisplayOrder}
              options={[
                {
                  label: "Featured Posts",
                  value: "featured-posts",
                },
                {
                  label: "Filters",
                  value: "filters",
                },
                {
                  label: "Post Grid",
                  value: "post-grid",
                },
              ]}
            />
          </div>
        </PanelBody>
        <PanelBody title={__("Post Block Class", "")} initialOpen={false}>
          <Select
            defaultValue={defaultClassValues}
            isMulti
            name="post-blockClasses"
            options={postBlockClasses}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={val => {
              setAttributes({
                postBlockClassNames: val ? val : [],
              })
            }}
          />
        </PanelBody>
      </InspectorControls>
      <div>
        <div className="button-wrapper">
          <Tooltip text="Reload block">
            <button className="btn btn-default reload" type="button" onClick={reloadBlock}></button>
          </Tooltip>
        </div>
        <Disabled>
          <ServerSideRender
            block="e25m/post-block"
            attributes={attributes}
            urlQueryArgs={{ isEditor: true }}
            httpMethod="POST"
            ErrorResponsePlaceholder={ServerSideRendererError}
            LoadingResponsePlaceholder={ServerSideRendererLoading}
          />
        </Disabled>
      </div>
    </div>
  )
};

export default withSelect((select, ownProps) => {
  const placeholderImage = ownProps.attributes.placeholderImage
  const theme = select("core").getCurrentTheme()
  const { block_classes: blockClasses = {} } = theme || {}
  return {
    image: placeholderImage ? select("core").getMedia(placeholderImage) : null,
    blockClasses,
  }
})(Edit)
