import { InnerBlocks } from '@wordpress/block-editor';
import Slider from 'react-slick';
import { Component, Fragment } from '@wordpress/element';
import { Disabled, Button } from '@wordpress/components';
import NavTitleButtons from './nav-title-buttons';
import { TabContext } from './../utils';
import BlockPreview from "~berg/ui-components/block-preview"

class EditorView extends Component {
	constructor(props) {
		super(props);
		this.setNavSlider = this.setNavSlider.bind(this);
		this.setContentSlider = this.setContentSlider.bind(this);
		this.addNewSlide = this.addNewSlide.bind(this);
		this.changeOrder = this.changeOrder.bind(this);

		const {
			navSlides,
			contentSlides,
			tabNavsSliderOptions,
			tabContentsSliderOptions,
			tabBlockClientId,
		} = props;

		this.state = {
			navSlides,
			contentSlides,
			tabNavsSliderOptions,
			tabContentsSliderOptions,
			navSlider: null,
			contentSlider: null,
			tabBlockClientId: tabBlockClientId,
		};
	}

	static getDerivedStateFromProps(props, state) {
		const stateToWatch = (({
			tabNavsSliderOptions,
			tabContentsSliderOptions,
			navSlides,
			contentSlides,
		}) => ({
			tabNavsSliderOptions,
			tabContentsSliderOptions,
			navSlides,
			contentSlides,
		}))(state);
		const propsToWatch = (({
			tabNavsSliderOptions,
			tabContentsSliderOptions,
			navSlides,
			contentSlides,
		}) => ({
			tabNavsSliderOptions,
			tabContentsSliderOptions,
			navSlides,
			contentSlides,
		}))(props);
		return JSON.stringify(propsToWatch) !== JSON.stringify(stateToWatch)
			? Object.assign({}, state, propsToWatch)
			: state;
	}

	setNavSlider(ref) {
		this.setState({
			navSlider: ref,
		});
	}

	setContentSlider(ref) {
		this.setState({
			contentSlider: ref,
		});
	}

	addNewSlide() {
		const selectedParentBlock = wp.data
			.select('core/block-editor')
			.getBlock(this.state.tabBlockClientId);
		const selectedParentBlockId = selectedParentBlock.clientId;
		const selectedParentBlockTabCount =
			selectedParentBlock.innerBlocks.length;
		const parentTabBlock = wp.blocks.createBlock('e25m/tab-slider-v2-tab');

		// Insert block
		wp.data
			.dispatch('core/block-editor')
			.insertBlocks(
				parentTabBlock,
				selectedParentBlockTabCount,
				selectedParentBlockId,
				true
			);

		// Push to array
		const newNavSlides = this.state.navSlides;
		const newContentSlides = this.state.contentSlides;
		const newNavObject = wp.blocks.createBlock(
			'e25m/tab-slider-v2-tab-nav'
		);
		const newContentObject = wp.blocks.createBlock(
			'e25m/tab-slider-v2-tab-content'
		);

		// Insert child block
		wp.data
			.dispatch('core/block-editor')
			.insertBlocks(newNavObject, 0, parentTabBlock.clientId, true);
		wp.data
			.dispatch('core/block-editor')
			.insertBlocks(newContentObject, 1, parentTabBlock.clientId, true);

		newNavSlides.push(newNavObject);
		newContentSlides.push(newContentObject);
		this.setState({
			navSlides: newNavSlides,
			contentSlides: newContentSlides,
		});
	}

	changeOrder(direction, index) {
		const currentClientId = this.state.navSlides[index].clientId;
		const parentTabClientId = wp.data
			.select('core/block-editor')
			.getBlockRootClientId(currentClientId);
		const parentTabSliderV2ClientId = wp.data
			.select('core/block-editor')
			.getBlockRootClientId(parentTabClientId);

		const newNavSlides = this.state.navSlides;
		const newContentSlides = this.state.contentSlides;
		const slideCount = newNavSlides.length;

		const temp1NavSlide = newNavSlides[index];
		const temp1ContentSlide = newContentSlides[index];

		if (direction === 'back' && index > 0) {
			wp.data
				.dispatch('core/block-editor')
				.moveBlockToPosition(
					parentTabClientId,
					parentTabSliderV2ClientId,
					parentTabSliderV2ClientId,
					index - 1
				);

			const temp2NavSlide = newNavSlides[index - 1];
			const temp2ContentSlide = newContentSlides[index - 1];

			newNavSlides[index] = temp2NavSlide;
			newContentSlides[index] = temp2ContentSlide;

			newNavSlides[index - 1] = temp1NavSlide;
			newContentSlides[index - 1] = temp1ContentSlide;
		} else if (direction === 'next' && index < slideCount - 1) {
			wp.data
				.dispatch('core/block-editor')
				.moveBlockToPosition(
					parentTabClientId,
					parentTabSliderV2ClientId,
					parentTabSliderV2ClientId,
					index + 1
				);

			const temp2NavSlide = newNavSlides[index + 1];
			const temp2ContentSlide = newContentSlides[index + 1];

			newNavSlides[index] = temp2NavSlide;
			newContentSlides[index] = temp2ContentSlide;

			newNavSlides[index + 1] = temp1NavSlide;
			newContentSlides[index + 1] = temp1ContentSlide;
		}

		this.setState({
			navSlides: newNavSlides,
			contentSlides: newContentSlides,
		});
	}

	render() {
		// Editor mode settings
		const editorModeTabNavsSliderSettings = {
			...this.props.editorModeTabNavsSliderSettings.desktop.settings,
			responsive: [
				this.props.editorModeTabNavsSliderSettings.mobile,
				this.props.editorModeTabNavsSliderSettings.tablet,
				this.props.editorModeTabNavsSliderSettings.tabletLandscape,
			],
		};
		const editorModeTabContentsSliderSettings = {
			...this.props.editorModeTabContentsSliderSettings.desktop.settings,
			responsive: [
				this.props.editorModeTabContentsSliderSettings.mobile,
				this.props.editorModeTabContentsSliderSettings.tablet,
				this.props.editorModeTabContentsSliderSettings.tabletLandscape,
			],
		};

		const tabSliderClassNames = this.props.tabSliderClassNames
			.map((x) => x.value)
			.join(' ');
		const navSliderClassNames = this.props.tabNavSliderClassNames
			.map((x) => x.value)
			.join(' ');
		const contentSliderClassNames = this.props.tabContentSliderClassNames
			.map((x) => x.value)
			.join(' ');

		const layoutSwitcher = () => (
			<div className="action-bar">
				<Button
					isPrimary
					onClick={() => {
						this.props.setEditMode(!this.props.editMode);
					}}
				>
					{this.props.editMode
						? 'Switch to preview mode'
						: 'Switch to edit mode'}
				</Button>
			</div>
		);

		return (
			<div
				id={`tab-slider-${this.props.blockID}`}
				className={`bs-tab-slider ${tabSliderClassNames} ${this.props.editMode ? 'edit-mode' : ''
					} ${this.props.tabSliderVisibility ? "enable" : "disable"}`}
			>
				{this.props.editMode ? (
					<Fragment>
						{layoutSwitcher()}
						<TabContext.Provider
							value={{
								[this.props.tabBlockClientId]: {
									tabs: this.props.tabs.map(
										(x) => x.clientId
									),
									editMode: this.props.editMode,
								},
							}}
						>
							<InnerBlocks
								allowedBlocks={['e25m/tab-slider-v2-tab']}
							/>
						</TabContext.Provider>
					</Fragment>
				) : (
					<Fragment>
						{layoutSwitcher()}
						<>
						<Slider
							ref={this.setNavSlider}
							{...editorModeTabNavsSliderSettings}
							className={navSliderClassNames}
							asNavFor={this.state.contentSlider}
						>
							{this.state.navSlides.map((slide, index) => {
								return (
									<div
										className="slick-slide-wrapper text-center"
										key={index}
									>
										<div>
											<BlockPreview blocks={[slide]} />
											<NavTitleButtons
												index={index}
												navSlides={this.state.navSlides}
												contentSlides={
													this.state.contentSlides
												}
												removeCurrentSlide={
													this.props
														.removeCurrentSlide
												}
												changeOrder={this.changeOrder}
											/>
										</div>
									</div>
								);
							})}
						</Slider>
						<Slider
							ref={this.setContentSlider}
							{...editorModeTabContentsSliderSettings}
							className={contentSliderClassNames}
							asNavFor={this.state.navSlider}
						>
							{this.state.contentSlides.map((slide, index) => {
								return (
									<div
										className="slick-slide-wrapper"
										key={index}
									>
										<BlockPreview blocks={[slide]} />
									</div>
								);
							})}
						</Slider>
						<div className="d-none">
							<Disabled>
								<InnerBlocks />
							</Disabled>
						</div>
						</>
					</Fragment>
				)}
			</div>
		);
	}
}

export default EditorView;
