/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";
import { MediaUpload, MediaUploadCheck } from "@wordpress/block-editor";
import { Button, PanelBody } from "@wordpress/components";

//Media Image
const ImageVariations = (props) => {
  const { attributes, setAttributes } = props;
  const {
    imgID,
    imgURL,
    desktopImgID,
    desktopImgURL,
    desktop2xImgID,
    desktop2xImgURL,
    mobileImgID,
    mobileImgURL,
    mobile2xImgID,
    mobile2xImgURL,
    desktopBreakpoint,
    mobileBreakpoint,
  } = attributes;

  // Remove file
  const removeImage = () => {
    setAttributes({
      imgURL: null,
      imgID: 0,
      imgAlt: null,
      hoverImgID: null,
      hoverImgURL: null,
      hoverImgALT: null,
    });
  };

  return (
    <>
      {/* Regular image */}
      <PanelBody
        title={__(`Regular size (Greater than ${desktopBreakpoint}px)`, "")}
        initialOpen={true}
      >
        <div className="components-base-control media-preview__row">
          <div className="editor-post-featured-image">
            <MediaUploadCheck>
              <MediaUpload
                onSelect={(img) =>
                  setAttributes({
                    imgID: img.id,
                    imgURL: img.url,
                    imgAlt: img.imgAlt,
                  })
                }
                allowedTypes={["image"]}
                value={imgID}
                render={({ open }) => (
                  <>
                    {imgURL ? (
                      <>
                        <Button onClick={open} isPrimary>
                          {__("Replace image", "")}
                        </Button>
                        <Button onClick={removeImage} isSecondary isDestructive>
                          {__("Remove image", "")}
                        </Button>
                      </>
                    ) : (
                      <Button
                        className={"editor-post-featured-image__toggle"}
                        onClick={open}
                      >
                        {__("Choose the Image", "")}
                      </Button>
                    )}
                  </>
                )}
              />
            </MediaUploadCheck>
          </div>
          {imgURL ? (
            <div className="media-preview__img">
              <img src={imgURL} />
            </div>
          ) : null}
        </div>
      </PanelBody>
      {/* Desktop image */}
      <PanelBody
        title={__(
          `Desktop (Between ${mobileBreakpoint}px and ${desktopBreakpoint}px)`,
          ""
        )}
        initialOpen={false}
      >
        <div className="components-base-control media-preview__row">
          <div className="editor-post-featured-image">
            <MediaUploadCheck>
              <MediaUpload
                onSelect={(img) =>
                  setAttributes({
                    desktopImgID: img.id,
                    desktopImgURL: img.url,
                  })
                }
                allowedTypes={["image"]}
                value={desktopImgID}
                render={({ open }) => (
                  <>
                    {desktopImgURL ? (
                      <>
                        <Button onClick={open} isPrimary>
                          {__("Replace image", "")}
                        </Button>
                        <Button
                          onClick={() =>
                            setAttributes({
                              desktopImgID: 0,
                              desktopImgURL: null,
                            })
                          }
                          isSecondary
                          isDestructive
                        >
                          {__("Remove image", "")}
                        </Button>
                      </>
                    ) : (
                      <Button
                        className={"editor-post-featured-image__toggle"}
                        onClick={open}
                      >
                        {__("Choose the Image", "")}
                      </Button>
                    )}
                  </>
                )}
              />
            </MediaUploadCheck>
          </div>
          {desktopImgURL ? (
            <div className="media-preview__img">
              <img src={desktopImgURL} />
            </div>
          ) : null}
        </div>
        {/* Desktop 2x image */}
        <div className="components-base-control media-preview__row">
          <div className="editor-post-featured-image">
            <MediaUploadCheck>
              <MediaUpload
                onSelect={(img) =>
                  setAttributes({
                    desktop2xImgID: img.id,
                    desktop2xImgURL: img.url,
                  })
                }
                allowedTypes={["image"]}
                value={desktop2xImgID}
                render={({ open }) => (
                  <>
                    {desktop2xImgURL ? (
                      <>
                        <Button onClick={open} isPrimary>
                          {__("Replace image", "")}
                        </Button>
                        <Button
                          onClick={() =>
                            setAttributes({
                              desktop2xImgID: 0,
                              desktop2xImgURL: null,
                            })
                          }
                          isSecondary
                          isDestructive
                        >
                          {__("Remove image", "")}
                        </Button>
                      </>
                    ) : (
                      <Button
                        className={"editor-post-featured-image__toggle"}
                        onClick={open}
                      >
                        {__("Choose the 2x Image", "")}
                      </Button>
                    )}
                  </>
                )}
              />
            </MediaUploadCheck>
          </div>
          {desktop2xImgURL ? (
            <div className="media-preview__img">
              <img src={desktop2xImgURL} />
            </div>
          ) : null}
        </div>
      </PanelBody>
      {/* Mobile image */}
      <PanelBody
        title={__(`Mobile (Less than ${mobileBreakpoint}px)`, "")}
        initialOpen={false}
      >
        <div className="components-base-control media-preview__row">
          <div className="editor-post-featured-image">
            <MediaUploadCheck>
              <MediaUpload
                onSelect={(img) =>
                  setAttributes({
                    mobileImgID: img.id,
                    mobileImgURL: img.url,
                  })
                }
                allowedTypes={["image"]}
                value={mobileImgID}
                render={({ open }) => (
                  <>
                    {mobileImgURL ? (
                      <>
                        <Button onClick={open} isPrimary>
                          {__("Replace image", "")}
                        </Button>
                        <Button
                          onClick={() =>
                            setAttributes({
                              mobileImgID: 0,
                              mobileImgURL: null,
                            })
                          }
                          isSecondary
                          isDestructive
                        >
                          {__("Remove image", "")}
                        </Button>
                      </>
                    ) : (
                      <Button
                        className={"editor-post-featured-image__toggle"}
                        onClick={open}
                      >
                        {__("Choose the Image", "")}
                      </Button>
                    )}
                  </>
                )}
              />
            </MediaUploadCheck>
          </div>
          {mobileImgURL ? (
            <div className="media-preview__img">
              <img src={mobileImgURL} />
            </div>
          ) : null}
        </div>
        {/* Mobile 2x image */}
        <div className="components-base-control media-preview__row">
          <div className="editor-post-featured-image">
            <MediaUploadCheck>
              <MediaUpload
                onSelect={(img) =>
                  setAttributes({
                    mobile2xImgID: img.id,
                    mobile2xImgURL: img.url,
                  })
                }
                allowedTypes={["image"]}
                value={mobile2xImgID}
                render={({ open }) => (
                  <>
                    {mobile2xImgURL ? (
                      <>
                        <Button onClick={open} isPrimary>
                          {__("Replace image", "")}
                        </Button>
                        <Button
                          onClick={() =>
                            setAttributes({
                              mobile2xImgID: 0,
                              mobile2xImgURL: null,
                            })
                          }
                          isSecondary
                          isDestructive
                        >
                          {__("Remove image", "")}
                        </Button>
                      </>
                    ) : (
                      <Button
                        className={"editor-post-featured-image__toggle"}
                        onClick={open}
                      >
                        {__("Choose the 2x Image", "")}
                      </Button>
                    )}
                  </>
                )}
              />
            </MediaUploadCheck>
          </div>
          {mobile2xImgURL ? (
            <div className="media-preview__img">
              <img src={mobile2xImgURL} />
            </div>
          ) : null}
        </div>
      </PanelBody>
    </>
  );
};

export default ImageVariations;
